import {GameResourceManager} from "./GameResourceManager";
export class SoundMgr {
    public static COOKIE_CF_MUSIC:string = "COOKIE_CF_MUSIC";
    public static COOKIE_CF_SOUND:string = "COOKIE_CF_SOUND";
    private static _instance: SoundMgr;
    public static get instance(): SoundMgr {
        if(SoundMgr._instance ) {
            return SoundMgr._instance
        } 
        SoundMgr._instance = new SoundMgr();
        return SoundMgr._instance;
    }
    /**背景音乐 */
    private _m_bPlayMusic: boolean = true;
    /**音效 */
    private _m_bPlayeSound: boolean = true;
    public m_strMusicURL: string;
    /**背景音乐 */
    public get m_bPlayMusic(): boolean {
        return this._m_bPlayMusic;
    }
    /**背景音乐 */
    public set m_bPlayMusic(value: boolean) {
        this._m_bPlayMusic = value;
        if(value ) {
            if(this.m_strMusicURL != null)
            this.playMusicByURL(this.m_strMusicURL);
        } else {
            if(this.musicChannel ) {
                this.musicChannel.stop();
            } 
        }
    }

    public init():void{
        Laya.SoundManager.autoStopMusic = false;
        // console.log("SoundMgr init1");
        let cookieMusic: string = Laya.LocalStorage.getItem(SoundMgr.COOKIE_CF_MUSIC);
        if(cookieMusic!=null && cookieMusic != "" ) {
            SoundMgr.instance.m_bPlayMusic = parseInt(cookieMusic) == 1? true:false;
            // Laya.SoundManager.setMusicVolume(parseInt(cookieMusic));//背景音乐开关
        }
        // console.log("SoundMgr init1 cookieMusic: "+ cookieMusic);
        let cookieSound: string = Laya.LocalStorage.getItem(SoundMgr.COOKIE_CF_SOUND);
        
        if(cookieSound!=null && cookieSound != "" ) {
            SoundMgr.instance.m_bPlayeSound = parseInt(cookieSound) == 1? true:false;
            // Laya.SoundManager.setSoundVolume(parseInt(cookieSound));//背景音乐开关
        }
        // console.log("SoundMgr init1 cookieSound: " + cookieSound);
    }
    /**音效 */
    public get m_bPlayeSound(): boolean {
        return this._m_bPlayeSound;
    }
    /**音效 */
    public set m_bPlayeSound(value: boolean) {
        this._m_bPlayeSound = value;
    }
    private musicChannel: Laya.SoundChannel;
    /**背景音乐 */
    public playMusicByURL(url: string): void {
        if(!this.m_bPlayMusic ) {
            return
        } 
        if(this.m_strMusicURL ) {
            
        } 
        if(this.m_strMusicURL == url && this.musicChannel ) {
            
        } 
        this.m_strMusicURL = url;
        Laya.SoundManager.stopMusic();
        this.musicChannel = Laya.SoundManager.playMusic(url, 0, new Laya.Handler(this, this.onComplete));
    }
    /**背景音乐 */
    public playMusicByName(name: string, pix: string = ".ogg"): void {
        
        let url: string = GameResourceManager.instance.getSoundURL(name, pix);
        this.playMusicByURL(url);
    }
    /**音效 */
    public playSound(url: string, loops: number = 1): void {
        if(!this.m_bPlayeSound ) {
            return
        } 
        Laya.SoundManager.playSound(url, loops, new Laya.Handler(this, this.onComplete));
    }
    /**音效 */
    public playSoundByName(name: string, loops: number = 1): void {
        let url: string = GameResourceManager.instance.getSoundURLogg(name);
        this.playSound(url, loops);
    }
    /**音效 */
    public stopSoundByName(name: string): void {
        let url: string = GameResourceManager.instance.getSoundURLogg(name);
        Laya.SoundManager.stopSound(url);
    }
    /**背景音乐 */
    public switchMusic(isOpen:boolean):void{
        let v = isOpen?1:0;
        // Laya.SoundManager.setMusicVolume(v);//背景音乐开关
        SoundMgr.instance.m_bPlayMusic = isOpen;
        Laya.LocalStorage.setItem(SoundMgr.COOKIE_CF_MUSIC, v.toString());
        
    }
    /**音效 */
    public switchSound(isOpen:boolean):void{
        let v = isOpen?1:0;
        SoundMgr.instance.m_bPlayeSound = isOpen;
        // Laya.SoundManager.setSoundVolume(v);//音效开关
        Laya.LocalStorage.setItem(SoundMgr.COOKIE_CF_SOUND, v.toString());
        
    }
    public static soundName_bg: string = "bgm";
    public static soundName1: string = "ui01.ogg";
    public static soundName2: string = "ui02.ogg";
    public static soundName3: string = "ui03.ogg";
    public static soundName4: string = "hit.wav";

    public static soundName30_1: string = "ui30_1.ogg";
    public static soundName28_1_star: string = "ui28_1_star.ogg";
    public static soundName28_2_star: string = "ui28_2_stars.ogg";
    public static soundName28_3_star: string = "ui28_3_stars.ogg";
    public static soundName28_bar: string = "ui28_bar_rise.ogg";
    public static soundName32: string = "ui32.ogg";
    public static soundName33: string = "ui33.ogg";
    public static soundName34: string = "ui34.ogg";
    public static soundName35: string = "ui35.ogg";
    public static soundName36: string = "ui36.ogg";
    public static soundName37: string = "ui37.ogg";
    public static soundName38: string = "ui38.ogg";
    public static soundName39: string = "ui39.ogg";
    public static soundName41: string = "ui41.ogg";
    public static soundName42: string = "ui42.ogg";
    public static soundName43: string = "ui43.ogg";
    public static soundName44: string = "ui44.ogg";
    public static soundName45: string = "ui45.ogg";
    public static soundNameUi40_1: string = "ui40_1.ogg";
    public static soundNameUi40_2: string = "ui40_2.ogg";
    public static soundNameUi40_3: string = "ui40_3.ogg";
    public static soundNameDressing: string = "dressing_score_sfx.ogg";
    public static soundNameAnswer: string = "answer_bonus_points.ogg";
    private onComplete(): void {
        
    }
}