import { GameLanguageMgr } from "../../../common/GameLanguageMgr";
import { LoadingManager } from "../../../common/LoadingManager";
import BaseDialog from "../../../common/base/BaseDialog";
import { NoticeMgr } from "../../../module/notice/NoticeMgr";
import HttpCmd from "../../../net/HttpCmd";
import { WebSocketService } from "../../../net/WebSocketService";
import { SheetDataManager } from "../../SheetDataManager";
import { Signal } from "../../event/Signal";
import { NumberUtil } from "../../util/NumberUtil";
import { ETHUnionProxy } from "../service/ETHUnionProxy";
import { UnionAttrDetailVo } from "../vo/UnionAttrDetailVo";
import { UnionAttrVo } from "../vo/UnionAttrVo";


export class UnionAttrLvUpDialog extends BaseDialog {
    private donateValue:number = 1000;
    private vo:UnionAttrVo;
    private maxValue:number;
    private detailVo:UnionAttrDetailVo;
    public constructor(_data: any) {
        super();
        this.vo = _data;
        this.detailVo = new UnionAttrDetailVo();
        this.view.btn_reduce.on(Laya.Event.CLICK,this,this.clickHandler,[this.view.btn_reduce])
        this.view.btn_plus.on(Laya.Event.CLICK,this,this.clickHandler,[this.view.btn_plus])
        this.view.btn_donate.on(Laya.Event.CLICK,this,this.clickHandler,[this.view.btn_donate])
        Signal.intance.on(WebSocketService.CMD_13001+'',this,this.onDonateSuccess)

        for (let index = 0; index < 4; index++) {
            const element:Laya.Button = this.view['p' + index];
            let img:Laya.Image = element.getChildByName('img_select') as Laya.Image;
            img.visible = false;
            element.on(Laya.Event.CLICK,this,this.onClickP,[index])
        }
    }

    public removeEvent(): void {
        super.removeEvent();
        Signal.intance.off(WebSocketService.CMD_13001+'',this,this.onDonateSuccess);
    }

    public get view(): any {
        return this._view as any;
    }
    public dispose(): void {
        super.dispose();
    }
    public createUI(): void {
        this._view = new Laya.View();
        this.addChild(this._view);
    }

    persentAry = [25,50,75,100]
    onClickP(idx: number) {
        console.log('selcet===' + idx);
        if(!this.detailVo.resourcesType){
            return;
        }
        let temp = this.maxValue/100* this.persentAry[idx];
        if(temp < 1000){
            return;
        }
        else{
            this.donateValue = temp - temp%1000;
            this.view.lable_num.text = this.donateValue.toString();
            this.updateContribute();
        }
        
        for (let index = 0; index < 4; index++) {
            const element:Laya.Button = this.view['p' + index];
            let img:Laya.Image = element.getChildByName('img_select') as Laya.Image;
            img.visible = false;
            if(index <= idx){
                img.visible = true;
            }
        }

    }

    async initData() {
        this.view.img_icon.skin = `gameIcon/attricon/img_daoju_${(this.vo.type - 1000)}.png`;
        this.updateView();
    }

    private updateContribute() {
        this.view.label_cb_right.text = GameLanguageMgr.instance.getLanguage('306')  + ` : ${parseInt(this.detailVo.contributeValue) + this.donateValue/1000}`;
    }

    async onDonateSuccess(){
        NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage('307'));
        // await this.updateView();
        LoadingManager.instance.hideLoading();
    }

    async updateView(){
        if(!this.view)return;
        let data = await HttpCmd.requestPost('cybertron-sys-api/api/v1/alliance/attribute/find',{attributeType:this.vo.type});
        Object.assign(this.detailVo,data);

        let itemItem = SheetDataManager.intance.m_dicItems.get(this.detailVo.resourcesType);
        this.view.img_cost.skin = "https://cdn.bunnyswap.cc/images/items/"+itemItem.icon;
        this.detailVo.tokenAddress = itemItem.tokenId;

        // let max = await EthereumManager.instance.getERC20TokenBalance(this.detailVo.tokenAddress);
        let max = WalletModel.instance.getValue(this.detailVo.resourcesType);
        this.view.lable_title.text = `${GameLanguageMgr.instance.getLanguage(this.vo.name)} : ${this.getShowValue(this.detailVo.value)}`;
        this.maxValue = parseFloat(max);
        this.view.lable_progress.text = GameLanguageMgr.instance.getLanguage('305')  + ` \n ${NumberUtil.filterNumFormat(this.detailVo.num)}/${NumberUtil.filterNumFormat(this.detailVo.totalNum)}`;
        this.view.label_attr_left.text = `${GameLanguageMgr.instance.getLanguage(this.detailVo.name)} : ${this.getShowValue(this.detailVo.value)}`;
        this.view.label_attr_right.text = `${GameLanguageMgr.instance.getLanguage(this.detailVo.name)} : ${this.getShowValue(this.detailVo.toValue)}`;
        this.view.label_lv_left.text = GameLanguageMgr.instance.getLanguage('304')  + ` : ${this.detailVo.level}`;
        this.view.label_lv_right.text = GameLanguageMgr.instance.getLanguage('304')  + ` : ${this.detailVo.toLevel}`;
        this.view.label_cb_left.text = GameLanguageMgr.instance.getLanguage('306')  + ` : ${this.detailVo.contributeValue}`;
        this.updateContribute();
    }

    private getShowValue(value:any){
        if(this.detailVo.type === 1003 || this.detailVo.type === 1005){
            return (parseFloat(value + '')*100).toFixed(1) + '%'; 
        }
        else{
            return parseInt(value);
        }
    }

    async clickHandler(node:any) {
        if(!this.detailVo.resourcesType){
            return;
        }
        switch (node) {
            case this.view.btn_reduce:
                if(this.donateValue > 1000){
                    this.donateValue -= 1000;
                }
                this.view.lable_num.text = this.donateValue.toString();
                this.updateContribute();
                break;
            case this.view.btn_plus:
                if(this.donateValue + 1000 <= this.maxValue){
                    this.donateValue += 1000;
                }
                this.view.lable_num.text = this.donateValue.toString();
                this.updateContribute();
                break;
            case this.view.btn_donate:
                if(this.donateValue >this.maxValue){
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(14))
                    return;
                }

                // let accountId = GlobalDataManager.instance.playerDto.id;
                // let amount = (window as any)['web3'].utils.toWei(this.donateValue + '');
                ETHUnionProxy.instance.donate(this.detailVo.resourcesType + '',this.donateValue + '');
                break;
        
            default:
                break;
        }
    }



}