
export class MessageReader {
    public constructor() {
        
    }
    public ReadShort(value: Laya.Byte): number {
        let _array: Uint8Array = new Uint8Array(value.buffer);
        let _index1: number = _array[value.pos] << 8;
        let _index2: number = _array[value.pos + 1];
        let _index3: number = _index1 | _index2;
        value.pos += 2;
        return _index3;
    }
    public ReadInt(value: Laya.Byte): number {
        let _array: Uint8Array = new Uint8Array(value.buffer);
        let _data: number = _array[value.pos] << 24 | _array[value.pos + 1] << 16 | _array[value.pos + 2] << 8 | _array[value.pos + 3];
        value.pos += 4;
        return _data;
    }
    public ReadByte(value: Laya.Byte): number {
        return value.getByte();
    }
    public ReadString(value: Laya.Byte): string {
        let _short: number = this.ReadShort(value);
        if(_short == 0 ) {
            return "";
        } 
        return value.getUTFBytes(_short);
    }
    public ReadBytes(value: Laya.Byte, len: number): Laya.Byte {
        let _buffer1: ArrayBuffer = value.buffer.slice(value.pos, len);
        let _byt: Laya.Byte = new Laya.Byte(_buffer1);
        return _byt;
    }
    public dispose(): void {
        
    }
}