import RoomPlayerMovePush from './RoomPlayerMovePush';

/** 玩家移动列表推送 */
class RoomPlayerMoveListPush {

    /** 多玩家移动数据列表 */
    moveList: Array<RoomPlayerMovePush> = [];

    static PROTOCOL_ID: number = 1042;

    protocolId(): number {
        return RoomPlayerMoveListPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomPlayerMoveListPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writePacketList(packet.moveList, 1018);
    }

    static read(buffer: any): RoomPlayerMoveListPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomPlayerMoveListPush();
        const list0 = buffer.readPacketList(1018);
        packet.moveList = list0;
        return packet;
    }
}

export default RoomPlayerMoveListPush;
