
/** 战斗伤害推送 */
class AttackPush {

    /** 出手攻击者的pid */
    attPid: number = 0;
    /** 挨打方的pid */
    defPid: number = 0;
    /** 本次伤害值 */
    attackValue: Array<number> = [];
    /** 总伤害 */
    allAtkValue: number = 0;
    /** 是否暴击 */
    bj: boolean = false;
    /** 是否闪避 */
    sb: boolean = false;
    /** 技能id */
    skill: number = 0;
    /** 挨打方的剩余血量,<1表示死亡 */
    defHp: number = 0;
    /** 进攻方总积分 */
    attScore: number = 0;
    /** 防守方总积分 */
    defScore: number = 0;

    static PROTOCOL_ID: number = 1024;

    protocolId(): number {
        return AttackPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: AttackPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeLong(packet.allAtkValue);
        buffer.writeLong(packet.attPid);
        buffer.writeLong(packet.attScore);
        buffer.writeLongList(packet.attackValue);
        buffer.writeBoolean(packet.bj);
        buffer.writeLong(packet.defHp);
        buffer.writeLong(packet.defPid);
        buffer.writeLong(packet.defScore);
        buffer.writeBoolean(packet.sb);
        buffer.writeInt(packet.skill);
    }

    static read(buffer: any): AttackPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new AttackPush();
        const result0 = buffer.readLong();
        packet.allAtkValue = result0;
        const result1 = buffer.readLong();
        packet.attPid = result1;
        const result2 = buffer.readLong();
        packet.attScore = result2;
        const list3 = buffer.readLongList();
        packet.attackValue = list3;
        const result4 = buffer.readBoolean(); 
        packet.bj = result4;
        const result5 = buffer.readLong();
        packet.defHp = result5;
        const result6 = buffer.readLong();
        packet.defPid = result6;
        const result7 = buffer.readLong();
        packet.defScore = result7;
        const result8 = buffer.readBoolean(); 
        packet.sb = result8;
        const result9 = buffer.readInt();
        packet.skill = result9;
        return packet;
    }
}

export default AttackPush;
