const { regClass } = Laya;
import { GameEvent } from "../../global/event/GameEvent";
import { Signal } from "../../global/event/Signal";
import { CheckCoinItemBase } from "./CheckCoinItem.generated";

@regClass('UTGHsPbQS6C5FohXv8T25Q')
export class CheckCoinItem extends CheckCoinItemBase {
    
    
    private isAddEvent:boolean = false;

    public constructor() {
        super();
    }

    public createChildren():void{
        super.createChildren();
        // this.visible = false;
    }

    private addEvent(){
        if(!this.isAddEvent){
            this.check_coin.on(Laya.Event.CLICK, this, this.onCheck);
            this.isAddEvent = true;
        }

    }



    public set dataSource(value: any) {
        super.dataSource = value;

        this.addEvent();
        let data = value;
        this.check_coin.selected = data[1];
        this.tf_coin.text = data[2];
    }

    
    private onCheck(){
        // this.check_coin.selected = !this.check_coin.selected;
        this._dataSource[1] = this.check_coin.selected;
        Signal.intance.event(GameEvent.EVENT_CHECK_ICON_SELECT);
    }
}