    private static isShake: boolean = false;
    public static shakeSprs(shakeSprs: any[], times: number = 2, offset: number = 4, speed: number = 32): void {
        if(Quick.isShake ) {
            return;
        } 
        Quick.isShake = true;
        let len: number = shakeSprs.length;
        let points: any[] = [];
        for(let i: number = 0; i < len; i++) {
            let shakeSpr: Laya.Sprite = shakeSprs[i];
            points.push([shakeSpr.x, shakeSpr.y]);
        }
        let offsetXYArray: any[] = [0, 0];
        let num: number = 0;
        let updateShake = function(): void {
            offsetXYArray[num % 2] = num++ % 4 < 2 ? 0 : offset;
            if(num > times * 4 + 1 ) {
                Laya.timer.clear(Laya.stage, updateShake);
                num = 0;
                Quick.isShake = false;
            } 
            for(let j: number = 0; j < len; j++) {
                let shakeSpr: Laya.Sprite = shakeSprs[j];
                shakeSpr.x = offsetXYArray[0] + points[j][0];
                shakeSpr.y = offsetXYArray[1] + points[j][1];
            }
        }
        Laya.timer.loop(speed, Laya.stage, updateShake);
    }

    
    public static shake(dis: Laya.Sprite, times: number = 2, offset: number = 4, speed: number = 32): void {
        if(Quick.isShake ) {
            return;
        } 
        Quick.isShake = true;
        let point: Laya.Point = new Laya.Point(dis.x, dis.y);
        let offsetXYArray: any[] = [0, 0];
        let num: number = 0;
        let updateShake = function():void{
            offsetXYArray[num % 2] = num++ % 4 < 2 ? 0 : offset;
        if(num > times * 4 + 1 ) {
            Laya.timer.clear(Laya.stage, updateShake);
            num = 0;
            Quick.isShake = false;
        } 
        dis.x = offsetXYArray[0] + point.x;
        dis.y = offsetXYArray[1] + point.y;
        }
        Laya.timer.loop(speed, Laya.stage, updateShake);
    }
    public static fullScreen(isFull:boolean): void {
        var jsonString = {"type":"fullScreen", "data":isFull}
        if(Laya.Browser.onIOS) {
            // alert("onIOS");
            if( "undefined" != typeof webkit){
                // alert("onIOS1");
                webkit.messageHandlers._tw_.postMessage(jsonString);
                // alert("onIOS2");
            }
            
        }else if(Laya.Browser.onAndroid){
            if(window && "undefined" != typeof window._tw_){
                window._tw_.callInto(jsonString);
            }
            
        }

    }



