const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { CoinComboListItemBase } from "./CoinComboListItem.generated";

@regClass('nREGzTKFSX6LuiRnM5nPXA')
export class CoinComboListItem extends CoinComboListItemBase {

    isAddEvent: boolean;

    private addEvent(){
        // if(!this.isAddEvent){
            
        //     // this.img_chanchu.on(Laya.Event.CLICK, this, this.onDigGet);
        //     this.selectBox.on(Laya.Event.CHANGE,this, this.onChange);
        //     this.isAddEvent = true;
        // }

    }
    public set dataSource(value: any) {
        super.dataSource = value;
        var coinName:any = value;
        if(!coinName ) {
            return
        }
        if(!coinName.label ) {
            return
        }
        this.addEvent();
        this.tf_name.text = coinName.label;


    }

    onChange(isSelect:boolean){
        if(isSelect){
            this.selectImg.visible = true;
            this.tf_name.color = "#ffffff";
        }else{
            this.tf_name.color = "#357cc0";
            this.selectImg.visible = false;
        }
    }

    onDisable(): void {
        // this.selectBox.off(Laya.Event.CHANGE,this, this.onChange);
    }
}