const { regClass } = Laya;
import { LayerManager } from "../../common/LayerManager";
import { SceneManager } from "../../common/SceneManager";
import { SceneType } from "../../common/baseScene/SceneType";
import { WebSocketService } from "../../net/WebSocketService";
import { MatchReadyRTBase } from "./MatchReadyRT.generated";

@regClass('mOlMVGD3TRCKNoddhyUGLw')
export class MatchReadyRT extends MatchReadyRTBase {

  public m_iLayerType: number;
  public m_iPositionType: number;
  onAwake(): void {
      this.m_iLayerType = LayerManager.M_POP;
      this.m_iPositionType = LayerManager.UP;
      // this.m_ioffsetX = 20;
      // if(GameSetting.m_bIsIphoneX ) {
      //     this.m_ioffsetY = GameSetting.IPHONEX_TOP;
      // } 
  }
  private type: number = 1;
  // private hasFree: boolean = false;

  onOpened(_type: any): void {

      this.type = _type;
  }




  private setToMainScene():void{
      SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN);
  }



    onEnable(): void {

        console.log("MatchReadyRT onEnable");
        // this.generateName();
        //侦听ui按钮点击事件
        if(this.btn_back ) {
          this.btn_back.on(Laya.Event.CLICK, this, this.setToMainScene)
      }
      
        this.btn_random.on(Laya.Event.CLICK, this, () => {
            this.generateName();
        });

        //侦听物理按钮点击事件
        this.btn_enter.on(Laya.Event.CLICK, this, () => {
            //点击后进入
            var username = this.input_username.text;
            console.log("phyBtn");
            if(username == ""){
              alert("用户名不能为空");
              return;
            }
              
            //建立websocket
            WebSocketService.instance.openId = username;
            WebSocketService.instance.initSocket();

            // Laya.Scene.open("scenes/match/Match.ls");
        });


        
    }

    generateRandomName(length: number): string {
        const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';
        let randomName = '';
        for (let i = 0; i < length; i++) {
          const randomIndex = Math.floor(Math.random() * characters.length);
          randomName += characters.charAt(randomIndex);
        }
        return randomName;
      }

      generateName(){
        this.input_username.text = this.generateRandomName(8); // 生成一个长度为8的随机名字
      }
      


    onDestroy(): void {

        // if( this.uiBtn.hasListener( Laya.Event.CLICK ) )
        //     console.log("uiBtn有点击事件监听");

    }
}