import Role from "../../prefab/Role";
import GameManager from "../logic/GameManager";
import KeyBoardManager = Laya.InputManager;
import { Signal } from "../../game/global/event/Signal";
import { GameEvent } from "../../game/global/event/GameEvent";
import Tool from "../util/Tool";
import { MatchModel } from "../../game/module/match/MatchModel";

const { regClass, property } = Laya;

/**
 * @author HJY 
 * @readme 摇杆控制类
 */
@regClass('7cTqWb35RwygSkuWy4QkgQ')
export default class Script extends Laya.Script {
    // 声明属主类型为 Laya.Box
    declare owner: Laya.Box;


    // // 设置私有属性变量
    // private directComponent: Role | null = null;
    private currRockerBar: Laya.Button | null = null;
    private img_freebar_arrow: Laya.Image;


    private freetrack: Laya.Image;


    private img_arrow_left: Laya.Image;
    private img_arrow_right: Laya.Image;
    private img_arrow_up: Laya.Image;
    private img_arrow_down: Laya.Image;

    private maxDistance: number = 80;
    private isMouseDown: boolean = false;
    private mouseDownPosition: Laya.Point = new Laya.Point();
    private _currentMouse: Laya.Point = new Laya.Point();
    private initRockerBarPosition: Laya.Point = new Laya.Point();


    public key1Pressed: boolean = false;
    public key2Pressed: boolean = false;
    public key3Pressed: boolean = false;
    public key4Pressed: boolean = false;
    // public direction: number = -4;
    public directionOffset: number = 0;

    // /** @prop {name:gameMap, tips:"按钮1", type:Button} */
    // @property({type:Laya.Button})
    // private key1: Laya.Button;
    // private key2: Laya.Button;
    // private key3: Laya.Button;
    // private key4: Laya.Button;

    // private isMoving: boolean;
    private touchId: number;
    // private centerX: number;
    // private centerY: number;
    // private _visible: boolean;

    public isKeyRun:boolean = false;

    onEnable(): void {
        //开启多点触控
        Laya.InputManager.multiTouchEnabled = true;
    }



    onStart(): void {
        this.currRockerBar = this.owner.getChildByName("freebar") as Laya.Button;
        this.img_freebar_arrow = this.currRockerBar.getChildByName("img_freebar_arrow") as Laya.Image;


        this.freetrack = this.owner.getChildByName("freetrack") as Laya.Image;

        this.img_arrow_left = this.freetrack.getChildByName("img_arrow_left") as Laya.Image;
        this.img_arrow_right = this.freetrack.getChildByName("img_arrow_right") as Laya.Image;
        this.img_arrow_up = this.freetrack.getChildByName("img_arrow_up") as Laya.Image;
        this.img_arrow_down = this.freetrack.getChildByName("img_arrow_down") as Laya.Image;
        this.img_freebar_arrow.visible = false;
        if (Laya.Browser.onPC) {
            
            this.freetrack.visible = true;
        }else{            
            this.img_arrow_left.visible = 
            this.img_arrow_right.visible = 
            this.img_arrow_up.visible = 
            this.img_arrow_down.visible = false;
        }
        this.initialise();

        // 如果有目标则获取其DirectMove组件
        // if (this.target) {
        //     this.directComponent = this.target.getComponent(Role);
        // }
    }

    // 事件和初始状态的初始化
    private initialise(): void {

        this.initRockerBarPosition.setTo(this.currRockerBar.x, this.currRockerBar.y);

        this.currRockerBar.on(Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
        Laya.stage.on(Laya.Event.MOUSE_MOVE, this, this.onMouseMove);
        Laya.stage.on(Laya.Event.MOUSE_UP, this, this.onMouseUp);
        // Laya.stage.on(Laya.Event.MOUSE_OUT, this, this.onMouseUp);

        // this.key1.on(Laya.Event.MOUSE_DOWN, this, this.onSkillKeyDown, [1, true]);
        // this.key1.on(Laya.Event.MOUSE_UP, this, this.onSkillKeyDown, [1, false]);
        // this.key1.on(Laya.Event.MOUSE_OUT, this, this.onSkillKeyDown, [1, false]);

        // this.key2.on(Laya.Event.MOUSE_DOWN, this, this.onSkillKeyDown, [2, true]);
        // this.key2.on(Laya.Event.MOUSE_UP, this, this.onSkillKeyDown, [2, false]);
        // this.key2.on(Laya.Event.MOUSE_OUT, this, this.onSkillKeyDown, [2, false]);

        // this.key3.on(Laya.Event.MOUSE_DOWN, this, this.onSkillKeyDown, [3, true]);
        // this.key3.on(Laya.Event.MOUSE_UP, this, this.onSkillKeyDown, [3, false]);
        // this.key3.on(Laya.Event.MOUSE_OUT, this, this.onSkillKeyDown, [3, false]);
    }

    onSkillKeyDown(e: Laya.Event, _index:number, isPress:boolean): void {
        e.stopPropagation();
        switch(_index){
            case 1:
                this.key1Pressed = isPress;
                break;
            case 2:
                this.key2Pressed = isPress;
                break;            
            case 3:
                this.key3Pressed = isPress;
                break;
            }
    }

    // 处理鼠标按下事件
    onMouseDown(e: Laya.Event): void {
        if(MatchModel.instance.stopAll)
            return;
        e.stopPropagation();
        this.isMouseDown = true;
        this.currRockerBar!.selected = true;
        if (!Laya.Browser.onPC) {            
            this.freetrack!.visible = true;
        }
        
        this.mouseDownPosition.setTo(this.owner.mouseX, this.owner.mouseY);
        console.log("onMouseDown this.owner.mouse: ", this.owner.mouseX, this.owner.mouseY);
        this.isKeyRun = false;
        this.touchId = e.touchId;
    }

    // 处理鼠标抬起事件
    onMouseUp(e: Laya.Event): void {
        // console.log("onMouseUp 1 ", e);
        if (!this.isMouseDown) {
            return;
        }
        // console.log("onMouseUp 2 ", e);
        this.endClick(e);
    }

    // 结束点击，重置摇杆的位置和鼠标按下状态
    private endClick(e: Laya.Event): void {
        // if (this.touchId == e.touchId) {
            this.isMouseDown = false;
            this.currRockerBar!.x = this.initRockerBarPosition.x;
            this.currRockerBar!.y = this.initRockerBarPosition.y;
            this.currRockerBar!.selected = false;
            // if (!Laya.Browser.onPC) {            
            //     this.freetrack!.visible = false;
            // }

            //停止DirectMove组件的任何移动
            // if (this.directComponent) this.directComponent.roleDirection = -4;
            this.setDir( -4 );
        // }
        
        
    }

    /*
    * 处理鼠标移动事件
    */
    onMouseMove(e: Laya.Event): void {
        if (!this.isMouseDown || !this.currRockerBar) {
            return;
        }


        // if (this.touchId == e.touchId) {
            


            e.stopPropagation();

            this._currentMouse.setTo(e.stageX, e.stageY);
            // console.log("onMouseMove e.stage: ", e.stageX, e.stageY);
            /**将舞台坐标转换为本地坐标 */
            const localMouse = this.owner.globalToLocal(this._currentMouse, false);

            // console.log("onMouseMove localMouse: ", localMouse.x, localMouse.y);

            const distanceX = localMouse.x - this.mouseDownPosition.x;
            const distanceY = localMouse.y - this.mouseDownPosition.y;
            const distance = Math.sqrt(distanceX ** 2 + distanceY ** 2);

            // console.log("onMouseMove mouseDownPosition: ", this.mouseDownPosition.x, this.mouseDownPosition.y);

            // 计算并设置摇杆新的位置
            if (distance < this.maxDistance) {
                this.currRockerBar.x = this.initRockerBarPosition.x + distanceX;
                this.currRockerBar.y = this.initRockerBarPosition.y + distanceY;
            } else {
                const radians = Math.atan2(distanceX, distanceY);
                this.currRockerBar.x = Math.sin(radians) * this.maxDistance + this.initRockerBarPosition.x;
                this.currRockerBar.y = Math.cos(radians) * this.maxDistance + this.initRockerBarPosition.y;
            }



            // 改变 Role 组件的移动方向
            // if (this.directComponent) {
                // const angle = Math.atan2(distanceX, distanceY) * 180 / Math.PI;
                // console.log("angle: " + angle);
                // this.directComponent.directMove(angle);

                if (Math.abs(distanceX) > this.directionOffset || Math.abs(distanceY) > this.directionOffset) {
                    // this.direction = this.angle;

                    let direction = Tool.judgePosition(distanceX, distanceY);
                    console.log("this.direction:" + direction);
                    this.setDir(direction);
                }
            // }
        // }
    }

    onUpdate(): void {
        if(MatchModel.instance.stopAll)
            return;
        this.getKeyboardPressed();
    }

    lastDirection:number;

    private setDir(direction:number){
        if(this.lastDirection != direction){
            GameManager.instance.setMyHeroDirection(direction);
            this.lastDirection = direction;
        }
        if(direction == -4){
            this.img_freebar_arrow.visible = false;

        }else{
            this.img_freebar_arrow.visible = true;
            // 计算箭头的旋转角度
            let angle: number = (direction - 1) * 45;
            // let angle: number = Math.atan2(distanceY, distanceX) * 180 / Math.PI;


            this.img_freebar_arrow.rotation = angle;
        }
        
        
    }




    private getKeyboardPressed() {
        if (KeyBoardManager.hasKeyDown(Laya.Keyboard.W) && KeyBoardManager.hasKeyDown(Laya.Keyboard.A)) {
            this.isKeyRun = true;
            this.setDir( 8 );
        }else if (KeyBoardManager.hasKeyDown(Laya.Keyboard.W) && KeyBoardManager.hasKeyDown(Laya.Keyboard.D)) {
            this.isKeyRun = true;
            this.setDir( 2 );
        }else if (KeyBoardManager.hasKeyDown(Laya.Keyboard.S) && KeyBoardManager.hasKeyDown(Laya.Keyboard.A)) {
            this.isKeyRun = true;
            this.setDir( 6 );
        }else if (KeyBoardManager.hasKeyDown(Laya.Keyboard.S) && KeyBoardManager.hasKeyDown(Laya.Keyboard.D)) {
            this.isKeyRun = true;
            this.setDir( 4 );
        }else if (KeyBoardManager.hasKeyDown(Laya.Keyboard.W)) {
            this.isKeyRun = true;
            this.setDir( 1 );
        } else if (KeyBoardManager.hasKeyDown(Laya.Keyboard.D)) {
            this.isKeyRun = true;
            this.setDir( 3 );
        } else if (KeyBoardManager.hasKeyDown(Laya.Keyboard.S)) {
            this.isKeyRun = true;
            this.setDir( 5 );
        } else if (KeyBoardManager.hasKeyDown(Laya.Keyboard.A)) {
            this.isKeyRun = true;
            this.setDir( 7 );
        } else {
            if(this.isKeyRun){
                this.setDir( -4 );
            }
            
        }
        if (KeyBoardManager.hasKeyDown(Laya.Keyboard.H) || KeyBoardManager.hasKeyDown(Laya.Keyboard.NUMPAD_1)) {
            if(this.key1Pressed) return;
            this.key1Pressed = true;
            Signal.intance.event(GameEvent.EVENT_CLICK_NUM, 1);
            Laya.timer.once(500, this, this.notKey1Pressed);
        }
        if (KeyBoardManager.hasKeyDown(Laya.Keyboard.J) || KeyBoardManager.hasKeyDown(Laya.Keyboard.NUMPAD_2)) {
            if(this.key2Pressed) return;
            this.key2Pressed = true;
            Signal.intance.event(GameEvent.EVENT_CLICK_NUM, 2);
            Laya.timer.once(500, this, this.notKey2Pressed);
        }
        if (KeyBoardManager.hasKeyDown(Laya.Keyboard.K) || KeyBoardManager.hasKeyDown(Laya.Keyboard.NUMPAD_3)) {
            if(this.key3Pressed) return;
            this.key3Pressed = true;
            Signal.intance.event(GameEvent.EVENT_CLICK_NUM, 3);
            Laya.timer.once(500, this, this.notKey3Pressed);
        }
        if (KeyBoardManager.hasKeyDown(Laya.Keyboard.L) || KeyBoardManager.hasKeyDown(Laya.Keyboard.NUMPAD_4)) {
            if(this.key4Pressed) return;
            this.key4Pressed = true;
            Signal.intance.event(GameEvent.EVENT_CLICK_NUM, 4);
            Laya.timer.once(500, this, this.notKey4Pressed);
        }
    }

    private notKey1Pressed(){
        this.key1Pressed = false;
    }
    private notKey2Pressed(){
        this.key2Pressed = false;
    }
    private notKey3Pressed(){
        this.key3Pressed = false;
    }
    private notKey4Pressed(){
        this.key4Pressed = false;
    }




    private getKeyPressed(button: Laya.Sprite) {
        //var pt = button.fromStagePoint(Laya.stage.getMousePoint());
        var pt = button.toParentPoint(new Laya.Point(button.x, button.y));
        if (pt.x > button.x &&
            pt.x < button.x + button.width &&
            pt.y > button.y &&
            pt.y < button.y + button.height) {
            return true;
        }
        // if (Laya.stage.mouseX > button.x &&
        //     Laya.stage.mouseX < button.x + button.width &&
        //     Laya.stage.mouseY > button.y &&
        //     Laya.stage.mouseY < button.y + button.height) {
        //     return true;
        // }
        return false;
    }

    private getDistance(centerX: number, centerY: number, mouseX: number, mouseY: number) {
        let dx: number = centerX - mouseX;
        let dy: number = centerY - mouseY;
        let distance: number = Math.sqrt(dx * dx + dy * dy);
        return distance;
    }


    onDisable(): void {
        //关闭多点触控，否则就无敌了
        Laya.InputManager.multiTouchEnabled = false;
    }



}