import {IBaseSheetItem} from "./IBaseSheetItem";
export class BaseSheetItem implements IBaseSheetItem {
    public constructor() {
        
    }
    public beFill(sourceObj: any): void {
        for(let key in sourceObj) {
            if(this.hasOwnProperty(key) ) {
                let value: any = sourceObj[key];
                if(value instanceof String ) {
                    if(String(value).indexOf("[[") >= 0 ) {
                        value = JSON.parse(value as string)
                    } 
                } 
                this[key] = value;
            } 
        }
    }
    public converNumberStrArr(arr: any[]): any[] {
        let result: any[] = [];
        for(let i: number = 0; i < arr.length; i++) {
            result.push(parseInt(arr[i]))
        }
        return result;
    }
}