const { regClass } = Laya;
import { NoticeDialogBase } from "./NoticeDialog.generated";

@regClass('iB7UvqEcQ1GFKaVEIy6Bmw')
export class NoticeDialog extends NoticeDialogBase {


    public constructor() {
        super();
        
    }

    onEnable(): void {        
    }




    public setOpenData(data: any) {
        
        if(data){
                if(data instanceof Array ){
                    this.alertTitle.text = data[0] as string;
                    this.alertDesc.text = data[1] as string;
                    

                }else{
                    this.alertDesc.text = data as string;
                }
        }
    }

    onDisable(): void {

    }



    
}