    public moveSpeed: number = 6;//配表速度
    private _speed: number = 6;
    public get speed(): number {
        return this._speed;
    }
    public set speed(value: number) {
        this._speed = value;
        this.biasSpeed = Math.round(this.speed * Math.sin(45*Math.PI/180));
        console.log("Role speed2: ", this.speed, this.biasSpeed, this.roomPlayerDto.id);
    }


    

    
    /** 斜线速度 */
    biasSpeed: number = 0;


    private lastDirectStr:string = "left";
    public directStr:string = "left";


    private _roleDirection: number = -1;
    public get roleDirection(): number {
        return this._roleDirection;
    }
    /** 设置方向
     * @param direct 方向
     */
    public set roleDirection(direct: number) {
        this._roleDirection = direct;
        if(direct == -4){
            return;
        }
        if(direct>=2 && direct <= 4){
            this.directStr = "right";
        }else if(direct>=6 && direct <= 8){
            this.directStr = "left";
        }
        if(this.lastDirectStr != this.directStr){
            this.heroBox.scaleX = this.directStr == "left"? 1 : -1;
            this.lastDirectStr = this.directStr;
        }

    }
    /** 设置方向
     * @param direct 方向
     */
    public set myRoleDirection(direct: number) {
       
        if(this.roleDirection != direct){
            this.roleDirection = direct;
            for(var i=1;i<9;i++){
            
                (this as any)["img_d_"+i].visible = i==direct;
    
            }
            this.img_gj.rotation = 45 * (direct - 1);
        }

    }




    /** 是否在控制中 */
    public inControl: boolean = false;



    /**
        攻击时是否不让控制方向
     */
    public isStop:boolean = false;
    /**
        是否锁定动作，不做跑动或静止动作
     */
    public isLockAni:boolean = false;
    /**
        动作是否改变过，改变过，才能触发恢复为跑动或静止动作
     */
    public aniChange:boolean = false;
    /**
        1=全距离位移，可穿越角色，但不能穿越障碍。
        2=位移过程中，命中目标即停止（用于伤害类位移技能）
     */
    public displacementType:number = 0;
    
    
    /** 移动时间戳 */
    timeStamp: number = 0;
    
    /** 当前区域 */
    currArea: number = 0;


    dunpaiSk:Laya.Skeleton;
    beijiSk:Laya.Skeleton;
    huaSpineSk:Laya.SpineSkeleton;


    chushengBackSpineSk:Laya.SpineSkeleton;
    chushengFrontSpineSk:Laya.SpineSkeleton;
    // dunpaiSk:Laya.Skeleton;

    public constructor() {
        super();
    }



    private addEvent(){
        if(!this.isAddEvent){
            // this.img_chanchu.on(Laya.Event.CLICK, this, this.onDigGet);
            // Laya.timer.frameLoop(1, this, this.onFrameUpdate);
            this.isAddEvent = true;
            
        }

    }

    // onFrameUpdate(){
    //     //幽灵状态，存活的玩家不可见
    //     if(this._isRealDead && !GameManager.instance.myHeroSpr._isDead){
    //         this.visible = false;
