    // }


    public set dataSource(value: any) {
        // super.dataSource = value;
        this.roomPlayerDto = value as RoomPlayerDto;
        if(!this.roomPlayerDto ) {
            return
        }
        
        this.pos(this.roomPlayerDto.x, this.roomPlayerDto.y);
        // var role:Role = this.getComponent(Role);
        // role.roleId = this.roomPlayerDto.id;
        // role.roleName = "P" + roomPlayerDto.pid;
        // role.itemId = 10;
        this.addEvent();
        this.speed = this.roomPlayerDto.speed;
        this.currArea = this.roomPlayerDto.area;
        

        console.log("MatchHeroSpr speed: " + this.speed);
        if(this.roomPlayerDto.hp <= 0){
            this._isDead = true;
            this.setDead();
            return;
        }
        this.heroVo = new HeroVo();
        this.heroVo.id = this.roomPlayerDto.heroId;
        this.heroVo.color = this.roomPlayerDto.heroColor;
        // this.heroVo.lv = this.roomPlayerDto.lv;
        this.moveSpeed = this.heroVo.heroItem.mov/30;// 6;

        
        this.setCurrentOutPut();
        this.updataBlood();
    }

    private setDead(){
        console.log("MatchHeroSpr setDead: " + this.isMe);
        this._isRealDead = true;
        this.alpha = 0.5;
        this.img_deadImg.visible = true;
        var oriY = this.img_deadImg.y;
        this.tf_name.visible = false;
        this.box_blood.visible = false;
        this.heroSk.visible = false;
        this.img_deadImg.y = 0;        
        this.img_deadImg.scale(0, 0);

        this.getComponent(Laya.RigidBody).mask = 1;
        var that = this;

        Laya.Tween.to(this.img_deadImg, {y: oriY, scaleX: 1, scaleY: 1 }, 400, Laya.Ease.linearInOut, Laya.Handler.create(this, function(){
            if(that.isMe)
            Signal.intance.event(GameEvent.EVENT_GameScene_Gray);
        }));
        // GameManager.instance.
            // this.getComponent(Laya.RigidBody).type = "kinematic";
    }

    updataBlood(){
        this._healthBar.value = this.roomPlayerDto.hp/this.roomPlayerDto.maxHp;
    }

    

    setCurrentOutPut() {
        // this.roomPlayerDto.speed;// playerVo.speed as number;
        
        this.heroSk.dataSource = this.heroVo;
        this.tf_name.text = StringTool.shortHeroStr(this.roomPlayerDto.name);
        this.img_country.skin = GameResourceManager.instance.getCountryIcon(this.roomPlayerDto.country+"");
        if(this.roomPlayerDto.dire == 0){
            this.roomPlayerDto.dire = 7;
        }
        if(GlobalDataManager.instance.playerDto.pid == this.roomPlayerDto.id){
            this.myRoleDirection = this.roomPlayerDto.dire;
        }else{
            this.roleDirection = this.roomPlayerDto.dire;
        }
        // let star = this.heroVo.heroItem.quality + '';
        // HeroModel.instance.setStar(this, star);
        // this.pos(this.roomPlayerDto.x, this.roomPlayerDto.y);
    }

    
