    // 待机
    idle(): void {
        console.log(`${this.roomPlayerDto.id} 播放 待机 动画`);
        if (this._isDead) {
            return;
        }
        if(this.heroSk.isReady)
            this.heroSk.sk.play(EnumHero.ANI_daiji, true, false);
    }

