    // 受击方法
    takeDamage(attackPush:AttackPush, attHeroSpr:MatchHeroSpr): void {
        var skillId:number = attackPush.skill, damage: number[] = attackPush.attackValue, defHp:number = attackPush.defHp;
        console.log(`${this.roomPlayerDto.id} 受到 ${damage} 伤害s`);
        if (this._isDead) {
            return;
        }

        this.roomPlayerDto.hp = defHp;

        console.log(`${this.roomPlayerDto.id} 受到 ${damage} 伤害`);

        // 更新血条
        this.updateHealthBar(attHeroSpr);

        // 显示伤害数字并播放跳动动画
        this.showDamageLabel(skillId, damage, attackPush);

        // 如果正在攻击中，中断攻击
        if (this._isAttacking) {
            console.log(`${this.roomPlayerDto.id} 正在攻击中，受到攻击中断攻击 暂时没有中断攻击`);
            Laya.timer.clear(this, this.onRealAttack);//暂时没有中断攻击
            // this.backAni();
            this._isAttacking = false;
        }

        // 播放受击动画
        this._isTakingDamage = true;
        this.isLockAni = true;

        
                // Laya.loader.load("resources/UI/role/spineAni/goblins.sk").then((templet: Laya.Templet) => {
                    //创建动画，缓冲区模式为1，可以启用换装
                    if(!this.beijiSk){
                        this.beijiSk = GameManager.instance.beijiTemplet.buildArmature(0);
                        // this.dunpaiSk.x = -45;
                        this.skNode.addChild(this.beijiSk);
                    }

                    this.beijiSk.play(0, false);
                // });

        this.playBeijiAnimation(() => {
            if(this.beijiSk){ //如果身上有被击动画的话
                this.beijiSk.stop();
                this.beijiSk.visible = false;
            }       
            // 攻击结束后造成伤害
            // target.takeDamage(damage);
            // 攻击完成后切回正常动作
            this._isTakingDamage = false;
            this.isLockAni = false;
            this.backAni();
        });

    }

