import { AnimatorControl } from "../AnimatorControl";
import GameManager, { MusicType } from "./GameManager";
import ParticleTool from "./ParticleTool";
import Tool from "../util/Tool";
import EnemyMove from "./EnemyMove";
import Role from "../../prefab/Role";
import { Signal } from "../../game/global/event/Signal";
import { GameEvent } from "../../game/global/event/GameEvent";
const { regClass, property } = Laya;

@regClass('xovkzdhOSuOc_JJKi_7s2g')
export default class PlayerCatch extends Laya.Script {

    @property({ type: AnimatorControl})
    public anim1: AnimatorControl;

    @property({ type: "string"})
    public animname: string = "";
    @property({ type: "boolean"})
    public force: boolean = false;


    // @property({ type: Laya.Sprite})
    // public player: Laya.Sprite;

    @property({ type: "boolean"})
    public isBig: boolean = false;

    @property({ type: Laya.Sprite})
    btn_pic: Laya.Sprite;

    private isCanClick = true;

    constructor() {
        super();
    }

    onStart(): void {
        Signal.intance.on(GameEvent.EVENT_CLICK_NUM3, this, this.onMouseClick);
        
    }

    public onMouseClick(evt: Laya.Event) {
        if (!this.isCanClick) {
            return;
        }
        // if (this.isCanClick) {
        //     this.isCanClick = false;
        // }

        Laya.Tween.to(this.btn_pic, { scaleX: 1.1, scaleY: 1.1 }, 250, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
            Laya.Tween.to(this.btn_pic, { scaleX: 1, scaleY: 1 }, 150, Laya.Ease.linearIn);
        }));

        GameManager.instance.myHeroSpr.getComponent(Role).catchItem();
        GameManager.instance.playSound(MusicType.ePuGong);

        // ParticleTool.playSkillEffect("resources/prefab/eff_play.lh", skillIndex, this.player);
        if (this.anim1) {
            this.anim1.playAnim(this.animname, this.force);
            setTimeout(() => {
                this.anim1.playAnim("idle", this.force);
                this.isCanClick = true;
            }, 500)
        }
    }


    onDestroy(): void {
        Signal.intance.offAllCaller(this);
        
    }


}