import {IBaseView} from "./IBaseView";
import {GameResourceManager} from "../GameResourceManager";
import {LayerManager} from "../LayerManager";
import {LoadingManager} from "../LoadingManager";
import {ModuleManager} from "../ModuleManager";
import {SceneManager} from "../SceneManager";
import {SceneType} from "../baseScene/SceneType";
import {GameSetting} from "../../global/GameSetting";
import {ModuleName} from "../../global/ModuleName";
export class BaseView extends Laya.Script implements IBaseView {
    protected _view: Laya.Scene;
    // protected newRoleSpr: Laya.Sprite;
    public m_iLayerType: number;
    public m_iPositionType: number;
    public m_canTouch_all: boolean;
    public m_ioffsetX: number;
    public m_ioffsetY: number;
    public m_strName: string;
    public m_helpId: number;
    private m_arrMapEvent: any[];
    public m_iAdaptation_Top: number;
    public m_iAdaptation_Buttom: number;
    public m_bAutoResizeWidth: boolean;
    public m_bAutoResizeHeith: boolean;
    public m_bAutoResize: boolean;
    public hideLoadingBySelf: boolean;
    private _loadPaths: any[];
    // public constructor() {
    //     super();
    // }

    onAdded(): void {
        
        this.m_iLayerType = LayerManager.M_PANEL;
        this.m_iPositionType = LayerManager.CENTER;
        this.m_canTouch_all = false;
        this.m_ioffsetX = 0;
        this.m_ioffsetY = 0;
        // this.m_strName;
        // this.m_helpId;
        this.m_arrMapEvent = [];
        this.m_iAdaptation_Top = 0;
        this.m_iAdaptation_Buttom = 0;
        this.m_bAutoResizeWidth = false;
        this.m_bAutoResizeHeith = false;
        this.m_bAutoResize = false;
        this.hideLoadingBySelf = false;
    }
    public get loadPaths(): any[] {
        return this._loadPaths;
    }
    public onStageResize(): void {
        // if((this.m_canTouch_all && this._view) && !GameSetting.usePcUI ) {
        //     this._view.size(LayerManager.instence.stageWidth, LayerManager.instence.stageHeight);
        // } 
        // if((this.m_bAutoResizeWidth || this.m_bAutoResize) && this._view ) {
        //     this._view.width = Laya.stage.width;
        // } 
        // if((this.m_bAutoResizeHeith || this.m_bAutoResize) && this._view ) {
        //     this._view.height = Laya.stage.height;
        // } 
        // LayerManager.instence.setPosition(this, this.m_iPositionType, this.m_ioffsetX, this.m_ioffsetY);
    }
    private setIphoneX(): void {
        if(GameSetting.m_bIsIphoneX && this._view ) {
            let len: number = this._view.numChildren;
            let childs: any[] = [];
            if(len ) {
                for(let i: number = 0, n: number = len; i < n; i++) {
                    let node: Laya.Box = this._view.getChildAt(i) as Laya.Box;
                    if(node.name === "ui_top" ) {
                        if(!isNaN(node.top) ) {
                            node.top += this.m_iAdaptation_Top
                        } else {
                            node.y += this.m_iAdaptation_Top
                        }
                    } else if(node.name == "ui_bottom" ) {
                        node.bottom += this.m_iAdaptation_Buttom;
                    } 
                }
            } 
        } 
    }


    onAwake(): void {

        if(GameSetting.m_bIsIphoneX ) {
            this.m_iAdaptation_Top = GameSetting.IPHONEX_TOP;
            this.m_iAdaptation_Buttom = GameSetting.IPHONEX_BUTTOM;
        } 
        if(this.loadPaths && this.loadPaths.length ) {
            this.hideLoadingBySelf = true;
            Laya.loader.load(this.loadPaths, Laya.Handler.create(this, this.loadPathsComplete));
        } else {
            this.createUI();
            this.createUILater();
            // this.callLater(this.createUILater);
        }
    }

    private createUILater():void{
        
        this.createUIEnd();
    }
    private loadPathsComplete(): void {
        this.createUI();
        this.loadPathsCompleteLater();
        // this.callLater(this.loadPathsCompleteLater);
    }

    private loadPathsCompleteLater(): void {
        
        this.createUIEnd();
        this.onStageResize();
        LoadingManager.instance.hideLoading();
    }
    public createUI(): void {
        
    }
    protected createUIEnd(): void {
        this.setIphoneX();
        this._addEvent();
        this.initData();
        this.onLoaded();
    }
    private _addEvent(): void {
        if(this.btn_com_help ) {
            this.btn_com_help.on(Laya.Event.CLICK, this, this.onHelp)
        }
        if(this.btn_back ) {
            this.btn_back.on(Laya.Event.CLICK, this, this.setToMainScene)
        }
        this.addEvent();
    }
    public addEvent(): void {
        
    }
    public initData(): void {
        
    }
    protected onLoaded(): void {
        
    }
    public removeEvent(): void {
        
    }
    public backToPreScene(): void {
        let preSceneData: any[] = SceneManager.intance.getPreSceneData();
        SceneManager.intance.setCurrentScene(preSceneData[0], preSceneData[1], preSceneData[2], preSceneData[3], false);
    }
    public setToMainScene(): void {
        SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN, true, true, 1);
    }
    public addMapEvent(target: Laya.Sprite, type: string, caller: any, listener: Function, args: any[] = null): void {
        target.on(type, caller, listener, args);
        this.m_arrMapEvent.push(target);
    }
    public removeAllMapEvent(): void {
        if(!this.m_arrMapEvent ) {
            return
        } 
        for(let i: number = 0; i < this.m_arrMapEvent.length; i++) {
            (this.m_arrMapEvent[i] as Laya.Sprite).offAll();
        }
        this.m_arrMapEvent.splice(0, this.m_arrMapEvent.length);
        this.m_arrMapEvent = null;
    }

    public dispose(): void {
        // if(this.newRoleSpr ) {
        //     while(this.newRoleSpr.numChildren > 0) {
        //         this.newRoleSpr.removeChildAt(0)
        //     }
        //     this.newRoleSpr = null;
        // } 
        console.log("<<< BaseView.dispose() className: " + this["constructor"].name + " ==== name: " + this._view.name + " ==== m_strName: " + this.m_strName);
        Laya.stage.off(Laya.Event.RESIZE, this, this.onStageResize);
        if(this.btn_com_help ) {
            this.btn_com_help.off(Laya.Event.CLICK, this, this.onHelp);
        } 
        this.removeEvent();
        this.removeAllMapEvent();
        ModuleManager.intance.removeViewFromModuleManger(this);
        this.m_arrMapEvent = null;
        if(this.m_strName && this.m_strName != "" ) {
            
        } else {
            this.m_strName = this["constructor"].name;
        }
        GameResourceManager.instance.clearModuleUrl(this.m_strName);
        this.m_strName = null;
        if(this._view ) {
            Laya.timer.clearAll(this._view);
            Laya.Tween.clearAll(this._view);
            this._view.offAll();
            this._view.removeSelf();
            this._view.destroy(true);
            this._view = null;
        } 
        Laya.timer.clearAll(this);
        Laya.Tween.clearAll(this);
        // this.offAll();
        // this.graphics.destroy();
        // this.removeSelf();
        // this.destroy(true);
    }
    protected sendData(commandID: number, data: any[] = null, _callBackHandler: Laya.Handler = null, _isShowLoding: boolean = true): void {
        if(data == null ) {
            data = []
        } 
    }
    protected get btn_com_help(): Laya.Button {
        if(this._view ) {
            return this._view.getChildByName("btn_com_help") as Laya.Button
        } else {
            return null
        }
    }
    public get btn_back(): Laya.Button {
        if(this._view ) {
            return this._view.getChildByName("btn_back") as Laya.Button;
        } else {
            return null
        }
    }
    protected onHelp(): void {
        if(this.m_helpId ) {
            ModuleManager.intance.openModule(ModuleName.HelpDialog, this.m_helpId)
        } 
    }
    public resetView(): void {
        // this._view.width = LayerManager.instence.m_iStageWidth;
        // this._view.height = LayerManager.instence.m_iStageHeight;
        this.onStageResize();
    }


}