
/** 短线重连-绑定登陆 */
class LoginBindingRequest {

    /** 前端请求序列 */
    sequence: number = 0;
    /** 登录令牌 */
    token: string = '';

    static PROTOCOL_ID: number = 1007;

    protocolId(): number {
        return LoginBindingRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: LoginBindingRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.sequence);
        buffer.writeString(packet.token);
    }

    static read(buffer: any): LoginBindingRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new LoginBindingRequest();
        if (!buffer.isReadable()) {
            return packet;
        }
        const result0 = buffer.readInt();
        packet.sequence = result0;
        if (!buffer.isReadable()) {
            return packet;
        }
        const result1 = buffer.readString();
        packet.token = result1;
        return packet;
    }
}

export default LoginBindingRequest;
