const { regClass } = Laya;
import { ModuleManager } from "../../common/ModuleManager";
import { ModuleName } from "../../global/ModuleName";
import { SkillItem } from "../../global/data/sheetItem/hero/Skilltem";
import { SkillListItemBase } from "./SkillListItem.generated";

@regClass('VRh1GpjHSyqm75euC37ZKw')
export class SkillListItem extends SkillListItemBase {

    public skillItem: SkillItem;
    private isAddEvent:boolean = false;

    public constructor() {
        super();
    }

    public createChildren():void{
        super.createChildren();
        // this.visible = false;
    }

    private addEvent(){
        if(!this.isAddEvent){
            this.on(Laya.Event.CLICK, this, this.toDetail);
            this.isAddEvent = true;
            // Signal.intance.on(HeroEvent.EVENT_HERO_DRAW_UPDATE_PRODUCTION, this, this.updateProduction);
        }

    }


    public set dataSource(value: any) {
        super.dataSource = value;
        this.skillItem = value as SkillItem;
        if(!this.skillItem ) {
            return
        }
        this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {

        if(!this.skillItem)return;
        this.skillIconItem.dataSource = this.skillItem;

    }

    
    private toDetail(){
        if(!this.skillItem){
            return;
        }

        ModuleManager.intance.openModule(ModuleName.SkillTipDialog, this.skillItem);

    }
}