
/** 登录请求 */
class LoginRequest {

    pid: number = 0;
    roomId: number = 0;
    loginToken: string = '';

    static PROTOCOL_ID: number = 1001;

    protocolId(): number {
        return LoginRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: LoginRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeString(packet.loginToken);
        buffer.writeLong(packet.pid);
        buffer.writeLong(packet.roomId);
    }

    static read(buffer: any): LoginRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new LoginRequest();
        const result0 = buffer.readString();
        packet.loginToken = result0;
        const result1 = buffer.readLong();
        packet.pid = result1;
        const result2 = buffer.readLong();
        packet.roomId = result2;
        return packet;
    }
}

export default LoginRequest;
