import {MessageWrite} from "./MessageWrite";
import {MessageReader} from "./MessageReader";
import {GameSetting} from "../../global/GameSetting";
import {GlobalDataManager} from "../../global/GlobalDataManager";

export class MsgBodyAnaly {
    private static _instance: MsgBodyAnaly;
    public static get instance(): MsgBodyAnaly {
        if(!MsgBodyAnaly._instance ) {
            MsgBodyAnaly._instance = new MsgBodyAnaly();
        } 
        return MsgBodyAnaly._instance;
    }
    public analyData: any;
    private cookie: string = "####1.0.0#0#";
    public setLoginCookie(): void {
        this.cookie = "####1.0.0#" + GameSetting.Login_UDID + "#" + GameSetting.UserAgent;
    }
    private encryptKey: string = "FFIYD#^3LB954JB90fzj*(d)sO7";
    private _curSendIndex: number = -1;
    public constructor() {
        
    }
    public parseCookie(_time: string, _roleID: string, _account: string, _version: string = "1.0.0"): void {
        let _key: string = this.encryptKey + _roleID + _time;
        let _md5: string = MD5Compress(_key);
        _version = GlobalDataManager.instance.M_S_VersionServer;
        let _encryptData: string = _md5 + "#" + _time + "#" + _roleID + "#" + _account + "#" + _version + "#" + this._curSendIndex + "#1" + "#" + GameSetting.Login_UDID + "#" + GameSetting.UserAgent;
        this.cookie = _encryptData;
    }
    public reflashCookie(): void {
        this.parseCookie(this.m_time, this.m_roleID, this.m_accout);
    }
    public analyClient(commandId: number, _data: any[]): Laya.Byte {
        let _write: MessageWrite = new MessageWrite();
        _write.writeString(this.cookie);
        _write.writeShort(commandId);
        let _arrData: any[] = this.analyData[commandId];
        if(_arrData && _arrData.length > 0 ) {
            this.writeByt(_arrData, _data, _write);
        } 
        return _write.writeByt;
    }
    private writeByt(value: any[], _data: any[], _write: MessageWrite): void {
        for(let a in value) {
            let _itemData: any = _data[a];
            if(value[a].indexOf("7/") != -1 ) {
                _write.writeShort(_itemData.length);
                let _arrData: any[] = this.analyData[String(value[a]).split("7/")[1]];
                if(_arrData ) {
                    for(let xm in _itemData) {
                        this.writeByt(_arrData, _itemData[xm], _write);
                    }
                } 
            } else {
                if(value[a] == "1" ) {
                    _write.writeInt(_itemData);
                } else if(value[a] == "2" ) {
                    _write.writeShort(_itemData);
                } else if(value[a] == "3" ) {
                    _write.writeString(_itemData);
                } else if((value[a] == "4" || value[a] == "5") || value[a] == "6" ) {
                    _write.writeShort(_itemData.length);
                    for(let n in _itemData) {
                        if(value[a] == "4" ) {
                            _write.writeInt(_itemData[n]);
                        } else if(value[a] == "5" ) {
                            _write.writeShort(_itemData[n]);
                        } else if(value[a] == "6" ) {
                            _write.writeString(_itemData[n]);
                        } 
                    }
                } 
            }
        }
    }
    public analyServer(byt: Laya.Byte): any[] {
        let _read: MessageReader = new MessageReader();
        let _msgID: number = _read.ReadShort(byt);
        let _arr: any[] = [_msgID];
        let _dataArr: any[] = this.analyData[_msgID];
        if(_dataArr && _dataArr.length > 0 ) {
            _arr = _arr.concat(this.readByt(_dataArr, byt, _read));
        } 
        return _arr;
    }
    public m_time: string;
    public m_roleID: string;
    public m_accout: string;
    public analyCookie(value: string = ""): void {
        console.log("RECE_COOKIE: " + value, 1);
        let _arr: any[] = value.split("#");
        this.m_time = _arr[0];
        this.m_roleID = _arr[1];
        this.m_accout = _arr[2];
        let _index: number = _arr[3];
        this._curSendIndex = _index;
        this._curSendIndex++;
        console.log("cookie:_time:" + this.m_time + "  _roleID：" + this.m_roleID + "	  _accout：" + this.m_accout);
        this.parseCookie(this.m_time, this.m_roleID, this.m_accout);
        GlobalDataManager.instance.timeStamp = parseFloat(this.m_time);
    }
    private readByt(value: any[], _byt: Laya.Byte, _read: MessageReader): any[] {
        let _arr: any[] = [];
        for(let a in value) {
            if(value[a].indexOf("7/") != -1 ) {
                let _len: number = _read.ReadShort(_byt);
                let _arrData: any[] = this.analyData[String(value[a]).split("7/")[1]];
                let _dataArr: any[] = [];
                if(_arrData && _len > 0 ) {
                    for(let m: number = 0; m < _len; m++) {
                        _dataArr.push(this.readByt(_arrData, _byt, _read));
                    }
                } 
                _arr.push(_dataArr);
            } else {
                if(value[a] == "1" ) {
                    _arr.push(_read.ReadInt(_byt));
                } else if(value[a] == "2" ) {
                    _arr.push(_read.ReadShort(_byt));
                } else if(value[a] == "3" ) {
                    _arr.push(_read.ReadString(_byt));
                } else if((value[a] == "4" || value[a] == "5") || value[a] == "6" ) {
                    let _itemLen: number = _read.ReadShort(_byt);
                    let _itemArr: any[] = [];
                    for(let n: number = 0; n < _itemLen; n++) {
                        if(value[a] == "4" ) {
                            _itemArr.push(_read.ReadInt(_byt));
                        } else if(value[a] == "5" ) {
                            _itemArr.push(_read.ReadShort(_byt));
                        } else if(value[a] == "6" ) {
                            _itemArr.push(_read.ReadString(_byt));
                        } 
                    }
                    _arr.push(_itemArr);
                } 
            }
        }
        return _arr;
    }
    public dispose(): void {
        
    }
}

function MD5Compress(_key: string): string {
    throw new Error("Function not implemented.");
}
