

class IntBoolDto {

    /** id */
    id: number = 0;
    /** bool值 */
    value: boolean = false;

    static PROTOCOL_ID: number = 25007;

    protocolId(): number {
        return IntBoolDto.PROTOCOL_ID;
    }

    static write(buffer: any, packet: IntBoolDto | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.id);
        buffer.writeBoolean(packet.value);
    }

    static read(buffer: any): IntBoolDto | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new IntBoolDto();
        const result0 = buffer.readInt();
        packet.id = result0;
        const result1 = buffer.readBoolean(); 
        packet.value = result1;
        return packet;
    }
}

export default IntBoolDto;
