public hasEnoughMoney(id: number, cost: number, showTip: boolean = true): boolean {
    id = parseInt(id.toString());
    cost = parseInt(cost.toString());
    switch(id) {
        // case EnumConsumeType.TYPE_GOLD:
        // {
        //     if(this.playerDto.gold < cost && showTip ) {
        //         Signal.intance.event("open_buy", 3);
        //     } 
        //     return this.playerDto.gold >= cost;
        // }
        // break;

        default://其他代币
        {
            if(this.playerDto.coins[id] < cost && showTip ) {
                Signal.intance.event("open_buy", 4);
            } 
            return this.playerDto.coins[id] >= cost
        }
        break;
    }
}



    public moneyInfoHander(data: any[], autoPatchEvent: boolean = true, needPop: boolean = true): void {
        let totalNeed: boolean = false;
        let needPush: boolean = false;
        let delta: number = 0;
        for(let info of data) {
            needPush = false;
            // if(info[0] == EnumConsumeType.TYPE_GOLD ) {

            //     delta = parseInt(info[1] + "") - this.playerDto.gold;
            //     this.playerDto.gold = parseInt(info[1] + "");

            //     needPush = delta > 0;

            // } else 
            if(info[0] == EnumConsumeType.TYPE_EXP ) {

                delta = parseInt(info[1] + "") - this.playerDto.exp;
                this.playerDto.exp = parseInt(info[1] + "");

                needPush = delta > 0;

            }else {//其他代币的时候
                delta = parseFloat(info[1] + "") - this.playerDto.coins[info[0]];
                this.playerDto.coins[info[0]] = parseFloat(info[1] + "");

                needPush = delta > 0;
            }
            if((needPush && GetItemDataModel.instance.needPop) && needPop ) {
                totalNeed = true;
                GetItemDataModel.instance.addConsumeByServer([info[0], delta]);
            } 
        }
        // this.playerDto.updateRoleBuyTimes(data[1]);
        if(autoPatchEvent ) {
            Signal.intance.event(GameEvent.ROLE_INFO_CHANGE);
        } 
        if(totalNeed && GetItemDataModel.instance.autoPop ) {
            GetItemService.instance.startShow()
        } 
    }

