
/** 首杀推送 */
class FirstBloodPush {

    pid: number = 0;

    static PROTOCOL_ID: number = 1028;

    protocolId(): number {
        return FirstBloodPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: FirstBloodPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeLong(packet.pid);
    }

    static read(buffer: any): FirstBloodPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new FirstBloodPush();
        const result0 = buffer.readLong();
        packet.pid = result0;
        return packet;
    }
}

export default FirstBloodPush;
