    updateExp(){

        let playerDto:PlayerDto = GlobalDataManager.instance.playerDto;
        let rate = GlobalDataManager.instance.getRealExp(playerDto)/GlobalDataManager.instance.getRealNextExp(playerDto);
        if(rate){
            this.txt_exp.text = (rate*100).toFixed(0) + '%'
        }
        else{
            this.txt_exp.text = '0%'
        }
        if(rate > 1){
            rate = 1;
        }
        this.levelTF.text = playerDto.lv.toString();
        this.img_exp.width = 118 * rate;

        // this.tf_force.text = playerDto.power;
    }

