    public setTab(value: number) {
        for(let i = 1;i<4;i++){
            if(value == i){
                (this as any)["btn_"+i].getChildByName("btn_yellowBg").visible = true; 
                ((this as any)["btn_"+i].getChildByName("Label") as Laya.Label).color = "#143268"; 
                // ((this as any)["btn_"+i].getChildByName("Label") as Laya.Label).stroke = 0; 
            }else{
                (this as any)["btn_"+i].getChildByName("btn_yellowBg").visible = false; 
                ((this as any)["btn_"+i].getChildByName("Label") as Laya.Label).color = "#ffffff"; 
                // ((this as any)["btn_"+i].getChildByName("Label") as Laya.Label).stroke = 4;              
            }
        }
    }
    


    onClickTab(_index:number) {
        if(this.currTab == _index)
            return;
        this.setTab(_index);
        if(_index == 1){
                this.arenaLv = 1;
                var shopItems = this.getRoomByType();            
            }else if(_index == 2){
                    this.arenaLv = 2;
                    var shopItems = this.getRoomByType();            
            }else if(_index == 3){
                this.arenaLv = 3;
                    var shopItems = this.getRoomByType(); 
            
            }
        
    }
    async getRoomByType() {

        this.arenaLv;//1：初级竞技场 2：通用竞技场 3：高级
        this.coinTypes;// 货币类型 [3001,3006,3007] ; 3001：cox 3006   3007

        let data = await HttpCmd.requestPost("wxgame/room/list", {lv: this.arenaLv, coinTypeList: this.coinTypes})



        this.list_room.array = data.roomList;


        Laya.timer.once(3000, this, this.getRoomByType);


    }


    

