    private openBuyDialog(type: string): void {
        switch(parseInt(type)) {
            case 1:
            {
                this.clickPlutPower()
            }
            break;
            case 2:
            {
                this.clickFlyBtn()
            }
            break;
            case 3:
            {
                this.buyGoldBtn()
            }
            break;
            case 4:
            {
                this.clickRecharge()
            }
            break;
            // case 5:
            // {
            //     ModuleManager.intance.openModule(ModuleName.GuildProofPrepareView)
            // }
            // break;
        }
    }
