

class AnimationRequest {

    /** 目标pid */
    targetId: number = 0;
    /** 类型,1=animal，2=滑 */
    type: number = 0;
    /** 技能id */
    skillId: number = 0;
    /** 扩展参数 */
    param: Map<string, string> = new Map();

    static PROTOCOL_ID: number = 1031;

    protocolId(): number {
        return AnimationRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: AnimationRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeStringStringMap(packet.param);
        buffer.writeInt(packet.skillId);
        buffer.writeLong(packet.targetId);
        buffer.writeInt(packet.type);
    }

    static read(buffer: any): AnimationRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new AnimationRequest();
        const map0 = buffer.readStringStringMap();
        packet.param = map0;
        const result1 = buffer.readInt();
        packet.skillId = result1;
        const result2 = buffer.readLong();
        packet.targetId = result2;
        const result3 = buffer.readInt();
        packet.type = result3;
        return packet;
    }
}

export default AnimationRequest;
