
/** 获取房间信息 */
class RoomInfoRequest {

    

    static PROTOCOL_ID: number = 1025;

    protocolId(): number {
        return RoomInfoRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomInfoRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        
    }

    static read(buffer: any): RoomInfoRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomInfoRequest();
        
        return packet;
    }
}

export default RoomInfoRequest;
