export class Dictionary {
    public SERIALIZATION_NUM: number;
    public constructor() {
        this.SERIALIZATION_NUM = (new Date()).getTime();
    }
    private _values: any[] = [];
    private _keys: any[] = [];
    public get values(): any[] {
        return this._values;
    }
    public get keys(): any[] {
        return this._keys;
    }
    public set(key: any, value: any): void {
        let index: number = this.indexOf(key);
        if(index >= 0 ) {
            this._values[index] = value;
            return;
        } 
        this._keys.push(key);
        this._values.push(value);
    }
    public unshift(key: any, value: any): void {
        let index: number = this.indexOf(key);
        if(index >= 0 ) {
            this._values[index] = value;
            return;
        } 
        this._keys.unshift(key);
        this._values.unshift(value);
    }
    public indexOf(key: any): number {
        let index: number = this._keys.indexOf(key);
        if(index >= 0 ) {
            return index
        } 
        key = key instanceof String ? Number(key) : (key instanceof Number ? key.toString() : key);
        return this._keys.indexOf(key);
    }
    public get(key: any): any {
        let index: number = this.indexOf(key);
        return index < 0 ? null : this._values[index];
    }
    public remove(key: any): boolean {
        let index: number = this.indexOf(key);
        if(index >= 0 ) {
            this._keys.splice(index, 1);
            this._values.splice(index, 1);
            return true;
        } 
        return false;
    }
    public clear(): void {
        this._values.length = 0;
        this._keys.length = 0;
    }
}