const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { ETHBlindBoxProxy } from "../../global/contract/service/ETHBlindBoxProxy";
import { ShopItem } from "../../global/data/sheetItem/shop/ShopItem";
import { NumberUtil } from "../../global/util/NumberUtil";
import { XUtils } from "../../util/XUtils";
import { ShopListItemBase } from "./ShopListItem.generated";

@regClass('awS07ko0RBaxCnxXWqiR4g')
export class ShopListItem extends ShopListItemBase {


    public shopItem: ShopItem;
    private isAddEvent:boolean = false;
    
    public constructor() {
        super();
    }


    //橙色  fdd378

    public set dataSource(value: any) {
        super.dataSource = value;
        this.shopItem = value as ShopItem;
        if(!this.shopItem ) {
            return
        }
        // this.addEvent();
        this.setCurrentOutPut();
    }



    async setCurrentOutPut() {

        
        
        this.tf_name.text = GameLanguageMgr.instance.getLanguage(this.shopItem.title);
            // this.tf_des.text = GameLanguageMgr.instance.getLanguage(this.shopItem.des);
        this.img_const.skin = GameResourceManager.instance.getMoneyIcon(this.shopItem.cost[0]);
        this.tf_const.text = NumberUtil.formatNumber(this.shopItem.cost[1])+"";

        if(this.shopItem.type == 1){

            this.tf_name.color = XUtils.qualityColors[parseInt(this.shopItem.icon) - 1];
            

            this.img_bg.skin =  GameResourceManager.instance.getHeroQuality(this.shopItem.icon);
            this.img_icon.skin = GameResourceManager.instance.getHeroEggIcon(this.shopItem.icon);
            // this.box_count.visible = false;


        }
    }




    
}