

class AnimationResponse {

    errorCode: number = 0;

    static PROTOCOL_ID: number = 1032;

    protocolId(): number {
        return AnimationResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: AnimationResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.errorCode);
    }

    static read(buffer: any): AnimationResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new AnimationResponse();
        const result0 = buffer.readInt();
        packet.errorCode = result0;
        return packet;
    }
}

export default AnimationResponse;
