

class AnimationPush {

    /** 出手者pid */
    attId: number = 0;
    /** 目标pid */
    defId: number = 0;
    /** 类型,1=animal，2=滑 */
    type: number = 0;
    /** 技能id */
    skillId: number = 0;
    /** 扩展参数 */
    param: Map<string, string> = new Map();

    static PROTOCOL_ID: number = 1034;

    protocolId(): number {
        return AnimationPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: AnimationPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeLong(packet.attId);
        buffer.writeLong(packet.defId);
        buffer.writeStringStringMap(packet.param);
        buffer.writeInt(packet.skillId);
        buffer.writeInt(packet.type);
    }

    static read(buffer: any): AnimationPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new AnimationPush();
        const result0 = buffer.readLong();
        packet.attId = result0;
        const result1 = buffer.readLong();
        packet.defId = result1;
        const map2 = buffer.readStringStringMap();
        packet.param = map2;
        const result3 = buffer.readInt();
        packet.skillId = result3;
        const result4 = buffer.readInt();
        packet.type = result4;
        return packet;
    }
}

export default AnimationPush;
