
/** 玩家移动请求 */
class RoomMoveRequest {

    x: number = 0;
    y: number = 0;
    /** 速度 */
    speed: number = 0;
    /** 方向 */
    dire: number = 0;
    time: number = 0;
    area: number = 0;

    static PROTOCOL_ID: number = 1013;

    protocolId(): number {
        return RoomMoveRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomMoveRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.area);
        buffer.writeInt(packet.dire);
        buffer.writeInt(packet.speed);
        buffer.writeLong(packet.time);
        buffer.writeInt(packet.x);
        buffer.writeInt(packet.y);
    }

    static read(buffer: any): RoomMoveRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomMoveRequest();
        const result0 = buffer.readInt();
        packet.area = result0;
        const result1 = buffer.readInt();
        packet.dire = result1;
        const result2 = buffer.readInt();
        packet.speed = result2;
        const result3 = buffer.readLong();
        packet.time = result3;
        const result4 = buffer.readInt();
        packet.x = result4;
        const result5 = buffer.readInt();
        packet.y = result5;
        return packet;
    }
}

export default RoomMoveRequest;
