const { regClass } = Laya;
import { TxtNoticeViewBase } from "./TxtNoticeView.generated";

@regClass('Nm27xWbYSG6mj-EXqPOZ_A')
export class TxtNoticeView extends TxtNoticeViewBase {

    onEnable(): void {
        
    }
    public notice(_notice:string): void {
        
        let value: string = _notice;
        this.label_tf.text = value;
        this.label_tf.x = 68;
        this.image_bg.width = 68 * 2 + this.label_tf.textField.textWidth;
        let line: number = value.length / 45;
        if(line > 1 ) {
            this.image_bg.height = this.label_tf.textField.textHeight + 25;
        } 
        this.alpha = 1;
        this.visible = true;
    }
    public get view(): any {
        return this;
    }
    public get width(): number {
        return this.image_bg.width;
    }

    onDisable(): void {
        console.log("txtNotice recover");
        Laya.Pool.recover("txtNotice", this);
    }
}