@regClass('3CibwwHGSPG2Qkeq4rbMag')
export class SelectHeadDialog extends SelectHeadDialogBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;



    public setOpenData(_data: any) {


    }


    onEnable(): void {
        
        this.list_head.vScrollBarSkin = "";
        
        // this.btn_close.on(Laya.Event.CLICK, this, this.onClose);
        this.btn_confirm.on(Laya.Event.CLICK, this, this.onRegister);
        
        this.btn_custom.on(Laya.Event.CLICK, this, this.onBtnChangePic);
        



        this.input_name.prompt =  GameLanguageMgr.instance.getLanguage('31');

    
        this.initContainer();        
    }


    // private onRenderList(cell: Laya.Box, index: number): void {
    //     let data = cell.dataSource as HeadItem;
    //     let selected = cell.getChildByName("selected") as Laya.Image;
    //     selected.visible = this.list_head.selectedIndex == index;
    //     let emojiImg:Laya.Button = cell.getChildByName("img_emoji") as Laya.Button;
    //     emojiImg.skin = GameResourceManager.instance.getPlayerIcon(data.id);
    // }

    private initContainer(): void {
        this.list_head.vScrollBarSkin = "";
        this.list_head.selectHandler = new Laya.Handler(this, this.handleSelectHead);
        this.createHeadList();

        
        this.initCountry();

    }
    private handleSelectHead(index: number): void {
        if(index != -1){
            let headItem: HeadItem = this.list_head.selectedItem;
            GlobalDataManager.instance.headId = headItem.id;
            this.playerIcon.headSkin = GlobalDataManager.instance.headId;
        }
    }
    private createHeadList(): void {
        // let arr: any[] = [];
        let m_dicHead: Dictionary = SheetDataManager.intance.m_dicHead;

        this.list_head.array = m_dicHead.values;
        this.list_head.visible = true;
        let selectIndex = -1;
        let i = 0;
        GlobalDataManager.instance.headId = GlobalDataManager.instance.playerDto.icon;
        this.playerIcon.headSkin = GlobalDataManager.instance.headId;//设置当前角色头像；

        this.input_name.text = GlobalDataManager.instance.playerDto.name;

        for(let entry of m_dicHead.values){
            if(entry.id == GlobalDataManager.instance.headId){
                selectIndex = i;
                break;
            }
            i++;
        }
        this.list_head.selectedIndex = selectIndex;
    }

//=======================  国家  ====================

initCountry(){
    this.list_country.selectHandler = new Laya.Handler(this, this.handleSelectCountry);
        this.createCountryList();
        this.box_country.on(Laya.Event.CLICK, this, this.onOpenCountry);
        this.list_country.on(Laya.Event.MOUSE_DOWN, this, this.onListDown);
        this.list_country.mouseHandler = Laya.Handler.create(this, this.onCountryItemMouse, null, false);
        this.list_country.renderHandler = Laya.Handler.create(this, this.onCountryItemRender, null, false);
        if (this.list_country.scrollBar) this.list_country.scrollBar.on(Laya.Event.MOUSE_DOWN, this, this.onScrollBarDown);
}
    /**
     * @private
     * 下拉列表的鼠标事件响应函数。
     */
    protected onCountryItemMouse(e: Event, index: number): void {
        let type: string = e.type;
        if (type === Laya.Event.CLICK) {
            this.list_country.selectedIndex = index;
            this.onHideCountry();
            // this.isOpen = false;
        }
    }
    
    /**
     * @private
     */
    private onListDown(e: Event): void {
        e.stopPropagation();
    }

    private onScrollBarDown(e: Event): void {
        e.stopPropagation();
    }

    private onCountryItemRender(cell: any, index: number): void {
        cell.onChange(this.list_country.selectedIndex == index);
    }
    
onOpenCountry(){
    if(this.img_country_pad.visible == false){
        this.img_country_pad.visible = true;
        Laya.stage.once(Laya.Event.MOUSE_DOWN, this, this.onHideCountry);
    }
}

onHideCountry(){
    this.img_country_pad.visible = false;
    Laya.stage.off(Laya.Event.MOUSE_DOWN, this, this.onHideCountry);
}


    private handleSelectCountry(index: number): void {
        console.log("handleSelectCountry: " + index);
        if(index != -1){
            let countryItem: CountryItem = this.list_country.selectedItem;
            GlobalDataManager.instance.countryId = countryItem.id;
            this.img_flag.skin = GameResourceManager.instance.getCountryIcon(countryItem.id);
            this.tf_country.text = GameLanguageMgr.instance.getLanguage(countryItem.name);
        }
    }
    private createCountryList(): void {
        // let arr: any[] = [];
        let m_dicCountry: Dictionary = SheetDataManager.intance.m_dicCountry;

        this.list_country.array = m_dicCountry.values;
        this.list_country.visible = true;
        let selectIndex = -1;
        let i = 0;
        GlobalDataManager.instance.countryId = GlobalDataManager.instance.playerDto.country;


        for(let entry of m_dicCountry.values){
            if(entry.id == GlobalDataManager.instance.countryId){
                selectIndex = i;
                break;
            }
            i++;
        }
        this.list_country.selectedIndex = selectIndex;
    }

//=======================  国家  =======================

/** 切换 用户上传图片  */
onBtnChangePic():void {

    // var _openUploadPicBack:Function = this.openUploadPicBack;
    var zIndex = Laya.Render.canvas.style.zIndex;
    var uploadP = GameLanguageMgr.instance.getUILang("Upload picture");
    var selectI = GameLanguageMgr.instance.getUILang("Select Image");
    var saveC = GameLanguageMgr.instance.getUILang("Save changes");
    openUploadPic(this, zIndex, uploadP, selectI, saveC);
    
}

/** 切换 用户上传图片返回  */
async openPicEnd(openWin:SelectHeadDialog, base64:string):Promise<void>{
    if(base64.length>500){
        // this.img_head.skin = base64;
        let temp = await HttpCmd.requestPost('wxgame/player/uploadImg',{base64Str: base64}, false, false);
        if(temp){
            GlobalDataManager.instance.headId = temp.imgUrl;
            openWin.playerIcon.headSkin = temp.imgUrl;
            openWin.list_head.selectedIndex = -1;
            console.log(temp.imgUrl);
        }

    }
}











    


    onRegister() {
        let createName:string = this.input_name.text;
        // let headId:number = this.headIcon;
        createName = createName.trim();
        // let bytesCount:number = 0;
        //     for (var i = 0; i < createName.length; i++)
        //     {
        //         var c = createName.charAt(i);
        //         if (/^[\u0000-\u00ff]$/.test(c)) //匹配双字节
        //             {
        //                 bytesCount += 1;
        //             }
        //         else
        //             {
        //                 bytesCount += 2;
        //             }
        //     }   

        //         if(bytesCount >12){
        //             NoticeMgr.instance.notice("The length entered exceeds the limit");
        //             return;
        //         }
                // if(temp == ""){
                //     NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(31));
                //     return;
                // }
                
                if(createName == ""){
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(31));//Please enter you name
                    return;
                }
                if( GlobalDataManager.instance.headId != -1){
                    
                    this.onModify(createName, GlobalDataManager.instance.headId, GlobalDataManager.instance.countryId);               

                }else{
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(134));//Please select a portrait
                }

    }
    async onModify(createName:string, headId:number, countryId:number) {

                if(createName == ""){
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(31));//Please enter you name
                    return;
                }

                // this.close();
                // return;

                let playerDto:PlayerDto = await ETHAccountProxy.instance.updateProfile(createName, headId.toString(), countryId);
                if(this.destroyed){
                    return;
                }

                if(playerDto){
                    GlobalDataManager.instance.playerDto = playerDto;
                    this.close();
                    Signal.intance.event(GameEvent.ROLE_INFO_CHANGE);

                }else{
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
                }
    }

    
}