import GameManager from "../../../script/logic/GameManager";
import { GameResourceManager } from "../../common/GameResourceManager";
import { ModuleManager } from "../../common/ModuleManager";
import { SoundMgr } from "../../common/SoundMgr";
import { ModuleName } from "../../global/ModuleName";
import { SheetDataManager } from "../../global/SheetDataManager";
import { RoomDto } from "../../global/contract/vo/RoomDto";
import { RoomPlayer } from "../../global/contract/vo/RoomPlayer";
import { HeroItem } from "../../global/data/sheetItem/hero/HeroItem";
import { ArenaItem } from "../../global/data/sheetItem/match/ArenaItem";
import HttpCmd from "../../net/HttpCmd";
import { WebSocketService } from "../../net/WebSocketService";
import ItemDto from "../../net/protocol/dto/ItemDto";
import MatchDto from "../../net/protocol/dto/MatchDto";
import RoomRankPlayerDto from "../../net/protocol/dto/RoomRankPlayerDto";
import { Dictionary } from "../../util/Dictionary";
import { GetItemDataModel } from "../item/model/GetItemDataModel";
import { GetItemService } from "../item/service/GetItemService";



export class MatchModel {
    private static _instance: MatchModel;
    public static get instance(): MatchModel {
        if(!MatchModel._instance ) {
            MatchModel._instance = new MatchModel()
        } 
        return MatchModel._instance;
    }
    private _inited: boolean = false;



    public constructor() {
        this.init();
    }

    private init(): void {
        if(!this._inited ) {
            this._inited = true;

        } 
    }



    public roomDto:RoomDto;
    public coolDownTime:number;
    public limitCd:number;
    public startCoolDownTime:number;//开始时准备时间

    public currArenaItem:ArenaItem;


    /** 没有开始前不能打 */
    public stopAll:boolean = false;


    
    private _matchDto: MatchDto;
    public get matchDto(): MatchDto {
        return this._matchDto;
    }
    public set matchDto(value: MatchDto) {
        this._matchDto = value;
        if( value && (value.startTime - value.nowTime) > 0 ){
            this.stopAll = true;
        }else{
            this.stopAll = false;
        }
    }
    public matchCoolDownTime:number;
    public reward:ItemDto[];


    private heroReadyDic:Dictionary = new Dictionary();


    setMatchRes(){
        // console.log(" ================ setMatchRes: 0");
        this.heroReadyDic = new Dictionary();
        var len = this.roomDto.members.length;

        var loadArr:any[] = [];

        var mapId = this.currArenaItem.mapid;
        if(this.heroReadyDic.get("map_"+mapId) == null){

            var mapId = MatchModel.instance.currArenaItem.mapid;
            
            var mapImg = GameResourceManager.instance.getMapImg(mapId);
            loadArr.push(mapImg);
            var mapPrefab = GameResourceManager.instance.getMapPrefab(mapId);
            loadArr.push(mapPrefab);
            this.heroReadyDic.set("map_"+mapId, true);
        }


        for(var i=0;i<len;i++){
            var roomPlayer:RoomPlayer = this.roomDto.members[i];
            if(this.heroReadyDic.get(roomPlayer.heroId+"_"+roomPlayer.heroColor) == null){
                var heroItem:HeroItem = SheetDataManager.intance.m_dicHeros.get(roomPlayer.heroId);
                // var heroRes = GameResourceManager.instance.getHeroHeadRes(heroItem.serialId, roomPlayer.heroColor);

                var heroSk = GameResourceManager.instance.getHeroSkRes(heroItem.serialId, roomPlayer.heroColor);
                // loadArr.push(heroRes);
                loadArr.push({ url: heroSk, type: Laya.Loader.SPINE });   
                this.heroReadyDic.set(roomPlayer.heroId+"_"+roomPlayer.heroColor, true);

            }
        }

        if(loadArr.length>0){
            // console.log(" ================ setMatchRes: start");
            Laya.loader.load(loadArr, null, Laya.Handler.create(this, this.onPreAssetPress1, null, false)).then(() => {
                //     // 加载完成后，处理逻辑
    
                // console.log(" ================ setMatchRes: end");
    
        
                });
            
        }


        
        

    }

    

    private onPreAssetPress1(value: number): void {
        // this.va = (value * 100 * .5).toFixed(0);
        // if(this.loadingView && this.va < 100 ) {
        //     if(this.va > this.m_iPrecess ) {
        //         this.loadingView.loading(this.va);
        //     } 
        // } 
    }


        
    async checkMatchRankData(){
        if(this.reward == null){//没收到奖励数据，主动去获取比赛结束数据
            let data:any = await HttpCmd.requestPost("wxgame/room/balanceInfo", {roomId: MatchModel.instance.matchDto.roomId});
            WebSocketService.instance.isMatching = false;//比赛结束不在使用socket

            if(MatchModel.instance.reward == null){
                MatchModel.instance.reward = data.reward;
                ModuleManager.intance.openModule(ModuleName.MatchEndRankView,  data.rankList);
            }
            
        }

    }
    onGoMatchEndRank(rankList:RoomRankPlayerDto[], reward:ItemDto[]){
        WebSocketService.instance.isMatching = false;//比赛结束不在使用socket
        // return;
        //服务器 主动推的奖励数据
        if(this.reward == null){
            MatchModel.instance.reward = reward;
            ModuleManager.intance.openModule(ModuleName.MatchEndRankView, rankList);
        }
        
    }

    clearData(){
        this.roomDto = null;
        this.matchDto = null;
        this.reward = null;
        this.heroReadyDic = null;
        this.currArenaItem = null;
        GameManager.instance.clearData();
        HttpCmd.needHeart = true;//恢复心跳
        SoundMgr.instance.playMusicByName(SoundMgr.soundName_bg, ".mp3");
        GetItemDataModel.instance.needPop = true;
        GetItemService.instance.startShow();
    }




}