import {Quick} from "./Quick";
import {GameLanguageMgr} from "../../common/GameLanguageMgr";
import {GlobalDataManager} from "../GlobalDataManager";
export class TimeUtil {
    public static OneDaySceond: number = 24 * 3600;
    public static OneHourSceond: number = 3600;
    public static OneMiniuteSecond: number = 60;
    public constructor() {
        
    }
    public static formatMM(totalMM: number): string {
        if(totalMM <= 0 ) {
            return '00:000';
        } 
        let _second: number, millisecon: number;
        _second = totalMM / 1000;
        millisecon = totalMM % 1000;
        let numberTime: string = "";
        if(_second <= 9 ) {
            numberTime += "0";
        } 
        numberTime += Math.floor(_second);
        numberTime += ":";
        if(millisecon <= 9 ) {
            numberTime += "00";
        } else if(millisecon <= 99 ) {
            numberTime += "0";
        } 
        numberTime += millisecon;
        return numberTime;
    }
    public static format(totalsec: number, _sign: string = ":", needHour: boolean = true): string {
        if(totalsec <= 0 ) {
            if(needHour ) {
                return '00:00:00';
            } 
            return '00:00';
        } 
        let _second: number, _day: number, remain: number, _hours: number, _minute: number;
        _hours = totalsec / 3600;
        remain = totalsec % 3600;
        _minute = remain / 60;
        _second = remain % 60;
        let numberTime: string = "";
        if(needHour ) {
            if(_hours <= 9 ) {
                numberTime += "0";
            } 
            numberTime += Math.floor(_hours) + _sign;
        } 
        if(_minute <= 9 ) {
            numberTime += "0";
        } 
        numberTime += Math.floor(_minute) + _sign;
        if(Math.ceil(_second) <= 9 ) {
            numberTime += "0";
        } 
        numberTime += Math.ceil(_second);
        return numberTime;
    }
    public static formatWithDay(totalsec: number, _sign: string = ":"): string {
        if(totalsec <= 0 ) {
            return '00:00';
        } 
        let _second: number, _day: number, remain: number, _hours: number, _minute: number;
        _day = totalsec / 86400;
        remain = totalsec % 86400;
        _hours = remain / 3600;
        remain = remain % 3600;
        _minute = remain / 60;
        _second = remain % 60;
        let numberTime: string = "";
        if(_day >= 1 ) {
            let lanId: number = _day > 1 ? 2005 : 2003;
            numberTime = GameLanguageMgr.instance.getLanguage(lanId, Math.floor(_day)) + " ";
            if(_hours <= 9 ) {
                numberTime += "0";
            } 
            if(_second % 2 != 0 ) {
                _sign = Quick.getColorText(_sign, "#412b20")
            } 
            numberTime += Math.floor(_hours) + _sign;
            if(_minute <= 9 ) {
                numberTime += "0";
            } 
            numberTime += Math.floor(_minute);
        } else {
            if(_hours <= 9 ) {
                numberTime += "0";
            } 
            numberTime = Math.floor(_hours) + _sign;
            if(_minute <= 9 ) {
                numberTime += "0";
            } 
            numberTime += Math.floor(_minute) + _sign;
            if(Math.ceil(_second) <= 9 ) {
                numberTime += "0";
            } 
            numberTime += Math.ceil(_second);
        }
        return numberTime;
    }
    public static getTimeArray(second: number, formatType: string = "HH-MM-SS"): any[] {
        second = Math.max(0, second);
        if(formatType.indexOf("-") == -1 ) {
            return [];
        } 
        let result: any[] = [];
        let types: any[] = formatType.split("-");
        for(let type of types) {
            if(type == "DD" ) {
                result.push(Math.floor(second / TimeUtil.OneDaySceond));
            } else if(type == "HH" ) {
                result.push(Math.floor(second % TimeUtil.OneDaySceond / TimeUtil.OneHourSceond));
            } else if(type == "MM" ) {
                result.push(Math.floor(second % TimeUtil.OneHourSceond / TimeUtil.OneMiniuteSecond));
            } else if(type == "SS" ) {
                result.push(second % TimeUtil.OneMiniuteSecond);
            } 
        }
        return result;
    }
    public static getServerDate(timeStamp: number): Date {
        let date: Date = new Date(timeStamp);
        return date;
    }
    //格式化时间 秒 返回 00:00
    public static formatTime(sec:number) {
        return (parseInt((sec / 60)+'') + 100).toString().substring(1) + ":" + (parseInt((sec % 60) +'') + 100).toString().substring(1);
    }

    //格式化时间 秒 返回 00:00:00
    public static formatTime000000(sec:number) {
        if (!sec) {
            return '00:00:00';
        }
        return (parseInt((sec / 3600)+'') + 100).toString().substring(1)+ ':' + (parseInt((sec % 3600 / 60)+'') + 100).toString().substring(1) + ":" + (parseInt((sec % 60) + '') + 100).toString().substring(1);
    }
    public static formatTimeStamp(time: number, formatType: string = "YY-MT-DD HH:MM:SS"): string {
        let date: Date = TimeUtil.getServerDate(time);
        // let types: any[] = formatType.split("-");
        let formatStr: string = "";
        // if((types.indexOf("MT") >= 0 || types.indexOf("YY") >= 0) || types.indexOf("DD") >= 0 ) {
            if(formatType.indexOf("YY") >= 0 ) {
                formatStr = date.getFullYear()+"";
                formatType = formatType.replace("YY", formatStr);

            } 
            if(formatType.indexOf("MT") >= 0 ) {
                let month: number = date.getMonth() + 1;
                if(month > 9 ) {
                    formatStr = month+"";
                } else {
                    formatStr = "0" + month;
                }
                
                formatType = formatType.replace("MT", formatStr);

            } 
            if(formatType.indexOf("DD") >= 0 ) {
                let _date: number = date.getDate();
                if(_date > 9 ) {
                    formatStr = _date+"";
                } else {
                    formatStr = "0" + _date;
                }

                formatType = formatType.replace("DD", formatStr);
            } 
        // } else if((types.indexOf("HH") >= 0 || types.indexOf("MM") >= 0) || types.indexOf("SS") >= 0 ) {
            if(formatType.indexOf("HH") >= 0 ) {
                let hour: number = date.getHours();
                if(hour > 9 ) {
                    formatStr = hour+"";
                } else {
                    formatStr = "0" + hour;
                }
                formatType = formatType.replace("HH", formatStr);
            } 
            if(formatType.indexOf("MM") >= 0 ) {
                let min: number = date.getMinutes();
                if(min > 9 ) {
                    formatStr = min+"";
                } else {
                    formatStr = "0" + min
                }
                formatType = formatType.replace("MM", formatStr);
            } 
            if(formatType.indexOf("SS") >= 0 ) {
                let sec: number = date.getSeconds();
                if(sec > 9 ) {
                    formatStr = sec+"";
                } else {
                    formatStr = "0" + sec
                }
                formatType = formatType.replace("SS", formatStr);
            } 
        // } 
        return formatType;
    }
    public static formatTimeStamps(time: number, formatType: string = "YY-MT-DD HH:MM:SS"): string {
        let date: Date = TimeUtil.getServerDate(time);
        let formatStr: string = formatType;
        formatStr = TimeUtil.parseTimeDetail(date, formatStr, "YY", false);
        formatStr = TimeUtil.parseTimeDetail(date, formatStr, "MT", false);
        formatStr = TimeUtil.parseTimeDetail(date, formatStr, "DD", false);
        formatStr = TimeUtil.parseTimeDetail(date, formatStr, "HH", true);
        formatStr = TimeUtil.parseTimeDetail(date, formatStr, "MM", true);
        formatStr = TimeUtil.parseTimeDetail(date, formatStr, "SS", true);
        return formatStr;
    }
    private static parseTimeDetail(date: Date, formatStr: string, signType: string, needZero: boolean): string {
        if(formatStr.indexOf(signType) >= 0 ) {
            let secStr: string;
            let sec: number;
            if(signType == "YY" ) {
                sec = date.getFullYear()
            } else if(signType == "MT" ) {
                sec = date.getMonth() + 1
            } else if(signType == "DD" ) {
                sec = date.getDate()
            } else if(signType == "HH" ) {
                sec = date.getHours()
            } else if(signType == "MM" ) {
                sec = date.getMinutes()
            } else if(signType == "SS" ) {
                sec = date.getSeconds()
            } 
            if(sec > 9 || !needZero ) {
                secStr = sec.toString()
            } else {
                secStr = "0" + sec
            }
            formatStr = formatStr.replace(signType, secStr);
        } 
        return formatStr;
    }
    public static getServerTimeStr(): string {
        return TimeUtil.formatTimeStamp(Laya.timer.currTimer);
    }
    public static getTimeArr(): any[] {
        let str: string = TimeUtil.getServerTimeStr();
        let arr: any[] = str.split("/");
        return arr;
    }
    public static get getDay(): number {
        let str: string = TimeUtil.getServerTimeStr();
        let arr: any[] = str.split("/");
        let day: number = parseInt(arr[2]);
        return day;
    }
    public static parseSheetTime(time1: any[]): string {
        let timeStr: string = "";
        if(time1 ) {
            let len: number = time1.length;
            for(let i: number = 0; i < len; i++) {
                let temp: string = "";
                if(time1[i] < 10 ) {
                    temp = "0" + time1[i]
                } else {
                    temp = time1[i]
                }
                if(i !== len - 1 ) {
                    temp = temp + ":"
                } 
                timeStr += temp;
            }
        } 
        return timeStr;
    }
}