import {BaseItem} from "../BaseItem";
export class SkillItem extends BaseItem {


    public id: number; //id	
    /**
        1=主动技能
        2=被动技能
        PS：防御性技能，利用BUFF实现，例如，减免即将受到的攻击50%伤害。
        释放技能后，人物播放对应的动作和特效，同时给自身增加一个持续1秒的BUFF，在BUFF持续时间内，受到的伤害降低50%。
    */
    public type: number; //类型
    public name:string; //名字
    public des:string; //描述

    		

    
    /**targetsType|目标类型
     */
    public targetsType: number; //targetsType|目标类型
    
    /**targetsNum|目标数量
     */
    public targetsNum: number; //targetsNum|目标数量
    
    /**distance|攻击距离
     */
    public distance: number; //distance|攻击距离




    /**icon */
    public icon: string; //icon|技能图标


    /**commCd|公共cd毫秒
     */
    public commCd: number; //commCd|公共cd毫秒


    /**cd|cd毫秒
     */
    public cd: number; //cd|cd毫秒




    public hurt: number; //hurt|伤害比，数值类型，万分比
    /**
            1:
        技能命中时，按总伤害值，拆分成N个伤害连续播放（伤害数值）
        格式：权重，时间戳|权重，时间戳|权重，时间戳
        |例如：[{30,50},{30,100},{40,150}]代表技能造成的总伤害，在50毫秒时蹦30%的伤害数值，在100毫秒时蹦30%的伤害数值，在150毫秒时蹦40%的伤害数值。
        配置为0时，则直接造成100%单个伤害数值。
    */
    public section: any[]; //section|伤害拆分

    /**配置技能对应的一些特殊效果（BUFF），部分BUFF有自身的BUFF特效。
        格式：[[目标，目标数量，buffid],[目标，目标数量，buffid]]
        目标定义
        1=自身
        2=攻击目标（技能命中的目标）
    */
    public buff: any[]; //buff|技能BUFF
    public releaseEff: number; //releaseEff|施法特效
    public flightEff: number; //flightEff|技能特效

    	




    public hitEff: number; //hitEff|打击特效
    /** 
    1=全距离位移，可穿越角色，但不能穿越障碍。
    2=位移过程中，命中目标即停止（用于伤害类位移技能）
     */
    public displacementType: number; //displacementType|位移类型
    public displacement: number; //displacement|是否位移



    public fightBack: number; //fightBack|是否击退


}