import RoomPlayerDto from '../dto/RoomPlayerDto';

/** 进入房间或离开房间推送 */
class RoomPlayerChangePush {

    /** 1=上线，2=离线 */
    type: number = 0;
    member: RoomPlayerDto | null = null;

    static PROTOCOL_ID: number = 1016;

    protocolId(): number {
        return RoomPlayerChangePush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomPlayerChangePush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writePacket(packet.member, 25011);
        buffer.writeInt(packet.type);
    }

    static read(buffer: any): RoomPlayerChangePush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomPlayerChangePush();
        const result0 = buffer.readPacket(25011);
        packet.member = result0;
        const result1 = buffer.readInt();
        packet.type = result1;
        return packet;
    }
}

export default RoomPlayerChangePush;
