const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { SheetDataManager } from "../../global/SheetDataManager";
import { ItemItem } from "../../global/data/sheetItem/item/ItemItem";
import { UnionItem } from "../../global/data/sheetItem/union/UnionItem";
import { NumberUtil } from "../../global/util/NumberUtil";
import { UnionRankListItemBase } from "./UnionRankListItem.generated";

@regClass('cbbqawzdSI2g161G69xpUg')
export class UnionRankListItem extends UnionRankListItemBase {

    public unionItem: UnionItem;
    private isAddEvent:boolean = false;
    
    public constructor() {
        super();
    }


    //橙色  fdd378






    public set dataSource(value: any) {
        super.dataSource = value;
        this.unionItem = value as UnionItem;
        if(!this.unionItem ) {
            return
        }
        // this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {

        // public id: number; //id|序号
        // public name: string; //name|名称		
        // public declaration: string; //declaration|工会宣言
        // public unionLv: number; //unionLv|工会等级
        // public number: number; //number|当前人数
        // public maxNumber: number; //maxNumber|最大人数


        
        this.tf_name.text = GameLanguageMgr.instance.getLanguage(this.unionItem.name);
            // this.tf_des.text = GameLanguageMgr.instance.getLanguage(this.unionItem.des);



        this.tf_lv.text = GameLanguageMgr.instance.getUILang("LV.") + this.unionItem.unionLv;
        this.tf_num.text = "<font color='#25ff63'>"+this.unionItem.number+"</font>/"+this.unionItem.maxNumber;

        this.img_lvBg.skin = "res/union1/img_trade_di_" + (this.unionItem.unionLv > 3? 4 : this.unionItem.unionLv) + ".png";
        this.img_lvIcon.skin = "res/union1/img_trade_" + (this.unionItem.unionLv > 3? 4 : this.unionItem.unionLv) + ".png";

    }

    onChange(isSelect:boolean){
        if(isSelect){
            this.bg.skin = "res/union1/img_trade_bg_2.png";
            // this.tf_name.color = "#ffffff";
        }else{
            // this.tf_name.color = "#357cc0";
            this.bg.skin = "res/union1/img_trade_bg_1.png";
        }
    }
}