const { regClass } = Laya;
import { ErrorPopManager } from "../../common/ErrorPopManager";
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { LayerManager } from "../../common/LayerManager";
import { ModuleManager } from "../../common/ModuleManager";
import { PlatFormManager } from "../../common/PlatFormManager";
import { SceneManager } from "../../common/SceneManager";
import { SceneType } from "../../common/baseScene/SceneType";
import { ModuleName } from "../../global/ModuleName";
import { SheetDataManager } from "../../global/SheetDataManager";
import { RoomDto } from "../../global/contract/vo/RoomDto";
import { ItemItem } from "../../global/data/sheetItem/item/ItemItem";
import { ArenaItem } from "../../global/data/sheetItem/match/ArenaItem";
import { GameEvent } from "../../global/event/GameEvent";
import { Signal } from "../../global/event/Signal";
import { Quick } from "../../global/util/Quick";
import { TimeUtil } from "../../global/util/TimeUtil";
import HttpCmd from "../../net/HttpCmd";
import { WebSocketService } from "../../net/WebSocketService";
import { MatchModel } from "../match/MatchModel";
import { NoticeMgr } from "../notice/NoticeMgr";
import { EnterRoomViewBase } from "./EnterRoomView.generated";

@regClass('IwpLh2K2T5Gh7QXYnJakAw')
export class EnterRoomView extends EnterRoomViewBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_PANEL;
    public m_iPositionType:number = LayerManager.CENTER;

    private reduceTime:number;

    onEnable(): void {

        // SoundMgr.instance.playMusicByName(SoundMgr.soundName_bg, ".mp3");
        this.btn_exit.on(Laya.Event.CLICK, this, this.onExitClick);

        Signal.intance.event(GameEvent.EVENT_ENTER_MATCH_ROOM, false);

        this.initContainer();
        //EVENT ::进入等待界面
        PlatFormManager.instance.sendCustumEvent(40);
    }


    private initContainer(): void {
        // this.list_wait_hero.vScrollBarSkin = "";
        // this.list_wait_hero.mouseHandler = new Laya.Handler(this, this.onListEvent);
        // this.list_wait_hero.renderHandler = new Laya.Handler(this, this.onRenderList);
        this.list_wait_hero.visible = true;
        var roomDto:RoomDto = MatchModel.instance.roomDto;

        var roomId = roomDto.roomId;
        var arenaId = roomDto.arenaId;
        var coinType = roomDto.coinType;


        // var itemItem:ItemItem = SheetDataManager.intance.m_dicItems.get(coinType);
        // var coinStr:string =  GameLanguageMgr.instance.getLanguage(itemItem.name);
        var arenaItem:ArenaItem = SheetDataManager.intance.m_dicArenaDic.get(arenaId);
        MatchModel.instance.currArenaItem = arenaItem;

        // var coinStr:string = coinType == 3001 ? "TOX" : "DTS";
        // var arenaStr:string = arenaId == 1001 ? "Novice Arena" : "General Arena";

        this.tf_roomName.text = GameLanguageMgr.instance.getLanguage(arenaItem.name);//coinStr + " " + arenaStr;

        this.arenaNo.text = roomId+"";
        


        this.initRoomData();
        this.updateExitCD();

        
        Laya.timer.loop(1000, this, this.onRefreshRoom);
    }


    public updateExitCD(): void {
        var joinCd = Math.round(MatchModel.instance.roomDto.joinCd/1000);
        var limitCd = joinCd - MatchModel.instance.currArenaItem.notQuit;
        if(limitCd > MatchModel.instance.currArenaItem.exitTime){
            limitCd = MatchModel.instance.currArenaItem.exitTime;
        }
        MatchModel.instance.limitCd = limitCd;

        if(MatchModel.instance.limitCd>0){
            
            this.onSetExitTime();
            Laya.timer.loop(1000, this, this.onSetExitTime);
        }else{
            Laya.timer.clear(this, this.onSetExitTime);
            
            this.btn_exit.disabled = true;
            this.btn_exit.label = "Exit";
        }

    }

    private onSetExitTime():void{
        if(MatchModel.instance.limitCd>=0){
            this.btn_exit.label = GameLanguageMgr.instance.getUILang("Exit") + " " + MatchModel.instance.limitCd + "S";
            MatchModel.instance.limitCd--;
            this.btn_exit.disabled = false;
        }else{
            Laya.timer.clear(this, this.onSetExitTime);
            this.btn_exit.disabled = true;
            this.btn_exit.label = "Exit";
        }

    }

    public updateBattleCD(): void {
        MatchModel.instance.coolDownTime = Math.round(MatchModel.instance.roomDto.joinCd/1000);
        if(MatchModel.instance.coolDownTime>0){
            // this.tf_coolTime.visible = true;
            this.onSetCoolTime();
            Laya.timer.loop(1000, this, this.onSetCoolTime);
        }else{
            // Laya.timer.clear(this, this.onSetCoolTime);
            this.onGoMatch();
            // this.tf_coolTime.visible = false;
        }

    }

    private onSetCoolTime():void{
        if(MatchModel.instance.coolDownTime>=0){
            this.tf_coolTime.text = GameLanguageMgr.instance.getLanguage(1089) + " <font color='#ff5388'>" +TimeUtil.format(MatchModel.instance.coolDownTime) + "</font>";
            MatchModel.instance.coolDownTime--;
        }else{
            // Laya.timer.clear(this, this.onSetCoolTime);
            // Laya.timer.clear(this, this.onRefreshRoom);//进入房间了 就不用再去获取房间信息了
            // this.tf_coolTime.visible = false;

            this.onGoMatch();

            //时间到准备连接socket;
        }

    }

    async onGoMatch():Promise<void>{
        Laya.timer.clear(this, this.onSetCoolTime);
        Laya.timer.clear(this, this.onRefreshRoom);//进入房间了 就不用再去获取房间信息了
        let data:any = await HttpCmd.requestPost("wxgame/room/info", {roomId: MatchModel.instance.roomDto.roomId});
        var roomDto: RoomDto = data.roomDto;

        if(roomDto.joinCd == 0){
            if(data.roomDto.enterGame == 1){//1=可以进战场，0=不能
                Laya.timer.clearAll(this);
            // MatchModel.instance.roomDto.socketIp
    
                WebSocketService.instance.initSocket();
    
            }else{
                this.close();
                
                Signal.intance.event(GameEvent.EVENT_ENTER_MATCH_ROOM, true);
                Quick.commTipWithYes(ErrorPopManager.instance.get(25));
    
            }
        }else{//服务器时间没到，继续倒计时
            MatchModel.instance.roomDto = roomDto;
            this.updateBattleCD();

        }

    }


    async onExitClick(){
        let data:any = await HttpCmd.requestPost("wxgame/room/leave", {roomId: MatchModel.instance.roomDto.roomId});
        if(data){
            this.close();

        }else{
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
        }
        
    }


    private handleSelectHead(index: number): void {
        // let vo: HeadItem = this.list_wait_hero.selectedItem;
        
    }
    private heros: any[];
    private initRoomData(): void {
        var currNum =  MatchModel.instance.roomDto.members.length;
        this.tf_people.text = currNum + "";
        var numberMax = MatchModel.instance.currArenaItem.numberMax;
        if(currNum >= numberMax){ //一旦满30人，无需等3分钟倒计时结束即可开战。
            this.onGoMatch();
            return;
        }
        
        // if(!this.heros)
        this.heros = [];
        for(var i=0;i<numberMax;i++){
            var hero = MatchModel.instance.roomDto.members[i];
            if(hero){
                this.heros.push(hero);
            }else{
                this.heros.push(null);
            }

        }
            // this.heros = MatchModel.instance.roomDto.members;
            // this.heros = this.heros.concat( this.heros.slice(0, 1));


        this.list_wait_hero.array = this.heros;
        this.list_wait_hero.visible = true;
        // let selectIndex = -1;

        // this.list_wait_hero.selectedIndex = selectIndex;
        // this.list_wait_hero.refresh();

        this.updateBattleCD();
        
    }

    async onRefreshRoom(){
        let data:any = await HttpCmd.requestPost("wxgame/room/info", {roomId: MatchModel.instance.roomDto.roomId})

        MatchModel.instance.roomDto = data.roomDto;

        this.initRoomData();

        MatchModel.instance.setMatchRes();
    }



    


    private handleClickBtn(currentTarget: Laya.Button): void {
        switch(currentTarget) {

            // case this.btn_change:
            // {
            //     this.onChangeHead();
            // }
            // break;
        }
    }


    onDisable(): void {
        this.removeEvent();
        console.log("EnterRoomView onDisable");
//不是主界面，才打开主界面
        if(SceneManager.intance.currSceneName != SceneType.M_SCENE_MAIN){
            SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN);
        }else{
            Signal.intance.event(GameEvent.EVENT_ENTER_MATCH_ROOM, true);
        }
        
    }

    public removeEvent():void{
        Laya.timer.clearAll(this);
        // Signal.intance.off(CreateLoginEvent.INIT_CREATELOGIN_EVENT, this, this.initCreateLoginView);
        // Signal.intance.off(GameEvent.EVENT_CHANGE_HEAD, this, this.onChangeHeadBack);

    }
}