
import { GameLanguageMgr } from "../../../common/GameLanguageMgr";
import { LoadingManager } from "../../../common/LoadingManager";
import { NoticeMgr } from "../../../module/notice/NoticeMgr";
import { WalletEvent } from "../../../module/wallet/event/WalletEvent";
import { WalletModel } from "../../../module/wallet/model/WalletModel";
import { WalletItemVo } from "../../../module/wallet/vo/WalletItemVo";
import { WalletValidateItemVo } from "../../../module/wallet/vo/WalletValidateItemVo";
import { Dictionary } from "../../../util/Dictionary";
import { EnumWallet } from "../../data/enumData/EnumWallet";
import { Signal } from "../../event/Signal";
import { EthereumConfig } from "../EthereumConfig";
import { EthereumManager } from "../EthereumManager";

export class ETHAssetsProxy {
    private assets_ary = [
        [10000, EthereumConfig.TOX_ADDRESS],
        [10001, EthereumConfig.ASSETS_ARCANITE_ADDRESS],
        [10002, EthereumConfig.ASSETS_CRYSTAL_ADDRESS],
        [10003, EthereumConfig.ASSETS_MITHRIL_ADDRESS],
        [10004, EthereumConfig.ASSETS_REDSTONE_ADDRESS],
        [10005, EthereumConfig.ASSETS_TRUEIRON_ADDRESS]
                        ]


    private assets_contract :any =null;

    private static _instance: ETHAssetsProxy;
    public static get instance(): ETHAssetsProxy {
        if(!ETHAssetsProxy._instance){
            ETHAssetsProxy._instance = new ETHAssetsProxy();
        }
        return ETHAssetsProxy._instance;
    }
    async init() {
        if (typeof (window as any)['ethereum'] === 'undefined') {
            return;
        }
        this.assets_contract = new (window as any)['web3'].eth.Contract(EthereumConfig.WITHDRAW_AND_RECHARGE_ABI, EthereumConfig.WITHDRAW_AND_RECHARGE_ADDRESS)
    }

    async depositWallet(tokenAddress:string,amount:any) {
        LoadingManager.instance.showLoading(true);
        let allow = await EthereumManager.instance.allowanceAddressByTokenAddress(EthereumConfig.WITHDRAW_AND_RECHARGE_ADDRESS,tokenAddress);
        if(!allow){
            LoadingManager.instance.hideLoading();
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(15));
            return ;
        }
        amount = (window as any)['web3'].utils.toWei(amount + '');
        await this.assets_contract.methods.recharge(tokenAddress.toLowerCase(),amount).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
        return true;
    }


    async withdrawToken(vo:WalletValidateItemVo) {
        LoadingManager.instance.showLoading(true);
        // tokenAddress:string,toAddress:string,amount:number,timestamp:number,hash:any,r:any,s:any,v:any
        await this.assets_contract.methods.withdrawToken(vo.tokenAddress,vo.to,vo.amount,vo.timestamp,vo.hash,vo.r,vo.s,vo.v).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
        return true;
    }

    //获取真铁等资源余额
    async updateAssetsBalance(){
        if(WalletModel.instance.walletDic == null){
            WalletModel.instance.walletDic = new Dictionary();
        }
        for (const iterator of this.assets_ary) {
            let itemId:number = iterator[0] as number;
            let address = iterator[1];
            let blc = await EthereumManager.instance.getERC20TokenBalance(address);    
            blc = parseInt(blc)        

            let walletItemVo:WalletItemVo =  WalletModel.instance.walletDic.get(itemId);
            if(!walletItemVo){
                walletItemVo = new WalletItemVo();
                walletItemVo.itemId = itemId;
                walletItemVo.value = 0;
                WalletModel.instance.walletDic.set(itemId, walletItemVo);
                
            }
            walletItemVo.ethValue = blc;
        }
        Signal.intance.event(WalletEvent.UPDATE_LOCAL_WALLET_LIST_EVENT);
    }

    updateCwt(toxValue: any){
        //tox 跟其他资源相同处理，防止后期也改为服务器取
        let walletItemVo:WalletItemVo =  WalletModel.instance.walletDic.get(EnumWallet.ASSETS_TOX);
        if(!walletItemVo){
            walletItemVo = new WalletItemVo();
            walletItemVo.itemId = EnumWallet.ASSETS_TOX;
            WalletModel.instance.walletDic.set(EnumWallet.ASSETS_TOX, walletItemVo);
        }
        walletItemVo.ethValue = toxValue;
    }


}