    public insertValidList(item: any) {
        if (!this.isFInd(item, this.validEnemyList)) {
            this.validEnemyList.push(item)
        }
    }

    private isFInd(item: any, _list: any[]): boolean {
        var state = false;
        if (!item || !_list || _list.length <= 0) {
            state = false;
        }
        for (let index = 0; index < _list.length; index++) {
            if ((_list[index] as Laya.Sprite) == item) {
                // if ((_list[index] as Laya.Sprite).id == item.id) {
                return true;
            }
        }
        return state;
    }

    private isFIndIndex(item: any, _list: any[]): number {
        var state = -1;
        if (!item || !_list || _list.length <= 0) {
            state = -1;
        }
        for (let index = 0; index < _list.length; index++) {
            // if ((_list[index] as Laya.Sprite).id == item.id) {
            if ((_list[index] as Laya.Sprite) == item) {
                state = index;
            }
        }
        return state;
    }

    public removeValidListByValue(item: any) {
        if (!item || !this.validEnemyList || this.validEnemyList.length <= 0) {
            return;
        }
        var _index = this.isFIndIndex(item, this.validEnemyList);
        if (_index != -1) {
            this.validEnemyList.splice(_index, 1);
        }
    }

