    getRoleById(roleId: number): MatchHeroSpr {
        let matchHeroSpr: MatchHeroSpr;
        this.matchHeroSprs && this.matchHeroSprs.forEach(_matchRole => {
            if (_matchRole.roomPlayerDto.id == roleId) {
                matchHeroSpr = _matchRole;
                return;
            }
        });
        return matchHeroSpr;
    }
    removeRoleById(roleId: number) {
        var matchHeroSpr: MatchHeroSpr;
        this.matchHeroSprs && this.matchHeroSprs.forEach(_matchRole => {
            if (_matchRole.roomPlayerDto.id == roleId) {
                matchHeroSpr = _matchRole;
                _matchRole.speed = 0;
                _matchRole.alpha = .3;
                return;
            }
        });

    }


