    playSound(musicType: MusicType) {
        var fileName = ""
        if (musicType == MusicType.ePuGong) {
            fileName = "resources/music/pugong"
        }
        else if (musicType == MusicType.eEnemyAttack) {
            fileName = "resources/music/enemyHit"
        }
        else if (musicType == MusicType.eDaZhao) {
            fileName = "resources/music/dazhao"
        }
        SoundMgr.instance.playSound(fileName + ".wav", 1);
    }

