

class RoomPlayerDto {

    id: number = 0;
    x: number = 0;
    y: number = 0;
    dire: number = 0;
    speed: number = 0;
    heroId: number = 0;
    heroColor: number = 0;
    name: string = '';
    icon: string = '';
    /** 是否队长，1=是，0=否 */
    leader: number = 0;
    /** 击杀数 */
    kill: number = 0;
    /** 剩余血量 */
    hp: number = 0;
    /** 总血量 */
    maxHp: number = 0;
    /** 总伤害 */
    atk: number = 0;
    /** 积分 */
    score: number = 0;
    /** 技能cd，单位毫秒 */
    skillCd: Map<number, number> = new Map();
    /** 公共cd，单位毫秒 */
    commonCd: number = 0;
    area: number = 0;
    country: number = 0;

    static PROTOCOL_ID: number = 25011;

    protocolId(): number {
        return RoomPlayerDto.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomPlayerDto | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.area);
        buffer.writeLong(packet.atk);
        buffer.writeLong(packet.commonCd);
        buffer.writeInt(packet.country);
        buffer.writeInt(packet.dire);
        buffer.writeInt(packet.heroColor);
        buffer.writeLong(packet.heroId);
        buffer.writeLong(packet.hp);
        buffer.writeString(packet.icon);
        buffer.writeLong(packet.id);
        buffer.writeInt(packet.kill);
        buffer.writeInt(packet.leader);
        buffer.writeLong(packet.maxHp);
        buffer.writeString(packet.name);
        buffer.writeLong(packet.score);
        buffer.writeIntLongMap(packet.skillCd);
        buffer.writeInt(packet.speed);
        buffer.writeInt(packet.x);
        buffer.writeInt(packet.y);
    }

    static read(buffer: any): RoomPlayerDto | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomPlayerDto();
        const result0 = buffer.readInt();
        packet.area = result0;
        const result1 = buffer.readLong();
        packet.atk = result1;
        const result2 = buffer.readLong();
        packet.commonCd = result2;
        const result3 = buffer.readInt();
        packet.country = result3;
        const result4 = buffer.readInt();
        packet.dire = result4;
        const result5 = buffer.readInt();
        packet.heroColor = result5;
        const result6 = buffer.readLong();
        packet.heroId = result6;
        const result7 = buffer.readLong();
        packet.hp = result7;
        const result8 = buffer.readString();
        packet.icon = result8;
        const result9 = buffer.readLong();
        packet.id = result9;
        const result10 = buffer.readInt();
        packet.kill = result10;
        const result11 = buffer.readInt();
        packet.leader = result11;
        const result12 = buffer.readLong();
        packet.maxHp = result12;
        const result13 = buffer.readString();
        packet.name = result13;
        const result14 = buffer.readLong();
        packet.score = result14;
        const map15 = buffer.readIntLongMap();
        packet.skillCd = map15;
        const result16 = buffer.readInt();
        packet.speed = result16;
        const result17 = buffer.readInt();
        packet.x = result17;
        const result18 = buffer.readInt();
        packet.y = result18;
        return packet;
    }
}

export default RoomPlayerDto;
