    private m_iPrecess: number;
    private va: any;
    private loadingView: any = Laya.Browser.window.loadingView;
    private onPreAssetPress1(value: number): void {
        this.va = (value * 100 * .5).toFixed(0);
        if(this.loadingView && this.va < 100 ) {
            if(this.va > this.m_iPrecess ) {
                this.loadingView.loading(this.va);
            } 
        } 
    }
    private onPreAssetPress2(value: number): void {
        this.va = (value * 100 *.5+.5).toFixed(0);
        if(this.loadingView && this.va < 100 ) {
            if(this.va > this.m_iPrecess ) {
                this.loadingView.loading(this.va);
            } 
        } 
    }
