    private ii: number = 100;
    public loadModuleUrl(name: any, complete: Laya.Handler = null, type: string = null, priority: number = 1, cache: boolean = true): void {
        console.log("loadModuleUrl m_strName: " + name);
        this.ii++;
        let urlArr: any[] = GameResourceManager.instance.m_objModuleReource[name];
        if(urlArr == null || urlArr.length < 1 ) {
            complete.run();
            return;
        } 
        Laya.loader.load(urlArr, Laya.Handler.create(this, this.loadItemComplete, [complete, urlArr]), Laya.Handler.create(this, this.onLoadProgress, [name], false), type, priority, cache, this.ii.toString());
    }
    private loadItemComplete(value: Laya.Handler, urlArr: any[]): void {
        
        if(!this.usedUrlDic ) {
            this.usedUrlDic = new Dictionary()
        } 
        for(let a in urlArr) {
            let urlStrKey: string = urlArr[a].url;
            let urlStrValue: number = this.usedUrlDic.get(urlStrKey);
            if(urlStrValue ) {
                this.usedUrlDic.set(urlStrKey, ++urlStrValue)
            } else {
                this.usedUrlDic.set(urlStrKey, 1)
            }
        }
        value.run();
    }
    private usedUrlDic: Dictionary;
    public clearModuleUrl(m_strName: string): void {
        console.log("clearModuleUrl m_strName: " + m_strName);
        let urlArr: any[] = GameResourceManager.instance.m_objModuleReource[m_strName];
        for(let a in urlArr) {
            let urlStrKey: string = urlArr[a].url;
            if(this.usedUrlDic ) {
                let urlStrValue: number = this.usedUrlDic.get(urlStrKey);
                urlStrValue -= 1;
                if(urlStrValue < 1 ) {
                    this.usedUrlDic.remove(urlStrKey);
                    Laya.loader.clearRes(urlStrKey);
                } else {
                    this.usedUrlDic.set(urlStrKey, urlStrValue);
                }
            } 
        }
    }
