
import { ErrorPopManager } from "../../common/ErrorPopManager";
import { ModuleManager } from "../../common/ModuleManager";
import BaseDialog from "../../common/base/BaseDialog";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import { ETHUtils } from "../../global/contract/utils/ETHUtils";
import { HeroVo } from "../../global/contract/vo/HeroVo";
import { Signal } from "../../global/event/Signal";
import { HeroNetModel } from "./HeroNetModel";
import { HeroPosVo } from "./HeroPosVo";

export class HeroFormationDialog extends BaseDialog {
    public constructor() {
        super();
    }

    public get view(): any {
        return this._view as any;
    }
    public dispose(): void {
        super.dispose();
    }
    public createUI(): void {
        // this._view = new any();
        this.addChild(this._view);

        Signal.intance.on('CANCEL_HERO_POS',this,this.cancelHero)
        Signal.intance.on('SET_HERO_POSTION_SUCCESS',this,this.setHeroPosSuccess)

        this.view.btn_set.clickHandler = new Laya.Handler(this,this.onSetHeroPos)

    }

    async onSetHeroPos(){
        await HeroNetModel.instance.setHeroPos();
    }

    setHeroPosSuccess(){
        ErrorPopManager.instance.showErrorWord(20000);
        ModuleManager.intance.disposeView(ModuleName.HeroFormationDialog)
    }


    public removeEvent(): void {
        Signal.intance.off('CANCEL_HERO_POS',this,this.cancelHero)
        Signal.intance.off('SET_HERO_POSTION_SUCCESS',this,this.setHeroPosSuccess)
    }
    async createUIEnd(){
        this.view.list.vScrollBarSkin = "";
        this.view.list.dataSource = [];
        this.view.list.mouseHandler = new Laya.Handler(this, this.onListEvent);
        this.view.list.renderHandler = new Laya.Handler(this, this.onRenderList);

        await HeroNetModel.instance.getHeroPos();
        HeroNetModel.instance.copyPosDic = ETHUtils.deepClone(HeroNetModel.instance.posDic);

        let heroVos:any[] = GlobalDataManager.instance.m_heroData.heroVoDic.values;
        
        if(heroVos && heroVos.length>0){
            let temp = heroVos.filter(
                item => item.status != 2
            );
            this.view.list.array = temp;
            
        }



        
        this.updateList();
    }

    cancelHero(pos: any){
        let vo = HeroNetModel.instance.copyPosDic.get(pos);
        vo.heroId = '0';
        this.updateList();
    }

    updateList(){
        for (const key of HeroNetModel.instance.copyPosDic.keys) {
            let vo:HeroPosVo = HeroNetModel.instance.copyPosDic.get(key);
            this.view['pos' + key].dataSource = vo;
        }
        this.view.list.refresh();
    }



    private onListEvent(e: Laya.Event, index: number): void {
        switch(e.type) {
            case Laya.Event.CLICK:
            {
                // this.view.list.selectedIndex = index;
                this.handleSelectHero(index)
            }
            break;
        }
    }

    private handleSelectHero(index: number): void {
        let vo: HeroVo = this.view.list.array[index];
        for (const iterator of HeroNetModel.instance.copyPosDic.values) {
            if(iterator.heroId == vo.id){

                this.cancelHero(iterator.position)
                return;
            }
        }
        for (const key of HeroNetModel.instance.copyPosDic.keys) {
            let pvo:HeroPosVo = HeroNetModel.instance.copyPosDic.get(key);
            if(!parseInt(pvo.heroId)){
                pvo.heroId = vo.id + '';
                this.view['pos' + key].dataSource = pvo;
                break;
            }
        }
        this.view.list.refresh();
        
    }

    private onRenderList(cell: Laya.Box, index: number): void {
        // let data = cell.dataSource as HeroVo;
        // let selected = cell.getChildByName("selected") as Laya.Image;
        // selected.visible = this.view.list.selectedIndex == index;
    }
    onComplete(): any {
    }

    public initData(): void {
      
    }
}