const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { ModuleManager } from "../../common/ModuleManager";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import { SheetDataManager } from "../../global/SheetDataManager";
import { RoomDto } from "../../global/contract/vo/RoomDto";
import { UnionItem } from "../../global/data/sheetItem/union/UnionItem";
import { Quick } from "../../global/util/Quick";
import { TimeUtil } from "../../global/util/TimeUtil";

import HttpCmd from "../../net/HttpCmd";
import { MatchModel } from "../match/MatchModel";
import { NoticeMgr } from "../notice/NoticeMgr";
import { RoomListItemBase } from "./RoomListItem.generated";

@regClass('YoCqj_dyTb2ZIAnhkSds6Q')
export class RoomListItem extends RoomListItemBase {


    
    public roomDto: RoomDto;
    private isAddEvent:boolean = false;
    public constructor() {
        super();
    }

    public createChildren():void{
        super.createChildren();
        // this.visible = false;
    }

    private addEvent(){
        if(!this.isAddEvent){
            this.btn_join.on(Laya.Event.CLICK, this, this.onJoin);
            this.isAddEvent = true;
            // Signal.intance.on(HeroEvent.EVENT_HERO_DRAW_UPDATE_PRODUCTION, this, this.updateProduction);
        }

    }
    // updateProduction() {
    //     if(this.roomDto && HeroModel.instance.heroOutputRateDic){
    //         let heroOutputRate:HeroOutputRate = HeroModel.instance.heroOutputRateDic.get(parseInt(this.roomDto.mineralLevel));
    //         if(heroOutputRate){

    //             (this.getChildByName("tf_output") as Laya.Label).text = GameLanguageMgr.instance.getLanguage(6049, heroOutputRate.capacity );

    
    //         }            
    //     }     
       
    // }

    private async onJoin():Promise<void>{
        if(this.roomDto.requirePass){
            ModuleManager.intance.openModule(ModuleName.InputPwdDialog, [this.roomDto]);
        }else{
            
            var arenaItem = SheetDataManager.intance.m_dicArenaDic.get(this.roomDto.arenaId);
        
            if(arenaItem.item && arenaItem.item.length>1){
                var itemId = arenaItem.item[0];
                var needNum = arenaItem.item[1];
                var currNum = GlobalDataManager.instance.m_packageData.getCountById(itemId);
                if(currNum < needNum){
                    Quick.titickNotEnought(itemId);
                    return;
                }
            }
            
            let data:any = await HttpCmd.requestPost("wxgame/room/join", {roomId:this.roomDto.roomId})
    
            MatchModel.instance.roomDto = data.roomDto;
    
                    if(data.roomDto){                      
                        
                        ModuleManager.intance.openModule(ModuleName.EnterRoomView);
    
                    }else{
                        NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
                    }

        }
               
    }


    public set dataSource(value: any) {
        super.dataSource = value;
        this.roomDto = value as RoomDto;
        if(!this.roomDto ) {
            return
        }
        this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {
        let headId = this.roomDto.leader.icon;
        this.playerIcon.headSkin = headId;
        

        this.tf_name.text = this.roomDto.leader.name;
        var unionItem:UnionItem = SheetDataManager.intance.m_dicUnions.get(this.roomDto.leader.unionId);
        if(unionItem){
            this.tf_union.text = GameLanguageMgr.instance.getLanguage(unionItem.simpleName);
        }else{
            this.tf_union.text = "";
        }
        this.tf_people.text = `<font color="#25ff63">`+this.roomDto.memberCount+`</font>/`+this.roomDto.maxCount;

        this.tf_seconds.text = TimeUtil.format(Math.round(this.roomDto.joinCd/1000), ":", false);
        // this.tf_seconds.text = GameLanguageMgr.instance.getLanguage(3016) +" "+ Math.round(this.roomDto.joinCd/1000);
        this.img_coin.skin = GameResourceManager.instance.getMoneyIcon(this.roomDto.coinType);


    }



    
}