const { regClass } = Laya;
import GameManager from "../../../script/logic/GameManager";
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { Quick } from "../../global/util/Quick";
import RoomPlayerDto from "../../net/protocol/dto/RoomPlayerDto";
import { MatchRankListItemBase } from "./MatchRankListItem.generated";

@regClass('XTVFXeI_R768e0Elv_ePAA')
export class MatchRankListItem extends MatchRankListItemBase {

    public roomPlayerDto: RoomPlayerDto;
    private isAddEvent:boolean = false;
    public constructor() {
        super();
    }

    private addEvent(){
        if(!this.isAddEvent){
            // this.btn_join.on(Laya.Event.CLICK, this, this.onJoin);
            this.isAddEvent = true;
            // Signal.intance.on(HeroEvent.EVENT_HERO_DRAW_UPDATE_PRODUCTION, this, this.updateProduction);
        }

    }



    public set dataSource(value: any) {
        // super.dataSource = value;
        this.roomPlayerDto = value as RoomPlayerDto;
        if(!this.roomPlayerDto ) {
            return
        }
        this.addEvent();
        this.setCurrentOutPut();
    }

    setCurrentOutPut() {
        
        
        this.tf_rank.text = (this.roomPlayerDto as any).rankNum + "";
        let headId = this.roomPlayerDto.icon;
        this.playerIcon.headSkin = headId;
        // this.img_head.skin = GameResourceManager.instance.getPlayerIcon(headId);

        this.tf_name.text = GameLanguageMgr.instance.getLanguage(this.roomPlayerDto.name);
        this.tf_kill.text = this.roomPlayerDto.kill + "";
        

        if(this.roomPlayerDto.id == GameManager.instance.myPlayer.id){
            this.img_bg.skin = "res/match1/img_ph2.png";
            this.tf_rank.color = "#000000";
            this.tf_name.color = "#ffe64b";
            this.img_kill.filters = [Quick.YELLOW];
            this.tf_kill.color = "#ffe64b";
            this.box_item.x = 6;
        }else{
            this.img_bg.skin = "res/match1/img_ph.png";
            this.tf_rank.color = "#ffffff";
            this.tf_name.color = "#ffffff";
            this.img_kill.filters = null;
            this.tf_kill.color = "#ffffff";
            this.box_item.x = 15;
        }
    }
}