const { regClass } = Laya;
import Tool from "../../../script/util/Tool";
import AttackPush from "../../net/protocol/login/AttackPush";
import { DamageNumberBase } from "./DamageNumber.generated";

@regClass('nHp6WKzZRIWvyhguidEfqg')
export class DamageNumber extends DamageNumberBase {

    static WHITE:string = "#FFFFFF";
    static ORANGE:string = "#FFA500";// 设置橙色字体
    static RED:string = "#FF0000";// 设置红色字体
    static GREEN:string = "#119c09";


    static TYPE_Normal:string = "Normal";
    static TYPE_Miss:string = "Miss";
    static TYPE_Critical:string = "Critical";


    // 初始化函数，设置数字和起始位置


    init(damage: number, attackPush:AttackPush, fontSize: number = 28) {

        var damageType;
        // 初始化伤害数字
        var randomX;
        if(damage!=0){
            randomX = Tool.getRandInRound(-30, 30);
            if(attackPush.bj){
                damageType = DamageNumber.TYPE_Critical;
            }else{
                damageType = DamageNumber.TYPE_Normal;
            }

        }else{
            randomX = Tool.getRandInRound(-30, 30);
            damageType = DamageNumber.TYPE_Miss;
        }

        // this.text = text;
        // this.fontSize = fontSize;
        this.pos(randomX, -180);
        this.alpha = 1;
        this.scale(1, 1);

        this.tf_damage.visible = false;
        this.box_crit.visible = false;
        this.img_miss.visible = false;

        // 根据伤害类型设置特效
        switch (damageType) {
            case DamageNumber.TYPE_Normal:
                this.tf_damage.text = damage+"";
                // 普通伤害效果
                this.showNormalDamageEffect();
                break;
            case DamageNumber.TYPE_Critical:
                this.fontClip_damage.value = damage+"";
                // 暴击伤害效果
                this.showCriticalDamageEffect();
                break;
            case DamageNumber.TYPE_Miss:
                // Miss效果
                this.showMissEffect();
                break;
            default:
                break;
        }

        // 使用流式布局算法自动调整伤害文本的位置
        // this.adjustPosition();
    }

    private showNormalDamageEffect() {
        this.tf_damage.visible = true;
        // 普通伤害效果，可根据需求自定义
        // this.color = DamageNumber.WHITE; // 设置白色字体
        Laya.Tween.to(this, { y: this.y - 50, alpha: 0 }, 1000, Laya.Ease.linearNone, Laya.Handler.create(this, this.onComplete));
    }

    private showCriticalDamageEffect() {
        // 暴击伤害效果
        // this.color = DamageNumber.RED; // 设置红色字体
        this.box_crit.visible = true;
        this.scale(0.2, 0.2); // 初始缩小
        Laya.Tween.to(this, { scaleX: 1.3, scaleY: 1.3, alpha: 1, y: this.y - 70 }, 500, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
            Laya.Tween.to(this, { scaleX: 1, scaleY: 1, alpha: 0 }, 500, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
                this.onComplete();
            }));
        }));
    }

    private showMissEffect() {
        // Miss效果，可根据需求自定义
        this.img_miss.visible = true;
        // this.color = DamageNumber.ORANGE; // 设置橙色字体
        Laya.Tween.to(this, { y: this.y - 50, alpha: 0 }, 1000, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
            this.onComplete();
        }));
    }

    // private adjustPosition() {
    //     // 使用流式布局算法自动调整伤害文本的位置
    //     const spacingX = 20; // X方向间距
    //     const offsetX = this.width / 2; // X方向偏移
    //     const parent = this.parent as Laya.Sprite;

    //     let xPos = 0; // 初始化 X 位置

    //     for (let i = 0; i < parent.numChildren; i++) {
    //         const child = parent.getChildAt(i) as DamageText;
            
    //         // 确保文本对象不是自己，且在 X 方向上不重叠
    //         if (child !== this && Math.abs(child.x - xPos) < offsetX) {
    //             xPos = child.x + child.width + spacingX; // 调整 X 位置
    //         }
    //     }

    //     this.x = xPos;
    // }









    // 动画完成时的处理
    onComplete() {
        // 从父节点移除并回收
        this.removeSelf();
        Laya.Pool.recover("damageNumber", this);
    }
}


