
import { GameLanguageMgr } from "../../../common/GameLanguageMgr";
import { LoadingManager } from "../../../common/LoadingManager";
import { NoticeMgr } from "../../../module/notice/NoticeMgr";
import HttpCmd from "../../../net/HttpCmd";
import { HttpNetService } from "../../../net/HttpNetService";
import { Dictionary } from "../../../util/Dictionary";
import { Cmd } from "../../Cmd";
import { Signal } from "../../event/Signal";
import { Quick } from "../../util/Quick";
import { EthereumConfig } from "../EthereumConfig";
import { EthereumManager } from "../EthereumManager";
import { MemberApplyVo } from "../vo/MemberApplyVo";
import { MemberVo } from "../vo/MemberVo";

export class ETHUnionProxy {
// @ts-ignore
    private create_union_contract = null;
    // @ts-ignore
    private member_sys_contract = null;
// @ts-ignore
    private union_war_contract = null;
    private price='';
    public memberList:Dictionary;
    private static _instance: ETHUnionProxy;
    public static get instance(): ETHUnionProxy {
        if(!ETHUnionProxy._instance){
            ETHUnionProxy._instance = new ETHUnionProxy();
        }
        return ETHUnionProxy._instance;
    }
    async init() {
        this.memberList = new Dictionary();
        // @ts-ignore
        if (typeof (window as any)['ethereum'] === 'undefined') {
            return;
        }

        this.create_union_contract = new (window as any)['web3'].eth.Contract(EthereumConfig.CREATE_UNION_ABI, EthereumConfig.CREATE_UNION_ADDRESS)
        this.member_sys_contract = new (window as any)['web3'].eth.Contract(EthereumConfig.UNION_NUMBER_SYS_ABI, EthereumConfig.UNION_NUMBER_SYS_ADDRESS)
        this.union_war_contract = new (window as any)['web3'].eth.Contract(EthereumConfig.UNION_WAR_ABI, EthereumConfig.UNION_WAR_ADDRESS)
    }

    /** 联盟战役质押挖矿可领取收益 */ 
    async poolEarned(difficulty:number,mineId:number){
        let temp = await this.union_war_contract.methods.earned(mineId).call();
        return (window as any)['web3'].utils.fromWei(temp);
    }

    /** 领取联盟战役质押挖矿收益 */ 
    async claim_pool_reward(unionId:number,difficulty:number,mineId:number){
        LoadingManager.instance.showLoading(true);
        await this.union_war_contract.methods.claimReward(unionId,difficulty,mineId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    /** 领取联盟成员收益 */ 
    async claim_member_reward(){
        LoadingManager.instance.showLoading(true);
        let {sendBeanDto} = await HttpCmd.requestPost('cybertron-sys-api/api/v1/alliance/bonus/receiveBonus',{});
        let vo:WalletValidateItemVo = new WalletValidateItemVo();
        Object.assign(vo,sendBeanDto);
        await this.union_war_contract.methods.memberClaim(vo.tokenAddress,vo.to,vo.amount,vo.timestamp,vo.hash,vo.r,vo.s,vo.v).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
        return true;

    }

    /** 放弃联盟战役矿池 */ 
    async quitOccupy(unionId:number,difficulty:number,mineId:number){
        LoadingManager.instance.showLoading(true);
        await this.union_war_contract.methods.quitOccupy(unionId,difficulty,mineId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    /** 设置管理员分成比例 
     * 3% = 0.03
     * */ 
    async setManagerBonusPercent(unionId:number,accountIds:any,bonusPercents:any){
        LoadingManager.instance.showLoading(true);
        HttpNetService.instance.SendData(15901,{accountIds,bonusPercents})
    }

    /** 查询下一次分红时间 */ 
    async queryNextGetTime(unionId:number,accountId:number):Promise<string>{
        return '';
    }


    //属性捐献
    async donate(itemId:string,amount:string){
        LoadingManager.instance.showLoading(true);
        HttpNetService.instance.SendData(13000,{itemId,amount})
    }

    //获取联盟创建消耗
    async getCreateCost() {
        let data = await this.create_union_contract.methods.getCreateUnionPrice().call();
        this.price = (window as any)['web3'].utils.fromWei(data[1]);
        return this.price;
    }

    //创建联盟  余额不足 会返回fALSE
    async createUnion(unionName:string,avator:string,declaration:string = ''){
        LoadingManager.instance.showLoading(true);
        if(!this.price){
            await this.getCreateCost();
        }
        let have = await EthereumManager.instance.getBalance();
        if(parseInt(have) < parseInt(this.price)){
            LoadingManager.instance.hideLoading();
            Quick.moneyNotEnought();
            // NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(14));
            return false;
        }

        let allow = await EthereumManager.instance.allowanceAddress(EthereumConfig.CREATE_UNION_ADDRESS);
        if(!allow){
            LoadingManager.instance.hideLoading();
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(15));
            return false;
        }

        await this.create_union_contract.methods.createUnion(unionName,avator,declaration).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
        return true;
    }

    //获取成员列表
    async getMemberList(unionId='1'){
        let list = await HttpCmd.requestPost('cybertron-sys-api/api/v1/allianceMember/memberList',{unionId});
        this.memberList = new Dictionary();
        for (const iterator of list) {
            let vo:MemberVo = new MemberVo();

            
            Object.assign(vo,iterator);
            // if(vo.accountId == 46)
            //     vo.contributeValue = 1000;
            this.memberList.set(vo.accountId,vo);
        }

        // let vo:MemberVo = new MemberVo();
        // vo.name = "20";
        // vo.accountId = 20;
        // vo.position = 2;
        // vo.contributeValue = 900;
        // this.memberList.set(vo.accountId,vo);

        // let vo1:MemberVo = new MemberVo();
        // vo1.name = "21";
        // vo1.accountId = 21;
        // vo1.position = 3;
        // vo1.contributeValue = 1000;
        // this.memberList.set(vo1.accountId,vo1);

        // let vo2:MemberVo = new MemberVo();
        // vo2.name = "22";
        // vo2.accountId = 22;
        // vo2.position = 2;
        // vo2.contributeValue = 1000;
        // this.memberList.set(vo2.accountId,vo2);

        // let vo3:MemberVo = new MemberVo();
        // vo3.name = "23";
        // vo3.accountId = 23;
        // vo3.position = 3;
        // vo3.contributeValue = 2000;
        // this.memberList.set(vo3.accountId,vo3);


        return this.memberList;
    }

    //获取成员申请列表
    async getMemberApplyList(unionId='1'){
        // let _unionId:number = GuildModel.instance.myGuildVo.id;
        let list = await HttpCmd.requestPost('cybertron-sys-api/api/v1/allianceApply/applyList');
        // if(GuildModel.instance.applyMembers == null)
        GuildModel.instance.applyMembers = new Dictionary();
        for (const iterator of list) {
            let vo:MemberApplyVo = new MemberApplyVo();
            Object.assign(vo,iterator);
            GuildModel.instance.applyMembers.set(vo.accountId,vo)
        }
        return GuildModel.instance.applyMembers;
    }

    //获取成员申请历史列表
    async getMemberApplyHistoryList(unionId='1'){
        // let _unionId:number = GuildModel.instance.myGuildVo.id;
        let list = await HttpCmd.requestPost('cybertron-sys-api/api/v1/allianceApply/historyRecordList');
        // if(GuildModel.instance.applyMembers == null)
        let historyRecordList = new Dictionary();
        for (const iterator of list) {
            let vo:MemberApplyVo = new MemberApplyVo();
            Object.assign(vo,iterator);
            historyRecordList.set(vo.accountId,vo)
        }
        return historyRecordList;
    }

    //修改联盟信息
    async editUnion(unionId:number,unionName:string,avator:string,declaration:string = ''){
        LoadingManager.instance.showLoading(true);
        await this.create_union_contract.methods.updateUnionProfile(unionId,unionName,avator,declaration).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    //转让联盟
    async transferUnion(toAccountId:number,unionId:number){
        LoadingManager.instance.showLoading(true);
        await this.create_union_contract.methods.transferUnion(toAccountId,unionId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    //申请加入联盟  改为服务端
    async applyJoinUnion(accountId:number,unionId:number){
        await HttpCmd.requestPost('cybertron-sys-api/api/v1/allianceMember/applyJoinUnion',{unionId})
        NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(2005));//"申请成功");
    }

    //通过申请
    async confirmApplyJoin(accountId:number,unionId:number){
        LoadingManager.instance.showLoading(true);
        await this.member_sys_contract.methods.confirmApplyJoin(accountId,unionId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    //拒绝申请
    async refuseApplyJoin(accountId:number,unionId:number){
        let temp = await HttpCmd.requestPost('cybertron-sys-api/api/v1/allianceMember/refuseApplyJoin',{accountId,unionId})
        Signal.intance.event(Cmd.RECV_MSG(Cmd.MSG_UNION_GET_UNION_MEMBER_APPLY_REFUSE_S2C),[[temp]]);
    }

    //踢出联盟
    async kickOutUnion(accountId:number,unionId:number){
        LoadingManager.instance.showLoading(true);
        await this.member_sys_contract.methods.kickOutUnion(accountId,unionId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    //退出联盟
    async quitUnion(accountId:number,unionId:number){
        LoadingManager.instance.showLoading(true);
        await this.member_sys_contract.methods.quitUnion(accountId,unionId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    //添加管理
    async addManager(accountId:number,unionId:number){
        LoadingManager.instance.showLoading(true);

        let maxStr = await this.member_sys_contract.methods.getMaxManagerCount().call();
        let max = parseInt(maxStr);

        let curStr = await this.member_sys_contract.methods.getUnionManagerCount(unionId).call();
        let cur = parseInt(curStr);
        if(cur >= max){
            LoadingManager.instance.hideLoading();
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage('3013'));
            return false;
        }
        await this.member_sys_contract.methods.addManager(accountId,unionId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

    //解除管理
    async removeManager(accountId:number,unionId:number){
        LoadingManager.instance.showLoading(true);
        await this.member_sys_contract.methods.removeManager(accountId,unionId).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
    }

}