import { Dictionary } from "../../../util/Dictionary";

	/**
	 * 钱包数据 
	 */	
export class WalletModel {
    
    private static _instance: WalletModel;
    
  
//钱包集合  WalletItemVo 对象
    walletDic: Dictionary = new Dictionary();

    public static get instance(): WalletModel {
        if(!WalletModel._instance ) {
            WalletModel._instance = new WalletModel()
        } 
        return WalletModel._instance;
    }
    public constructor() {
        
    }

    /**
     * @msg: 获得合约余额
     * @param {number} assetType
     * @return {*}
     */
    getEthValue(assetType: number): any {
        if(WalletModel.instance.walletDic.get(assetType))
            return WalletModel.instance.walletDic.get(assetType).ethValue;
        return 0;
    }

    /**
     * @msg: 获得本地余额
     * @param {number} assetType
     * @return {*}
     */
     getValue(assetType: number): any {
        if(WalletModel.instance.walletDic.get(assetType))
            return WalletModel.instance.walletDic.get(assetType).value;
        return 0;
    }


}