

class ItemDto {

    type: number = 0;
    id: number = 0;
    count: number = 0;

    static PROTOCOL_ID: number = 25008;

    protocolId(): number {
        return ItemDto.PROTOCOL_ID;
    }

    static write(buffer: any, packet: ItemDto | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeDouble(packet.count);
        buffer.writeLong(packet.id);
        buffer.writeInt(packet.type);
    }

    static read(buffer: any): ItemDto | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new ItemDto();
        const result0 = buffer.readDouble();
        packet.count = result0;
        const result1 = buffer.readLong();
        packet.id = result1;
        const result2 = buffer.readInt();
        packet.type = result2;
        return packet;
    }
}

export default ItemDto;
