import {SpotInfo} from "./SpotInfo";
import {GameResourceManager} from "../../../../common/GameResourceManager";
import { SheetDictionary } from "../item/SheetDictionary";

export class SpotSheetDataManager {
    private static _instance: SpotSheetDataManager;
    public constructor() {
        
    }
    public static get instance(): SpotSheetDataManager {
        if(!SpotSheetDataManager._instance ) {
            SpotSheetDataManager._instance = new SpotSheetDataManager();
        } 
        return SpotSheetDataManager._instance;
    }
    private spotDic: SheetDictionary;
    public initSpotCfgData(): void {
        if(this.spotDic == null ) {
            // this.spotDic = new SheetDictionary();
            let json: any = GameResourceManager.instance.getResByURL("config/spot.json");
            this.spotDic = new SheetDictionary(json.data, SpotInfo);
            // let spotInfo: SpotInfo;
            // for(let value of json) {
            //     spotInfo = value;
            //     this.spotDic.set(spotInfo.ID + "spot", spotInfo);
            // }
        } 
    }
    public getSpotById(id: number): SpotInfo {
        this.initSpotCfgData();
        return this.spotDic.get(id);
    }
    public get getSpotDic(): SheetDictionary {
        this.initSpotCfgData();
        return this.spotDic;
    }
}