
import { Dictionary } from "../../../../util/Dictionary";
import {SheetDataManager} from "../../../SheetDataManager";
import { BaseSheetDataModel } from "../BaseSheetDataModel";
export class ItemSheetDataModel extends BaseSheetDataModel{
    private _itemsTypeMap: Dictionary = null;
    public constructor() {
        super("","","");
        this._itemsTypeMap = new Dictionary();
    }
    private getCombineKey(key1: any, key2: any, splitKey: string): string {
        return key1 + splitKey.toString() + key2;
    }
    public getItemsByChildType(childType: number, itemType: number): any[] {
        let combineKey: string = this.getCombineKey(childType, itemType, "childType_itemType");
        if(this._itemsTypeMap.get(combineKey) ) {
            return this._itemsTypeMap.get(combineKey)
        } 
        this._itemsTypeMap.set(combineKey, []);
        let items: any[] = SheetDataManager.intance.m_dicItems.values;
        for(let item of items) {
            if(item.child_type == childType && item.itm_type == itemType ) {
                this._itemsTypeMap.get(combineKey).push(item);
            } 
        }
        return this._itemsTypeMap.get(combineKey);
    }
}