const { regClass } = Laya;
import { LayerManager } from "../../common/LayerManager";
import { MatchModel } from "./MatchModel";
import { MatchReadyViewBase } from "./MatchReadyView.generated";

@regClass('xpPoFvX_SqiYMO5AaOwGyA')
export class MatchReadyView extends MatchReadyViewBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_PANEL;
    public m_iPositionType:number = LayerManager.CENTER;

    private reduceTime:number;

    onEnable(): void {

        // SoundMgr.instance.playMusicByName(SoundMgr.soundName_bg, ".mp3");

    }

    onOpened(param: any): void {

        this.updateBattleCD();

        
    }


    
  public updateBattleCD(): void {
        // var remainStartTime =  5000;
        var remainStartTime =  MatchModel.instance.matchDto.startTime - MatchModel.instance.matchDto.nowTime;
        
        MatchModel.instance.startCoolDownTime = Math.round(remainStartTime/1000);
        if(MatchModel.instance.startCoolDownTime>0){
            this.onSetCoolTime();
            Laya.timer.loop(1000, this, this.onSetCoolTime);
        }else{
            Laya.timer.clear(this, this.onSetCoolTime);
        }

    }

    private onSetCoolTime():void{
        if(MatchModel.instance.startCoolDownTime>=0){
            this.fontClip_reduceTime.value = MatchModel.instance.startCoolDownTime + "";

            this.scaleEffect();

            MatchModel.instance.startCoolDownTime--;
        }else{
            Laya.timer.clear(this, this.onSetCoolTime);

            this.onMatchStartEnd();

        }

    }
    // 使用Tween进行放大缩小效果
    scaleEffect() {
    var that = this;
    // 缩放到1.3倍
    Laya.Tween.to(this.fontClip_reduceTime, { scaleX: 1.3, scaleY: 1.3 }, 200, Laya.Ease.linearInOut, Laya.Handler.create(this, function(){
        // 还原到原始大小
        Laya.Tween.to(that.fontClip_reduceTime, { scaleX: 1, scaleY: 1 }, 200, Laya.Ease.linearInOut);
    }));
    }


    onMatchStartEnd(){
        Laya.timer.once(10000, this, this.close);
        // this.close();
    }




    onDisable(): void {
        this.removeEvent();
        MatchModel.instance.stopAll = false;
    }

    public removeEvent():void{
        Laya.timer.clearAll(this);
        Laya.Tween.clearAll(this);

    }
}