import {GameLanguageMgr} from "../../../../../../common/GameLanguageMgr";
import {BaseSheetItem} from "../../../BaseSheetItem";
export class CatagoryChildTypeSheetItem extends BaseSheetItem {
    private _childtypeID: number;
    private _childtype: string;
    private _icon: string;
    private _if_use: boolean;
    public constructor() {
        super();
    }
    public get icon(): string {
        return this._icon;
    }
    public set icon(value: string) {
        this._icon = value;
    }
    private _childtypeL: string = "";
    public get childtype(): string {
        if(this._childtypeL == "" ) {
            this._childtypeL = GameLanguageMgr.instance.getLanguage(this._childtype)
        } 
        return this._childtypeL;
    }
    public set childtype(value: string) {
        this._childtype = value;
    }
    public get childtypeID(): number {
        return this._childtypeID;
    }
    public set childtypeID(value: number) {
        this._childtypeID = value;
    }
    public get if_use(): boolean {
        return this._if_use;
    }
    public set if_use(value: boolean) {
        this._if_use = value;
    }
}