    public static initConfig(data:any){
        this.configDic = new Dictionary();
        for (const iterator of data) {
            let value = iterator.sstringvalue
            if(iterator.sadditional === 'JSON'){
                value = JSON.parse(value);
            }
           this.configDic.set(iterator.skey2,value);
           (this as any)[iterator.skey2] = value;
        }
        SheetDataManager.intance.resetTokenData();//根据获取的配置 重设token
    }
