

class TargetMoveResponse {

    errorCode: number = 0;
    time: number = 0;

    static PROTOCOL_ID: number = 1036;

    protocolId(): number {
        return TargetMoveResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: TargetMoveResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.errorCode);
        buffer.writeLong(packet.time);
    }

    static read(buffer: any): TargetMoveResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new TargetMoveResponse();
        const result0 = buffer.readInt();
        packet.errorCode = result0;
        const result1 = buffer.readLong();
        packet.time = result1;
        return packet;
    }
}

export default TargetMoveResponse;
