const { regClass, property } = Laya;

@regClass('q0uAMn-DTUm70T4fGtxGlw')
export class AnimatorControl extends Laya.Script {
    private _anim: Laya.Animator;

    constructor() {
        super();
    }

    onStart(): void {
        this._anim = this.owner.getComponent(Laya.Animator);
    }
    private isPlayanimaByName(layerIndex: number = 0): boolean {
        let layer: Laya.AnimatorControllerLayer = this._anim.getControllerLayer(layerIndex);
        if (layer == undefined) {
            return false;
        }
        let state = layer.getCurrentPlayState()
        if (state == undefined) {
            return false;
        }
        if (state.animatorState.clip.islooping) {
            return true;
        }
        //@ts-ignore
        return state._finish;
    }
    /**
     * 检查是否正在播放制动动画
     * @param	name 名字播放动画。
     */
    private isSameByName(name: string, layerIndex: number = 0): boolean {
        let layer: Laya.AnimatorControllerLayer = this._anim.getControllerLayer(layerIndex);
        if (layer == undefined) {
            return false;
        }
        let state = layer.getCurrentPlayState()
        if (state == undefined) {
            return false;
        }
        if (state.animatorState.name == name) {
            return true
        }
    }
    /**
     * 播放动画。
     * @param	name 如果为null则播放默认动画，否则按名字播放动画片段。
     * @param	isForce 是否强制替换动作
     */
    public playAnim(name: string, isForce: boolean = false) {
        if (isForce) {
            if (this.isSameByName(name)) {
                return;
            } else {
                this._anim.play(name, 0, 0)
            }
            return;
        }
        if (this.isPlayanimaByName()) {
            return;
        } else {
            this._anim.play(name, 0, 0)
        }

    }

}
