
import { AnimatorControl } from "../AnimatorControl";
import GameManager, { MusicType } from "./GameManager";
import ParticleTool from "./ParticleTool";
const { regClass, property } = Laya;

@regClass('eWO-rqURQ6ag7phXtJPZCA')
export default class EnemyMove extends Laya.Script {
    //@property()
    followTarget: Laya.Sprite3D = null;

    @property({ type: AnimatorControl})
    public anim: AnimatorControl;

    @property({ type: "number"})
    public speed: number = 0.02;

    private mLife: number = 5;

    private mIsEnd: boolean = false;

    private mIsAttack: boolean = false;

    private mIsAttackStart: boolean = false;

    private mCanPlayFirst: boolean = true;

    private mAttackDelayTime: number = 0;

    private mCurrentIndex: number = 0;
    constructor() {
        super();
        this.followTarget = GameManager.instance.getCurrentHero();
    }


    /**
     * 每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
     * 此方法为虚方法，使用时重写覆盖即可
    */
    onUpdate(): void {
        if (this.mIsEnd) {
            return;
        }
        if (this.followTarget == null) {
            this.followTarget = GameManager.instance.getCurrentHero();
        }
        var Vec1 = new Laya.Vector3(this.followTarget.transform.position.x, 0, this.followTarget.transform.position.z);
        var Vec2 = new Laya.Vector3((this.owner as Laya.Sprite3D).transform.position.x, 0, (this.owner as Laya.Sprite3D).transform.position.z)
        var dis = Laya.Vector3.distance(Vec1, Vec2);
        if (dis < 10) {
            if (dis <= 1) {
                if (this.mIsAttack) {
                    this.isAttackendByPlayer();
                }
                else {
                    if (this.mCanPlayFirst) {
                        this.mCanPlayFirst = false;
                        this.attackPlayer();
                    }
                    else {
                        this.mAttackDelayTime = this.mAttackDelayTime + Laya.timer.delta;
                        if (this.mAttackDelayTime > 1000) {
                            this.mAttackDelayTime = 0;
                            this.attackPlayer();
                        }
                    }
                    this.mIsAttackStart = false;
                }
                GameManager.instance.insertValidList(this.owner);
            }
            else {
                this.mAttackDelayTime = 0;
                var cha = new Laya.Vector3();
                Laya.Vector3.subtract(Vec1, Vec2, cha);
                Laya.Vector3.normalize(cha, cha);
                (this.owner as Laya.Sprite3D).transform.lookAt(Vec1, Laya.Vector3.Up);
                var curpos: Laya.Vector3 = (this.owner as Laya.Sprite3D).transform.position;
                curpos.vadd(new Laya.Vector3(cha.x * this.speed, 0, cha.z * this.speed), curpos);
                (this.owner as Laya.Sprite3D).transform.position = curpos;
                this.anim && this.anim.playAnim("run", true);
                this.mCanPlayFirst = true
                this.mIsAttackStart = false;
                this.mIsAttack = false;
            }
        }
        else {
            if (this.anim) {
                this.anim.playAnim("ldle", true);
            }
            GameManager.instance.removeValidListByValue(this.owner);
        }

        if (this.mLife <= 0) {
            this.mIsEnd = true;
            if (this.anim) {
                this.anim.playAnim("die", true);
            }
            setTimeout(() => {
                GameManager.instance.removeValidListByValue(this.owner);
                this.owner.removeSelf();
                var plane = GameManager.instance.getCurrentPlane();
                var index = this.mCurrentIndex;
                GameManager.instance.createEnemy(plane.getChildAt(index) as Laya.Sprite3D, index);
            }, 600)
        }
    }

    setCurrentIndex(index: number) {
        this.mCurrentIndex = index;
    }

    public setLife(_life: number) {
        this.mLife = _life;
    }

    private attackPlayer() {
        ParticleTool.playEnemyHurtSkillEffect("resources/prefab/eff_monster.lh", this.owner.getChildAt(0) as Laya.Sprite3D);
        GameManager.instance.playSound(MusicType.eEnemyAttack);
        this.anim && this.anim.playAnim("attack", true);
        setTimeout(() => {
            if (this.mIsEnd) {
                return;
            }
            this.anim && this.anim.playAnim("ldle", true);
        }, 500)
    }

    private isAttackendByPlayer() {
        //打断攻击
        if (!this.mIsAttackStart) {
            this.anim && this.anim.playAnim("shouji", true);
            ParticleTool.playNormalEffect("resources/prefab/eff_shouji.lh", this.owner.getChildAt(0) as Laya.Sprite3D);
            setTimeout(() => {
                if (this.mIsEnd) {
                    return;
                }
                this.anim && this.anim.playAnim("shouji", true);
                this.mIsAttack = false;
            }, 500);
            this.mIsAttackStart = true;
        }
    }


    public getLife() {
        return this.mLife;
    }

    public setIsAttack(state: boolean) {
        this.mIsAttack = state;
    }
}