        // 解析URL参数的方法
        private static getURLParameters(url: string): any {
            const params: any = {};
            url.replace(/[?&]+([^=&]+)=([^&]*)/gi, (_match: string, key: any, value: any):string => params[key] = decodeURIComponent(value));
            return params;
        }









    public static cheakStr(nameStr: string): boolean {
        let checkStr: RegExp = new RegExp("[A-Za-z0-9_-\\s]", "g");
        let ary: any[] = nameStr.match(checkStr);
        if(ary && ary.length == nameStr.length ) {
            return true;
        } 
        return false;
    }
