import {GameLanguageMgr} from "./GameLanguageMgr";
import {NoticeMgr} from "../module/notice/NoticeMgr";
export class ErrorPopManager {
    private static _instance: ErrorPopManager;
    public static get instance(): ErrorPopManager {
        if(!ErrorPopManager._instance ) {
            ErrorPopManager._instance = new ErrorPopManager();
        } 
        return ErrorPopManager._instance;
    }
    public errData: any;
    private _curErrTxt: string = "";
    public constructor() {
        
    }
    public initErrData(_data: any): void {
        this.errData = _data;
    }
    public get(id: any): string {
        if(this.errData[id])
            return this.errData[id]["words"];
        else{
            console.warn("服务端错误码语言包未配表 错误码：" + id);
            return id;
        }
            
    }
    public showErrorWord(id: number, para: number = 999999): void {
        if(this.errData[id] && this.errData[id].hasOwnProperty("words") ) {
            this._curErrTxt = this.errData[id]["words"];
            if(para != 999999 ) {
                this._curErrTxt = GameLanguageMgr.instance.replacePlaceholder(this._curErrTxt, [para]);
            } 
        } else {
            this._curErrTxt = "" + id;
        }
        NoticeMgr.instance.notice(this._curErrTxt);
    }
    public showErrByString(value: string, color: string = "#ff0000"): void {
        this._curErrTxt = value;
        let _txt: Laya.Label = new Laya.Label(this._curErrTxt);
        _txt.width = 500;
        _txt.align = "center";
        _txt.height = 40;
        _txt.fontSize = 26;
        _txt.color = color;
        _txt.mouseEnabled = false;
        _txt.strokeColor = "#000000";
        _txt.stroke = 0.5;
        Laya.stage.addChild(_txt);
        _txt.x = (Laya.stage.width - _txt.width) / 2;
        _txt.y = (Laya.stage.height - _txt.height) / 2;
        Laya.Tween.to(_txt, {y: _txt.y - 100, alpha: 0}, 500, null, Laya.Handler.create(this, this.completeHandler, [_txt]), 1000);
    }
    private completeHandler(value: Laya.Label): void {
        if(value && value.parent ) {
            value.parent.removeChild(value);
        } 
    }
    public dispose(): void {
        this.errData = null;
    }
}