const { regClass } = Laya;
import GameManager from "../../../script/logic/GameManager";
import { GameResourceManager } from "../../common/GameResourceManager";
import { LayerManager } from "../../common/LayerManager";
import { ModuleManager } from "../../common/ModuleManager";
import { PlatFormManager } from "../../common/PlatFormManager";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import { SheetDataManager } from "../../global/SheetDataManager";
import { ArenaItem } from "../../global/data/sheetItem/match/ArenaItem";
import { GameEvent } from "../../global/event/GameEvent";
import { Signal } from "../../global/event/Signal";
import { Quick } from "../../global/util/Quick";
import { TimeUtil } from "../../global/util/TimeUtil";
import RoomPlayerDto from "../../net/protocol/dto/RoomPlayerDto";
import FirstBloodPush from "../../net/protocol/login/FirstBloodPush";
import MutiKillPush from "../../net/protocol/login/MutiKillPush";
import { MatchMap1001 } from "./MatchMap1001";
import { MatchModel } from "./MatchModel";
import { MatchSceneBase } from "./MatchScene.generated";
import { MatchSkillListItem } from "./MatchSkillListItem";

@regClass('sX0uLhOQSmm8b-_sHNqe5w')
export class MatchScene extends MatchSceneBase {

    public m_iLayerType: number;
  public m_iPositionType: number;
  onAwake(): void {
      this.m_iLayerType = LayerManager.M_POP;
      this.m_iPositionType = LayerManager.UP;
      // this.m_ioffsetX = 20;
      // if(GameSetting.m_bIsIphoneX ) {
      //     this.m_ioffsetY = GameSetting.IPHONEX_TOP;
      // } 

      GameManager.instance.init();
    //   this.m_scene = this.owner;
    //   GameManager.instance.scene = this.m_scene;
      // GameManager.instance.gameMap = this.gameMap;
      // GameManager.instance.box_players = this.box_players;
      
      // GameManager.instance.sceneWindow = this.sceneWindow;
      // GameManager.instance.initPlayer(this.roleAni);

    //   GameManager.instance.createPlayers(this.enemy);
    //   GameManager.instance.createItems(this.dropBox);
      //GameManager.instance.setCurrentPlane(this.planeNode);
  }
  private liveNum:number = 0;
  // private hasFree: boolean = false;

  onOpened(_type: any): void {
    var remainStartTime =  MatchModel.instance.matchDto.startTime - MatchModel.instance.matchDto.nowTime;
    if(remainStartTime>0){
      ModuleManager.intance.openModule(ModuleName.MatchReadyView);
    }

    var arenaId = MatchModel.instance.matchDto.arenaId;


    var arenaItem:ArenaItem = SheetDataManager.intance.m_dicArenaDic.get(arenaId);
    MatchModel.instance.currArenaItem = arenaItem;

      
    var mapId = MatchModel.instance.currArenaItem.mapid;
    var mapPrefab = Laya.loader.getRes("gameIcon/map/map_"+mapId+".lh");

    var matchMap1001:MatchMap1001 = Laya.Pool.getItemByCreateFun("MatchMap1001", mapPrefab.create, mapPrefab);
    matchMap1001.init();
    GameManager.instance.gameMap = matchMap1001;
    GameManager.instance.box_players = matchMap1001.box_players;

    this.matchBox.removeChildren();

    this.matchBox.addChild(matchMap1001);



    var playerDto = GlobalDataManager.instance.playerDto;
    this.tf_roleName.text = playerDto.name;
      

      let headId = playerDto.icon;
      this.playerIcon.headSkin = headId;
      // this.img_head.skin = GameResourceManager.instance.getPlayerIcon(headId);
      // this.that = this;
      GameManager.instance.initMatch(this, this.onInitEnd);
      //EVENT ::打开主界面
      PlatFormManager.instance.sendCustumEvent(42);
      
  }

  onInitEnd(obj:any){
    obj.updateSkill();
    obj.updateKill();
    obj.updateBattleCD();
    // 注册每帧更新事件
    Laya.timer.frameLoop(10, obj, obj.updateCharactersOrder);

  }


  onRestoreData(){
    GameManager.instance.updateMatch(this, this.onInitEnd);
  }


  updateSkill(){

    var myPlayer = GameManager.instance.myPlayer;
    var myHeroItem = SheetDataManager.intance.m_dicHeros.get(myPlayer.heroId);
    // myHeroItem.skillIds= [40001,40002,40003,40004,50001];//TODO:: TEST 测试完去掉
    console.log("updateSkill");
    console.log(myHeroItem.skillIds);

    var len = myHeroItem.skillIds.length;
    for(var i = 0; i < len; i++){
        var skillId:number = myHeroItem.skillIds[i];
        var skillItem = SheetDataManager.intance.m_dicSkills.get(skillId);
        var matchSkillListItem = ( (this as any)["matchSkill" + (i+1)] as MatchSkillListItem);
        matchSkillListItem.keyNum = (i+1);
        matchSkillListItem.dataSource = skillItem;
        var skillCd:number = myPlayer.skillCd.get(skillId);//毫秒
        if(skillCd){
          matchSkillListItem.updateSkillCooldown(skillCd);
        }
        
        matchSkillListItem.updateGlobalCooldown(myPlayer.commonCd);
        matchSkillListItem.visible = true;

    }

  }



  updateKill(){
    if(GameManager.instance.myPlayer)
        this.tf_kill.text = GameManager.instance.myPlayer.kill+"";
    this.list_rank.array = this.getSortPlayer(GameManager.instance.matchHeroDic.values);
    this.tf_remainer.text = this.liveNum + "";
    this.tf_total.text = "/ " + GameManager.instance.matchHeroDic.values.length;
  }


  getSortPlayer(heros:RoomPlayerDto[]){
    // 根据 age 字段进行升序排序
    var newHeros = heros.slice();
    var endHeros = newHeros.sort((a, b) => b.score - a.score);
    this.liveNum = 0;
    var rankNum = 1;

    endHeros.forEach((roomPlayerDto: RoomPlayerDto) => {
      (roomPlayerDto as any).rankNum = rankNum;
      rankNum++;
      if(roomPlayerDto.hp>0){
        this.liveNum++;
      }
      
    });
    return endHeros;
  }





  public updateBattleCD(): void {
    MatchModel.instance.matchCoolDownTime = Math.round(MatchModel.instance.matchDto.remainderCd/1000);
    if(MatchModel.instance.matchCoolDownTime>0){
        this.onSetCoolTime();
        Laya.timer.loop(1000, this, this.onSetCoolTime);
    }else{
        Laya.timer.clear(this, this.onSetCoolTime);
    }

}

private onSetCoolTime():void{
    if(MatchModel.instance.matchCoolDownTime>=0){
        this.fontClip_reduceTime.value = TimeUtil.format(MatchModel.instance.matchCoolDownTime, ":", false);
        // this.fontClip_reduceTime.value = MatchModel.instance.matchCoolDownTime + "";
        // this.tf_reduceTime.text = MatchModel.instance.matchCoolDownTime + "";
        if(MatchModel.instance.matchCoolDownTime>90){
          // this.tf_reduceTime.color = "#119c09";
          // this.fontClip_reduceTime.filters = null;
          this.fontClip_reduceTime.skin = "res/match1/fontClip_1.png";
        }else if(MatchModel.instance.matchCoolDownTime>10){
          // this.tf_reduceTime.color = "#df8236";
          // this.fontClip_reduceTime.filters = [Quick.YELLOW1];
          this.fontClip_reduceTime.skin = "res/match1/fontClip_2.png";
        }else{
          // this.tf_reduceTime.color = "#de2208";
          // this.fontClip_reduceTime.filters = [Quick.RED1];
          this.fontClip_reduceTime.skin = "res/match1/fontClip_3.png";
          this.scaleEffect();
        }
        // this.tf_reduceTime.text = TimeUtil.format(MatchModel.instance.matchCoolDownTime);
        if(!MatchModel.instance.stopAll)
          MatchModel.instance.matchCoolDownTime--;
    }else{
        Laya.timer.clear(this, this.onSetCoolTime);

        Laya.timer.once(10000, this, this.checkMatchRankData); //10秒时间到，还没收到排行数据，主动请求一下;
       
    }

}

checkMatchRankData(){
  MatchModel.instance.checkMatchRankData();
}
// 使用Tween进行放大缩小效果
scaleEffect() {
  var that = this;
  // 缩放到1.3倍
  Laya.Tween.to(this.fontClip_reduceTime, { scaleX: 1.3, scaleY: 1.3 }, 200, Laya.Ease.linearInOut, Laya.Handler.create(this, function(){
      // 还原到原始大小
      Laya.Tween.to(that.fontClip_reduceTime, { scaleX: 1, scaleY: 1 }, 200, Laya.Ease.linearInOut);
  }));
}


private updateCharactersOrder(): void {
  // 根据人物的 y 坐标动态调整 zOrder
  GameManager.instance.matchHeroSprs.sort((a, b) => {
      return a.y - b.y;
  });

  // 更新人物的 zOrder
  for (let i = 0; i < GameManager.instance.matchHeroSprs.length; i++) {
    GameManager.instance.matchHeroSprs[i].zOrder = i;
    // console.log(GameManager.instance.matchHeroSprs[i].y, GameManager.instance.matchHeroSprs[i].roomPlayerDto.id, GameManager.instance.matchHeroSprs[i].roomPlayerDto.name, ", zOrder: ", i);
  }
}
    private onFirstBloodPush(firstBloodPush:FirstBloodPush): void {
      // 收到第一滴血数据
      var pid:number = firstBloodPush.pid;
      var attPlayerDto:RoomPlayerDto = GameManager.instance.matchHeroDic.get(pid);

      let headId = attPlayerDto.icon;
      this.img_firstBlood_head.skin = GameResourceManager.instance.getPlayerIcon(headId);

      this.img_firstBlood.visible = true;
      this.img_firstBlood.alpha = 0;
      this.img_firstBlood.y = -200;

      Laya.Tween.to( this.img_firstBlood, { y: 92, alpha: 1 }, 200, Laya.Ease.linearInOut, Laya.Handler.create(this, this.onFirstBlood1) );
    }

    private onFirstBlood1():void{


      this.zi_firstBlood.scale(1, 1); // 初始缩小
      Laya.Tween.to(this.zi_firstBlood, { scaleX: 1.5, scaleY: 1.5}, 200, Laya.Ease.linearNone, Laya.Handler.create(this, this.onFirstBlood2));
    }

    private onFirstBlood2():void{
      Laya.Tween.to(this.zi_firstBlood, { scaleX: 1, scaleY: 1}, 200, Laya.Ease.linearNone, Laya.Handler.create(this, this.onFirstBlood3));  
    }
    private onFirstBlood3():void{
        // 还原到原始大小
        Laya.Tween.to(this.img_firstBlood, { y: -200, alpha: 0 }, 200, Laya.Ease.linearInOut, Laya.Handler.create(this, this.onFirstBlood4), 1500);
    }

    private onFirstBlood4():void{
        this.img_firstBlood.visible = false;    
    }





    private onMutiKillPush(mutiKillPush:MutiKillPush): void {
      this.fontClip_kill.value = mutiKillPush.kill + "";
      var is3More = false;
      if(mutiKillPush.kill>=3){
        is3More = true;
        this.img_mutiKill.skin = "res/match1/img_kill_3s.png";
      }else{
        this.img_mutiKill.skin = "res/match1/img_kill_2s.png";
      }
      // 收到多杀数据
      
      var pid:number = mutiKillPush.pid;
      var attPlayerDto:RoomPlayerDto = GameManager.instance.matchHeroDic.get(pid);

      let headId = attPlayerDto.icon;
      this.img_mutiKill_head.skin = GameResourceManager.instance.getPlayerIcon(headId);

      
      this.img_mutiKill.visible = true;
      this.img_mutiKill.alpha = 0;
      this.img_mutiKill.y = -200;

      Laya.Tween.to( this.img_mutiKill, { y: 175, alpha: 1 }, 200, Laya.Ease.linearInOut, Laya.Handler.create(this, this.onMutiKill1, [is3More]) );
    }

    private onMutiKill1(is3More:boolean):void{


      this.zi_mutiKill.scale(1, 1); // 初始缩小
      if(is3More){
        Laya.Tween.to(this.zi_mutiKill, { scaleX: 1.5, scaleY: 1.5}, 200, Laya.Ease.linearNone, Laya.Handler.create(this, this.onMutiKill2));
      }else{
        this.onMutiKill3(2000);
      }
      
    }

    private onMutiKill2():void{
      Laya.Tween.to(this.zi_mutiKill, { scaleX: 1, scaleY: 1}, 200, Laya.Ease.linearNone, Laya.Handler.create(this, this.onMutiKill3, [1500]));  
    }
    private onMutiKill3(delayTime:number):void{
        // 还原到原始大小
        Laya.Tween.to(this.img_mutiKill, { y: -200, alpha: 0 }, 200, Laya.Ease.linearInOut, Laya.Handler.create(this, this.onMutiKill4), delayTime);
    }

    private onMutiKill4():void{
        this.img_mutiKill.visible = false;    
    }


    private onGameSceneGray(): void {
      this.matchBox.gray = true;
    }



    onEnable(): void {
      // this.onMutiKillPush(44);

        console.log("MatchScene onEnable");
        Signal.intance.on(GameEvent.UPDATE_KILL_NUM, this, this.updateKill);
        Signal.intance.on(GameEvent.EVENT_RESTORE_DATA, this, this.onRestoreData);
        Signal.intance.on(GameEvent.EVENT_FirstBlood, this, this.onFirstBloodPush);
        Signal.intance.on(GameEvent.EVENT_MutiKill, this, this.onMutiKillPush);
        Signal.intance.on(GameEvent.EVENT_GameScene_Gray, this, this.onGameSceneGray);
        Signal.intance.on(GameEvent.EVENT_Game_Delay, this, this.onGameDelay);
        // 注册窗口获得焦点事件
        Laya.stage.on(Laya.Event.FOCUS, this, this.onWindowFocus);

        //   this.btn_back.on(Laya.Event.CLICK, this, this.setToMainScene)


        
    }

    onWindowFocus(){
      GameManager.instance.sendRestoreData();
    }

    onGameDelay(dT:number){
      this.tf_delay.text = dT+"";
    }

    onDisable(): void {
      Signal.intance.off(GameEvent.UPDATE_KILL_NUM, this, this.updateKill);
      Signal.intance.off(GameEvent.EVENT_RESTORE_DATA, this, this.onRestoreData);
      Signal.intance.off(GameEvent.EVENT_FirstBlood, this, this.onFirstBloodPush);
      Signal.intance.off(GameEvent.EVENT_MutiKill, this, this.onMutiKillPush);
      Signal.intance.off(GameEvent.EVENT_GameScene_Gray, this, this.onGameSceneGray);
      Signal.intance.off(GameEvent.EVENT_Game_Delay, this, this.onGameDelay);
      Laya.stage.off(Laya.Event.FOCUS, this, this.onWindowFocus);
      Laya.timer.clearAll(this);
    }



}