const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { LayerManager } from "../../common/LayerManager";
import { ModuleManager } from "../../common/ModuleManager";
import { PlatFormManager } from "../../common/PlatFormManager";
import { SoundMgr } from "../../common/SoundMgr";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import { ETHUtils } from "../../global/contract/utils/ETHUtils";
import { Quick } from "../../global/util/Quick";
import { StringTool } from "../../util/StringTool";
import { NoticeMgr } from "../notice/NoticeMgr";
import { GameSettingLanguageDialogBase } from "./GameSettingLanguageDialog.generated";
import SwitchButton from "./item/SwitchButton";

@regClass('yJfJZzgZRluE_rgB44yTvg')
export class GameSettingLanguageDialog extends GameSettingLanguageDialogBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;


    private musicSwitchButton:SwitchButton;
    private soundSwitchButton:SwitchButton;
    private languageSwitchButton:SwitchButton;
    private inviteUrl:string;
    public constructor(_data: any) {
        super();
    }

    onEnable(): void {        

        this.musicSwitchButton = this.box_music.getComponent(SwitchButton);
        this.soundSwitchButton = this.box_sound.getComponent(SwitchButton);
        this.languageSwitchButton = this.box_language.getComponent(SwitchButton);
        this.initContainer();
        // this.img_open1 = new Laya.Handler(this, this.onClickHandler, [this.img_open1]);
        this.box_music.on(Laya.Event.CLICK, this, this.onClickHandler, [this.box_music]);
        this.box_sound.on(Laya.Event.CLICK, this, this.onClickHandler, [this.box_sound]);
        this.box_language.on(Laya.Event.CLICK, this, this.onClickHandler, [this.box_language]);
        this.btn_copy.clickHandler = new Laya.Handler(this, this.onClickHandler, [this.btn_copy]);
        this.btn_referral.on(Laya.Event.CLICK, this, this.onClickHandler, [this.btn_referral]);
        
    }
    private onClickHandler(btn: any): void {
        switch(btn) {
            case this.box_music:
            {             
                  
                this.musicSwitchButton.isLeft = !this.musicSwitchButton.isLeft;
                SoundMgr.instance.switchMusic(this.musicSwitchButton.isLeft);//背景音乐开关
                // localStorage.setItem("");


            }
            break;
            case this.box_sound:
            {
                this.soundSwitchButton.isLeft = !this.soundSwitchButton.isLeft;
                SoundMgr.instance.switchSound(this.soundSwitchButton.isLeft);//音效开关
            }
            break;
            case this.btn_referral:
            {
                ModuleManager.intance.openModule(ModuleName.InviteRewardDialog)
            }
            break;
            case this.box_language:
            {
                this.languageSwitchButton.isLeft = !this.languageSwitchButton.isLeft;
                let lanType = this.languageSwitchButton.isLeft?"zh":"en";
                
                GameLanguageMgr.instance.FGM_SetLanguage(lanType);
                PlatFormManager.instance.reload();
            }
            break;
            case this.btn_copy:
            {                
                let shareUrl = this.inviteUrl;
                Quick.doCopy(shareUrl);
                // let isSuccess:boolean = this.webCopyString(shareUrl);
                // if(isSuccess){
                NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(132));//复制成功
            }
            break;
        }
    }

    private initContainer(): void {
        this.musicSwitchButton.isLeft = SoundMgr.instance.m_bPlayMusic;//背景音乐开关
        this.soundSwitchButton.isLeft = SoundMgr.instance.m_bPlayeSound;//音效开关

        this.languageSwitchButton.isLeft = GlobalDataManager.instance.m_strLanguage == 6;
        this.inviteUrl = ETHUtils.getInviteUrl();
        // inviteUrl.substr

        this.label_url.text = StringTool.shortStr(this.inviteUrl, 50) ;


    }


    public addEvent(): void {
        
    }
    public removeEvent(): void {
        
    }

    //@param str 要复制的文本
    //@return 复制是否成功
    public webCopyString(str:string):boolean {
        let input:string = str;
        const el:any = window.document.createElement('textarea');
        el.value = input;
        el.setAttribute('readonly', '');
        el.style.contain = 'strict';
        el.style.position = 'absolute';
        el.style.left = '-9999px';
        el.style.fontSize = '12pt'; // Prevent zooming on iOS
        const selection:any = window.getSelection();
        let originalRange:any = false;
        if (selection.rangeCount > 0) {
            originalRange = selection.getRangeAt(0);
        }
        window.document.body.appendChild(el);
        el.select();
        el.selectionStart = 0;
        el.selectionEnd = input.length;
        let success:boolean = false;
        try {
            success = window.document.execCommand('copy');
        }catch(err) {
            console.error(err);
            
        }
        window.document.body.removeChild(el);
        if (originalRange) {
            selection.removeAllRanges();
            selection.addRange(originalRange);
        }
        return success;
    }
}