
/** N杀推送 */
class MutiKillPush {

    pid: number = 0;
    kill: number = 0;

    static PROTOCOL_ID: number = 1030;

    protocolId(): number {
        return MutiKillPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: MutiKillPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.kill);
        buffer.writeLong(packet.pid);
    }

    static read(buffer: any): MutiKillPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new MutiKillPush();
        const result0 = buffer.readInt();
        packet.kill = result0;
        const result1 = buffer.readLong();
        packet.pid = result1;
        return packet;
    }
}

export default MutiKillPush;
