    public isCloseAll: boolean = true;
    public getPreSceneData(): any[] {
        if(this.m_arrFromScenes.length < 1 ) {
            return [SceneType.M_SCENE_MAIN, true, 1, null];
        } else {
            let secenData: any[] = this.m_arrFromScenes.pop();
            console.log("pop-----------route：" + this.m_arrFromScenes.join(" | "));
            return secenData;
        }
        return [SceneType.M_SCENE_MAIN, true, 1, null];
    }
    public removePreSceneData(): void {
        this.m_arrFromScenes = [];
    }
    public setBackScene(): void {
        let preSceneData: any[] = this.getPreSceneData();
        this.setCurrentScene(preSceneData[0], preSceneData[1], false);
    }
    public setMainScene(type: number = 1, data: any = null): void {
        this.setCurrentScene(SceneType.M_SCENE_MAIN, data);
    }
