const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { SheetDataManager } from "../../global/SheetDataManager";
import { ItemItem } from "../../global/data/sheetItem/item/ItemItem";
import { ShopItem } from "../../global/data/sheetItem/shop/ShopItem";
import { NumberUtil } from "../../global/util/NumberUtil";
import { XUtils } from "../../util/XUtils";
import { ShopListItem2Base } from "./ShopListItem2.generated";

@regClass('din69PYeRbSapMB2H6E06g')
export class ShopListItem2 extends ShopListItem2Base {

    public shopItem: ShopItem;
    private isAddEvent:boolean = false;
    
    public constructor() {
        super();
    }


    //橙色  fdd378






    public set dataSource(value: any) {
        super.dataSource = value;
        this.shopItem = value as ShopItem;
        if(!this.shopItem ) {
            return
        }
        // this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {
        
        this.tf_name.text = GameLanguageMgr.instance.getLanguage(this.shopItem.title);
            // this.tf_des.text = GameLanguageMgr.instance.getLanguage(this.shopItem.des);
        this.img_const.skin = GameResourceManager.instance.getMoneyIcon(this.shopItem.cost[0]);
        this.tf_const.text = NumberUtil.formatNumber(this.shopItem.cost[1])+"";

        if(this.shopItem.type == 2){
            var itemItem:ItemItem = SheetDataManager.intance.m_dicItems.get(this.shopItem.icon);
            this.img_bg.skin =  GameResourceManager.instance.getItemQualityIcon(itemItem.quality+"");

            this.tf_name.color = XUtils.qualityColors[itemItem.quality - 1];
            
            this.img_icon.skin = GameResourceManager.instance.getItemIcon(this.shopItem.icon);
            // this.box_count.visible = true;
        }        
        // this.tf_get.text = GameLanguageMgr.instance.getLanguage("111", this.shopItem.mineralLevel);
    }

}