

class RoomRankPlayerDto {

    /** 玩家唯一id */
    id: number = 0;
    /** 出战英雄 */
    heroId: number = 0;
    heroColor: number = 0;
    name: string = '';
    icon: string = '';
    /** 是否队长，1=是，0=否 */
    leader: number = 0;
    /** 排名 */
    rankNum: number = 0;
    /** 击杀数 */
    kill: number = 0;
    /** 伤害值 */
    atk: number = 0;
    /** 积分 */
    score: number = 0;
    /** 是否死亡 */
    die: boolean = false;
    country: number = 0;

    static PROTOCOL_ID: number = 25012;

    protocolId(): number {
        return RoomRankPlayerDto.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomRankPlayerDto | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeLong(packet.atk);
        buffer.writeInt(packet.country);
        buffer.writeBoolean(packet.die);
        buffer.writeInt(packet.heroColor);
        buffer.writeLong(packet.heroId);
        buffer.writeString(packet.icon);
        buffer.writeLong(packet.id);
        buffer.writeInt(packet.kill);
        buffer.writeInt(packet.leader);
        buffer.writeString(packet.name);
        buffer.writeInt(packet.rankNum);
        buffer.writeLong(packet.score);
    }

    static read(buffer: any): RoomRankPlayerDto | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomRankPlayerDto();
        const result0 = buffer.readLong();
        packet.atk = result0;
        const result1 = buffer.readInt();
        packet.country = result1;
        const result2 = buffer.readBoolean(); 
        packet.die = result2;
        const result3 = buffer.readInt();
        packet.heroColor = result3;
        const result4 = buffer.readLong();
        packet.heroId = result4;
        const result5 = buffer.readString();
        packet.icon = result5;
        const result6 = buffer.readLong();
        packet.id = result6;
        const result7 = buffer.readInt();
        packet.kill = result7;
        const result8 = buffer.readInt();
        packet.leader = result8;
        const result9 = buffer.readString();
        packet.name = result9;
        const result10 = buffer.readInt();
        packet.rankNum = result10;
        const result11 = buffer.readLong();
        packet.score = result11;
        return packet;
    }
}

export default RoomRankPlayerDto;
