    public static get instance(): HeroModel {
        if(!HeroModel._instance ) {
            HeroModel._instance = new HeroModel()
        } 
        return HeroModel._instance;
    }
    public constructor() {
        this.init();
    }


    public init(): void {
        if(!this._inited ) {

            this._inited = true;
        } 
    }

    public initUpgrade(): void {
        if(!this._initUpgrade ) {
            let sheetManager: SheetDataManager = SheetDataManager.intance;
            if(!sheetManager.m_dicHeroLv ) {
                //hero_lv|英雄等级	hero_star|英雄星级	race|种族
                sheetManager.m_dicHeroLv = sheetManager.initConfig("hero_lv", ["hero_lv", "hero_star", "race"], HeroLvSheetItem)
            }
            if(!sheetManager.m_dicHeroAttup ) {
                //race|種族	 star_lv|星級	
                sheetManager.m_dicHeroAttup = sheetManager.initConfig("hero_attup", ["star_lv", "race"], HeroAttupSheetItem)
            }
            this._initUpgrade = true;
        }
    }



    public heroList: any[] = [];

    private getHead(sex: number): string {
        let index: number = Math.floor(Math.random() * 3) + 1;
        return "common/common_icon/img_" + sex + "_" + index + ".png";
    }

    getNextHero(vo:HeroVo):HeroVo {
        let index = GlobalDataManager.instance.m_heroData.heroVoDic.keys.indexOf(vo.pk);
        if(GlobalDataManager.instance.m_heroData.heroVoDic.keys.length === 1){
            return null;
        }
        if(index <= GlobalDataManager.instance.m_heroData.heroVoDic.keys.length - 2){
            return GlobalDataManager.instance.m_heroData.heroVoDic.values[index + 1] 
        }
        return null;
    }
    getLastHero(vo:HeroVo):HeroVo {
        let index = GlobalDataManager.instance.m_heroData.heroVoDic.keys.indexOf(vo.pk);

        if(index > 0){
            return GlobalDataManager.instance.m_heroData.heroVoDic.values[index- 1]
        }
        return null;
    }

    public setStar(ui:any, _star: string):void{
        console.log("setStar: _star " + _star);

        let star:number = parseInt(_star);

        var ziNum = Math.floor(star/6);
        var yellowNum = star%6;
        var totalNum = ziNum + yellowNum
        let m:number = 0;

        for(let i:number = 0; i< ziNum; i++){
            ui["img_star" + (i+1) ].skin = "gameIcon/hero/heroDetail/img_star_2.png";
            ui["img_star" + (i+1) ].visible = true;
        }
        for(let i:number = ziNum; i< yellowNum; i++){
            ui["img_star" + (i+1) ].skin = "gameIcon/hero/heroDetail/img_star_1.png";
            ui["img_star" + (i+1) ].visible = true;
        }
        for(let i:number = totalNum; i< 5; i++){
            // ui["img_star" + (i+1) ].skin = "gameIcon/hero/heroDetail/img_star_0.png";
            ui["img_star" + (i+1) ].visible = false;
        }
        // for(let i:number = 1;i<6;i++){
        //     // if(i==1){
        //     //     m = ui["img_star"+i].width/2;
        //     // }
            
        //     ui["img_star"+i].visible = i<=star;
        // }
         

        let totalW:number = ui.box_star.width;
        let offsetX:number = 46;
        
        if(totalNum == 1){
            ui["img_star"+1].x = totalW/2 -m;
        }else if(totalNum == 2){
            ui["img_star"+1].x = totalW/2 - offsetX/2 -m;
            ui["img_star"+2].x = totalW/2 + offsetX/2 -m;
        }else if(totalNum == 3){
            ui["img_star"+1].x = totalW/2 -m;
            ui["img_star"+2].x = totalW/2 - offsetX -m;
            ui["img_star"+3].x = totalW/2 + offsetX -m;
        }else if(totalNum == 4){
            ui["img_star"+1].x = totalW/2 - offsetX/2 -m;
            ui["img_star"+2].x = totalW/2 + offsetX/2 -m;
            ui["img_star"+3].x = totalW/2 - offsetX*(1/2+1) -m;
            ui["img_star"+4].x = totalW/2 + offsetX*(1/2+1) -m;
        }else{
            ui["img_star"+1].x = totalW/2 -m;
            ui["img_star"+2].x = totalW/2 - offsetX -m;
            ui["img_star"+3].x = totalW/2 + offsetX -m;
            
            ui["img_star"+4].x = totalW/2 - offsetX*2 -m;
            ui["img_star"+5].x = totalW/2 + offsetX*2 -m;
        }


        
    }
    public getRoundName(_roundType: number): string {
        let str: string = GameLanguageMgr.instance.getLanguage(10014, 16, 8);
        if(_roundType == 4 ) {
            str = GameLanguageMgr.instance.getLanguage(10014, 2, 1);
        } else if(_roundType == 3 ) {
            str = GameLanguageMgr.instance.getLanguage(10014, 4, 2);
        } else if(_roundType == 2 ) {
            str = GameLanguageMgr.instance.getLanguage(10014, 8, 4);
        } 
        return str;
    }
 

