const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { LayerManager } from "../../common/LayerManager";
import { ModuleManager } from "../../common/ModuleManager";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import { SheetDataManager } from "../../global/SheetDataManager";
import { RoomDto } from "../../global/contract/vo/RoomDto";
import { ItemItem } from "../../global/data/sheetItem/item/ItemItem";
import { ArenaItem } from "../../global/data/sheetItem/match/ArenaItem";
import { Quick } from "../../global/util/Quick";
import HttpCmd from "../../net/HttpCmd";
import { MatchModel } from "../match/MatchModel";
import { NoticeMgr } from "../notice/NoticeMgr";
import { InputPwdDialogBase } from "./InputPwdDialog.generated";

@regClass('bn6StZy0ROGBH9GCTateCA')
export class InputPwdDialog extends InputPwdDialogBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;


    private roomDto:RoomDto;
    public setOpenData(_data: any) {
        
        if(_data){
            this.roomDto = _data[0];
         
            
        }else{
            
        }
    }

    onEnable(): void {
        
        // this.btn_close.on(Laya.Event.CLICK, this, this.onClose);
        this.btn_join.on(Laya.Event.CLICK, this, this.onJoin);
    
        this.initContainer();        
    }
    initContainer() {
        var itemItem:ItemItem = SheetDataManager.intance.m_dicItems.get(this.roomDto.coinType);
        var coinStr:string =  GameLanguageMgr.instance.getLanguage(itemItem.name);



        var arenaItem:ArenaItem = SheetDataManager.intance.m_dicArenaDic.get(this.roomDto.arenaId);

        // var coinStr:string = coinType == 3001 ? "TOX" : "DTS";
        // var arenaStr:string = arenaId == 1001 ? "Novice Arena" : "General Arena";

        this.tf_roomName.text = GameLanguageMgr.instance.getLanguage(arenaItem.name);

        this.input_pwd.text = "";
    }
    // onClose() {
    //     this.dispose();
    // }



    async onJoin() {
        let pwd:string = this.input_pwd.text;
        // if(pwd != "" && this.roomDto.requirePass){
        //     NoticeMgr.instance.notice("密码不正确！");
        //     return;
        // }
        var arenaItem = SheetDataManager.intance.m_dicArenaDic.get(this.roomDto.arenaId);
        
            if(arenaItem.item && arenaItem.item.length>1){
                var itemId = arenaItem.item[0];
                var needNum = arenaItem.item[1];
                var currNum = GlobalDataManager.instance.m_packageData.getCountById(itemId);
                if(currNum < needNum){
                    Quick.titickNotEnought(itemId);
                    return;
                }
            }
        let data:any = await HttpCmd.requestPost("wxgame/room/join", {roomId:this.roomDto.roomId, pass:pwd})
    
        MatchModel.instance.roomDto = data.roomDto;

                if(data.roomDto){                      
                    
                    ModuleManager.intance.openModule(ModuleName.EnterRoomView);
                    this.close();

                }else{
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
                }
    }
}