const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { LayerManager } from "../../common/LayerManager";
import { SkillItem } from "../../global/data/sheetItem/hero/Skilltem";
import { SkillTipDialogBase } from "./SkillTipDialog.generated";

@regClass('PiBuXnP8ToeveVbB_bfhUQ')
export class SkillTipDialog extends SkillTipDialogBase {

    private skillItem:SkillItem;
    // private equipMenu:EquipMenuView;
    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;


    public setOpenData(_data: any) {
        
        if(_data){
            this.skillItem = _data;
            
        }else{
            
        }
    }
    onEnable(): void {


        this.item.dataSource = this.skillItem;


        this.tf_name.text = GameLanguageMgr.instance.getLanguage(this.skillItem.name);

        // this.txt_type.text = GameLanguageMgr.instance.getLanguage('6060') + GameLanguageMgr.instance.getLanguage(this.skillItem.type);

        // this.txt_desc.text = GameLanguageMgr.instance.getLanguage(this.skillItem.des);



        // type|技能类型
        // distance|攻击距离
        // hurt|伤害比，数值类型，万分比。
        // cd|cd毫秒
        // displacement|是否位移
        // targetsType|目标类型

        var attrs:any[] = [
            ["type", "S_type", GameLanguageMgr.instance.getUILang("S_type_" + this.skillItem.type) ], 
            ["distance", "S_distance", this.skillItem.distance ], 
            ["hurt", "S_hurt", (this.skillItem.hurt/100) + "%" ], 
            ["cd", "S_cd", (this.skillItem.cd/1000) + GameLanguageMgr.instance.getUILang("S_S") ], 
            ["displacement", "S_displacement", this.skillItem.displacement ], 
            ["targetsType", "S_targetsType", GameLanguageMgr.instance.getUILang("S_targetsType" + this.skillItem.targetsType) ]
        ];

        this.list_skillAttrs.array = attrs;

        
        


        // Signal.intance.on(GameEvent.EVENT_HERO_CARD_BACK,this,this.onBack);
    }


    // public onStageResize():void{
    //     super.onStageResize();
    // }
    
    public removeEvent():void{
        // Signal.intance.off(GameEvent.EVENT_HERO_CARD_BACK,this,this.onBack);
    }
}