
/** 资源推送 */
class ResPush {

    /** 资源类型 */
    type: number = 0;
    /** 资源id */
    id: number = 0;
    /** 资源数量 */
    count: number = 0;

    static PROTOCOL_ID: number = 1012;

    protocolId(): number {
        return ResPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: ResPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.type);
        buffer.writeInt(packet.id);
        buffer.writeLong(packet.count);
    }

    static read(buffer: any): ResPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new ResPush();
        if (!buffer.isReadable()) {
            return packet;
        }
        const result0 = buffer.readInt();
        packet.type = result0;
        if (!buffer.isReadable()) {
            return packet;
        }
        const result1 = buffer.readInt();
        packet.id = result1;
        if (!buffer.isReadable()) {
            return packet;
        }
        const result2 = buffer.readLong();
        packet.count = result2;
        return packet;
    }
}

export default ResPush;
