    //通过服务端获取AccountVo
    async getAccountVoFromServer() {
        let info = await HttpCmd.requestPost('cybertron-sys-api/api/v1/account/find',{address:EthereumManager.instance.walletAddress},false,false);
        if(info){
            let vo:PlayerDto = new PlayerDto();
            Object.assign(vo,info)
            console.log('info========' + JSON.stringify(vo))
            return vo;
        }
        return info; 
    }

