    //创建账号
    async createAcccount(nickName:string,gender:number,avatar:string, countryId:number){
        

        try {

        // const {request} = await EthereumManager.instance.publicClient.simulateContract({
        //     account:EthereumManager.instance.walletAddress,
        //     address: GameSetting.TestChain > 0 ? EthereumConfig.CREATE_ACCOUNT_ADDRESS_TEST : EthereumConfig.CREATE_ACCOUNT_ADDRESS,
        //     abi: EthereumConfig.CREATE_ACCOUNT_ABI,
        //     functionName: 'register',
        //     args: [nickName, countryId,gender,avatar]
        //   });
        //   console.log(request);
        //   var hash = await EthereumManager.instance.walletClient.writeContract(request)
        var createAddress:string;
        if(GameSetting.vType == 2){
            createAddress = EthereumConfig.CREATE_ACCOUNT_ADDRESS_SN;
        }else if(GameSetting.vType == 1){
            createAddress = EthereumConfig.CREATE_ACCOUNT_ADDRESS_S0;
        }else{
            createAddress = EthereumConfig.CREATE_ACCOUNT_ADDRESS_TEST;
        }
          var hash = await EthereumManager.instance.walletClient.writeContract({
            account:EthereumManager.instance.walletAddress,
            address: createAddress,
            abi: EthereumConfig.CREATE_ACCOUNT_ABI_S0,
            functionName: 'register',
            args: [nickName, countryId, gender, avatar]
          });
          console.log("============" + hash);
          // 7. Wait for the transaction receipt
            await EthereumManager.instance.publicClient.waitForTransactionReceipt({
                hash,
            });

            console.log(`Tx successful with hash: ${hash}`);
            //   console.log(data);
          console.log("============");
          return hash;
        } catch (err) {
            LoadingManager.instance.hideLoading();
            var msg:string = err.shortMessage;
            if(msg){
                if(msg.indexOf("does not match") != -1){
                    msg = GameLanguageMgr.instance.getLanguage(1118);// "请先切换到MATCH链";
                }
            }
            // console.log(err);
            console.log(msg);
            NoticeMgr.instance.notice(msg);
            if (err instanceof BaseError) {
              const revertError = err.walk(err => err instanceof ContractFunctionRevertedError)
              if (revertError instanceof ContractFunctionRevertedError) {
                const errorName = revertError.data?.errorName ?? ''
                // do something with `errorName`
                console.log("============errorName: " + errorName);
              }
            }
            return null;
          }
        // this.create_contract = new (window as any)['web3'].eth.Contract(EthereumConfig.CREATE_ACCOUNT_ABI, EthereumConfig.CREATE_ACCOUNT_ADDRESS)


        await this.create_contract.methods.register(nickName, countryId,gender,avatar).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
            console.log(err);
            console.log(result);
            if (err) {
                LoadingManager.instance.hideLoading();
                return false;
            }
        });
        let info = await this.getAccountInfo();
        LoadingManager.instance.hideLoading();
        return info;
    }
