    //修改用户资料
    async updateProfile(nickName:string,avatar:string, countryId:number) {
        var sex = GlobalDataManager.instance.playerDto.sex;
        var playerDto = GlobalDataManager.instance.playerDto;
        let info = await HttpCmd.requestPost('wxgame/player/changeInfo',{name:nickName,sex:sex,icon:avatar,country:countryId});
        if(info){
            
            playerDto.icon = info.icon;
            playerDto.country = info.country;
            playerDto.name = info.name;
            playerDto.sex = info.sex;
            return playerDto;
        }
        return null;
    }

    async getTokenId() {
        if(this.tokenId){
            return this.tokenId;
        }
        this.tokenId = await this.account_contract.methods.tokenOfOwnerByIndex(EthereumManager.instance.walletAddress,0).call();
        this.tokenId = parseInt(this.tokenId + '');
        return this.tokenId;
    }

    //获取账户信息
    async getAccountInfo() {
        if(!this.tokenId){
            await this.getTokenId();
        }
        let info = await this.account_contract.methods.getAccountInfo(this.tokenId).call();
        let vo:PlayerDto = new PlayerDto();
        vo.pid = info[0];
        vo.name = info[1];
        vo.icon = info[2];
        vo.lv = info[3];
        vo.birthHeight = info[4];
        vo.birthPlace = info[5];
        vo.sex = info[6];
        vo.influence = info[7];
        console.log('info========' + JSON.stringify(vo))
        return vo;
    }





