    private initContainer(): void {
        this.list_head.vScrollBarSkin = "";
        this.list_head.selectHandler = new Laya.Handler(this, this.handleSelectHead);
        // this.list_head.renderHandler = new Laya.Handler(this, this.onRenderList);
        // this.list_head.visible = true;
        this.createHeadList();
    }
    private handleSelectHead(index: number): void {
        if(index != -1){
            let headItem: HeadItem = this.list_head.selectedItem;
            GlobalDataManager.instance.headId = headItem.id;
            this.playerIcon.headSkin = GlobalDataManager.instance.headId;
        }
    }
    private createHeadList(): void {
        // let arr: any[] = [];
        let m_dicHead: Dictionary = SheetDataManager.intance.m_dicHead;

        this.list_head.array = m_dicHead.values;
        this.list_head.visible = true;
        let selectIndex = -1;
        let i = 0;
        // GlobalDataManager.instance.headId = GlobalDataManager.instance.playerDto.icon;
        this.playerIcon.headSkin = GlobalDataManager.instance.headId;//设置当前角色头像；


        for(let entry of m_dicHead.values){
            if(entry.id == GlobalDataManager.instance.headId){
                selectIndex = i;
                break;
            }
            i++;
        }
        this.list_head.selectedIndex = selectIndex;
    }


/** 切换 用户上传图片  */
onBtnChangePic():void {

    // var _openUploadPicBack:Function = this.openUploadPicBack;
    var zIndex = Laya.Render.canvas.style.zIndex;
    var uploadP = GameLanguageMgr.instance.getUILang("Upload picture");
    var selectI = GameLanguageMgr.instance.getUILang("Select Image");
    var saveC = GameLanguageMgr.instance.getUILang("Save changes");
    openUploadPic(this, zIndex, uploadP, selectI, saveC);
    
}

/** 切换 用户上传图片返回  */
async openPicEnd(openWin:ChangeHeadDialog, base64:string):Promise<void>{
    if(base64.length>500){
        // this.img_head.skin = base64;
        let temp = await HttpCmd.requestPost('wxgame/player/uploadImg',{base64Str: base64}, false, false);
        if(temp){
            GlobalDataManager.instance.headId = temp.imgUrl;
            openWin.playerIcon.headSkin = temp.imgUrl;
            console.log(temp.imgUrl);
        }

    }
}

