export class BaseAlert extends Laya.Dialog {
    protected _view: Laya.View;
    public m_strName: string;
    public canClickMask: boolean = true;
    public constructor() {
        super();
        this.preinitialize();
    }
    protected _onClick(e: Laya.Event): void {
        let btn: Laya.Button = e.target as Laya.Button;
        if(btn ) {
            switch(btn.name) {
                case Laya.Dialog.CLOSE:
                break;
                case Laya.Dialog.CANCEL:
                break;
                case Laya.Dialog.SURE:
                break;
                case Laya.Dialog.NO:
                break;
                case Laya.Dialog.OK:
                break;
                case Laya.Dialog.YES:
                {
                    this.destroy()
                }
                break;
            }
        } 
    }
    protected preinitialize(): void {
        this.init();
    }
    public init(): void {
        
    }
    protected createChildren(): void {
        super.createChildren();
        this.createUI();
        this.initialize();
    }
    public createUI(): void {
        
    }
    protected initialize(): void {
        this.addEvent();
        this.initData();
    }
    public addEvent(): void {
        
    }
    public initData(): void {
        
    }
    public removeEvent(): void {
        
    }
    public disposeDialog(): void {
        Laya.Dialog.manager.close(this);
    }
    public destroy(destroyChild: boolean = true): void {
        this.removeEvent();
        if(this._view ) {
            this._view.destroy();
            this._view = null;
        } 
        super.destroy();
    }
    protected sendData(cmdId: number, data: any[] = null): void {
        if(data == null ) {
            data = []
        } 
    }
}