import ItemDto from '../dto/ItemDto';
import RoomRankPlayerDto from '../dto/RoomRankPlayerDto';

/** 游戏结束推送 */
class RoomGameOverPush {

    roomId: number = 0;
    /** 排行榜 */
    rankList: Array<RoomRankPlayerDto> = [];
    /** 奖励 */
    reward: Array<ItemDto> = [];

    static PROTOCOL_ID: number = 1020;

    protocolId(): number {
        return RoomGameOverPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomGameOverPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writePacketList(packet.rankList, 25012);
        buffer.writePacketList(packet.reward, 25008);
        buffer.writeLong(packet.roomId);
    }

    static read(buffer: any): RoomGameOverPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomGameOverPush();
        const list0 = buffer.readPacketList(25012);
        packet.rankList = list0;
        const list1 = buffer.readPacketList(25008);
        packet.reward = list1;
        const result2 = buffer.readLong();
        packet.roomId = result2;
        return packet;
    }
}

export default RoomGameOverPush;
