const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { LayerManager } from "../../common/LayerManager";
import { SoundMgr } from "../../common/SoundMgr";
import { ModuleName } from "../../global/ModuleName";
import { GameEvent } from "../../global/event/GameEvent";
import { Signal } from "../../global/event/Signal";
import { ItemVo } from "../../global/vo/pack/ItemVo";
import { ItemGetDialogBase } from "./ItemGetDialog.generated";
import { KnapsackItem } from "./KnapsackItem";
import { GetItemDataModel } from "./model/GetItemDataModel";

@regClass('tC2uDJDVQMaI41Lys60O5Q')
export class ItemGetDialog extends ItemGetDialogBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;


    public constructor(_data: any) {
        super();

    }



    onEnable(): void {
        
        this.initContainer();
			SoundMgr.instance.playSoundByName(SoundMgr.soundName32);
    }
    
    private initContainer():void
    {
        
        var data:any = GetItemDataModel.instance.getOneItem();
        if(data)
        {
            var itemVo:ItemVo;
            if(data instanceof Array){
                itemVo = new ItemVo(data[0]);
                itemVo.newAddCnt = data[1];
            }else{
                itemVo = data;
            }


            this.showItem(itemVo);

        }

    }
    
    /**
     * 显示道具 
     * @param itemVO
     */		
    private showItem(data:ItemVo):void
    {
        (this.knapsackItem as KnapsackItem).showAdd = true;
        (this.knapsackItem as KnapsackItem).dataSource = data;
        this.tf_name.text = GameLanguageMgr.instance.getLanguage(data.itemItem.name);
        // (this.knapsackItem.getChildByName("img_icon") as Laya.Image).skin = GameResourceManager.instance.getItemIcon(data.itemItem.icon);
        // (this.knapsackItem.getChildByName("txt_num") as Laya.Label).text = "x" + data.newAddCnt;
        

    }

    onDisable(): void {

        Signal.intance.event(GameEvent.EVENT_CHECK_REMAIN,ModuleName.ItemGetDialog);
    }


}