const { regClass } = Laya;
import { AlertManager } from "../../common/AlertManager";
import { AlertType } from "../../common/AlertType";
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { LayerManager } from "../../common/LayerManager";
import { SceneManager } from "../../common/SceneManager";
import { SceneType } from "../../common/baseScene/SceneType";
import { GameSetting } from "../../global/GameSetting";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { SheetDataManager } from "../../global/SheetDataManager";
import { UnionDto } from "../../global/contract/vo/UnionDto";
import { UnionItem } from "../../global/data/sheetItem/union/UnionItem";
import { Quick } from "../../global/util/Quick";
import HttpCmd from "../../net/HttpCmd";
import { StringTool } from "../../util/StringTool";
import { NoticeMgr } from "../notice/NoticeMgr";

import { UnionSceneBase } from "./UnionScene.generated";

@regClass('VfpfbqXcSQalDIkuosz8Rw')
export class UnionScene extends UnionSceneBase {

    public m_iLayerType: number;
    public m_iPositionType: number;
    onAwake(): void {
        this.m_iLayerType = LayerManager.M_POP;
        this.m_iPositionType = LayerManager.UP;
        // this.m_ioffsetX = 20;
        // if(GameSetting.m_bIsIphoneX ) {
        //     this.m_ioffsetY = GameSetting.IPHONEX_TOP;
        // } 
    }
    private currTab:number = 0;
    private type: number = 1;
    // private hasFree: boolean = false;

    onOpened(_type: any): void {

        this.type = _type;
    }

    onEnable(): void {
  
        // Signal.intance.on(GameEvent.EVENT_HERO_DRAW_BACK, this, this.onDrawBack);

        // this.hideLoadingBySelf = true;

        this.btn_search.on(Laya.Event.CLICK, this, this.onSearch);
        this.btn_join.on(Laya.Event.CLICK, this, this.onJoin);


        if(this.btn_back ) {
            this.btn_back.on(Laya.Event.CLICK, this, this.setToMainScene)
        }
        this.list_union.vScrollBarSkin = "";
        this.list_union.selectHandler = Laya.Handler.create(this, this.onSelectItem, null, false);
        this.list_union.renderHandler = Laya.Handler.create(this, this.onItemRender, null, false);

        // this.listPanel2.vScrollBarSkin = "";



        this.box_show.visible = false;
        this.box_list.visible = false;

        this.initLuckDrawView();

        let dHeight = (Laya.stage.height - GameSetting.DESIGN_HEIGHT)/2;
        
        // this.btn_back.y -= dHeight;

    }



    private unionData:any[];

    

    private async initLuckDrawView():Promise<void>{
        var data = await HttpCmd.requestPost("wxgame/union/list", {});


        if(data){ 
            this.unionData = data.unionList;
                            



        }else{
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
        }

        this.getItemByName("");
        this.box_list.visible = true;


    }


    private items:any[] = [];
    // private hasItem:boolean = false;
    getItemByName(searchName:string) {
        this.list_union.selectedIndex = -1;
        
        this.items = [];
        // this.hasItem = false;
        if(this.unionData){
            var len = this.unionData.length;
        
            for (var i=0;i<len;i++) {
                var _unionData:UnionDto = this.unionData[i];
                var _item:UnionItem = SheetDataManager.intance.m_dicUnions.get(_unionData.id);
                var itemName = GameLanguageMgr.instance.getLanguage(_item.name);
    
                if (StringTool.caseInsensitiveSearchWithRegex(itemName, searchName)) {
                    _item.unionLv = _unionData.lv;
                    _item.number = _unionData.memberNum;
    
                    // this.hasItem = true;
    
                    this.items.push(_item);                
                }
            }
        }

        
        
        // var m_dicUnionItems = SheetDataManager.intance.m_dicUnions.values;
        


        this.list_union.array = this.items;
        if(this.items.length>0)
            this.list_union.selectedIndex = 0;
        else
            this.list_union.selectedIndex = -1;

    }


    onSearch(){
        var searchName:string = this.input_search.text;
        this.getItemByName(searchName);

    }

    onJoin(){
        
        if(this.currUnionItem){
            AlertManager.instance().AlertByType(AlertType.BASEALERTVIEW, GameLanguageMgr.instance.getLanguage(141),//"确认加入公会吗?"
        AlertType.CANCEL, Laya.Handler.create(this, this.onJoinSure));
        }
        

    }

    async onJoinSure(){
        if(this.currUnionItem){
            var data = await HttpCmd.requestPost("wxgame/union/join", {unionId: this.currUnionItem.id});
            var unionDto:UnionDto = data.unionDto;


            if(unionDto){
                GlobalDataManager.instance.playerDto.union = unionDto.id;
                SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MYUNION);
                NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(2000));
            }else{
                NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
            }
        }

    }

    private currUnionItem:UnionItem;



    private onItemRender(cell: any, index: number): void {
        cell.onChange(this.list_union.selectedIndex == index);
    }
            // 

        public onSelectItem(selectIndex:number, x:any): void {
            this.currUnionItem = this.items[selectIndex];
            console.log(this.currUnionItem);
            if(this.currUnionItem){
                // this.img_icon.skin = GameResourceManager.instance.getItemIcon(unionItem.icon);
    
                this.tf_name.text = GameLanguageMgr.instance.getLanguage(this.currUnionItem.name);
                
        
                // this.txt_type.text = GameLanguageMgr.instance.getLanguage('6060') + GameLanguageMgr.instance.getLanguage(unionItem.type);
        
                this.tf_des.text = GameLanguageMgr.instance.getLanguage(this.currUnionItem.declaration);

                this.tf_name.text = GameLanguageMgr.instance.getLanguage(this.currUnionItem.name);
                // this.tf_des.text = GameLanguageMgr.instance.getLanguage(unionItem.des);
    
    
    
            this.tf_lv.text = GameLanguageMgr.instance.getUILang("LV.") + this.currUnionItem.unionLv;
            this.tf_num.text = "<font color='#25ff63'>"+this.currUnionItem.number+"</font>/"+this.currUnionItem.maxNumber;
    
            this.img_lvBg.skin = "res/union1/img_trade_di_" + (this.currUnionItem.unionLv > 3? 4 : this.currUnionItem.unionLv) + ".png";
            this.img_lvIcon.skin = "res/union1/img_trade_" + (this.currUnionItem.unionLv > 3? 4 : this.currUnionItem.unionLv) + ".png";


                this.box_show.visible = true;
            }else{
                
                // this.img_icon.skin = null;    
                // this.tf_name.text = "";
                // this.tf_des.text = "";
                this.box_show.visible = false;
            }
            
        }



    private setToMainScene():void{
        SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN);
    }






    public removeEvent():void{
        // Signal.intance.off(GameEvent.EVENT_HERO_DRAW_BACK, this, this.onDrawBack);

    }
}