
//播放粒子特效
export default class ParticleTool {

    /**
     * 播放英雄粒子
     * @param resName 资源路径
     * @param skillIndex  技能id
     * @param parentNode 父节点
     */
    public static playSkillEffect(resName: string, skillIndex: number, parentNode: Laya.Sprite3D) {
        var array = ["Attack01", "Attack02", "Attack03", "Attack04"];
        var skillName = array[skillIndex];
        Laya.Sprite3D.load(resName, Laya.Handler.create(null, function (sp: any) {
            parentNode.addChild(sp);
            var _p = sp.getChildByName(skillName) as Laya.Sprite3D;
            _p.active = true;
            var _partice = _p.getComponent(Laya.ShurikenParticleRenderer);
            _partice._particleSystem.play();
            setTimeout(() => {
                sp.removeSelf();
            }, 1000)
        }))
    }

    /**
     * 播放敌人攻击特效
     * @param resName 资源路径
     * @param parentNode 父节点
     */
    public static playEnemyHurtSkillEffect(resName: string, parentNode: Laya.Sprite3D) {
        Laya.Sprite3D.load(resName, Laya.Handler.create(null, function (sp: any) {
            parentNode.addChild(sp);
            var _name = "Attack";
            var _p = sp.getChildByName(_name) as Laya.Sprite3D;
            var _partice = _p.getComponent(Laya.ShurikenParticleRenderer);
            _partice._particleSystem.play();
            if (_p.numChildren > 0) {
                for (let index = 0; index < _p.numChildren; index++) {
                    var _p1 = _p.getChildAt(index);
                    var _partice1 = _p1.getComponent(Laya.ShurikenParticleRenderer);
                    _partice1._particleSystem.play();
                }
            }
            setTimeout(() => {
                sp.removeSelf();
            }, 1000)
        }))
    }

    /**
     * 播放普通特效
     * @param resName 资源路径
     * @param parentNode 父节点
     */
    public static playNormalEffect(resName: string, parentNode: Laya.Sprite3D) {
        Laya.Sprite3D.load(resName, Laya.Handler.create(null, function (sp: any) {
            parentNode.addChild(sp);
            var _p = sp;
            var _partice = _p.getComponent(Laya.ShurikenParticleRenderer);
            _partice._particleSystem.play();
            if (_p.numChildren > 0) {
                for (let index = 0; index < _p.numChildren; index++) {
                    var _p1 = _p.getChildAt(index);
                    var _partice1 = _p1.getComponent(Laya.ShurikenParticleRenderer);
                    _partice1._particleSystem.play();
                }
            }
            setTimeout(() => {
                sp.removeSelf();
            }, 1000);
        }))
    }

}