import GmRequest from './login/GmRequest';
import GmResponse from './login/GmResponse';
import LoginBindingRequest from './login/LoginBindingRequest';
import LoginBindingResponse from './login/LoginBindingResponse';
import DisconnectPush from './login/DisconnectPush';
import ResPush from './login/ResPush';
import RoomMoveRequest from './login/RoomMoveRequest';
import RoomMoveResponse from './login/RoomMoveResponse';
import RoomPlayerChangePush from './login/RoomPlayerChangePush';
import RoomPlayerMovePush from './login/RoomPlayerMovePush';
import RoomGameOverPush from './login/RoomGameOverPush';
import AttackRequest from './login/AttackRequest';
import AttackResponse from './login/AttackResponse';
import AttackPush from './login/AttackPush';
import RoomInfoRequest from './login/RoomInfoRequest';
import RoomInfoResponse from './login/RoomInfoResponse';
import FirstBloodPush from './login/FirstBloodPush';
import MutiKillPush from './login/MutiKillPush';
import AnimationRequest from './login/AnimationRequest';
import AnimationResponse from './login/AnimationResponse';
import AnimationPush from './login/AnimationPush';
import TargetMoveRequest from './login/TargetMoveRequest';
import TargetMoveResponse from './login/TargetMoveResponse';
import TargetMovePush from './login/TargetMovePush';
import GameStartPush from './login/GameStartPush';
import RoomPlayerMoveListPush from './login/RoomPlayerMoveListPush';
import IntBoolDto from './dto/IntBoolDto';
import ItemDto from './dto/ItemDto';
import RoomDto from './dto/RoomDto';
import MatchDto from './dto/MatchDto';
import RoomPlayerDto from './dto/RoomPlayerDto';
import RoomRankPlayerDto from './dto/RoomRankPlayerDto';

const protocols = new Map<number, any>();

// initProtocol
protocols.set(101, Error);
protocols.set(103, Ping);
protocols.set(104, Pong);
protocols.set(1001, LoginRequest);
protocols.set(1002, LoginResponse);
protocols.set(1004, LoginAgainPush);
protocols.set(1005, GmRequest);
protocols.set(1006, GmResponse);
protocols.set(1007, LoginBindingRequest);
protocols.set(1008, LoginBindingResponse);
protocols.set(1010, DisconnectPush);
protocols.set(1012, ResPush);
protocols.set(1013, RoomMoveRequest);
protocols.set(1014, RoomMoveResponse);
protocols.set(1016, RoomPlayerChangePush);
protocols.set(1018, RoomPlayerMovePush);
protocols.set(1020, RoomGameOverPush);
protocols.set(1021, AttackRequest);
protocols.set(1022, AttackResponse);
protocols.set(1024, AttackPush);
protocols.set(1025, RoomInfoRequest);
protocols.set(1026, RoomInfoResponse);
protocols.set(1028, FirstBloodPush);
protocols.set(1030, MutiKillPush);
protocols.set(1031, AnimationRequest);
protocols.set(1032, AnimationResponse);
protocols.set(1034, AnimationPush);
protocols.set(1035, TargetMoveRequest);
protocols.set(1036, TargetMoveResponse);
protocols.set(1038, TargetMovePush);
protocols.set(1040, GameStartPush);
protocols.set(1042, RoomPlayerMoveListPush);
protocols.set(25007, IntBoolDto);
protocols.set(25008, ItemDto);
protocols.set(25009, RoomDto);
protocols.set(25010, MatchDto);
protocols.set(25011, RoomPlayerDto);
protocols.set(25012, RoomRankPlayerDto);

class ProtocolManager {
    static getProtocol(protocolId: number): any {
        const protocol = protocols.get(protocolId);
        if (protocol === null) {
            throw '[protocolId:' + protocolId + ']协议不存在';
        }
        return protocol;
    }

    static write(buffer: any, packet: any): void {
        const protocolId = packet.protocolId();
        buffer.writeInt(protocolId);
        const protocol = ProtocolManager.getProtocol(protocolId);
        protocol.write(buffer, packet);
    }

    static read(buffer: any): any {
        const protocolId = buffer.readInt();
        const protocol = ProtocolManager.getProtocol(protocolId);
        const packet = protocol.read(buffer);
        return packet;
    }
}

export default ProtocolManager;
