
/** GM指令响应 */
class GmResponse {

    /** 前端请求序列 */
    sequence: number = 0;
    /** 状态码 */
    code: number = 0;

    static PROTOCOL_ID: number = 1006;

    protocolId(): number {
        return GmResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: GmResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.sequence);
        buffer.writeInt(packet.code);
    }

    static read(buffer: any): GmResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new GmResponse();
        if (!buffer.isReadable()) {
            return packet;
        }
        const result0 = buffer.readInt();
        packet.sequence = result0;
        if (!buffer.isReadable()) {
            return packet;
        }
        const result1 = buffer.readInt();
        packet.code = result1;
        return packet;
    }
}

export default GmResponse;
