const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { UnionRankListItemBase } from "../union/UnionRankListItem.generated";

@regClass('O6o6TJNORIGW9WXbhJf91w')
export class UnionRankItem extends UnionRankListItemBase {

    public constructor() {
        super();
    }

    public createChildren(): void {
        super.createChildren();
    }


    public set dataSource(value: any) {
        if (!value) return;

        // super.dataSource = value;
        const isPlayerSelf = value.id === GlobalDataManager.instance.playerDto.pid;
        const rank = value.rank;
        // const unionLv = Math.min(value.unionLv, 4);

        // this.tf_name.text = value.name;
        this.tf_union.text = GameLanguageMgr.instance.getLanguage(value.name)
        this.tf_rank.text = rank;
        this.tf_score.text = value.score;
        // this.img_lvIcon.skin = `res/union1/img_trade_${unionLv}.png`;

        if (isPlayerSelf) {
            this.bg.skin = "res/matchRank1/img_ranking_bg_me.png";
        } else {
            this.bg.skin = rank < 4 ? `res/rank/img_guild_${rank}di.png` : "res/rank/img_guild_4di.png";
        }

        if (rank < 4) {
            this.img_rankIcon1.skin = `res/matchRank1/img_ranking_${rank}.png`;
            this.img_rankIcon1.visible = true;
            this.img_rankIcon2.visible = false;
        } else {
            this.img_rankIcon2.skin = isPlayerSelf
                ? "res/matchRank1/img_ranking_ranking_me.png"
                : "res/matchRank1/img_ranking_ranking_4.png";

            this.img_rankIcon2.visible = true;
            this.img_rankIcon1.visible = false;
        }
    }
}