import { Quick } from "../global/util/Quick";
import {Debugger} from "./Debugger";
export class Debug {
    public static debugger: Debugger;
    public static isRelease: boolean;
    public static Initialize(stage: Laya.Stage, isRelease: boolean): void {
        Debug.isRelease = isRelease;
        if(this.debugger == null ) {
            this.debugger = new Debugger(stage, isRelease);
            
        } 
    }

    /**
	 * type: 0= 普通输出，1=日志输出
	 */
	public static log(message:any, type:number = 0, color:number = 0):void
	{
//		return;
		var logMsg:string = Quick.GetLogTime + " " + message;
		if(type){
			Debug.addLog(logMsg, type, color);
		}			
		else{
			// if(!Laya.PlatformClass)//手机版不输出 类型0的日志
				// console.log(logMsg);
		}
	}


    private static addLog(message: string, type: number = 0, color: number = 0): void {
        if(this.debugger ) {
            this.debugger.appendText(message, color)
        } 
        console.log(message);
    }


    public static addConsoleLog(...args: any): void {
        if(Debug.debugger ) {
            Debug.debugger.appendText(args, 0)
        } 
        // console.log(message);
    }
}