
/** move */
class RoomMoveResponse {

    x: number = 0;
    y: number = 0;
    /** 速度 */
    speed: number = 0;
    /** 方向 */
    dire: number = 0;
    errorCode: number = 0;
    time: number = 0;

    static PROTOCOL_ID: number = 1014;

    protocolId(): number {
        return RoomMoveResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomMoveResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.dire);
        buffer.writeInt(packet.errorCode);
        buffer.writeInt(packet.speed);
        buffer.writeLong(packet.time);
        buffer.writeInt(packet.x);
        buffer.writeInt(packet.y);
    }

    static read(buffer: any): RoomMoveResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomMoveResponse();
        const result0 = buffer.readInt();
        packet.dire = result0;
        const result1 = buffer.readInt();
        packet.errorCode = result1;
        const result2 = buffer.readInt();
        packet.speed = result2;
        const result3 = buffer.readLong();
        packet.time = result3;
        const result4 = buffer.readInt();
        packet.x = result4;
        const result5 = buffer.readInt();
        packet.y = result5;
        return packet;
    }
}

export default RoomMoveResponse;
