const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { AttrListItemBase } from "./AttrListItem.generated";

@regClass('lWiDi5bCR-CNy4v3ZMd42w')
export class AttrListItem extends AttrListItemBase {
    
    public set dataSource(value: any) {
        super.dataSource = value;
        var attrData = value as any[];
        if(!attrData ) {
            return
        }


        this.img_icon.skin = GameResourceManager.instance.getAttrIcon(attrData[0]);
        this.tf_name.text = GameLanguageMgr.instance.getUILang(attrData[1]);
        this.tf_num.text = attrData[2];

    }
}