

class GameStartPush {

    time: number = 0;

    static PROTOCOL_ID: number = 1040;

    protocolId(): number {
        return GameStartPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: GameStartPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeLong(packet.time);
    }

    static read(buffer: any): GameStartPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new GameStartPush();
        const result0 = buffer.readLong();
        packet.time = result0;
        return packet;
    }
}

export default GameStartPush;
