const { regClass, property } = Laya;
import { BtnColorFilter } from "../filters/BtnColorFilter";
import { SoundMgr } from "../SoundMgr";
@regClass('cL1AmqOiTHmtQWKayPNcTA')
export default class Button extends Laya.Script {
  owner: Laya.Box;
  /** @prop {name:noSound, tips:"不要声音", type:boolean, default:false}*/
  @property({type:"boolean"})
  public noSound: boolean = false;
  constructor() {
    super();
  }
  onEnable() {
    let oldScale = this.owner.scaleX;
    this.owner.on(Laya.Event.MOUSE_DOWN, this, this.onDown);
    Laya.stage.on(Laya.Event.MOUSE_UP, this, this.onUp);
  }

  onDown(){
    if(!this.noSound)
    SoundMgr.instance.playSoundByName(SoundMgr.soundName4);
    BtnColorFilter.addClickFilter(this.owner);
    // Utils.asyncTween(
    //   this.owner,
    //   { scaleX: oldScale * 0.9, scaleY: oldScale * 0.9 },
    //   100
    // );
  }

  onUp(){
    // Utils.asyncTween(this.owner, { scaleX: oldScale, scaleY: oldScale }, 100);
    BtnColorFilter.clearClickFilter(this.owner);
  }

  onDisable(): void {
    BtnColorFilter.clearClickFilter(this.owner);
    this.owner.off(Laya.Event.MOUSE_DOWN, this, this.onDown);
    Laya.stage.off(Laya.Event.MOUSE_UP, this, this.onUp);
  }
}
