const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { LayerManager } from "../../common/LayerManager";
import { PlatFormManager } from "../../common/PlatFormManager";
import { SceneManager } from "../../common/SceneManager";
import { SceneType } from "../../common/baseScene/SceneType";
import { NumberUtil } from "../../global/util/NumberUtil";
import { Quick } from "../../global/util/Quick";
import { ItemVo } from "../../global/vo/pack/ItemVo";
import HttpCmd from "../../net/HttpCmd";
import ItemDto from "../../net/protocol/dto/ItemDto";
import RoomRankPlayerDto from "../../net/protocol/dto/RoomRankPlayerDto";
import { NoticeMgr } from "../notice/NoticeMgr";
import { MatchEndViewBase } from "./MatchEndView.generated";
import { MatchModel } from "./MatchModel";

@regClass('y-nP_lRrQVef9uttVJ0rDw')
export class MatchEndView extends MatchEndViewBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_PANEL;
    public m_iPositionType:number = LayerManager.CENTER;

    private currRoomRankPlayerDto: RoomRankPlayerDto;

    onEnable(): void {

        // SoundMgr.instance.playMusicByName(SoundMgr.soundName_bg, ".mp3");


        this.btn_next.on(Laya.Event.CLICK, this, this.setToMainScene);
        this.btn_next15.on(Laya.Event.CLICK, this, this.setToMainScene);
        this.btn_receive.on(Laya.Event.CLICK, this, this.onReceive);
        this.btn_receive.on(Laya.Event.CLICK, this, this.onReceive);
        this.btn_help.on(Laya.Event.CLICK, this, this.onHelp);
        //EVENT ::打开比赛奖励界面
        PlatFormManager.instance.sendCustumEvent(43);

    }

    private onHelp():void{
        Quick.commTipWithYes(GameLanguageMgr.instance.getLanguage("HELP"));
    }
    private setToMainScene():void{
        SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN);
    }

    private onReceive():void{
        this.setToMainScene();
        if(this.currRoomRankPlayerDto.rankNum < 16){
            // NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(2000));// 冠军包
        }else{
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(2000));// 领取金币奖励
        }
        
    }


    public setOpenData(roomRankPlayerDto: RoomRankPlayerDto) {
        this.currRoomRankPlayerDto = roomRankPlayerDto;

        
        if(roomRankPlayerDto.rankNum < 4){
            this.img_rankIcon.skin = "res/matchEnd1/img_battleend_ranking_"+roomRankPlayerDto.rankNum+".png";

        }else{
            this.img_rankIcon.skin = "res/matchEnd1/img_battleend_ranking_4.png";
        }



        this.list_reward.vScrollBarSkin = "";


        this.tf_rank.text = roomRankPlayerDto.rankNum + "";
        
        

        // this.tf_myPoints.text = roomRankPlayerDto.score + "";

        var items:ItemVo[] = [];

        var itemDtos:ItemDto[] = MatchModel.instance.reward;
        itemDtos.forEach(itemDto => {
            var itemVo = new ItemVo(itemDto.id);
            itemVo.newAddCnt = itemDto.count;
            // itemVo.newAddCnt = NumberUtil.formatNumber(itemDto.count,2);//有小数保留2位
            items.push(itemVo);
        });

        
        // var i1 = new ItemVo(3001); i1.m_iNum = 100;
        // var i2 = new ItemVo(3002); i2.m_iNum = 1000;
        // var i3 = new ItemVo(3003); i3.m_iNum = 200;
        // let heros: any[] = [i1, i2, i3];
        var spaceX = this.list_reward.spaceX;
        var itemRenderW = this.list_reward.itemTemplate.width;
        var len:number = items.length;
        var listW:number = len * itemRenderW + spaceX*(len-1);
        
        this.list_reward.width = listW;

        // if(roomRankPlayerDto.rankNum < 16){
        //     this.btn_receive.label = GameLanguageMgr.instance.getUILang("Redeem CP");
        //     this.btn_receive.visible = true;
        //     this.btn_next.visible = true;
        //     this.btn_next15.visible = false;
        //     this.list_reward_cp.visible = true;//冠军包奖励
        //     this.list_reward.right = 759;
        // }else{
            // this.btn_receive.label = GameLanguageMgr.instance.getUILang("Receive");
            this.btn_receive.visible = false;

            this.btn_next.visible = false;
            this.btn_next15.visible = true;

            this.list_reward_cp.visible = false;

            
            this.list_reward.right = (this.box_outside.width - listW) >> 1;
        // }

        

        this.list_reward.array = items;
        this.list_reward.visible = true;
        let selectIndex = -1;

        this.list_reward.selectedIndex = selectIndex;

    }

    onDisable(): void {
        this.removeEvent();
        MatchModel.instance.clearData();
        
    }

    public removeEvent():void{
        // Signal.intance.off(CreateLoginEvent.INIT_CREATELOGIN_EVENT, this, this.initCreateLoginView);
        // Signal.intance.off(GameEvent.EVENT_CHANGE_HEAD, this, this.onChangeHeadBack);

    }
}