import MatchDto from '../dto/MatchDto';

/** 获取房间信息返回 */
class RoomInfoResponse {

    errorCode: number = 0;
    /** 房间信息都在里面 */
    matchDto: MatchDto | null = null;

    static PROTOCOL_ID: number = 1026;

    protocolId(): number {
        return RoomInfoResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomInfoResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.errorCode);
        buffer.writePacket(packet.matchDto, 25010);
    }

    static read(buffer: any): RoomInfoResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomInfoResponse();
        const result0 = buffer.readInt();
        packet.errorCode = result0;
        const result1 = buffer.readPacket(25010);
        packet.matchDto = result1;
        return packet;
    }
}

export default RoomInfoResponse;
