
import { LayerManager } from "../../../common/LayerManager";
import { BaseView } from "../../../common/base/BaseView";
import HttpCmd from "../../../net/HttpCmd";
import { WebSocketService } from "../../../net/WebSocketService";
import { Signal } from "../../event/Signal";
import { GameSetting } from "../../GameSetting";


export class UnionAttrView extends BaseView {
    
    public constructor() {
        super();
    }
    public get view(): any {
        return this._view as any;
    }
    public createUI(): void {
        this._view = new Laya.View();
        this.m_iPositionType = LayerManager.UP;
        this.addChild(this._view);
        this.onStageResize();
        this.view.tab.dataSource = 3;//设置tab Index

        this.view.attr_list.vScrollBarSkin = "";
        this.view.attr_list.array=[];
        Signal.intance.on(WebSocketService.CMD_13001+'',this,this.getAttrList);
        this.getAttrList();
    }

    async getAttrList(){
        let list = await HttpCmd.requestPost('cybertron-sys-api/api/v1/alliance/attribute/findAll',{});
        if(this.destroyed)
            return;
        if(list.length){
            this.view.attr_list.array = list.slice(0,5)
        }
    }

    public removeEvent(): void {
        super.removeEvent();
        Signal.intance.off(WebSocketService.CMD_13001+'',this,this.getAttrList);
    }
    


    public onStageResize():void{
        super.onStageResize();
        let dH = Laya.stage.height - GameSetting.DESIGN_HEIGHT;
        this.view.la_img_bg.height  += dH;
        this.view.attr_list.height  += dH;
        this.view.height  += dH;
    }

}


