

class TargetMoveRequest {

    /** 目标pid */
    targetId: number = 0;
    targetX: number = 0;
    targetY: number = 0;
    skillId: number = 0;
    /** 谁触发的 */
    sourceId: number = 0;
    time: number = 0;

    static PROTOCOL_ID: number = 1035;

    protocolId(): number {
        return TargetMoveRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: TargetMoveRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.skillId);
        buffer.writeLong(packet.sourceId);
        buffer.writeLong(packet.targetId);
        buffer.writeInt(packet.targetX);
        buffer.writeInt(packet.targetY);
        buffer.writeLong(packet.time);
    }

    static read(buffer: any): TargetMoveRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new TargetMoveRequest();
        const result0 = buffer.readInt();
        packet.skillId = result0;
        const result1 = buffer.readLong();
        packet.sourceId = result1;
        const result2 = buffer.readLong();
        packet.targetId = result2;
        const result3 = buffer.readInt();
        packet.targetX = result3;
        const result4 = buffer.readInt();
        packet.targetY = result4;
        const result5 = buffer.readLong();
        packet.time = result5;
        return packet;
    }
}

export default TargetMoveRequest;
