    /**
 * 设置切换左侧按钮状态
 * @param btn 
 * @param visible 
 * @param selectColor 
 * @param unselectColor 
 */
    private setTabState(btn: any, visible: boolean, selectColor: string, unselectColor: string) {
        btn.getChildByName("selectBox").visible = visible;
        btn.getChildByName("tf_txt").color = visible ? selectColor : unselectColor;
    }

       /**
     * 玩家排行榜
     */
       private onClickSch() {
        this.box_1.visible = true;
        this.list_hero.visible = false;
        this.subWalletBox.visible = false;
        this.setTabState(this.btn_sch, true, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_jia, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_yi, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_subwallet, false, "#143f6b", "#7ff1fe");
    }

    /**
     * 公会排行榜
     */
    private onClickJia() {
        this.box_1.visible = false;
        this.list_hero.visible = true;
        this.subWalletBox.visible = false;
        this.setTabState(this.btn_jia, true, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_sch, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_yi, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_subwallet, false, "#143f6b", "#7ff1fe");
        this.initContainer()
    }

    /**
     * 奖学金排行榜
     */
    private onClickYi() {
        this.box_1.visible = false;
        this.list_hero.visible = true;
        this.subWalletBox.visible = false;
        this.initContainer();
        this.setTabState(this.btn_yi, true, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_sch, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_jia, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_subwallet, false, "#143f6b", "#7ff1fe");
    }

    /**
     * 奖学金排行榜
     */
    private onClickSubwallet() {
        this.box_1.visible = false;
        this.list_hero.visible = false;
        this.subWalletBox.visible = true;
        // this.initContainer();
        this.setTabState(this.btn_subwallet, true, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_sch, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_jia, false, "#143f6b", "#7ff1fe");
        this.setTabState(this.btn_yi, false, "#143f6b", "#7ff1fe");

        let subwalletList: any[] = this.subwalletDataList;
        if (subwalletList) {
            // if(heroVos && heroVos.length>9){
            this.subWalletList.selectedIndex = 1;
            this.subWalletList.array = subwalletList;
            this.subWalletList.visible = true;
            // this.tf_noData.visible = false;
            let selectIndex = -1;

            this.subWalletList.selectedIndex = selectIndex;

        }
    }

    //组件被禁用时执行，例如从节点从舞台移除后
    onDisable(): void {
        this.removeEvent();
    }
    public removeEvent(): void {
        this.btn_back.off(Laya.Event.CLICK, this, this.setToMainScene)
    }
