import {ModuleName} from "../../../global/ModuleName";
import { HeroVo } from "../../../global/contract/vo/HeroVo";
import {GameEvent} from "../../../global/event/GameEvent";
import {Signal} from "../../../global/event/Signal";
import {GetItemDataModel} from "../model/GetItemDataModel";
export class GetItemService {
    private static _instance: GetItemService;
    private _isPop: boolean = false;
    public static get instance(): GetItemService {
        if(!GetItemService._instance ) {
            GetItemService._instance = new GetItemService();
        } 
        return GetItemService._instance;
    }
    public get model(): GetItemDataModel {
        return GetItemDataModel.instance;
    }
    public get isPop(): boolean {
        return this._isPop;
    }
    public startShow(): void {
        console.log("startShow");
        var item = this.model.hasRemain();
        if(!this._isPop && item ) {
            this._isPop = true;
            if(item instanceof HeroVo){
                Signal.intance.event(GameEvent.EVENT_OPEN_MODULE, ModuleName.HeroGetDialog);
            }else{
                Signal.intance.event(GameEvent.EVENT_OPEN_MODULE, ModuleName.ItemGetDialog);
            }
        } 
    }
    private onCheckRemain(data: string): void {
        if( data == ModuleName.ItemGetDialog  ||  data == ModuleName.HeroGetDialog ) {
            var item = this.model.hasRemain();
            if( item ) {
                if(item instanceof HeroVo){
                    Signal.intance.event(GameEvent.EVENT_OPEN_MODULE, ModuleName.HeroGetDialog);
                }else{
                    Signal.intance.event(GameEvent.EVENT_OPEN_MODULE, ModuleName.ItemGetDialog);
                }
                
            } else {
                this._isPop = false
            }
        } 
    }
    public constructor() {
        
        Signal.intance.on(GameEvent.EVENT_CHECK_REMAIN, this, this.onCheckRemain);
    }
}
