const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { RoleAttrListItemBase } from "./RoleAttrListItem.generated";

@regClass('WJ5TDeRBSkC9yMqJ72B0Ow')
export class RoleAttrListItem extends RoleAttrListItemBase {

    public set dataSource(value: any) {
        super.dataSource = value;
        var attrData = value as any[];
        if(!attrData ) {
            return
        }


        this.img_icon.skin = "res/role/img_icon_"+(attrData[0])+".png";
        this.tf_name.text = GameLanguageMgr.instance.getUILang(attrData[1]);
        this.tf_num.text = attrData[2];

    }
    
}