export class BtnColorFilter extends Laya.ColorFilter {
    private static clickFilter: BtnColorFilter = new BtnColorFilter([1, 0, 0, 0, -34, 0, 1, 0, 0, -34, 0, 0, 1, 0, -34, 0, 0, 0, 1, 0]);
    public constructor(mat: any[] = null) {
        super(mat);
    }
    public static addClickFilter(target: Laya.Sprite): void {
        let filters: any[] = target.filters || [];
        filters.push(BtnColorFilter.clickFilter);
        target.filters = filters;
    }
    public static clearClickFilter(target: Laya.Sprite): void {
        let filters: any[] = target.filters;
        if(filters != null && filters.length > 0 ) {
            for(let i: number = filters.length - 1; i > -1; i--) {
                let filter: any = filters[i];
                if(filter instanceof BtnColorFilter ) {
                    filters.splice(i, 1)
                } 
            }
            target.filters = filters;
        } 
    }
}