import { GameLanguageMgr } from "../../../common/GameLanguageMgr";
import BaseDialog from "../../../common/base/BaseDialog";
import { UnionAttrVo } from "../vo/UnionAttrVo";


export class UnionAttributeDetailsDialog extends BaseDialog {
    private vo:UnionAttrVo;
    public constructor(vo:UnionAttrVo) {
        super();
        this.vo = vo;
    }

    public get view(): any {
        return this._view as any;
    }
    public dispose(): void {
        super.dispose();
    }
    public createUI(): void {
        this._view = new Laya.View();
        this.addChild(this._view);

    }

    public initData(): void {
        this.view.img_icon.skin = `gameIcon/attricon/img_daoju_${(this.vo.type - 1000)}.png`;
        this.view.label_hp.text = `${GameLanguageMgr.instance.getLanguage(this.vo.name)} : ${this.getShowValue(this.vo.value)}`;
        this.view.label_level.text  = GameLanguageMgr.instance.getLanguage('304')  + ` : ${this.vo.level}`;
        this.view.lable_desc.text = `${GameLanguageMgr.instance.getLanguage(this.vo.desc)}`;
    }

    private getShowValue(value: any){
        if(this.vo.type === 1003 || this.vo.type === 1005){
            return (parseFloat(value + '')*100).toFixed(1) + '%'; 
        }
        else{
            return parseInt(value);
        }
    }



}