    private sendOutHandler(): void {
        Laya.timer.clear(this, this.sendOutHandler);
        if(this._isSending ) {
            this._isSending = false;
            if(HttpNetService.ErrorIndex >= HttpNetService.RepeatTimes ) {
                LoadingManager.instance.hideLoading();
                HttpNetService.ErrorTips = "Client Error!";
                ModuleManager.intance.openModule(ModuleName.ClientErrView);
                this._callDataArr.length = 0;
            } else {
                HttpNetService.RepeatHandler.run();
            }
        } 
    }
    public EnCodeByte(value: Laya.Byte): Laya.Byte {
        value.pos = 0;
        let _read: MessageReader = new MessageReader();
        let _byt: Laya.Byte = _read.ReadBytes(value, value.length);
        _byt.pos = 0;
        let _newByt: Laya.Byte = new Laya.Byte();
        let index: number = 0;
        let _data: any;
        let _newData: number = 0;
        for(let j: number = 0; j < _byt.length; j++) {
            if(index >= 6 ) {
                index = 1;
            } else {
                index += 1;
            }
            _data = _byt.getByte();
            _newData = _data;
            _data >>= 8 - index;
            _newData <<= index;
            _newData |= _data;
            _newData ^= 3;
            _newByt.writeByte(_newData);
        }
        return _newByt;
    }
    public DecryptByte(value: Laya.Byte): Laya.Byte {
        value.pos = 0;
        let _read: MessageReader = new MessageReader();
        let _byt: Laya.Byte = _read.ReadBytes(value, value.length);
        _byt.pos = 0;
        let _newByt: Laya.Byte = new Laya.Byte();
        let index: number = 0;
        let _data: any;
        let _uint8Arr: Uint8Array = new Uint8Array(_byt.buffer);
        for(let a: number = 0; a < _uint8Arr.length; a++) {
            if(index >= 6 ) {
                index = 1;
            } else {
                index += 1;
            }
            _data = _uint8Arr[a];
            _data ^= 3;
            _data <<= 8 - index;
            _uint8Arr[a] ^= 3;
            _uint8Arr[a] >>= index;
            _uint8Arr[a] |= _data;
        }
        _newByt = new Laya.Byte(_uint8Arr.buffer);
        return _newByt;
    }
