import { HeroVo } from "../../global/contract/vo/HeroVo";
import { Signal } from "../../global/event/Signal";
import { NumberUtil } from "../../global/util/NumberUtil";
import HttpCmd from "../../net/HttpCmd";
import { HeroModel } from "../luckdraw/model/HeroModel";
import { NoticeMgr } from "../notice/NoticeMgr";
import { HeroDetailDialogBase } from "./HeroDetailDialog.generated";
import { Hero1Event } from "./event/Hero1Event";

@regClass('Qri7gUY9SiWsYhtvNCEOQQ')
export class HeroDetailDialog extends HeroDetailDialogBase {

    private _vo:HeroVo;
    // private equipMenu:EquipMenuView;
    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;


    public setOpenData(_data: any) {
        
        if(_data){
            this._vo = _data;
            
        }else{
            
        }
    }

    onComplete(): any {
        // this.eff.play("idle", true);
    }

    onEnable(): void {
        
        this.showViewByVo(this._vo);
        // this.sk.url = this._vo.skSkin;
        // this.sk["_complete"] = new Laya.Handler(this,this.stopAni)
        this.btn_last.on(Laya.Event.CLICK,this,this.onClickLast);
        this.btn_next.on(Laya.Event.CLICK,this,this.onClickNext);
        this.btn_upgrade.on(Laya.Event.CLICK,this,this.onClickUpgrade);
        this.btn_out.on(Laya.Event.CLICK,this,this.onClickOut);
      

    }

    // private stopAni():void{
    //     this.sk && this.sk.paused();
    //     this.sk && this.sk.play("idle", true);
    // }

    onClickUpgrade() {
        ModuleManager.intance.openModule(ModuleName.HeroUpgradeDialog, this._vo);
        
    }
    onClickNext() {
        let vo = HeroModel.instance.getNextHero(this._vo);
        if(vo){
            this._vo = vo;
            this.showViewByVo(vo);
        }
    }
    onClickLast() {
        let vo = HeroModel.instance.getLastHero(this._vo);
        if(vo){
            this._vo = vo;
            this.showViewByVo(vo);
        }
    }


    public addEvent(): void {
        Signal.intance.on(Hero1Event.UPDATE_HERO_UPGRADE_EVENT, this, this.onHeroUpgrade);
        this.stage.on(Laya.Event.CLICK,this,this.hideTips)
        for (let index = 1; index < 7; index++) {
            let btn:Laya.Box = (this as any)['equip' + index] as Laya.Box
            btn.on(Laya.Event.CLICK,this,this.onClickEquip)
        }
    }

    private hideTips(){
        // this.equipMenu.visible = false;
    }

    public onClickEquip(evt:Laya.Event){
        // evt.stopPropagation();
        // let node:EquipItem = evt.currentTarget as EquipItem;
        // EquipNetModel.instance.selectPos = node.dataSource.position;
        // KnapsackNetModel.instance.itemIdForTips = node.dataSource.itemId;
        // EquipNetModel.instance.equipPosVo = node.dataSource;
        // if(!node.dataSource.itemId){
        //     ModuleManager.intance.openModule(ModuleName.EquipSelectDialog)
        //     return;
        // }
        // this.equipMenu.info = node.dataSource;
        // this.equipMenu.visible = true;
        // this.equipMenu.x = node.x + 50;
        // this.equipMenu.y = node.y + 100;
        // if(this.equipMenu.x + 191 > 640){
        //     this.equipMenu.x = 640 - 200;
        // }
    }

    public removeEvent(): void {
        Signal.intance.off(Hero1Event.UPDATE_HERO_UPGRADE_EVENT, this, this.onHeroUpgrade);
    }

    private onHeroUpgrade(){
        this._vo = GlobalDataManager.instance.m_heroData.heroVoDic.get(this._vo.pk);
        if(this._vo){
            this.showViewByVo(this._vo);
        }
        
    }

    public updateEquip(){
        let ary = this._vo.equipDtoList;

        for (let idx = 0; idx < ary.length; idx++) {
            const element = ary[idx];
            (this as any)['equip' + (idx + 1)].dataSource = element;
        }
    }

    async showViewByVo(vo:HeroVo){
        // this.img_head.skin = vo.simgurl;

        this.txt_name.text = vo.name;
        // this.label_lv.text = GameLanguageMgr.instance.getLanguage(304) + '：' + vo.lv;


        // this.img_role.skin = vo.simgurl;


        let star = vo.heroItem.level + '';
        HeroModel.instance.setStar(this, star);
        this.box_star.visible = true;

        this.heroSk.dataSource = vo;


        // public maxHp: number; //maxHp|生命	
        // public atk: number; //atk|攻击	
        // public def: number; //def|防御	
        // public mov: number; //mov|速度	
        // public crt: number; //crt|暴击率	
        // public crtRate: number; //crtRate|爆伤	
        // public immunity: number; //immunity|抗暴	
        // public dex: number; //dex|命中率	
        // public miss: number; //miss|闪避率


        var attrs:any[] = [["maxHp", "MaxHP", vo.heroItem.maxHp], ["atk", "ATK", vo.heroItem.atk], ["def", "DEF", vo.heroItem.def], ["mov", "MOV", vo.heroItem.mov],
        ["crt", "CRT", (vo.heroItem.crt/100) + "%"], ["crtRate", "CrtRate", (vo.heroItem.crtRate/100) + "%" ], ["immunity", "Immunity", vo.heroItem.immunity], ["dex", "Dex", (vo.heroItem.dex/100).toFixed(2) + "%"], ["miss", "MISS", vo.heroItem.miss]];

        this.list_attrs.array = attrs;

        

        // this.lb_attr1.text = GameLanguageMgr.instance.getLanguage(32) + '：' + vo.heroItem.maxHp + ''; //生命
        // this.lb_attr2.text = GameLanguageMgr.instance.getLanguage(33) + '：' + vo.heroItem.atk + '';//攻击
        // this.lb_attr3.text = GameLanguageMgr.instance.getLanguage(34) + '：' + vo.heroItem.def + '';//防御
        // this.lb_attr4.text = GameLanguageMgr.instance.getLanguage(35) + '：' +vo.heroItem.mov + '';//速度

        var skills:any[] = [];
        vo.heroItem.skillIds.forEach((skillId) => {
            var skill = SheetDataManager.intance.m_dicSkills.get(skillId);
            skills.push(skill);

        });

        this.list_skills.array = skills;
        // this.lb_attr5.text = GameLanguageMgr.instance.getLanguage(36) + '：' +(vo.initialCrit*100).toFixed(2) + '%';
        // await ETHBlindBoxProxy.instance.heroOutputRate();
        // let heroOutputRate:HeroOutputRate = HeroModel.instance.heroOutputRateDic.get(parseInt(vo.mineralLevel + ''));
        // if(heroOutputRate){
        //     this.lb_attr6.text = GameLanguageMgr.instance.getLanguage(6049, heroOutputRate.capacity );
        // }   
        

  
        // this.img_sign_0.visible = false;
        // this.img_sign_2.visible = false;

        // switch (vo.status) {
        //     case 0:
        //         this.img_sign_0.visible = true;
        //         break;
        //     case 2:
        //         this.img_sign_2.visible = true;
        //         break;
        // }

        // this.updateEquip();
        this.updatePk();
    }

    updatePk(){
        if(GlobalDataManager.instance.playerDto.fightHeroPk == this._vo.pk){
            this.box_sign.visible = true;
            this.btn_out.disabled = true;
            this.btn_out.label = GameLanguageMgr.instance.getLanguage(1038);
        }else{
            this.box_sign.visible = false;
            this.btn_out.disabled = false;
            this.btn_out.label = GameLanguageMgr.instance.getLanguage(1037);
        }
    }


    async onClickOut() {
        let that = this;
        let data:any = await HttpCmd.requestPost("wxgame/hero/heroFight", {pk:this._vo.pk})


                if(data){ 
                    GlobalDataManager.instance.playerDto.fightHeroPk = data.pk;
                    that.updatePk();
                    Signal.intance.event(Hero1Event.UPDATE_HERO_EVENT);


                }else{
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
                }
    }
}