
/** 短线重连-绑定登陆返回 */
class LoginBindingResponse {

    /** 前端请求序列 */
    sequence: number = 0;
    /** 错误码 */
    errorCode: number = 0;

    static PROTOCOL_ID: number = 1008;

    protocolId(): number {
        return LoginBindingResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: LoginBindingResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.sequence);
        buffer.writeInt(packet.errorCode);
    }

    static read(buffer: any): LoginBindingResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new LoginBindingResponse();
        if (!buffer.isReadable()) {
            return packet;
        }
        const result0 = buffer.readInt();
        packet.sequence = result0;
        if (!buffer.isReadable()) {
            return packet;
        }
        const result1 = buffer.readInt();
        packet.errorCode = result1;
        return packet;
    }
}

export default LoginBindingResponse;
