import { GameLanguageMgr } from "../../../common/GameLanguageMgr";
import { GameResourceManager } from "../../../common/GameResourceManager";
import { SheetDataManager } from "../../SheetDataManager";
import { ItemDto } from "../../contract/vo/ItemDto";
import { EnumConsumeType } from "../../data/enumData/EnumConsumeType";
import { ItemItem } from "../../data/sheetItem/item/ItemItem";

export class ItemVo {
    public constructor(__id: number = 0) {
        if(__id == 0 ) {
            return
        } 
        this.m_iId = __id;
    }
    public m_iOriginalId: number;
    private _m_iId: number;
    private _m_iNum: number;
    private _m_iLevel: number = 0;
    public m_iExp: number;
    public m_bNew: boolean;
    private _m_bSet: boolean;
    public itemItem: ItemItem;

    private _m_sIcon: string;
    public get m_sIcon(): string {
        return this._m_sIcon;
    }
    public set m_sIcon(value: string) {
        this._m_sIcon = value;
    }

    public m_iQuality: number;
    public _m_arrDisc: any[] = [];
    public m_iPrice: number;
    public m_strBuyIcon: string;
    public m_iSellPrice: number;
    public m_strSellIcon: string;
    public newAddCnt: number = 0;
    public isLvUp: boolean = false;
    public isLastUse: number;
    public type: number;
    public subType: number;

    public needTip:boolean = true;

    public get m_iLevel(): number {
        return this._m_iLevel > 0 ? this._m_iLevel : 1;
    }
    public set m_iLevel(value: number) {
        this._m_iLevel = value ? value : 0;
    }
    // public get m_iSalonLevel(): number {
    //     return this._m_iLevel;
    // }
    // public set m_iSalonLevel(value: number) {
    //     this._m_iLevel = value;
    // }
    public get m_iNum(): number {
        return this._m_iNum;
    }
    // public get m_BaseNum(): number {
    //     if(this._m_iNum > 0 && this.m_iLevel > 1 ) {
    //         return this._m_iNum - 1;
    //     } 
    //     return this._m_iNum;
    // }
    public set m_iNum(value: number) {
        this._m_iNum = value;
    }
    public copyItem(): ItemVo {
        let vo: ItemVo = new ItemVo();
        vo.m_iId = this.m_iId;
        vo.m_iOriginalId = this.m_iOriginalId;
        return vo;
    }
    public get m_bTry(): boolean {
        if(this.m_iId == this.m_iOriginalId ) {
            this._m_bSet = false;
        } else {
            this._m_bSet = true;
        }
        return this._m_bSet;
    }

    public get m_strName(): string {
        return GameLanguageMgr.instance.getLanguage(this.itemItem.name);
    }
    public set m_bTry(value: boolean) {
        this._m_bSet = value;
    }
    public m_bSalon: boolean = false;
    public get m_iId(): number {
        return this._m_iId;
    }
    private m_bIsSetId: boolean = false;
    public set m_iId(value: number) {
        this._m_iId = value;
        if(!this.m_bIsSetId ) {
            this.m_bIsSetId = true;
            this.m_iOriginalId = value;
        } 
        this.setConfigData();
    }
    public initDataByArr(itemDto: ItemDto): void {
        this.m_iId = itemDto.id;
        this.m_iNum = itemDto.count;
        // this.m_iLevel = arr[2];
    }
    // public initFaceDataByArr(arr: any[]): void {
    //     this.m_iNum = 1;
    //     this.m_iId = arr[0];
    //     this.m_iLevel = arr[1];
    //     this.m_iExp = arr[2];
    // }
    private setConfigData(): void {
        if(this.m_iId == 0 ) {
            return
        } 
        this.itemItem = SheetDataManager.intance.m_dicItems.get(this.m_iId.toString());
        if(!this.itemItem ) {
            console.log("[          <items> find out id:" + this.m_iId + "             ]");
            return;
        } 
        this.subType = parseInt(this.itemItem.subType + "");

        this.type = parseInt(this.itemItem.type + "");
        this.m_sIcon = this.itemItem.icon;

        if(this.itemItem.buy_price ) {
            this.m_iPrice = this.itemItem.buy_price[0][1];
            let montyId: number = this.itemItem.buy_price[0][0];
            let monyItem: ItemItem = SheetDataManager.intance.m_dicItems.get(montyId);
            if(monyItem ) {
                this.m_strBuyIcon = GameResourceManager.instance.getItemIcon(monyItem.icon);
            }
        } 
        if(this.itemItem.sell_price ) {
            this.m_iSellPrice = parseFloat(this.itemItem.sell_price[0][1]);
            // montyId = this.m_itemVo.sell_price[0][0];
            // monyItem = SheetDataManager.intance.m_dicItems.get(montyId);
            // if(monyItem ) {
            //     this.m_strSellIcon = GameResourceManager.instance.getIconUrl(monyItem.item_img);
            // } 
        } 
        this.m_iQuality = this.itemItem.quality;
        this.setQualityData();
    }
   
    
    public m_arrQualityData: any[];
    private setQualityData(): void {
        this.m_arrQualityData = this.itemItem.starArr;
        return;
    }
    public get getIconURL(): string {
        return GameResourceManager.instance.getItemIcon(this.itemItem.icon)
        // if(EnumConsumeType.isConsumeType(this._m_iId) ) {
        //     return GameResourceManager.instance.getConsumeconUrl(this._m_iId)
        // } else {
        //     return GameResourceManager.instance.getIconUrl(this.m_sIcon)
        // }
    }
    public copy(): ItemVo {
        let itemVo: ItemVo = new ItemVo(this.m_iId);
        itemVo.m_iLevel = this.m_iLevel;
        return itemVo;
    }
    
    public compareName(): string {
        return GameLanguageMgr.instance.getLanguage(this.itemItem.name);
    }
    public compareQuality(): number {
        return parseFloat(this.m_iQuality + "");
    }
    public compareSellPrice(): number {
        return parseFloat(this.itemItem.sell_price[1] + "");
    }
    public compareNum(): number {
        return parseFloat(this.m_iNum + "");
    }
    public compareLevel(): number {
        return parseFloat(this.m_iLevel + "");
    }
    public compareFragment(): number {
        let itemID: number = this.itemItem.id;
        // let hasCount: number = BeautySalonDataModel.instance.getFragmentsCount(itemID);
        // let fragementCount: number = SheetDataManager.intance.getBeautyPieceModel().getNeedFragmentsCount(itemID);
        // return hasCount / fragementCount;
        return 0;
    }
    
    public compareTypeId(): number {
        return parseInt(this.m_iId + "");
    }
    

    
    
}