    /**isAll : 服务器发的是否是背包所有的数量 */
    public updataItemVoByArr(dataArr: any[], isAll: boolean = false): void {
        // console.log("updataItemVoByArr");

        let updateIds: any[] = [];
        let dispatchGetItem: boolean = false;
        let itemDto: ItemDto;
        for(let i: number = 0; i < dataArr.length; i++) {
            
            if(dataArr[i] instanceof Array){
                itemDto = new ItemDto();
                itemDto.id = dataArr[i][0];
                itemDto.count = dataArr[i][1];

            }else{
                itemDto = dataArr[i];
            }
            let itemVo: ItemVo = this.m_dicItemList.get(itemDto.id);
            if(itemVo == null) {
                itemVo = new ItemVo(itemDto.id);
                this.pushItem(itemVo);
                itemVo.m_bNew = true;
            }
            if(isAll ) {
                itemVo.newAddCnt = itemDto.count - itemVo.m_iNum;
                itemVo.m_iNum = itemDto.count;
            } else {
                itemVo.newAddCnt = itemDto.count;
                itemVo.m_iNum = itemDto.count + itemDto.count;
            }

            // itemVo.isLvUp = isLvUp;
            itemVo.initDataByArr(itemDto);
            updateIds.push(itemVo.m_iId);
            if(itemVo.m_iNum <= 0 ) {
                this.removeItem(itemVo)
            } 
            if(itemVo.newAddCnt > 0 && GetItemDataModel.instance.needPop ) {
                if(itemVo.itemItem.subType.toString() == EnumeCatagoryChildType.PET.toString() && itemVo.itemItem.type.toString() == EnumItemType.Item_Type_Items.toString() ) {
                    return
                } 
                GetItemDataModel.instance.addItem(itemVo);
                dispatchGetItem = true;
            }
        }
        if((dispatchGetItem && GetItemDataModel.instance.needPop) && GetItemDataModel.instance.autoPop ) {
            GetItemService.instance.startShow()
        } 
        if(updateIds.length > 0 ) {
            Signal.intance.event(GameEvent.PACKAGE_UPDATE_ITEM, [updateIds])
        } 
    }
