    private sortOnQa(a: ItemVo, b: ItemVo): number {
        let aState: number = a.itemItem.quality;
        let bState: number = b.itemItem.quality;
        if(aState < bState ) {
            return 1;
        } else if(aState > bState ) {
            return -1;
        } else {
            return 0;
        }
    }

