export class BlindBoxVo {
    exchangeToken:string;
    //挖矿等级
    mineralLevel:string;
    price:string;
    //抽卡星级
    rarity:string;
    //显示的价格
    showPrice:string;
    //卡池索引  抽卡时传参
    idx:number;

    /** 消耗的货币数量 */
    currentPrice:string;
    /** 消耗的货币合约地址 */
    currentToken:string;

    /** 最大购买测次数 */
    totalSupply:string;
    /** 已经售出数量 */
    sellAmount:string;
    /** 系列 */
    series:string;
    /** 种族 */
    race:string;
    /** 消耗的货币名称 */
    currentTokenName:string;
    /** 消耗货币合约实体 */
    tokenContract: any;
    /**
     * @msg: 商店类型 1=普通商店 2=限定商店
     * @param {*}
     * @return {*}
     */
    type: number;
}