const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { SheetDataManager } from "../../global/SheetDataManager";
import RoomRankPlayerDto from "../../net/protocol/dto/RoomRankPlayerDto";
import { MatchEndRankListItemBase } from "./MatchEndRankListItem.generated";

@regClass('vpTYNMB4TfGFK2gaXiPAeg')
export class MatchEndRankListItem extends MatchEndRankListItemBase {

    public roomRankPlayerDto: RoomRankPlayerDto;
    private isAddEvent:boolean = false;
    public constructor() {
        super();
    }

    private addEvent(){
        if(!this.isAddEvent){
            // this.btn_join.on(Laya.Event.CLICK, this, this.onJoin);
            this.isAddEvent = true;
            // Signal.intance.on(HeroEvent.EVENT_HERO_DRAW_UPDATE_PRODUCTION, this, this.updateProduction);
        }

    }



    public set dataSource(value: any) {
        super.dataSource = value;
        this.roomRankPlayerDto = value as RoomRankPlayerDto;
        if(!this.roomRankPlayerDto ) {
            return
        }
        this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {
        if(this.roomRankPlayerDto.id == GlobalDataManager.instance.playerDto.pid){
            this.bg.skin = "res/matchRank1/img_ranking_bg_me.png";
        }else{
            if(this.roomRankPlayerDto.rankNum < 4){
                this.bg.skin = "res/matchRank1/img_ranking_bg_"+this.roomRankPlayerDto.rankNum+".png";
            }                
            else
                this.bg.skin = "res/matchRank1/img_ranking_bg_4.png";
        }

        if(this.roomRankPlayerDto.rankNum < 4){
            this.img_rankIcon1.skin = "res/matchRank1/img_ranking_"+this.roomRankPlayerDto.rankNum+".png";
            this.img_rankIcon1.visible = true;
            this.img_rankIcon2.visible = false;
        }else{
            if(this.roomRankPlayerDto.id == GlobalDataManager.instance.playerDto.pid){
                this.img_rankIcon2.skin = "res/matchRank1/img_ranking_ranking_me.png";
            }else{
                this.img_rankIcon2.skin = "res/matchRank1/img_ranking_ranking_4.png";
            }
            
            this.img_rankIcon2.visible = true;
            this.img_rankIcon1.visible = false;
        }
        let headId = this.roomRankPlayerDto.icon;


        this.playerIcon.headSkin = headId;
        
        this.playerIcon.gray = this.roomRankPlayerDto.die;
        

        this.tf_name.text = this.roomRankPlayerDto.name;
        this.tf_rank.text = this.roomRankPlayerDto.rankNum + "";
        this.tf_kill.text = this.roomRankPlayerDto.kill + "";
        this.tf_score.text = this.roomRankPlayerDto.score + "";
    }
}