import {BaseItem} from "../BaseItem";
export class ArenaItem extends BaseItem {


    public id: number; //id	
    public name:string; //名字
    public coinType: number; //货币类型
    public lv: number; //等级
    

    public heroLevel: number; //英雄星级限定
    public item: any[]; //item|消耗道具
    

    public queueTime: number; //队列等待时长（秒）		
    public numberMin: number; //最小参战人数
    public numberMax: number; //最大参战人数
    public fightTime: number; //战斗时长（秒）
    public mapid: number; //mapid|地图id

    public exitTime: number; //加入队列退出倒计时/秒
    public notQuit: number; //开场剩余时间不允许退出/秒


}