import {BaseItem} from "../BaseItem";
export class SheetDictionary {
    private _values: any[];
    private _keys: any[];
    private _valuesObject: any = {};
    private sheetObject: any;
    private BaseItemClass: any;
    public constructor(data: any, BaseItemClass: any) {
        this.sheetObject = data;
        this.BaseItemClass = BaseItemClass;
    }
    public get keys(): any[] {
        if(this._keys ) {
            return this._keys
        } 
        this._keys = [];
        let item: BaseItem;
        for(let key in this.sheetObject) {
            if(!key || key == "" ) {
                continue;
            } 
            this._keys.push(key);
        }
        return this._keys;
    }
    public get values(): any[] {
        if(this._values ) {
            return this._values
        } 
        this._values = [];
        let item: BaseItem;
        for(let key in this.sheetObject) {
            if(this._valuesObject[key] ) {
                item = this._valuesObject[key];
            } else {
                item = new this.BaseItemClass();
                item.init(this.sheetObject[key]);
                this._valuesObject[key] = item;
            }
            this._values.push(item);
        }
        return this._values;
    }
    public get(key: any): any {
        if(this._valuesObject[key] ) {
            return this._valuesObject[key];
        } 
        let data: any = this.sheetObject[key];
        if( data ) {
            return data;
        } 
        let item: BaseItem;
        if(this.BaseItemClass ) {
            item = new this.BaseItemClass();
            if( item.init == null ) {
                console.log("ERROR :<BaseItem> SheetDataManager -->initConfig()" + "  class : " + this.BaseItemClass);
            } 
            item.init(data);
        } 
        this._valuesObject;
        let returnData: any = item ? item : data;
        this._valuesObject[key] = returnData;
        return returnData;
    }
    public indexOf(key: any): number {
        let index: number = this._keys.indexOf(key);
        if(index >= 0 ) {
            return index
        } 
        key = key instanceof String ? Number(key) : (key instanceof Number ? key.toString() : key);
        return this._keys.indexOf(key);
    }
    public clear(): void {
        this.sheetObject = null;
    }
}