import { TxtLoadingView } from "../module/notice/TxtLoadingView";

export class LoadingManager {
    private static _instance: LoadingManager;
    public static get instance(): LoadingManager {
        if(!LoadingManager._instance ) {
            LoadingManager._instance = new LoadingManager();
        } 
        return LoadingManager._instance;
    }
    private txtLoadingView: TxtLoadingView;
    public bgImg: Laya.Image;
    private _backLayer: Laya.Sprite;
    private m_txtLabel: Laya.Label;
    private _isLoading: boolean = false;
    public get isLoading(): boolean {
        return this._isLoading;
    }
    // private effectManager: EffectManager;
    // public walkingAni: Laya.Animation;
    // private AniConfPath: string = "effect/load/load0.json";
    public init(): void {
        // if(this.effectManager == null ) {
        //     this.effectManager = new EffectManager();
        // } 
        if(!this.txtLoadingView ) {
            var txtLoadingPrefab: Laya.Prefab = Laya.loader.getRes("scenes/notice/TxtLoadingView.lh");
            this.txtLoadingView = txtLoadingPrefab.create() as TxtLoadingView;

        } 
    }


    public showLoading(showImmediately: boolean = false): void {
        console.log("showLoading..." + showImmediately);
        if(!this.txtLoadingView ) {
            return;
        } else {
            if(showImmediately ) {
                this._isLoading = true;
                this.showMaskLoading();
                this.showLazyLoading();
                this._isLoading = false;
            } else {
                this._isLoading = true;
                this.showMaskLoading();
                Laya.timer.once(3000, this, this.showLazyLoading);
            }
            if(this.txtLoadingView.m_txtLabel ) {
                this.txtLoadingView.m_txtLabel.visible = false;
            } 
        }
    }
    public showLoadingByInfo(text: string = ""): void {
        console.log("showLoadingByInfo..." + text);
        this._isLoading = true;

            this.showMaskLoading();
            this.showLazyLoading();

        this.txtLoadingView.m_txtLabel.text = text;
        // let textWidth: number;
        // if(this.txtLoadingView.m_txtLabel.textField ) {
        //     textWidth = this.txtLoadingView.m_txtLabel.textField.textWidth
        // } 
        // this.txtLoadingView.m_txtLabel.x = Laya.stage.width - textWidth >> 1;
        this.txtLoadingView.m_txtLabel.visible = true;
    }

    private showMaskLoading(): void {
        if(this._isLoading ) {

                this.txtLoadingView.backLayer.name="backLayer";
                this.txtLoadingView.backLayer.mouseEnabled = true;
                this.txtLoadingView.backLayer.mouseThrough = false;

            this.txtLoadingView.backLayer.visible = true;
        } 
    }
    private showLazyLoading(): void {
        if(this._isLoading ) {
            console.log("showLazyLoading...");

            this.txtLoadingView.visible = true;
            Laya.stage.addChild(this.txtLoadingView);
            // this.txtLoadingView.backLayer.alpha = 0.5;
            this.txtLoadingView.x = (Laya.stage.width - this.txtLoadingView.width) / 2;
            this.txtLoadingView.y = (Laya.stage.height - this.txtLoadingView.height) / 2;
        } 
        Laya.timer.clear(this, this.showLazyLoading);
    }
    public hideLoading(): void {
        console.log("hideLoading...");
        this._isLoading = false;
        if(!this.txtLoadingView ) {
            return;
        } else {
            this.txtLoadingView.removeSelf();

        }

    }
    public constructor() {
        
    }
    public dispose(): void {
        if(this.txtLoadingView ) {
            this.txtLoadingView.destroy();
            this.txtLoadingView.removeSelf();
            this.txtLoadingView = null;
        } 
        if(this.txtLoadingView.backLayer ) {
            this.txtLoadingView.backLayer.destroy();
            this.txtLoadingView.backLayer.removeSelf();
            this.txtLoadingView.backLayer = null;
        } 
    }
}