const { regClass } = Laya;

@regClass('-1MGS4JRSMOn_RKPUuuV9Q')
export default class SwitchButton extends Laya.Script {

    owner: Laya.Box;
  constructor() {
    super();
  }
  
    
    private btn_bg:Laya.Button;
    private label_left:Laya.Label;
    private label_right:Laya.Label;
    onAwake() {
        


    }
    private _isLeft: boolean = false;
    public get isLeft(): boolean {
        return this._isLeft;
    }
    public set isLeft(value: boolean) {
        this._isLeft = value;
        this.btn_bg = this.owner.getChildByName("btn_bg") as Laya.Button;
        this.label_left = this.owner.getChildByName("label_left") as Laya.Label;
        this.label_right = this.owner.getChildByName("label_right") as Laya.Label;
        if(this.isLeft){
            this.btn_bg.x = 4;
            this.label_left.color = "#213343";
            this.label_right.color = "#52a5e0";

        }else{
            this.btn_bg.x = this.owner.width - this.btn_bg.width - 4;
            this.label_left.color = "#52a5e0";
            this.label_right.color = "#213343";
        }      
    }


}