export class StringTool {
    public constructor() {
        
    }
    		/**
		 * 去除空格和换行符
		 * @param str
		 * @return 
		 */
		public static trim(str:string,vList:any=null):string
		{
			if(!vList)
			{
				vList=[" ","\r","\n","\t",String.fromCharCode(65279)];
			}
			var rst:string;
			var i:number;
			var len:number;
			rst=str;
			len=vList.length;
			for(i=0;i<len;i++)
			{
				rst=StringTool.getReplace(rst,vList[i],"");
			}
			return rst;
		}
        		/**
		 * 替换文本 
		 * @param str
		 * @param oStr
		 * @param nStr
		 * @return 
		 */
		public static getReplace(str:string,oStr:string,nStr:string):string
		{
			if(!str) return "";
//			if(!oStr) return str;
//			if(!nStr) return str;
			var rst:string;
			rst=str.replace(new RegExp(oStr, "g"),nStr);
			return rst;
		}
		public static emptyStrDic:any=
			{
				" ":true,
				"\r":true,
				"\n":true,
				"\t":true
			};
		public static isEmpty(str:string):Boolean
		{
			if(str.length<1) return true;
			return StringTool.emptyStrDic.hasOwnProperty(str);
		}
		public static trimLeft(str:string):string
		{
			var i:number;
			i=0;
		    var len:number;
			len=str.length;
			while(StringTool.isEmpty(str.charAt(i))&&i<len)
			{
				i++;
			}
			if(i<len)
			{
				return str.substr(i);
			}
			return "";
		}
		public static trimRight(str:string):string
		{
			var i:number;
			i=str.length-1;
//			trace(str+" "+str.length);
			while(StringTool.isEmpty(str.charAt(i))&&i>=0)
			{
//				i=str.length-1;
				i--;
			}
//			trace(str.charAt(i));
			var rst:string;
			rst=str.substring(0,i)
//			trace(rst+" "+rst.length);
			if(i>=0)
			{
				return str.substring(0,i+1);
			}
			return "";
		}
		public static trimSide(str:string):string
		{
			var rst:string;
			rst=StringTool.trimLeft(str);
			rst=StringTool.trimRight(rst);
			return rst;
		}

		public static shortStr(str:string, maxLen:number):string{
			if(str.length > maxLen){
				return str.substr(0, maxLen) + '...';
			}
			else{
				return str;
			}
		}
		public static shortHeroStr(str:string, maxLen:number = 10):string{
			if(str.length > maxLen){
				let strs:string[] = str.split("#");
				let str1:string = strs[0];
				let str2:string = strs[1];
				return str1.substr(0, 3) + '...#'+str2;
			}
			else{
				return str;
			}
		}

		//不区分大小写搜索

		static caseInsensitiveSearchWithRegex(source: string, searchTerm: string): boolean {
			return new RegExp(searchTerm, 'i').test(source);
		  }
}