import {FunctionRedStateVo} from "./FunctionRedStateVo";
import {GameResourceManager} from "../../../../common/GameResourceManager";
import {GameSetting} from "../../../GameSetting";
import {EnumFunctionId} from "../../enumData/EnumFunctionId";
import {SystemOpenVO} from "../../../vo/SystemOpenVO";
import {Dictionary} from "../../../../util/Dictionary";
export class SysteSheetModel {
    private _openTypeList: Dictionary;
    public bafflesInfo: any[] = [[330, 740, 9], [294, 113.95, 1], [712.95 - 80, 343.45, 3], [1251.35, 125.95, 7]];
    public m_dicSystemInfoCfg: Dictionary;
    public redHotStateMap: Dictionary = new Dictionary();
    public hasRedState(funId: any): number {
        let vo: FunctionRedStateVo = this.redHotStateMap.get(funId + "");
        if(vo){
            if(vo.redState>0)
                return vo.redState;
        }
        return 0;
    }
    public openSystem: Dictionary = new Dictionary();
    public updateSystems: any[];
    public constructor() {
        this.m_dicSystemInfoCfg = new Dictionary();
        this._openTypeList = new Dictionary();
        this.parseJson();
    }
    public initSystemOpen(_openSystem: any[]): void {
        this.openSystem = new Dictionary();
        for(let j: number = 0; j < _openSystem.length; j++) {
            this.openSystem.set(_openSystem[j], true);
            if(_openSystem[j] + "" == EnumFunctionId.FUN_ID_PHONE.toString() ) {
                
            } 
        }
    }
    public parseJson(): void {
        let json: any = GameResourceManager.instance.getResByURL("config/system_open.json");
        let systemInfo: SystemOpenVO;
        for(let info in json) {
            systemInfo = json[info] as SystemOpenVO;
            if(systemInfo ) {
                this.m_dicSystemInfoCfg.set(systemInfo.functionID, systemInfo);
                let arr: any[] = this._openTypeList.get(systemInfo.open_type);
                if(!arr ) {
                    arr = [];
                    this._openTypeList.set(systemInfo.open_type, arr);
                } 
                arr.push(systemInfo);
            } 
        }
    }
    public getSystemsByType(position: number): any[] {
        let arr: any[] = [];
        for(let i: number = 0; i < this.m_dicSystemInfoCfg.values.length; i++) {
            let value: any = this.m_dicSystemInfoCfg.values[i];
            if(value.positionID == position ) {
                arr.push(value);
            } 
        }
        return arr;
    }
    public getLockFun(openType: number, param: any): SystemOpenVO {
        let arr: any[] = this._openTypeList.get(openType);
        for(let vo of arr) {
            if(vo.parameter1 + "" == param + "" ) {
                return vo
            } 
        }
        return null;
    }
    public getBuildSystem(): void {
        
    }
    public isOpen(sysId: any): boolean {
        if(GameSetting.buildClickState ) {
            return true
        } 
        if(sysId instanceof String ) {
            sysId = parseInt(sysId + "")
        } 
        let open: any = this.openSystem.get(sysId);
        return open != null;
    }
}