import {BaseItem} from "../BaseItem";
export class HeroItem extends BaseItem {


    public id: number; //id	
    public name:string; //名字

    public serialId: number; //serialId|系列ID
    /** 1=Dinoo’s ( 魔龙族）
        2=Baddos (from baddies 恶鬼族 )
        3=Tauros (from taurus 牛妖族)
        4=Spiros (from spirit 精灵族) 
     */
    public race: number; //种族   
    public icon: string; //icon| 图标    (准备弃用)

    public quality: number; //quality|英雄品质,用于图片框
    public level: number; //level|星级

    
    public skillIds:any[];//技能ids



    public maxHp: number; //maxHp|生命	
    public atk: number; //atk|攻击	
    public def: number; //def|防御	
    public mov: number; //mov|速度	
    public crt: number; //crt|暴击率	
    public crtRate: number; //crtRate|爆伤	
    public immunity: number; //immunity|抗暴	
    public dex: number; //dex|命中率	
    public miss: number; //miss|闪避率



}