import RoomPlayerDto from './RoomPlayerDto';


class MatchDto {

    arenaId: number = 0;
    coinType: number = 0;
    roomId: number = 0;
    /** 房间玩家列表 */
    members: Array<RoomPlayerDto> = [];
    /** 比赛剩余倒计时,毫秒 */
    remainderCd: number = 0;
    /** 推送游戏开始的时间 ms */
    startTime: number = 0;
    /** 系统当前时间 ms */
    nowTime: number = 0;

    static PROTOCOL_ID: number = 25010;

    protocolId(): number {
        return MatchDto.PROTOCOL_ID;
    }

    static write(buffer: any, packet: MatchDto | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.arenaId);
        buffer.writeInt(packet.coinType);
        buffer.writePacketList(packet.members, 25011);
        buffer.writeLong(packet.nowTime);
        buffer.writeLong(packet.remainderCd);
        buffer.writeLong(packet.roomId);
        buffer.writeLong(packet.startTime);
    }

    static read(buffer: any): MatchDto | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new MatchDto();
        const result0 = buffer.readInt();
        packet.arenaId = result0;
        const result1 = buffer.readInt();
        packet.coinType = result1;
        const list2 = buffer.readPacketList(25011);
        packet.members = list2;
        const result3 = buffer.readLong();
        packet.nowTime = result3;
        const result4 = buffer.readLong();
        packet.remainderCd = result4;
        const result5 = buffer.readLong();
        packet.roomId = result5;
        const result6 = buffer.readLong();
        packet.startTime = result6;
        return packet;
    }
}

export default MatchDto;
