import {BaseItem} from "../BaseItem";
export class BuffItem extends BaseItem {


    public id: number; //id	
    /**buff名称 */
    public name:string; //buff名称
    /**group|组别 */
    public group:number; //group|组别
    /**addType|叠加类型
        1=叠加(相同的属性，或者相同的效果，叠加在一起，但不超过限制范围，如闪避属性，最高不超过闪避属性上限）
        2=共存（同类型的BUFF，不论有多少个，共同存在，但取效果最大的生效）
        3=覆盖（同类型的BUFF，新获得的生效，已存在的不移除，也不生效，但如果新的BUFF时间到了已失效，老的还存在，则老的继续生效）
        4=替换（同类型的，新的会替换老的，新的生效，旧的移除）
    */
    public addType:number; //addType|叠加类型    
    /**triggerTiming|触发时机
        1=立即生效
    */
    public triggerTiming:number; //triggerTiming|触发时机    
    /**buffEffect|效果
        1=属性提升 格式 1，属性id，+-数值
        [1,1015,5000]

    */
    public buffEffect:any[]; //buffEffect|效果    
    

    /**des|描述 */
    public des: string; //des|描述	   

    /**持续时间ms */
    public time: number; //

}