const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { LayerManager } from "../../common/LayerManager";
import { ItemItem } from "../../global/data/sheetItem/item/ItemItem";
import { ItemVo } from "../../global/vo/pack/ItemVo";
import { KnapsackTipDialogBase } from "./KnapsackTipDialog.generated";

@regClass('or8jNdIhRpSbAG2JBhH-wA')
export class KnapsackTipDialog extends KnapsackTipDialogBase {

    private itemVo:ItemVo;
    // private equipMenu:EquipMenuView;
    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;


    public setOpenData(_data: any) {
        
        if(_data){
            this.itemVo = _data;
            
        }else{
            
        }
    }
    onEnable(): void {


        this.item.dataSource = this.itemVo;

        let itemsStItem:ItemItem = this.itemVo.itemItem;

        this.tf_name.text = GameLanguageMgr.instance.getLanguage(itemsStItem.name);

        this.txt_type.text = GameLanguageMgr.instance.getLanguage('6060') + GameLanguageMgr.instance.getLanguage(itemsStItem.type);

        this.txt_desc.text = GameLanguageMgr.instance.getLanguage(itemsStItem.des);
        this.btn_use.visible = itemsStItem.use!="0";

        this.btn_use.clickHandler = new Laya.Handler(this,this.useItem);

        // Signal.intance.on(GameEvent.EVENT_HERO_CARD_BACK,this,this.onBack);
    }

    async useItem(){
        // await KnapsackNetModel.instance.useItem();
        this.close();
    }


    // public onStageResize():void{
    //     super.onStageResize();
    // }
    
    public removeEvent():void{
        // Signal.intance.off(GameEvent.EVENT_HERO_CARD_BACK,this,this.onBack);
    }
}