
import { GameLanguageMgr } from "../../../common/GameLanguageMgr";
import { LoadingManager } from "../../../common/LoadingManager";
import { Dictionary } from "../../../util/Dictionary";
import { GameEvent } from "../../event/GameEvent";
import { Signal } from "../../event/Signal";
import { EthereumConfig } from "../EthereumConfig";
import { EthereumManager } from "../EthereumManager";
import { ETHUtils } from "../utils/ETHUtils";
import { BlindBoxVo } from "../vo/BlindBoxVo";

export class ETHSeriesHeroMallProxy {
    private mail_contract:any = null;
    private isOpen:boolean;
    //付费抽卡列表
    public mailList:Dictionary;
    

    private static _instance: ETHSeriesHeroMallProxy;
    public static get instance(): ETHSeriesHeroMallProxy {
        if(!ETHSeriesHeroMallProxy._instance){
            ETHSeriesHeroMallProxy._instance = new ETHSeriesHeroMallProxy();
        }
        return ETHSeriesHeroMallProxy._instance;
    }
    public init():void {
        if (typeof (window as any)['ethereum'] === 'undefined') {
            return;
        }   
        this.mail_contract = new (window as any)['web3'].eth.Contract(EthereumConfig.SERIESHEROMALL_ABI, EthereumConfig.SERIESHEROMALL_ADDRESS);
    }

    //页面初始化时调用 等待初始化结束才可进行操作
    async initData(){
        this.mailList = new Dictionary();
        LoadingManager.instance.showLoading(true);
        let len = await this.mail_contract.methods.getPricesLength().call();
        for (let i:number = 0 ;i < len;i++) {

            let iterator = await this.mail_contract.methods.getPriceByIndex(i).call();
            let vo:BlindBoxVo = new BlindBoxVo();
            vo.type = 2;
            vo.idx = i;
            Object.assign(vo,iterator);
            this.mailList.set(vo.idx, vo);
            vo.showPrice = (window as any)['web3'].utils.fromWei(vo.currentPrice + '');
            if(vo.currentToken != EthereumConfig.ADDRESS0){
                let tokenContract = new (window as any)['web3'].eth.Contract(EthereumConfig.TOKEN_ERC20ABI, vo.currentToken);
                vo.tokenContract = tokenContract;
                vo.currentTokenName = await tokenContract.methods.symbol().call();
            }
            else{
                vo.currentTokenName = 'BNB';
            }
        }
        LoadingManager.instance.hideLoading();
    }


    //付费抽卡  参数idx 购买索引 return 1 达到购买限制 2没有库存 3余额不足 4授权失败 5活动或功能未开启 6不在白名单无权购买 999购买失败   0成功 
    async recruit(idx: any){
        LoadingManager.instance.showLoading(true);


        //检测是否在白名单
        let isInWhiteList = await this.mail_contract.methods.accountIsInWhiteList(idx,EthereumManager.instance.walletAddress).call();
        if(!isInWhiteList){
            LoadingManager.instance.hideLoading(); 
            Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[GameLanguageMgr.instance.getLanguage(6055)]); 
            return;
        }

        //检测商店是否开启
        this.isOpen = await this.mail_contract.methods.getIsOpen().call();
        if(!this.isOpen){
            LoadingManager.instance.hideLoading();
            Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[GameLanguageMgr.instance.getLanguage(12)]); 
            return ;
        }

        let vo:BlindBoxVo = this.mailList.get(idx);
        //检测库存是否充足
        if(vo.totalSupply === vo.sellAmount){
            LoadingManager.instance.hideLoading();
            Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[GameLanguageMgr.instance.getLanguage(13)]);
            return;
        }

        //检测余额是否充足
        let price = vo.currentPrice;
        let tokenAddress = vo.currentToken;
        let have = '0';
        let decimals = '18';
        if(tokenAddress != EthereumConfig.ADDRESS0){
            have = await vo.tokenContract.methods.balanceOf(EthereumManager.instance.walletAddress).call();
            decimals = await vo.tokenContract.methods.decimals().call();
        }
        else{
            have = (window as any)['web3'].eth.getBalance(EthereumManager.instance.walletAddress);
        }
        let need = ETHUtils.fromWei(price, decimals, 2);
        if(parseFloat(have) < parseFloat(need)){
            LoadingManager.instance.hideLoading();
            
            Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[GameLanguageMgr.instance.getLanguage(14)]);
            return;
        }

        //如果不是主币需要授权在交易
        if(tokenAddress != EthereumConfig.ADDRESS0){
            let allow = await EthereumManager.instance.allowanceAddressByTokenAddress(EthereumConfig.SERIESHEROMALL_ADDRESS,tokenAddress);
            if(!allow){
                LoadingManager.instance.hideLoading();
                Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[GameLanguageMgr.instance.getLanguage(15)]);
                return;
            }
            await this.mail_contract.methods.recruit(idx).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
                if (err) {
                    LoadingManager.instance.hideLoading();
                    Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[GameLanguageMgr.instance.getLanguage(16)]);
                    return;
                }
            });
        }
        else{

            await this.mail_contract.methods.recruit(idx).send({ from: EthereumManager.instance.walletAddress,value:price}, (err: any, result: any) => {
                if (err) {
                    LoadingManager.instance.hideLoading();
                    Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[GameLanguageMgr.instance.getLanguage(16)]);
                    return;
                }
            });
        }
        
    }
}