const { regClass } = Laya;
import { GameResourceManager } from "../../common/GameResourceManager";
import { SkillItem } from "../../global/data/sheetItem/hero/Skilltem";
import { SkillIconItemBase } from "./SkillIconItem.generated";

@regClass('qJVd2M3_Td-5eIx8PsQpAw')
export class SkillIconItem extends SkillIconItemBase {

    public skillItem: SkillItem;
    public isBig:boolean = false;
    private isAddEvent:boolean = false;

    public constructor() {
        super();
    }

    public set dataSource(value: any) {
        super.dataSource = value;
        this.skillItem = value as SkillItem;
        if(!this.skillItem ) {
            return
        }

        
        this.txt_num.text = '';


        this.img_icon.skin = GameResourceManager.instance.getSkillIcon(this.skillItem.icon, this.isBig);//"https://cdn.bunnyswap.cc/images/items/"+itemsStItem.icon;
        // this.img_box.skin = 'res/common/common_icon/img_' + itemItem.quality + '.png'
        


        
        // this.tf_get.text = GameLanguageMgr.instance.getLanguage("111", this.skillItem.mineralLevel);



    }

    
}