const { regClass } = Laya;
import { AlertType } from "../../common/AlertType";
import { BaseAlertDialogBase } from "./BaseAlertDialog.generated";

@regClass('8G0DBvD7T_2CVupdfQXrBw')
export class BaseAlertDialog extends BaseAlertDialogBase {


    protected _okHandler: Laya.Handler;
    protected _cancelHandler: Laya.Handler;
    public destroyDoCancel: boolean = false;
    private canClose:boolean = true;
    public constructor() {
        super();
        
    }

    onEnable(): void {
        this.on(Laya.Event.CLICK, this, this.onClickEvent);
    }


    public onClickEvent(e: Laya.Event): void {
        let target: Laya.Sprite = e.target;
        if(target.name == "okBtn" ) {
            this._okHandler && this._okHandler.runWith(null);
            this.close();
        } else if(target.name == "cancleBtn" ) {
            this.destroyDoCancel = false;
            this._cancelHandler && this._cancelHandler.runWith(null);
            this.close();
        } 
    }

    public setOpenData(_data: any) {
        
        if(_data){
            this._okHandler = _data.okHandler;
            this._cancelHandler = _data.cancelHandler;
            this.destroyDoCancel = _data.destroyDoCancel;
            if(_data.flag < 0){
                this.cancleBtn.visible = false;
                this.okBtn.visible = false;
                this.canClose = false;
            }
            else if(_data.flag & AlertType.YES && !(_data.flag & AlertType.NO) ) {
                this.okBtn.x = this.width - this.okBtn.width >> 1;
                this.cancleBtn.visible = false;
                this.okBtn.label = "Sure";
            } else if(_data.flag & AlertType.NO && !(_data.flag & AlertType.YES) ) {
                this.okBtn.visible = false;
            } else if(_data.flag & AlertType.YES && _data.flag & AlertType.NO ) {
                
            } 

            if(_data.data && this.alertDesc ) {
                if(_data.data instanceof Array ){
                    this.alertTitle.text = _data.data[0] as string;
                    this.alertDesc.text = _data.data[1] as string;
                    

                }else{
                    this.alertDesc.text = _data.data as string;
                }
                
            } 
            if(_data.initHandler ) {
                _data.initHandler.runWith(this);
            } 
            
        }
    }

    onDisable(): void {
        if(this.destroyDoCancel && this._cancelHandler ) {
            this._cancelHandler.run()
        }
        this._okHandler = null;
        this._cancelHandler = null;
    }

    close(type?: string): void{
        if(this.canClose)
        super.close(type);
    }

    


    
}