import {GameLanguageMgr} from "../../common/GameLanguageMgr";
import {GlobalDataManager} from "../GlobalDataManager";
export class NumberUtil {
    public static toFixed(number: number, decimalPointCount: number): number {
        if(number.toString().indexOf(".") == -1 ) {
            return number;
        } else {
            let str: string = number.toFixed(decimalPointCount);
            return parseFloat(str);
        }
    }
    public static toFixedWithPercent(number: number, decimalPointCount: number, addStr: string = "+"): string {
        let str: string;
        if(number.toString().indexOf(".") == -1 ) {
            str = addStr + number * 100 + "%";
        } else {
            decimalPointCount = decimalPointCount - 2;
            if(decimalPointCount < 0 ) {
                decimalPointCount = 0
            } 
            str = addStr + (number * 100).toFixed(decimalPointCount) + "%";
        }
        return str;
    }
    public static getGoldStr(num: any): string {
        let str: string;
        if(num > 999999 ) {
            str = parseInt(String(num / 100000)) + "m";
        } else {
            str = num;
        }
        return str;
    }
    public static numStringFormat(num: string): string {
        let str: string = num;
        if(GlobalDataManager.instance.m_strLanguage == 2 ) {
            if(num.length > 4 ) {
                str = String(num).replace(/(\d)(?=(\d{3})+$)/g, "$1 ")
            } 
        } else {
            str = String(num).replace(/(\d)(?=(\d{3})+$)/g, "$1,");
        }
        return str;
    }
    public static numSimple(num: string): string {
        let str: string = num;
        let knum: number = parseInt(parseInt(str) / 1000 + "");
        if(knum > 0 ) {
            return knum + "k";
        } 
        return NumberUtil.numStringFormat(str);
    }
    public static numStringFormat2(num: number): string {
        let str: string;
        if(num > 999999 ) {
            str = NumberUtil.getNumStr(num, 1000000, "M");
        } else if(num > 999 ) {
            str = NumberUtil.getNumStr(num, 1000, "K");
        } else {
            return num + "";
        }
        return str;
    }
    public static getNumStr(numI: number, numM: number, lauguage: string): string {
        let str: string = parseInt(Math.floor(numI / numM) + "") + lauguage;
        return str;
    }
    public static getTimeStr(times: number): string {
        let str: string;
        if(times < 3600 ) {
            str = GameLanguageMgr.instance.getLanguage(9024, Math.ceil(times / 60));
        } else if(times < 3600 * 24 ) {
            str = GameLanguageMgr.instance.getLanguage(9025, Math.ceil(times / 3600));
        } else {
            str = GameLanguageMgr.instance.getLanguage(9025, Math.ceil(times / (3600 * 24)));
        }
        return str;
    }
    public static BigInt(value: number): number {
        return 0;
    }
    public static isInteger(obj: number): boolean {
        return obj % 1 === 0;
    }


    static formatNumber(num: number, digits=2): number {
        if (Number.isInteger(num)) {
          // 如果是整数，直接转换为字符串
          return num;
        } else {
          // 如果不是整数，保留两位小数
          return parseFloat( num.toFixed(2) );
        }
      }



    public static filterNumFormat (num:any, digits=2):string {
        if(!num){
            return num;
        }
        if (num < 1E3 && num > 0)
         return parseFloat(num).toFixed(digits);
        if (num === '0.00') return num
        var si = [
          {value: 1, symbol: ""},
        //   {value: 1E3, symbol: "k"},
          {value: 1E6, symbol: "M"},
          {value: 1E9, symbol: "G"},
          {value: 1E12, symbol: "T"},
          {value: 1E15, symbol: "P"},
        ];
        var rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
        var i;
        for (i = si.length - 1; i > 0; i--) {
          if (num >= si[i].value) {
            break;
          }
        }
        let temp = (num / si[i].value).toString();
        if(temp.indexOf('.') > -1){
            if(digits>0)
                temp = temp.substr(0,temp.indexOf('.')+ digits + 1);
            else
                temp = temp.substr(0,temp.indexOf('.')+ digits);
        }
        return temp.replace(rx, "$1") + si[i].symbol;
        // if(!num || isNaN(num)){
        //     return '0'
        // }
        // num = Number(num);
        // if(num > 100000000){
        //     return (num / 1000000000).toFixed(0) + '亿';
        // }
        // else if(num >= 10000){
        //     return (num / 10000).toFixed(0) + '万';
        // }else{
        //     return num;
        // }
      }

      public static fromWei(value:any, decimals:number, point = 0) {
        if (!value) {
          return 0
        }
        let len = value.length - decimals + point
        if (len <= 0) {
          return 0
        }
        let temp = value.toString().slice(0, len)
        if (point) {
          let tt = temp.length - point
          if (tt <= 0) {
            let ttt = '0.'
            for (let index = 0; index < Math.abs(tt); index++) {
              ttt += '0'
            }
            temp = ttt + temp
          } else {
            temp = temp.slice(0, temp.length - point) + '.' + temp.slice(temp.length - point)
          }
        }
        return temp
      }
}