const { regClass } = Laya;
import GameManager from "../../../script/logic/GameManager";
import { LayerManager } from "../../common/LayerManager";
import { ModuleManager } from "../../common/ModuleManager";
import { PlatFormManager } from "../../common/PlatFormManager";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import RoomRankPlayerDto from "../../net/protocol/dto/RoomRankPlayerDto";
import { MatchEndRankViewBase } from "./MatchEndRankView.generated";

@regClass('NlnToPI4TTuxIDhRKLPChg')
export class MatchEndRankView extends MatchEndRankViewBase {

    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_PANEL;
    public m_iPositionType:number = LayerManager.CENTER;

    private myRank:RoomRankPlayerDto;

    onEnable(): void {

        // SoundMgr.instance.playMusicByName(SoundMgr.soundName_bg, ".mp3");
        this.btn_back.on(Laya.Event.CLICK, this, this.goMatchEnd)

    }
    

    public setOpenData(rankList: any[]) {

        this.mcLanList.vScrollBarSkin = "";

        rankList.forEach((rank: RoomRankPlayerDto) => {
            if(rank.id == GlobalDataManager.instance.playerDto.pid){
                // this.myMatchEndRankListItem.dataSource = rank;
                this.myRank = rank;
                return;
            }
            
        });
        // rankList = rankList.concat( rankList.slice());rankList = rankList.concat( rankList.slice());rankList = rankList.concat( rankList.slice());rankList = rankList.concat( rankList.slice());rankList = rankList.concat( rankList.slice());rankList = rankList.concat( rankList.slice());rankList = rankList.concat( rankList.slice());rankList = rankList.concat( rankList.slice());

        this.mcLanList.array = rankList;

        let selectIndex = -1;

        this.mcLanList.selectedIndex = selectIndex;
        
        this.showListEffect();
        if(GameManager.instance.myPlayer.hp>0){
            this.successAni.visible = true;
        }else{
            this.successAni.visible = false;
        }

        //EVENT ::打开比赛排行界面
        PlatFormManager.instance.sendCustumEvent(43);

    }


    private  allItems: any[] = [];

    showListEffect(){
        this.allItems = [];
        
        var len = this.mcLanList.array.length;
        for(var i:number = 0; i<len; i++){
            var _item = this.mcLanList.getCell(i);
            if(_item){
                _item.skewX = .5;
                _item.alpha = 0;
                this.allItems.push(_item);
                Laya.timer.once(100 + i * 500, this, this.startShowItem, [i], false);
            }else{
                console.log("showListEffect _item is null");
            }
        }



        // this.startShowItem();
        
    }

    startShowItem(i:number){
        var item:Laya.Sprite = this.allItems[i];
        if(item == null){
            console.log("no list item now");
            return;
        }
        // item.visible = true;
        item.scaleX = 0;
        item.alpha = 0;
        this.mcLanList.tweenTo(i, 500);
        Laya.Tween.to(item, { scaleX: 1, alpha: 1 }, 100, Laya.Ease.circOut, null, 0, false);

    }

    goMatchEnd(){
        ModuleManager.intance.openModule(ModuleName.MatchEndView, this.myRank);
        this.close();
    }



    onDisable(): void {
        this.removeEvent();
    }

    public removeEvent():void{
        Laya.Tween.clearAll(this);
        Laya.timer.clearAll(this);
        // Signal.intance.off(CreateLoginEvent.INIT_CREATELOGIN_EVENT, this, this.initCreateLoginView);
        // Signal.intance.off(GameEvent.EVENT_CHANGE_HEAD, this, this.onChangeHeadBack);

    }
}