import {GlobalDataManager} from "./GlobalDataManager";
import {ModuleName} from "./ModuleName";
import {GameLanguageMgr} from "../common/GameLanguageMgr";
import {EnumConsumeType} from "./data/enumData/EnumConsumeType";
import {GameEvent} from "./event/GameEvent";
import {Signal} from "./event/Signal";
import {SystemOpenVO} from "./vo/SystemOpenVO";
import {NoticeMgr} from "../module/notice/NoticeMgr";
export class GlobalService {
    private static _instace: GlobalService;
    private _addNewFList: any[] = [];
    private delayShowNewFun: boolean = false;
    public static get instance(): GlobalService {
        if(GlobalService._instace == null ) {
            GlobalService._instace = new GlobalService(new singClass())
        } 
        return GlobalService._instace;
    }
    public constructor(cls: singClass) {
        
    }
    public addNewOpenFun(value: any[]): void {
        this._addNewFList = this._addNewFList.concat(value);
        if(!this.delayShowNewFun ) {
            this.showNewFun();
        } 
    }
    public setDelayShowFun(value: boolean): void {
        if(this.delayShowNewFun != value ) {
            this.delayShowNewFun = value;
            if(!value ) {
                this.showNewFun();
            } 
        } 
    }
    private showNewFun(): void {
        if(this._addNewFList.length > 0 ) {
            let funId: number = this._addNewFList.pop();
            let funVo: SystemOpenVO = GlobalDataManager.instance.systemOpenModel.m_dicSystemInfoCfg.get(funId.toString()) as SystemOpenVO;
            if(!funVo ) {
                this.showNewFun();
            } else if(funVo.if_open.toString() == "1" || funVo.if_pop.toString() != "1" ) {
                this.showNewFun();
            } else {
                this.swithToNewFun(funId);
            }
        } 
    }
    public swithToNewFun(funId: any, needPop: boolean = false): void {
        let funVo: SystemOpenVO = GlobalDataManager.instance.systemOpenModel.m_dicSystemInfoCfg.get(funId) as SystemOpenVO;
        if(!funVo ) {
            return
        } 
        if(funVo.if_open.toString() == "1" ) {
            NoticeMgr.instance.notice("system_open.json-->funId:" + funVo.functionID + "is close");
            return;
        } 
        if(funVo.if_pop.toString() == "1" || needPop ) {
            Signal.intance.event(GameEvent.EVENT_OPEN_MODULE, [ModuleName.NewFunUnLockDialog, funId]);
        } 
    }
    public hasUnlockFunction(functionId: string, showTip: boolean = true): boolean {
        let vo: SystemOpenVO = GlobalDataManager.instance.systemOpenModel.m_dicSystemInfoCfg.get(functionId + "") as SystemOpenVO;
        let bool: boolean = GlobalDataManager.instance.systemOpenModel.isOpen(parseInt(functionId));
        if(!bool && showTip ) {
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(vo.lock_word))
        } 
        return bool;
    }
    public popOutTip(coseType: number): void {
        if(coseType.toString() == EnumConsumeType.TYPE_ENDURANCE.toString() ) {
            Signal.intance.event("open_buy", 2)
        } 
    }
}
class singClass {
    
}