        //     let mapsheetItem:MapSheetItem = SheetDataManager.intance.m_dicMaps.get(playerDto.birthPlace);

        //     this.tf_city.text = GameLanguageMgr.instance.getLanguage(mapsheetItem.name);

        // }else{
        //     this.tf_city.text = "Null";
        // }
        var countryItem:CountryItem = SheetDataManager.intance.m_dicCountry.get(playerDto.country);

