import MatchDto from '../dto/MatchDto';

/** 登录响应 */
class LoginResponse {

    errorCode: number = 0;
    /** 自己的pid */
    pid: number = 0;
    /** 房间信息都在里面 */
    matchDto: MatchDto | null = null;

    static PROTOCOL_ID: number = 1002;

    protocolId(): number {
        return LoginResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: LoginResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.errorCode);
        buffer.writePacket(packet.matchDto, 25010);
        buffer.writeLong(packet.pid);
    }

    static read(buffer: any): LoginResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new LoginResponse();
        const result0 = buffer.readInt();
        packet.errorCode = result0;
        const result1 = buffer.readPacket(25010);
        packet.matchDto = result1;
        const result2 = buffer.readLong();
        packet.pid = result2;
        return packet;
    }
}

export default LoginResponse;
