const { regClass } = Laya;
import { GameResourceManager } from "../../common/GameResourceManager";
import { ModuleManager } from "../../common/ModuleManager";
import { ModuleName } from "../../global/ModuleName";
import { SheetDataManager } from "../../global/SheetDataManager";
import { NumberUtil } from "../../global/util/NumberUtil";
import { ItemVo } from "../../global/vo/pack/ItemVo";
import { PackageData } from "../../global/vo/pack/PackageData";
import { KnapsackItemBase } from "./KnapsackItem.generated";

@regClass('LzxJYKybT_m3CJav3RyDmg')
export class KnapsackItem extends KnapsackItemBase {


    
    public itemVo: ItemVo;
    public showAdd:boolean = false;
    private isAddEvent:boolean = false;

    public constructor() {
        super();
    }

    public createChildren():void{
        super.createChildren();
        // this.visible = false;
    }

    private addEvent(){
        if(!this.isAddEvent){
            // this.on(Laya.Event.CLICK, this, this.toDetail);
            this.isAddEvent = true;
            // Signal.intance.on(HeroEvent.EVENT_HERO_DRAW_UPDATE_PRODUCTION, this, this.updateProduction);
        }

    }


    // private async onRecruit():Promise<void>{
    //     if(this.itemVo.type == 1){
    //         await ETHBlindBoxProxy.instance.recruit(this.itemVo.id); 
    //     }else{
    //         await ETHBlindBoxProxy.instance.recruitItem(this.itemVo.id); 
    //         // await ETHSeriesHeroMallProxy.instance.recruit(this.itemVo.id);

    //     }
               
    // }


    public set dataSource(value: any) {
        super.dataSource = value;
        this.itemVo = value as ItemVo;
        if(!this.itemVo ) {
            return
        }
        this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {

        if(!this.itemVo)return;
        let itemItem = this.itemVo.itemItem;
        if(itemItem){
            if(this.showAdd){
                this.txt_num.text = "" + NumberUtil.formatNumber(this.itemVo.newAddCnt,2);//有小数保留2位
            }else{
                if(this.itemVo.m_iNum){
                    this.txt_num.text = "" + NumberUtil.formatNumber(this.itemVo.m_iNum,2);//有小数保留2位
                }else{
                    this.txt_num.text = "" + NumberUtil.formatNumber(this.itemVo.newAddCnt,2);//有小数保留2位
                }
                
            }
    
            this.img_icon.skin = GameResourceManager.instance.getItemIcon(itemItem.icon);//"https://cdn.bunnyswap.cc/images/items/"+itemsStItem.icon;
            this.img_box.skin = 'gameIcon/items/itemBg/img_' + itemItem.quality + '.png'
        }else{
            this.txt_num.text = '';

    
            this.img_icon.skin = null;//"https://cdn.bunnyswap.cc/images/items/"+itemsStItem.icon;
            this.img_box.skin = 'gameIcon/items/itemBg/img_0.png'
        }
        
        


        
        // this.tf_get.text = GameLanguageMgr.instance.getLanguage("111", this.itemVo.mineralLevel);



    }

    
    private toDetail(){
        if(!this.itemVo || !this.itemVo.needTip){
            return;
        }
        
        if(this.itemVo.type === PackageData.TYPE_Equipment){
            ModuleManager.intance.openModule(ModuleName.EquipDetailDialog, this.itemVo);
        }
        else{
            ModuleManager.intance.openModule(ModuleName.KnapsackTipDialog, this.itemVo);
        }
    }
}