import { LoadingManager } from "../../common/LoadingManager";
import { EthereumConfig } from "../../global/contract/EthereumConfig";
import { EthereumManager } from "../../global/contract/EthereumManager";
import { Signal } from "../../global/event/Signal";
import HttpCmd from "../../net/HttpCmd";
import { Dictionary } from "../../util/Dictionary";
import { HeroPosVo } from "./HeroPosVo";


export class HeroNetModel {
    private static _instance: HeroNetModel;
    public static get instance(): HeroNetModel {
        if(!HeroNetModel._instance ) {
            HeroNetModel._instance = new HeroNetModel()
        } 
        return HeroNetModel._instance;
    }
    private _inited: boolean = false;

    public itemIdForTips:number;

    public posDic:Dictionary;

    public copyPosDic:Dictionary;

    public contract:any = null;

    public constructor() {
        this.init();
    }

    private init(): void {
        if(!this._inited ) {
            this._inited = true;
            this.posDic = new Dictionary(); 
            this.contract = new (window as any)['web3'].eth.Contract(EthereumConfig.HEROFORMATION_ABI, EthereumConfig.HEROFORMATION_ADDRESS)
        } 
    }

    async getHeroPos(){
        let ary = await HttpCmd.requestPost('cybertron-sys-api/api/v1/heroes/positionList',{})
        ary.forEach((element: any) => {
            let vo:HeroPosVo = new HeroPosVo();
            Object.assign(vo,element);
            this.posDic.set(vo.position,vo);
        });
        return this.posDic;
    }

    async setHeroPos(){
        // let pass = false;
        // for (const iterator of this.copyPosDic.values) {
        //     if(parseInt(iterator.heroId))
        //     {
        //         pass = true;
        //         break;
        //     }        
        // }
        // if(!pass){
        //     NoticeMgr.instance.notice('至少上阵一个英雄');
        //     return false;
        // }
        LoadingManager.instance.showLoading(true);

        let only = this.checkOnlyChangePos();
        if(only){
            await HttpCmd.requestPost('cybertron-sys-api/api/v1/heroes/setPosition',this.copyPosDic.values);
            this.posDic = this.copyPosDic;
            LoadingManager.instance.hideLoading();
            Signal.intance.event('SET_HERO_POSTION_SUCCESS')
            return this.posDic;
        }
        else{
            let ary: number[][] = [];
            this.copyPosDic.values.forEach(vo=>{
                if(parseInt(vo.heroId)){
                    ary.push([parseInt(vo.heroId),parseInt(vo.position)]);
                }
            })
    
            await this.contract.methods.formation(ary).send({ from: EthereumManager.instance.walletAddress}, (err: any, result: any) => {
                if (err) {
                    LoadingManager.instance.hideLoading();
                    return false;
                }
            });
        }
    }

    /** 如果只是更改站位  直接调用服务器不走合约 */
    checkOnlyChangePos():boolean{

        let oldA:Array<number> = [];
        let newA:Array<number> = [];
        for (const iterator of this.posDic.values) {
            if(parseInt(iterator.heroId)){
                oldA.push(parseInt(iterator.heroId));
            }
        }
        for (const iterator of this.copyPosDic.values) {
            if(parseInt(iterator.heroId)){
                newA.push(parseInt(iterator.heroId));
            }
        }

        oldA.sort();
        newA.sort();

        return oldA.toString() === newA.toString();
    }
}