    public set dataSource(value: any) {
        super.dataSource = value;
        this.itemVo = value as ItemVo;
        if(!this.itemVo ) {
            return
        }
        // this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {

        if(!this.itemVo)return;
        let itemItem = this.itemVo.itemItem;
        this.knapsackItem.dataSource = this.itemVo;
        if(itemItem){
            this.disabled = false;
        }else{
            this.disabled = true;
        }


    }
    // public set selected(value: boolean) {
    //     super.selected = value;
