    public replacePlaceholder(source: string, args: any[]): string {
        if(source == "" ) {
            return "";
        } 
        let pattern: RegExp = /{(\d+)}/g;
        if(args.length > 0 ) {
            source = source.replace(pattern, function(): string {
                return args[arguments[1]];
            });
        } 
        return this.replaceLanByBR(source);
    }
    public replaceLanByBR(value: string): string {
        let _str: string = value + "";
        // while(_str.indexOf("/n") != -1) {
        //     _str = _str.replace("/n", "<br>");
        // }
        return _str;
    }
    public getLanguage(value: any, ...arg: (string | number)[]): string {
        // console.log("languageId: " + value);
        return this.getLanguage2(value, arg);
    }
    public getLanguage2(value: any, replaceArr: any[]): string {
        if(this._configData && this._configData[value] ) {
            let _str: string = this._configData[value].value as string;
            if(_str && _str != "" ) {
                let str: string = this.replacePlaceholder(_str, replaceArr);
                // str = str.replace(/^"*/g, "");
                // str = str.replace(/"*$/g, "");
                return str;
            } 
        } 
        return this.replacePlaceholder(value + "", replaceArr);
    }
    public getUILang(text: string, arg1: any = null, arg2: any = null, arg3: any = null, arg4: any = null): string {
        text = Laya.Text.langPacks && Laya.Text.langPacks[text] ? Laya.Text.langPacks[text] : text;
        if(arguments.length > 1 ) {
            for(let i: number = 0, n: number = arguments.length; i < n; i++) {
                text = text.replace("{" + i + "}", arguments[i + 1]);
            }
        } 
        return text;
    }

