const { regClass, property } = Laya;
@regClass('w5pr4pHJTMOM3bYxCyc4Ag')
export default class ArrowGuang extends Laya.Script {
  owner: Laya.Image;

  constructor() {
    super();
  }
  onEnable() {
    Laya.timer.frameLoop(1, this, this.onRunRoll);
    
    this.onScaleEnd1();

  }
  onRunRoll(){
    this.owner.rotation++;

  }
  private onScaleEnd1():void{
    Laya.Tween.to(this.owner, {scaleX:1.1, scaleY:1.1, alpha:.8}, 500, null, Laya.Handler.create(this, this.onScaleEnd2));
}

  private onScaleEnd2():void{
      Laya.Tween.to(this.owner, {scaleX:.8, scaleY:.8, alpha:.5}, 500, null, Laya.Handler.create(this, this.onScaleEnd1));
  }


  onDisable(): void {
    Laya.Tween.clearAll(this);
    Laya.timer.clearAll(this);
  }
}
