const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { LayerManager } from "../../common/LayerManager";
import { ModuleManager } from "../../common/ModuleManager";
import { SceneManager } from "../../common/SceneManager";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import { SheetDataManager } from "../../global/SheetDataManager";
import { PlayerDto } from "../../global/contract/vo/PlayerDto";
import { ItemItem } from "../../global/data/sheetItem/item/ItemItem";
import { ArenaItem } from "../../global/data/sheetItem/match/ArenaItem";
import { Quick } from "../../global/util/Quick";
import HttpCmd from "../../net/HttpCmd";
import { MatchModel } from "../match/MatchModel";
import { NoticeMgr } from "../notice/NoticeMgr";
import { CreateRoomDialogBase } from "./CreateRoomDialog.generated";

@regClass('_y9qJpZZRk2p26xdgYPRyA')
export class CreateRoomDialog extends CreateRoomDialogBase {



    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;


    private arenaLv:number;
    private coinType = 3001;
    private arenaItem:ArenaItem;
    public setOpenData(_data: any) {
        
        if(_data){
            this.arenaLv = _data[0];
            // this.coinType = _data[1];
            
            
        }
    }


    onEnable(): void {
        
        // this.btn_close.on(Laya.Event.CLICK, this, this.onClose);
        this.btn_create.on(Laya.Event.CLICK, this, this.onCreate);
    
        this.initContainer();        
    }
    initContainer() {
        let playerDto:PlayerDto = GlobalDataManager.instance.playerDto;
        if(!playerDto)
            return;
        this.tf_roleName.text = playerDto.name;
      
        // this.levelTF.text = playerDto.lv.toString();

        let headId = playerDto.icon;
        this.playerIcon.headSkin = headId;
        this.img_country.skin = GameResourceManager.instance.getCountryIcon(playerDto.country+"");
        // this.img_head.skin = GameResourceManager.instance.getPlayerIcon(headId);



        this.initCombo();
    }


    private coinIds:any[] = [];
    private comboList: Laya.List;


    initCombo(): void {

        
        this.coinIds = SheetDataManager.intance.getGeneralValueById(5);
        var comboData:string[] = [];
        this.coinIds.forEach((coinId: any) => {
            var itemItem:ItemItem = SheetDataManager.intance.m_dicItems.get(coinId);
            comboData.push(GameLanguageMgr.instance.getLanguage(itemItem.name));
        });


        // //通过预制体创建资源
        // Laya.loader.load("scenes/createRole/CoinComboList.lh").then(res => {

        //     let node = res.create();
        //     this.comboList = node;

        //     //修改数据源，这里用模拟数据替代
        //     // this.comboList.array = comboData;


        //     //将自定义的List数据替换给下拉框自己创建的list
        //     this.coinCombo.list = this.comboList;


        //             //模拟list数据
        
        //     this.coinCombo.labels= comboData.join(",");
            
        //     //修改数据源，这里用模拟数据替代
        //     // this.coinCombo.list.array = comboData;
        //     //列表数据长度
        //     console.log("comboData.length: " + comboData.length);
        //     this.coinCombo.list.repeatY = comboData.length;

        //         //将自定义的List数据替换给下拉框自己创建的list

        //     this.coinCombo.list.width = this.coinCombo.width;
        //     this.coinCombo.list.renderHandler = Laya.Handler.create(this, this.onCountryItemRender, null, false);
        //     // this.coinCombo.list.hScrollBarSkin = "";
            this.coinCombo.selectHandler = new Laya.Handler(this, this.onSelected2);
            
        //     this.coinCombo.selectedIndex = 0;
        // });

        this.coinCombo.dataSource = comboData;
        this.coinCombo.selectedIndex = 0;

    }




    onSelected2(index: number): void {

        if(index != -1){
            this.coinType = this.coinIds[index];
            console.log("您选中了 coinType" + this.coinType );
            this.setRoomInfo();
        }
        
    }
    setRoomInfo() {
        this.arenaItem = SheetDataManager.intance.getSheetDataByMultiKey(SheetDataManager.intance.m_dicArenaMulDic,[this.arenaLv,this.coinType]);
            
        // var arenaStr:string = this.arenaLv == 1001 ? "Novice Arena" : "General Arena";

        this.tf_roomName.text = GameLanguageMgr.instance.getLanguage(this.arenaItem.name);

        this.tf_min.text = this.arenaItem.numberMin+"";
        this.tf_max.text = this.arenaItem.numberMax+"";
    }



    async onCreate() {
        if(this.arenaItem.item && this.arenaItem.item.length>1){
            var itemId = this.arenaItem.item[0];
            var needNum = this.arenaItem.item[1];
            var currNum = GlobalDataManager.instance.m_packageData.getCountById(itemId);
            if(currNum < needNum){
                Quick.titickNotEnought(itemId);
                return;
            }
        }
        let pwd:string = this.input_pwd.text;
        var arenaId = this.arenaItem.id;
        if(arenaId){
            // arenaLv = 1:初级 2:中级 3：高级
            let data:any = await HttpCmd.requestPost("wxgame/room/create", {password:pwd, arenaId: arenaId})

            MatchModel.instance.roomDto = data.roomDto;
            if(data.roomDto){
                        
                this.close();
                ModuleManager.intance.openModule(ModuleName.EnterRoomView);

            }else{
                NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
            }
        }
        
                
    }
    
}