import {BaseItem} from "../BaseItem";
export class ShopItem extends BaseItem {


    public id: number; //id	
    public type: number; //商店类型	
    public icon: string; //道具icon	
    public title: string; //标题语言包id		
    public des: string; //描述语言包id		
    public cost: any[]; //消耗【金币id,数量】	
    public dropId: number; //掉落id
    public open: number; //open|开关设置0=关闭，1=开启

}