import { ErrorPopManager } from "../common/ErrorPopManager";
import { EthereumManager } from "../global/contract/EthereumManager";
import { Debug } from "../util/Debug";
import HttpRequest = Laya.HttpRequest;
import { GlobalDataManager } from "../global/GlobalDataManager";
import { Quick } from "../global/util/Quick";
import { GameSetting } from "../global/GameSetting";
import { ModuleName } from "../global/ModuleName";
import { GameEvent } from "../global/event/GameEvent";
import { Signal } from "../global/event/Signal";
export default class HttpCmd{
	public static httpRoot:string = getServer();

	/**
	 * 发送http请求
	 * @param handler 请求回调；
	 * @param action 方法
	 * @param srvArgs 参数；
	*/
	public static requestGet(action:string, srvArgs?:any,needLogin=false,needToken=true):any{
		// console.log(Quick.GetLogTime + " REQUEST_GET action:", action, srvArgs);
		return new Promise((resolve,reject)=>{
			var xhr:HttpRequest = Laya.Pool.getItem("HttpRequest");
			if(!xhr){
				xhr = new HttpRequest();
			}
			xhr.http.timeout = 2000;//设置超时时间；
			xhr.on(Laya.Event.COMPLETE,null,completeHandler);
			xhr.on(Laya.Event.ERROR,null,errorHandler);
			xhr.http.onreadystatechange = ()=>{
					// In local files, status is 0 upon success in Mozilla Firefox
					if(xhr.http.readyState === XMLHttpRequest.DONE) {
					  var status = xhr.http.status;
					  if ((status >= 200 && status < 400)) {
						// The request has been completed successfully
						// console.log(xhr.http.responseText);
					  } else {
						// Oh no! There has been an error with the request!
						reject()
						recover();
					  }
					}
			}
			
			let token = localStorage.getItem('TOX-COOKIE' + EthereumManager.instance.walletAddress);
			let header = ["Content-Type", "application/json"];
			if(token){
				header = [...header,...['Authorization',token]]
			}
			
			//数据拼接
			let realUrl = HttpCmd.httpRoot+ "/" + action+parseArgs(srvArgs);
			if(HttpCmd.httpRoot != "https://api.cybertron.world"){
				Debug.log("REQUEST_GET "+ realUrl, 1);
			}
			xhr.send(realUrl, null,"get",'json',header);
			
			function parseArgs(args:any):string{
				if(typeof args === "string"){
					return args;
				}
				let str = ''
				for(let i in args){
					str += "&" + i+"="+args[i];
				}
				// str += "&t="+Laya.Browser.now();
				return str;
			}
			
			function completeHandler(data:any):void{
				if(data.code === '0'){
					resolve(data.data)
				}else{
					ErrorPopManager.instance.showErrorWord(parseInt(data.code));
					// NoticeMgr.instance.notice('ERROR-CODE=' + data.code);
					reject(data.code)
				}
				recover();
				
			}

			function errorHandler(data:Object,e:Event):void{
				debugger
				//需要一些特殊的处理
				reject(data)
				recover(); 
			}
			
			function recover():void{
				xhr.off(Laya.Event.COMPLETE,null,completeHandler);
				xhr.off(Laya.Event.ERROR,null,errorHandler);
				Laya.Pool.recover("HttpRequest", xhr)
			}
		})
	}

	public static needHeart:boolean = true;




	public static startHeart(){
		// if(GameSetting.Debug != 2)
		Laya.timer.loop(1000, this, this.onHeart);
	}
	static async onHeart() {
		// if(!HttpCmd.needHeart)
		// 	return;
		let startTime = new Date().getTime();
		var data = await this.requestPost("wxgame/sys/ping", {}, true, true);
		
		if(!HttpCmd.needHeart){
			let endTime = new Date().getTime();
			var dTime = endTime - startTime;
			Signal.intance.event(GameEvent.EVENT_Game_Delay, dTime);
		}

		var time = data.time;
		GlobalDataManager.instance.m_serverTime = time;
		var resChange = data.resChange;
		var itemChange = data.itemChange;
		// resChange =  [ [3001, 1 ] ];//测试数据
		if(resChange && resChange.length>0){
			GlobalDataManager.instance.moneyInfoHander(resChange);
		}
		if(itemChange && itemChange.length>0){
			GlobalDataManager.instance.itemsDataUpdataHandler(itemChange, true);
			
		}
	}


	//示例
	// let loginRequest = {address:this.walletAddress,
	//     time:time,
	//     randomHex:randomHex,
	//     r:r,
	//     s:s,
	//     v:v
	// };
	// let res = await HttpCmd.requestPost('cybertron-sys-api/api/v1/auth/login',loginRequest);

    private static seq:number = 0;
	/**
	 * 发送http请求
	 * @param action 方法
	 * @param srvArgs 参数；
	*/
	public static requestPost(action:string, srvArgs?:any,needLogin=false,needToken=true):any{
		// if(GameSetting.isTest){ 
		// 	return [];		
		// }
		let loginToken = localStorage.getItem('TOX-COOKIE'+ EthereumManager.instance.walletAddress);
		if(needLogin && !loginToken){
			return [];
		}
		return new Promise((resolve,reject)=>{

			var xhr:HttpRequest = Laya.Pool.getItem("HttpRequest");
			if(!xhr){
				xhr = new HttpRequest();
			}
			xhr.http.timeout = 10000;//设置超时时间；
			xhr.once(Laya.Event.COMPLETE,null,completeHandler);
			xhr.once(Laya.Event.ERROR,null,errorHandler);
		
			let header: any[] = [];//["Content-Type", "application/json"];
			if(loginToken && needToken){
				// srvArgs.loginToken = loginToken;
				header = [...header,...['Authorization', loginToken]]
			}
			var currSeq = HttpCmd.seq + 1;
			HttpCmd.seq = currSeq;
			if(HttpCmd.httpRoot != "https://api.cybertron.world" && action != "wxgame/sys/ping"){
				console.log(Quick.GetLogTime + " " + currSeq+ " >>> HTTP_POST "+ HttpCmd.httpRoot + "/" + action + " " + JSON.stringify(srvArgs), loginToken);
			}
			//数据拼接
			xhr.send(HttpCmd.httpRoot + "/" + action, srvArgs,"post",'json',header);

			
			async function completeHandler(data:any){
				if(HttpCmd.httpRoot != "https://api.cybertron.world"){
					if(GameSetting.Debug>0 || action != "wxgame/sys/ping"){
						console.log(Quick.GetLogTime + " " + currSeq+ " <<< hTTP_RCV data: " + JSON.stringify(data), 1);
					}
				}
				if(data.errorCode == 0 || data.errorCode === '0'){
					resolve(data)
				}else{
					if(data.errorCode === 10){ //没有出战英雄
						Quick.heroNotEnought();
					}
					else if(data.errorCode === 18){//门票不足
						Quick.titickNotEnought();
					}
					else if(data.errorCode === 26){
						Quick.RemError()
						// ErrorPopManager.instance.showErrorWord(parseInt(data.errorCode));
					}
					else if(data.errorCode === 29){//没有找到角色数据
					}
					else if(data.errorCode === 30){//没有找到角色数据
						//請先招募一個英雄後再綁定邀請碼
						Quick.NoHeroError(ErrorPopManager.instance.get(data.errorCode));
					}
					else if(data.errorCode === 32){//会长不能用邀请码
						Quick.NoHeroError(ErrorPopManager.instance.get(data.errorCode));
					}
					else if(data.errorCode === 2 || data.errorCode === 21){//未授权登录(不在白名单列表中)
						Quick.alertNoAll(ErrorPopManager.instance.get(data.errorCode));
					}
					else if(data.errorCode === 5){//token 登录后过期
						if(GameSetting.Debug == 0){//线上环境
							localStorage.setItem('TOX-COOKIE' + EthereumManager.instance.walletAddress,'')
							var loginData = await EthereumManager.instance.refreshToken();
							if(loginData){
								HttpCmd.requestPost(action, srvArgs,needLogin,needToken);//获取数据后再次请求
							}
						}else{
							localStorage.setItem('TOX-COOKIE' + EthereumManager.instance.walletAddress,'')
							ErrorPopManager.instance.showErrorWord(parseInt(data.errorCode));
						}
					}
					else if(data.errorCode === 17){//token 登录时有错
						if(GameSetting.Debug == 0){//线上环境
							localStorage.setItem('TOX-COOKIE' + EthereumManager.instance.walletAddress,'');
							EthereumManager.instance.signAndLogin();
							ErrorPopManager.instance.showErrorWord(parseInt(data.errorCode));
						}else{
							localStorage.setItem('TOX-COOKIE' + EthereumManager.instance.walletAddress,'')
							ErrorPopManager.instance.showErrorWord(parseInt(data.errorCode));
						}
					}
					else if(data.errorCode === 9){//房间不存在或者已删除
							
							Signal.intance.event(GameEvent.EVENT_CLOSE_MODULE,ModuleName.EnterRoomView);

							Quick.commTipWithYes(ErrorPopManager.instance.get(data.errorCode));

					}
					else{
						ErrorPopManager.instance.showErrorWord(parseInt(data.errorCode));
					}
					reject(data.errorCode)
				}
				recover();
			}

			function errorHandler(data:Object,e:Event):void{
				console.log("errorHandler data: " + data, 1);
				ErrorPopManager.instance.showErrorWord(500);
				reject(data)
				//需要一些特殊的处理
				recover();
			}
			
			function recover():void{
				xhr.off(Laya.Event.COMPLETE,null,completeHandler);
				xhr.off(Laya.Event.ERROR,null,errorHandler);
				Laya.Pool.recover("HttpRequest", xhr)
			}
		})
		
	}
}