        // this.listPanel2.vScrollBarSkin = "";



        this.box_show.visible = false;
        this.box_2.visible = false;

        this.initLuckDrawView();
        // ETHBlindBoxProxy.instance.heroOutputRate();

        let dHeight = (Laya.stage.height - GameSetting.DESIGN_HEIGHT)/2;
        
        // this.btn_back.y -= dHeight;

