    public setTab(value: number) {
        for(let i = 1;i<3;i++){
            if(value == i){
                (this as any)["btn_"+i].getChildByName("selectBox").visible = true;
                (this as any)["btn_"+i].getChildByName("tf_txt").color = "#143f6b";
            }else{
                (this as any)["btn_"+i].getChildByName("selectBox").visible = false;            
                (this as any)["btn_"+i].getChildByName("tf_txt").color = "#7ff1fe";   
            }
        }
    }
    

    private async initLuckDrawView():Promise<void>{
        await ETHBlindBoxProxy.instance.ownerBag();
        
        if(this.destroyed)
            return;
        if(this.type){
            this.onClickTab(this.type);//设置tab Index
        }else{
            this.onClickTab(1);//设置tab Index
        }

    }

    onClickTab(_index:number) {
        if(this.currTab == _index)
            return;
        this.setTab(_index);
        if(_index == 1){

                
                this.box_2.visible = true;
                
                if(this.destroyed){
                    return;
                }
                var shopItems = this.getItemByType(1);

                this.listPanel1.array = shopItems;
                


                // this.listPanel1.visible = true;
                // this.listPanel2.visible = false;

            
        }else{
            this.box_2.visible = true;
                
                if(this.destroyed){
                    return;
                }
                var shopItems = this.getItemByType(2);

                this.listPanel1.dataSource = shopItems;
                // this.listPanel1.visible = false;
                // this.listPanel2.visible = true;

        }
        if(this.hasItem){
            this.listPanel1.selectedIndex = 0;
        }else{
            this.listPanel1.selectedIndex = -1;
        }
        
    }
    private items:any[] = [];
    private hasItem:boolean = false;
    getItemByType(shopType: number) {
        var type = shopType == 1?PackageData.TYPE_Consumption : PackageData.TYPE_Equipment;
        this.items = [];
        this.hasItem = false;

        
        
        var m_dicShopItems = GlobalDataManager.instance.m_packageData.getListByType(type);
        
        for (var i=0;i<30;i++) {
            var _item = m_dicShopItems[i]
            if (_item) {
                if(i == 0){
                    this.hasItem = true;
                }
                this.items.push(_item);                
            }else{
                this.items.push(new ItemVo(0));   
            }
        }

        return this.items;
    }




            // 

        public onSelectItem(selectIndex:number, x:any): void {
            var itemVo = this.items[selectIndex];
            console.log(itemVo);
            if(itemVo){
                let itemItem:ItemItem = itemVo.itemItem;
                this.img_icon.skin = GameResourceManager.instance.getItemIcon(itemItem.icon);
    
                this.tf_name.text = GameLanguageMgr.instance.getLanguage(itemItem.name);
                
        
                // this.txt_type.text = GameLanguageMgr.instance.getLanguage('6060') + GameLanguageMgr.instance.getLanguage(itemItem.type);
        
                this.tf_des.text = GameLanguageMgr.instance.getLanguage(itemItem.des);
                this.box_show.visible = true;
            }else{
                
                this.img_icon.skin = null;    
                this.tf_name.text = "";
                this.tf_des.text = "";
                this.box_show.visible = false;
            }
            
        }



