
/** 账户在别处登录 */
class LoginAgainPush {

    

    static PROTOCOL_ID: number = 1004;

    protocolId(): number {
        return LoginAgainPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: LoginAgainPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        
    }

    static read(buffer: any): LoginAgainPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new LoginAgainPush();
        
        return packet;
    }
}

export default LoginAgainPush;
