import RoomPlayerDto from './RoomPlayerDto';


class RoomDto {

    id: number = 0;
    map: number = 0;
    maxX: number = 0;
    maxY: number = 0;
    players: Array<RoomPlayerDto> = [];

    static PROTOCOL_ID: number = 25009;

    protocolId(): number {
        return RoomDto.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomDto | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeLong(packet.id);
        buffer.writeInt(packet.map);
        buffer.writeInt(packet.maxX);
        buffer.writeInt(packet.maxY);
        buffer.writePacketList(packet.players, 25011);
    }

    static read(buffer: any): RoomDto | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomDto();
        const result0 = buffer.readLong();
        packet.id = result0;
        const result1 = buffer.readInt();
        packet.map = result1;
        const result2 = buffer.readInt();
        packet.maxX = result2;
        const result3 = buffer.readInt();
        packet.maxY = result3;
        const list4 = buffer.readPacketList(25011);
        packet.players = list4;
        return packet;
    }
}

export default RoomDto;
