import { IBaseItem } from "./IBaseItem";
export class BaseItem  implements IBaseItem{
    protected _keys: any[] = [];
    public constructor() {
        
    }
    public init(obj: any): void {
        for(let key in obj) {
            this._keys.push(key);
            let value: any = obj[key];
            if(obj[key] instanceof String ) {
                if(String(obj[key]).indexOf("[[") >= 0 ) {
                    value = JSON.parse(obj[key]);
                } 
            } 
            this[key] = value;
        }
        // if(GameSetting.IsCheckItem ) {
        //     this.checkItems()
        // } 
    }
    protected THIS_ID: string = "id";
    protected ITEM_DROPS: any[];
    protected TABLE_NAME: string;
    public checkItems(): void {
        
    }
    
}