import {GameResourceManager} from "./GameResourceManager";
import {IGameDispose} from "../global/GameInterface/IGameDispose";
export class SceneLoadMgr {
    public constructor() {
        
    }
    private static _instance: SceneLoadMgr;
    public static get instance(): SceneLoadMgr {
        if(!SceneLoadMgr._instance ) {
            SceneLoadMgr._instance = new SceneLoadMgr();
        } 
        return SceneLoadMgr._instance;
    }
    private _mapUrl: string = "";
    private _mapWidth: number = 0;
    private _mapHeight: number = 0;
    private _gridWidth: number = 0;
    private _gridHeight: number = 0;
    private _xPics: number = 0;
    private _yPics: number = 0;
    private _fuzzySprite: Laya.Sprite;
    private _fuzzyName: string = "";
    private _fuzzyCallBack: Laya.Handler;
    public loadScene(mapWidth: number, mapHeight: number, gridWidth: number, gridHeight: number, _mapImg: Laya.Sprite, _mapName: string, _callBack: Laya.Handler): void {
        if(this._fuzzyName != "" && this._fuzzyName != _mapName ) {
            this.dispose();
        } 
        this._mapWidth = mapWidth;
        this._mapHeight = mapHeight;
        this._gridWidth = gridWidth;
        this._gridHeight = gridHeight;
        this._fuzzySprite = _mapImg;
        this._xPics = Math.floor(this._mapWidth / gridWidth);
        this._yPics = Math.floor(this._mapHeight / gridHeight);
        this._fuzzyName = _mapName;
        this._fuzzyCallBack = _callBack;
        let _altlasXmlUrl: string = GameResourceManager.instance.setResURL("scene/subScene/test1/" + _mapName + ".json");
        Laya.loader.load([{url: _altlasXmlUrl, type: Laya.Loader.ATLAS}], Laya.Handler.create(this, this.loadSceneComplete), Laya.Handler.create(this, this.loadProcess, null, false), null, 0, true, null, true);
    }
    private loadProcess(): void {
        
    }
    private loadSceneComplete(): void {
        let _nima: any = Laya.Loader.getAtlas(GameResourceManager.instance.setResURL("scene/subScene/test1/" + this._fuzzyName + ".json"));
        if(this._fuzzySprite ) {
            this._fuzzySprite.graphics.clear();
            let _index: number = 0;
            let sprite: Laya.Sprite = new Laya.Sprite();
            sprite.name = "mapSprite";
            this._fuzzySprite.addChildAt(sprite, 0);
            for(let a: number = 0; a < this._yPics; a++) {
                for(let b: number = 0; b < this._xPics; b++) {
                    let _textures: Laya.Texture = Laya.loader.getRes(_nima[_index]);
                    let img: Laya.Image = new Laya.Image();
                    sprite.addChild(img);
                    img.texture = _textures;
                    img.size(_textures.width, _textures.height);
                    img.pos(b * this._gridWidth, a * this._gridHeight);
                    _index++;
                }
            }
        } 
        if(this._fuzzyCallBack != null ) {
            this._fuzzyCallBack.run();
        } 
    }
    public dispose(): void {
        this._fuzzyCallBack = null;
        if(this._fuzzyName && this._fuzzyName != "" ) {
            Laya.loader.clearRes(GameResourceManager.instance.setResURL("atlas/" + this._fuzzyName + ".json"))
        } 
    }
}