import {AlertType} from "./AlertType";
import {BaseAlert} from "./base/BaseAlert";
export class BaseAlertView extends BaseAlert {
    public _alertView: Laya.View;
    public okBtn: Laya.Button;
    public cancleBtn: Laya.Button;
    public alertTitle: Laya.Label;
    public descTf: Laya.Label;
    private _flag: number;
    private _message: string;
    protected _okHandler: Laya.Handler;
    protected _cancelHandler: Laya.Handler;
    public destroyDoCancel: boolean = false;
    private sp_downLoad: Laya.Box;
    public constructor() {
        super();
    }
    public addEvent(): void {
        this.okBtn = this._alertView.getChildByName("okBtn") as Laya.Button;
        this.cancleBtn = this._alertView.getChildByName("cancleBtn") as Laya.Button;
        this.alertTitle = this._alertView.getChildByName("alertTitle") as Laya.Label;
        this.descTf = this._alertView.getChildByName("alertDesc") as Laya.Label;
        this._alertView.on(Laya.Event.CLICK, this, this.onClickEvent);
    }
    public createUI(): void {
        this.createAlert();
    }
    public createAlert(): void {
        if(!this._alertView ) {
            this._alertView = new Laya.View();
        } 
        this.addChild(this._alertView);
    }
    public onClickEvent(e: Laya.Event): void {
        let target: Laya.Sprite = e.target;
        if(target.name == "okBtn" ) {
            this._okHandler && this._okHandler.runWith(null);
            this.close();
        } else if(target.name == "cancleBtn" ) {
            this.destroyDoCancel = false;
            this._cancelHandler && this._cancelHandler.runWith(null);
            this.close();
        } 
    }
    public alert(flag: number, okHandler: Laya.Handler, cancelHandler: Laya.Handler, data: any = null, destroyDoCancel: boolean = false, initHandler: Laya.Handler = null): void {
        let offsetX: number = 66;
        this._okHandler = okHandler;
        this._cancelHandler = cancelHandler;
        this.destroyDoCancel = destroyDoCancel;
        if(flag & AlertType.YES && !(flag & AlertType.NO) ) {
            this.okBtn.x = this._alertView.width - this.okBtn.width >> 1;
            this.cancleBtn.visible = false;
            this.okBtn.label = "Sure";
        } else if(flag & AlertType.NO && !(flag & AlertType.YES) ) {
            this.okBtn.visible = false;
        } else if(flag & AlertType.YES && flag & AlertType.NO ) {
            
        } 

        if(data && this.descTf ) {
            if(data instanceof Array ){
                this.alertTitle.text = data[0] as string;
                this.descTf.text = data[1] as string;
                

            }else{
                this.descTf.text = data as string;
            }
            
        } 
        if(initHandler ) {
            initHandler.runWith(this);
        } 
    }
    public removeSelf(): Laya.Node {
        if(this.destroyDoCancel && this._cancelHandler ) {
            this._cancelHandler.run()
        } 
        return super.removeSelf();
    }
    public removeEvent(): void {
        super.removeEvent();
        this._okHandler = null;
        this._cancelHandler = null;
    }
}