const { regClass } = Laya;
import { CoinComboBase } from "./CoinCombo.generated";

@regClass('AqEOSlrnRkmI9RUPCJUVqQ')
export class CoinCombo extends CoinComboBase {
    set defaultLabel(value: string) {
        this._defaultLabel = value;
        this._selectedIndex < 0 && (this._button.label = value);
        this._button.labelAlign = "center";
    }

    onEnable(): void {
        this.list = this.comboList;


            
    }

    public set dataSource(value: any) {
        super.dataSource = value;
        var comboData:any[] = value;
        if(!comboData ) {
            return
        }
        // this.addEvent();

        //模拟list数据
        
        this.labels= comboData.join(",");
            
        //修改数据源，这里用模拟数据替代
        // this.list.array = comboData;
        //列表数据长度
        console.log("comboData.length: " + comboData.length);
        this.list.repeatY = comboData.length;

            //将自定义的List数据替换给下拉框自己创建的list

        this.list.width = this.width;
        this.list.renderHandler = Laya.Handler.create(this, this.onCountryItemRender, null, false);
        // this.list.hScrollBarSkin = "";
        // this.selectHandler = new Laya.Handler(this, this.onSelected2);
        
        // this.selectedIndex = 0;

    }

    private onCountryItemRender(cell: any, index: number): void {
        cell.onChange(this.list.selectedIndex == index);
    }


    
    set isOpen(value: boolean) {
        //var Laya = ILaya.Laya;
        console.log("isOpen: " + value);
        if (this._isOpen != value) {
            this._isOpen = value;
            this._button.selected = this._isOpen;
            if (this._isOpen) {
                this._list || this._createList();
                this._listChanged && !this._isCustomList && this.changeList();
                this._itemChanged && this.changeItem();

                var p: Laya.Point = this.localToGlobal(Laya.Point.TEMP.setTo(0, 0));
                var py: number = p.y + this._button.height;
                py = py + this._listHeight <= Laya.stage.height ? py : p.y - this._listHeight < 0 ? py : p.y - this._listHeight;
                this.img_pad.addChild(this._list);
                // this.img_pad.pos(0, 0);
                this.img_pad.pos(p.x, py);
                this.img_pad.visible = true;;
                Laya.stage.addChild(this.img_pad);

                // this._list.pos(p.x, py);
                this.img_pad.zOrder = 1001;

                
                //Laya.stage.once(Event.MOUSE_DOWN, this, removeList);
                //Laya.stage.on(Event.MOUSE_WHEEL, this, _onStageMouseWheel);
                //parent.addChild(_list);
                Laya.stage.once(Laya.Event.MOUSE_DOWN, this, this.removeList);
                Laya.stage.on(Laya.Event.MOUSE_WHEEL, this, this._onStageMouseWheel);
                this._list.selectedIndex = this._selectedIndex;
            } else {
                this._list && this._list.removeSelf();
                this.img_pad && this.img_pad.removeSelf();
            }
        }
    }
}