import {IBaseSheetDataModel} from "./IBaseSheetDataModel";
import {BaseSheetItem} from "./BaseSheetItem";
import {IBaseSheetItem} from "./IBaseSheetItem";
import {GameResourceManager} from "../../../common/GameResourceManager";
import {Dictionary} from "../../../util/Dictionary";
export class BaseSheetDataModel implements IBaseSheetDataModel {
    protected _idKeyName: string = "";
    protected _jsonName: string = "";
    protected _sheetItemCls: any = null;
    protected _idToValueMap: Dictionary;
    protected _items: any[];
    protected _isParse: boolean = false;
    public constructor(jsonName: string, idKeyName: string, sheetItemCls: any) {
        this._idKeyName = idKeyName;
        this._jsonName = jsonName;
        this._sheetItemCls = sheetItemCls;
        this._idToValueMap = new Dictionary();
        this._items = [];
    }
    public parseJson(): void {
        if(this._isParse ) {
            return
        } 
        let json: any = GameResourceManager.instance.getResByURL("config/" + this._jsonName + ".json");
        if(!json ) {
            throw new Error("无效的配置数据" + this._jsonName)
        } 
        for(let obj of json) {
            let item: BaseSheetItem = new this._sheetItemCls();
            item.beFill(obj);
            this._idToValueMap.set(obj[this._idKeyName], item);
            this._items.push(item);
            this.addItemProcess(item);
        }
        this._isParse = true;
    }
    protected addItemProcess(item: BaseSheetItem): void {
        
    }
    public getItemById(id: number): IBaseSheetItem {
        if(this.excuteIsParse() ) {
            if(this._idToValueMap.get(id) ) {
                return this._idToValueMap.get(id);
            } else {
                return null;
            }
        } else {
            return null;
        }
    }
    public getAllItems(): any[] {
        if(this.excuteIsParse() ) {
            return this._items
        } else {
            return null
        }
    }
    protected excuteIsParse(): boolean {
        if(!this._isParse ) {
            
        } 
        return this._isParse;
    }
}