
/** 游戏内GM指令 */
class GmRequest {

    /** 前端请求序列 */
    sequence: number = 0;
    /** 指令，参照指令文档，正式服将禁止使用 */
    param: string = '';

    static PROTOCOL_ID: number = 1005;

    protocolId(): number {
        return GmRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: GmRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.sequence);
        buffer.writeString(packet.param);
    }

    static read(buffer: any): GmRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new GmRequest();
        if (!buffer.isReadable()) {
            return packet;
        }
        const result0 = buffer.readInt();
        packet.sequence = result0;
        if (!buffer.isReadable()) {
            return packet;
        }
        const result1 = buffer.readString();
        packet.param = result1;
        return packet;
    }
}

export default GmRequest;
