import RoomPlayerMovePush from "./protocol/login/RoomPlayerMovePush";
import TargetMovePush from "./protocol/login/TargetMovePush";
import ProtocolManager from "./protocol/ProtocolManager";
export class WebSocketService {
    // public static m_strHttpHost: string = getWSServer() + "/cybertron-websocket/websocket?jwt=";
    public static m_strHttpHost: string = getWSServer();
    public m_socket: Laya.Socket;
    private m_iConnectTimes: number = 0;
    private m_iMaxTimes: number = 10;
    private m_iHeartTime: number = 60000;
    // private cookie:string;
    private isConnect:boolean;
    private isOtherLogin:boolean;


    //心跳
    public static CMD_99999:number=99999;


    // /**网络延迟时间*/
    // private delay:number=0;

    // private lastT:number = -1;




    public constructor() {
        this.m_socket = new Laya.Socket();
        this.m_socket.on(Laya.Event.OPEN, this, this.onSocketOpen);
        this.m_socket.on(Laya.Event.CLOSE, this, this.onSocketClose);
        this.m_socket.on(Laya.Event.MESSAGE, this, this.onMessageReveived);
        this.m_socket.on(Laya.Event.ERROR, this, this.onConnectError);
    }
    private static _instance: WebSocketService;
    public static get instance(): WebSocketService {
        if(WebSocketService._instance == null ) {
            WebSocketService._instance = new WebSocketService()
        } 
        return WebSocketService._instance;
    }

    private _isMatching: boolean = false;
    public get isMatching(): boolean {
        return this._isMatching;
    }
    public set isMatching(value: boolean) {
        this._isMatching = value;
        if(!value){
            this.m_socket.close();//比赛结束 主动关闭socket
        }
    }

    
    public initSocket(): void {

        this.isMatching = true;

        //正式环境启用
        // var ws = "ws://120.53.94.125:10011";
        var ws = MatchModel.instance.roomDto.connectAddr;

        WebSocketService.m_strHttpHost =  ws;
        
        // this.cookie = localStorage.getItem('TOX-COOKIE'+ EthereumManager.instance.walletAddress);
        this.reconnect();
        
    }
    /**
     * 连接成功
     */
    public onSocketOpen(): void {
        this.isConnect = true;
        this.m_iConnectTimes = 0;
        Debug.log("websocket----websocket connected", 1);
        // Laya.timer.loop(30*1000, this, this.sendHeart);
        // HttpNetService.instance.reSendData();
        this.onLogin();


        
    }

    public openId:string;


    private onLogin():void{


        // 发送登录伪代码
        let loginRequest = new LoginRequest();
        loginRequest.roomId = MatchModel.instance.roomDto.roomId;
        loginRequest.loginToken = localStorage.getItem('TOX-COOKIE'+ EthereumManager.instance.walletAddress);
        loginRequest.pid = GlobalDataManager.instance.playerDto.pid;
        this.sendData(loginRequest);
    }


    public onSocketClose(e: any): void {
        console.log("websocket----closed");
        if(!this.isMatching){
            return; //不在比赛中，不用重连
        }
        if(this.isOtherLogin)
            return;
        this.isConnect = false;
        console.log("websocket----closed m_iConnectTimes:" + this.m_iConnectTimes+", m_iMaxTimes: "+this.m_iMaxTimes);
        if(this.m_iConnectTimes <= this.m_iMaxTimes ) {
            Laya.timer.once(1500, this, function(): void {
                WebSocketService.instance.reconnect();
                WebSocketService.instance.m_iConnectTimes++;
            });
            
        } else {
            LoadingManager.instance.hideLoading();
        }
    }
    public onConnectError(e: any): void {
        console.log("websocket----onConnectError e:"+ + e);
        if(!this.isMatching){
            return; //不在比赛中，不用重连
        }
        if(this.isOtherLogin)
            return;
        this.isConnect = false;
        if(this.m_iConnectTimes <= this.m_iMaxTimes ) {
            Laya.timer.once(1500, this, function(): void {
                WebSocketService.instance.reconnect();
                WebSocketService.instance.m_iConnectTimes++;
            });
        } else {
            LoadingManager.instance.hideLoading();
        }
    }

    private reconnect(){
        console.log("websocket----reconnect: " + WebSocketService.m_strHttpHost);
        if(!this.isMatching){
            return; //不在比赛中，不用重连
        }
        
        // this.cookie = localStorage.getItem('TOX-COOKIE'+ EthereumManager.instance.walletAddress);
        // if(!this.cookie)return;
        // this.m_socket.connectByUrl(WebSocketService.m_strHttpHost + this.cookie);
        this.m_socket.connectByUrl(WebSocketService.m_strHttpHost);
        console.log("websocket----connect websocket");
    }

    public onSendMessage(): void {
        // this.m_socket.flush();
    }
    public sendData(packet?:any): void {
        if(!this.isMatching){
            return; //不在比赛中，不用使用
        }
        if(this.isConnect){
            // this.m_socket.send(JSON.stringify(data));

            const buffer = new ByteBuffer();
            buffer.setWriteOffset(4);
            ProtocolManager.write(buffer, packet);
            buffer.writeBoolean(false);
            const writeOffset = buffer.writeOffset;
            buffer.setWriteOffset(0);
            buffer.writeRawInt(writeOffset - 4);
            buffer.setWriteOffset(writeOffset);
            this.m_socket.send(buffer.buffer);


            console.log(Quick.GetLogTime + " " + "websocket ------>>>>>>>> send message PROTOCOL_ID:" + packet.protocolId()+ "，内容：" + JSON.stringify(packet));
        }else{
            Debug.log("socket send error： " + JSON.stringify(packet));
        }
        
    }
    async onMessageReveived(event: any) {

        // let {cmd,code,data} = JSON.parse(message);


        const data = event;
        const buffer = new ByteBuffer();
        buffer.writeBytes(data);
        buffer.setReadOffset(4);
        const packet = ProtocolManager.read(buffer);

        let cmd = packet.protocolId();
        // let message = packet.protocolId();

        // if( WebSocketService.m_strHttpHost.indexOf('wss') === -1){
            // console.log(message);
            
            console.log(Quick.GetLogTime + " " + "websocket ------<<<<<<<< recv PROTOCOL_ID: " + cmd + "，内容：" +  JSON.stringify(packet));
        // }

        let errorCode = packet.errorCode;
        if(errorCode && errorCode > 0){
            
            console.warn("errorCode " + errorCode + ", errorMessage： " + ErrorPopManager.instance.get(errorCode));
//             24;//SOCKET房间不存在或已解散,无法进入战场
// 25;//SOCKET房间人数不足,无法进入战场
            if(errorCode == 24 || errorCode == 25){
                // let loginResponse:LoginResponse = packet;
                this.isMatching = false;
                
                Signal.intance.event(GameEvent.EVENT_CLOSE_MODULE,ModuleName.EnterRoomView);
                if(SceneManager.intance.currSceneName != SceneType.M_SCENE_MATCH){
                    SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN);
                    Quick.commTipWithYes(ErrorPopManager.instance.get(errorCode));
                } //不是比赛界面，才能回主界面
                    


            }
            // ErrorPopManager.instance.showErrorWord(errorCode);
            return;
        }



        Signal.intance.event("msg_" + cmd, [data]);//转发到各个监听器
        switch (cmd) {
            case LoginResponse.PROTOCOL_ID:
                console.log("收到了登录返回");

                    let loginResponse:LoginResponse = packet;
                    // Object.assign(loginResponse, packet);

                    // console.log("姓名： " + loginResponse.name);
                    HttpCmd.needHeart = false;
                    MatchModel.instance.matchDto = loginResponse.matchDto;
                    SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MATCH);
                
                
                // GameManager.instance.loginResponse = loginResponse.matchDto;
                // Laya.Scene.open("scenes/match/Match.ls");
                break;

            case GameStartPush.PROTOCOL_ID:
                console.log("收到比赛开始数据");
                let gameStartPush:GameStartPush = packet;
                ModuleManager.intance.disposeView(ModuleName.MatchReadyView);
                MatchModel.instance.stopAll = false;

                break;

            case RoomPlayerMovePush.PROTOCOL_ID:
                console.log("收到移动数据");
                let roomPlayerMovePush:RoomPlayerMovePush = packet;
                GameManager.instance.onRevMovePush(roomPlayerMovePush);
                // Object.assign(roomPlayerMovePush, packet);

                // WebSocketService.instance.onSetMoveData()
                break;

            case RoomPlayerMoveListPush.PROTOCOL_ID:
                console.log("收到移动数据列表");
                let roomPlayerMoveListPush:RoomPlayerMoveListPush = packet;
                var moveList = roomPlayerMoveListPush.moveList;
                moveList.forEach((roomPlayerMovePush: any) => {
                    GameManager.instance.onRevMovePush(roomPlayerMovePush);
                });
                // let time = new Date().getTime();
                // this.lastT
                
                // Object.assign(roomPlayerMovePush, packet);

                // WebSocketService.instance.onSetMoveData()
                break;
            case TargetMovePush.PROTOCOL_ID:
                console.log("收到推人移动数据");
                let targetMovePush:TargetMovePush = packet;
                GameManager.instance.onRevTargetMovePush(targetMovePush);
                // Object.assign(roomPlayerMovePush, packet);

                // WebSocketService.instance.onSetMoveData()
                break;
            case AttackPush.PROTOCOL_ID:
                console.log("收到伤害数据");
                let attackPush:AttackPush = packet;
                GameManager.instance.onRevAttackPush(attackPush);

                break;
            case AnimationPush.PROTOCOL_ID:
                console.log("收到动画数据");
                let animationPush:AnimationPush = packet;
                GameManager.instance.onRevAnimationPush(animationPush);
                break;
            case RoomPlayerChangePush.PROTOCOL_ID:
                console.log("收到玩家加入或离开数据");
                let roomPlayerChangePush:RoomPlayerChangePush = packet;
                // Object.assign(roomPlayerChangePush, packet);
                GameManager.instance.addPlayer(roomPlayerChangePush);
                
                // WebSocketService.instance.onSetMoveData()
                break;
            case FirstBloodPush.PROTOCOL_ID:
                console.log("收到第一滴血数据");
                let firstBloodPush:FirstBloodPush = packet;
                Signal.intance.event(GameEvent.EVENT_FirstBlood, [firstBloodPush]);
                break;
            case MutiKillPush.PROTOCOL_ID:
                console.log("收到多杀数据");
                let mutiKillPush:MutiKillPush = packet;
                Signal.intance.event(GameEvent.EVENT_MutiKill, [mutiKillPush]);
                break;
            case RoomGameOverPush.PROTOCOL_ID:
                console.log("收到比赛结束");
                let roomGameOverPush:RoomGameOverPush = packet;
                // Object.assign(roomGameOverPush, packet);
                if(MatchModel.instance.matchDto){
                // if(MatchModel.instance.matchDto && MatchModel.instance.matchDto.roomId == roomGameOverPush.roomId){
                    MatchModel.instance.onGoMatchEndRank(roomGameOverPush.rankList, roomGameOverPush.reward);                    
                }
                break;
                
                // WebSocketService.instance.onSetMoveData()
               
            case RoomInfoResponse.PROTOCOL_ID:
                console.log("收到了恢复数据返回");
                let roomInfoResponse:RoomInfoResponse = packet;                
                GameManager.instance.onRevRestorePush(roomInfoResponse.matchDto);
                
                // GameManager.instance.loginResponse = loginResponse.matchDto;
                // Laya.Scene.open("scenes/match/Match.ls");
                break;                

            case Error.PROTOCOL_ID:
                console.log("收到了错误信息返回");
                let error:Error = new Error();
                Object.assign(error, packet);
    
                console.log("errorMessage： " + error.errorMessage);
                ErrorPopManager.instance.showErrByString(error.errorMessage);
                break;

            // case WebSocketService.CMD_10000:
            //     let vo:HeroVo = new HeroVo();
            //     Object.assign(vo,data)
            //     GlobalDataManager.instance.addHero(vo);
            //     LoadingManager.instance.hideLoading();
            //     Signal.intance.event(GameEvent.EVENT_HERO_DRAW_BACK,[[vo]]);
            //     console.log("websocket----------------------------card");
            //     break;
    
            // case WebSocketService.CMD_10002:
            //     let vo1:HeroVo = new HeroVo();
            //     Object.assign(vo1,data)
            //     GlobalDataManager.instance.addHero(vo1);
            //     LoadingManager.instance.hideLoading();

            //     Signal.intance.event(GameEvent.EVENT_HERO_CARD_BACK,[[vo1]]);

            //     KnapsackNetModel.instance.useHeroCard = vo1;
            //     ModuleManager.intance.openModule(ModuleName.UseCardDialog)
            //     console.log("websocket----------------------------card");
            //     break;
            // case WebSocketService.CMD_10001:
            //     let hero:HeroVo = new HeroVo();
            //     Object.assign(hero,data)
            //     GlobalDataManager.instance.updateHero(hero);
            //     console.log("websocket----------------------------hero status");
            //     break;
            // case WebSocketService.CMD_11000:
            //     ETHMailProxy.instance.updateInfo();
            //     console.log("websocket----------------------------email");
            //     break;

            // case WebSocketService.CMD_13001:
            //     Signal.intance.event(WebSocketService.CMD_13001+'');
            //     Signal.intance.event(GameEvent.UPDATE_ASSETS);
            //     console.log("websocket----------------------------union info");
            //     break;

            // case WebSocketService.CMD_13002:
            // case WebSocketService.CMD_13003:
            //     await ETHStakingProxy.instance.initPoolList();
            //     DigModel.instance.isRunning = false;
            //     Signal.intance.event(GameEvent.UPDATE_DIG_CTROL);
            //     LoadingManager.instance.hideLoading();
            //     console.log("websocket----------------------------pool info");
            //     break;
            case LoginAgainPush.PROTOCOL_ID://并且弹窗提示账号已经在其他客户端登录了
                this.isOtherLogin = true;
                Laya.timer.clearAll(this);
                Quick.otherLogin();
                console.log("websocket----------------------------otherLogin");
                break;
            case WebSocketService.CMD_20001://
                let accoutVo = GlobalDataManager.instance.playerDto;
                Object.assign(accoutVo,data)
                Signal.intance.event(GameEvent.ACCOUNTVO_UPDATE);
                console.log("accout----------------------------update");
                break;
            case WebSocketService.CMD_10099:
                LoadingManager.instance.hideLoading();
                Signal.intance.event('SET_HERO_POSTION_SUCCESS');
                console.log("formation----------------------------update");
                break;
            default:
                break;
        }
    }

    private sendHeart(): void {
        if(!this.m_socket.connected)return;
        // this.sendData({cmd:WebSocketService.CMD_99999})
        this.sendData(new Ping());
        // console.log('send---heart')
    }

}