const { regClass } = Laya;
import { GameResourceManager } from "../../common/GameResourceManager";
import { ModuleManager } from "../../common/ModuleManager";
import { ModuleName } from "../../global/ModuleName";
import { PlayerDto } from "../../global/contract/vo/PlayerDto";
import { PackageData } from "../../global/vo/pack/PackageData";
import { PlayerIconBase } from "./PlayerIcon.generated";

@regClass('OPT5t2CoSmuYp2zUJWwUWA')
export class PlayerIcon extends PlayerIconBase {
    public playerDto: PlayerDto;
    public showAdd:boolean = false;
    private isAddEvent:boolean = false;

    public constructor() {
        super();
    }

    public createChildren():void{
        super.createChildren();
        // this.visible = false;
    }

    private addEvent(){
        if(!this.isAddEvent){
            this.on(Laya.Event.CLICK, this, this.toDetail);
            this.isAddEvent = true;
        }

    }



    public set dataSource(value: any) {
        super.dataSource = value;
        this.playerDto = value as PlayerDto;
        if(!this.playerDto ) {
            return
        }
        this.addEvent();
        this.setCurrentOutPut();
    }


    public set headSkin(headId: any) {
        this.img_head.skin = GameResourceManager.instance.getPlayerIcon(headId);
    }

    async setCurrentOutPut() {

        if(!this.playerDto)return;
        let headId = this.playerDto.icon;
        

        this.img_head.skin = GameResourceManager.instance.getPlayerIcon(headId);//"https://cdn.bunnyswap.cc/images/items/"+itemsStItem.icon;

    }

    
    private toDetail(){
        if(!this.playerDto || !this.playerDto.needTip){
            return;
        }
        
        ModuleManager.intance.openModule(ModuleName.RoleInfoDialog);
    }
}