const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { SheetDataManager } from "../../global/SheetDataManager";
import { HeroVo } from "../../global/contract/vo/HeroVo";
import { RoomPlayer } from "../../global/contract/vo/RoomPlayer";
import { UnionItem } from "../../global/data/sheetItem/union/UnionItem";
import { StringTool } from "../../util/StringTool";
import { HeroModel } from "../luckdraw/model/HeroModel";
import { MatchHeroListItemBase } from "./MatchHeroListItem.generated";

@regClass('PiGdVEY9T-K8CCfWLChS0w')
export class MatchHeroListItem extends MatchHeroListItemBase {

    
    public roomPlayer: RoomPlayer;
    public heroVo: HeroVo;
    private isAddEvent:boolean = false;
    public constructor() {
        super();
    }

    public createChildren():void{
        super.createChildren();
        // this.visible = false;
    }

    private addEvent(){
        if(!this.isAddEvent){
            // this.img_chanchu.on(Laya.Event.CLICK, this, this.onDigGet);
            // this.on(Laya.Event.CLICK,this, this.toDetail)
            this.isAddEvent = true;
        }

    }


    public set dataSource(value: any) {
        super.dataSource = value;
        this.roomPlayer = value as RoomPlayer;

        if(!this.roomPlayer ) {
            this.img_back.skin = "res/matchReady/img_jjc_0.png";
            this.box_show.visible = false;
            return
        }

        this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {
        this.img_back.skin = "res/matchReady/img_jjc_1.png";
        // this.heroVo = new HeroVo();
        // this.heroVo.id = this.roomPlayer.heroId;
        // this.img_hero.skin = this.heroVo.simgurl;
        // this.tf_heroName.text = GameLanguageMgr.instance.getLanguage(this.heroVo.name);

        // this.tf_name.text = StringTool.shortHeroStr(this.heroVo.heroItem.name);
        this.tf_name.text = StringTool.shortHeroStr(this.roomPlayer.name);
        // let star = this.heroVo.heroItem.quality + '';
        // HeroModel.instance.setStar(this, star);
        // let status:any = this.heroVo.status;;
        this.box_show.visible = true;
        if(this.destroyed){
            return;
        }

        let headId = this.roomPlayer.icon;
        this.playerIcon.headSkin = headId;
        // this.img_head.skin = GameResourceManager.instance.getPlayerIcon(headId);

        
        this.img_sign.visible = this.roomPlayer.leader > 0;
        var unionItem:UnionItem = SheetDataManager.intance.m_dicUnions.get(this.roomPlayer.unionId);
        if(unionItem){
            this.tf_union.text = GameLanguageMgr.instance.getLanguage(unionItem.simpleName);
        }else{
            this.tf_union.text = "";
        }


    }

    
    toDetail() {
        if(this.heroVo.id){
            // ModuleManager.intance.openModule(ModuleName.HeroDetailDialog,this.heroVo) 
    }
}

}