import {LoadingManager} from "./LoadingManager";
import {GameResourceManager} from "./GameResourceManager";
import {LayerManager} from "./LayerManager";
import BaseDialog from "./base/BaseDialog";
import {IBaseView} from "./base/IBaseView";
import {GameEvent} from "../global/event/GameEvent";
import {Signal} from "../global/event/Signal";
import {Dictionary} from "../util/Dictionary";
export class ModuleManager extends Laya.DialogManager {
    private static _instance: ModuleManager;
    public views: Dictionary = new Dictionary();
    public constructor() {
        super();
        if(ModuleManager._instance ) {
            throw new Error("LayerManager是单例,不可new.");
        } 
        ModuleManager._instance = this;
        Laya.Dialog.manager = this;
    }
    public static get intance(): ModuleManager {
        if(ModuleManager._instance ) {
            return ModuleManager._instance
        } 
        ModuleManager._instance = new ModuleManager();
        return ModuleManager._instance;
    }
    public openModule(moduleName: string, data: any = null): void {
        Signal.intance.event(GameEvent.EVENT_OPEN_MODULE, [moduleName, data]);
    }
    public hasOpenView(moduleName: string): boolean {
        return this.views.get(moduleName) != null;
    }
    public getOpenView(moudleName: string): any {
        return this.views.get(moudleName);
    }
    public disposeView(moudleName: string): any {
        if(this.getOpenView(moudleName) ) {
            let temp: any = this.getOpenView(moudleName);
            if(temp instanceof Array ) {
                for(let i: number = 0; i < temp.length; i++) {
                    (temp[i] as Laya.Dialog).close();
                }
            } else {
                (temp as Laya.Dialog).close();
            }
        } 
    }
    public init(): void {
        Signal.intance.on(GameEvent.EVENT_OPEN_MODULE, this, this.onModulePanel);
        Signal.intance.on(GameEvent.EVENT_CLOSE_MODULE, this, this.disposeView);
    }
    public initMainView(): void {
        
    }
    private m_arrLoadingArr: any[] = [];
    private onModulePanel(name: string, data: any = null): void {
        console.log("ModuleManager.onModulePanel moudleName:" + name);
        if(this.m_arrLoadingArr.indexOf(name) != -1 ) {
            return;
        } 
        if(this.getOpenView(name) ) {
            return;
        } 
        // if(GameSetting.m_bInstantGame == true && name == ModuleName.RechargeDialog ) {
        //     let str: any = GameLanguageMgr.instance.getLanguage(500004);
        //     AlertManager.instance().AlertByType(AlertType.BASEALERTVIEW, str, AlertType.YES);
        //     return;
        // } 
        LoadingManager.instance.showLoading(false);
        this.m_arrLoadingArr.push(name);
        GameResourceManager.instance.loadModuleUrl(name, Laya.Handler.create(this, this.onLoaded, [name, data]));
    }
    private onLoaded(className: string, data: any = null): void {
        let index: number = this.m_arrLoadingArr.indexOf(className);
        if(index != -1 ) {
            this.m_arrLoadingArr.splice(index, 1);
        } 
        let baseView: any;
        // let compClass: any = Laya.ClassUtils.getClass(className);
        // if(compClass ) {
            var moduleView = GameResourceManager.instance.m_objModuleViews[className];
            Laya.loader.load(moduleView).then(res=> {
                
                    baseView = <Laya.Box>res.create();
                    if(data ) {
                        baseView.setOpenData(data);
                    }

                baseView.m_strName = className;
                // } 
                if(baseView instanceof Laya.Box ) {
                    LayerManager.instence.addToLayer(baseView, baseView.m_iLayerType);
                    LayerManager.instence.setPosition(baseView, baseView.m_iPositionType, baseView.m_ioffsetX, baseView.m_ioffsetY);
                    this.addViewToModuleManger(baseView);
                } else if(baseView instanceof Laya.Dialog ) {
                    let baseDialog: Laya.Dialog = baseView as Laya.Dialog;
                    if(baseDialog.m_iLayerType == LayerManager.M_POP){
                        baseDialog.isModal = true;
                        baseDialog.isShowEffect = true;
                        this.open(baseDialog, false, true)

                    }else{
                        baseDialog.isShowEffect = false;
                        this.open(baseDialog)
                    }
                    
                    // LayerManager.instence.addToLayer(baseDialog, baseDialog.m_iLayerType);
                    this.addViewToModuleManger(baseDialog);
                } 
                if(baseView ) {
                    Signal.intance.event(GameEvent.EVENT_MODULE_ADDED, baseView);
                } 
                if(!baseView.hideLoadingBySelf) {
                    LoadingManager.instance.hideLoading()
                } 
            });


            

    }
    private addViewToModuleManger(baseView: any): void {
        let viewName: string = baseView.m_strName;
        if(!this.views.get(viewName) ) {
            this.views.set(viewName, baseView)
        } else {
            if(this.views.get(viewName) instanceof Array ) {
                this.views.get(viewName).push(baseView);
            } else {
                this.views.set(viewName, [this.views.get(viewName), baseView]);
            }
        }
        this.on(Laya.Event.CLOSE, this, this.removeViewFromModuleManger);
        // baseView.on(Laya.Event.CLOSE, this, this.removeViewFromModuleManger);
    }
    public removeViewFromModuleManger(baseView: any): void {
        
        if(!baseView || !this.views) {
            return
        }
        let viewName: string = baseView.m_strName;
        console.log("removeViewFromModuleManger viewName: " + viewName);
        baseView.off(Laya.Event.CLOSE, this, this.removeViewFromModuleManger);
        let viewDetail: any = this.views.get(viewName);
        if(viewDetail instanceof Array ) {
            let delectIndex: number = viewDetail.indexOf(baseView);
            if(delectIndex != -1 ) {
                viewDetail.splice(delectIndex, 1);
                if(viewDetail.length == 0 ) {
                    this.views.remove(viewName)
                } 
            } 
        } else {
            this.views.remove(viewName);
        }
    }
    public closeAll(): void {
        let temps: any[] = [];
        for(let viewDetail of this.views.values) {
            if(viewDetail instanceof Array ) {
                for(let i: number = 0; i < viewDetail.length; i++) {
                    let viewItemDetail: any = viewDetail[i];
                    if(viewItemDetail instanceof BaseDialog ) {
                        temps.push(viewItemDetail)
                    } 
                }
            } else {
                if(viewDetail instanceof BaseDialog ) {
                    temps.push(viewDetail)
                } 
            }
        }
        for(let j: number = 0; j < temps.length; j++) {
            temps[j].dispose();
        }
        temps = [];
        super.closeAll();
    }

        /**
     * 关闭对话框。
     * @param dialog 需要关闭的对象框 <code>Dialog</code> 实例。
     */
        doClose(dialog: Laya.Dialog): void {
            super.doClose(dialog);
            // this._clearDialogEffect(dialog);
            // if (dialog.isShowEffect && dialog.closeEffect != null) dialog.closeEffect.runWith([dialog]);
            // else this.doClose(dialog);
            this.event(Laya.Event.CLOSE, dialog);
        }
}