    public get m_fragrances(): any[] {
        if(this._m_fragrances ) {
            return this._m_fragrances
        } 
        this._m_fragrances = [];
        for(let item of this.m_dicItems.values) {
            if(item && item.child_type + "" == EnumeCatagoryChildType.FRAGRANCE + "" ) {
                this._m_fragrances.push(item);
            } 
        }
        return this._m_fragrances;
    }
