    public getDataModel(dataType: string): BaseSheetDataModel {
        if(!this._modelList.get(dataType) ) {
            switch(dataType) {
                case SheetDataManager.DataType_Items:
                {
                    this._modelList.set(dataType, new ItemSheetDataModel())
                }
                break;
                case SheetDataManager.DataType_CatagoryChildType:
                {
                    this._modelList.set(dataType, new CatagoryChildTypeSheetDataModel())
                }
                break;
                // case SheetDataManager.DataType_BeautyPiece:
                // {
                //     this._modelList.set(dataType, new BeautyPieceSheetDataModel())
                // }
                // break;
                // case SheetDataManager.DataType_BeautyExp:
                // {
                //     this._modelList.set(dataType, new BeautyExpSheetDataModel())
                // }
                // break;
            }
        } 
        return this._modelList.get(dataType);
    }
    public static getArrFromDictionary(dic: Dictionary, key: string, value: any): any[] {
        let arr: any[] = [];
        for(let i: number = 0; i < dic.values.length; i++) {
            if(dic.values[i][key] + "" == value + "" ) {
                arr.push(dic.values[i]);
            } 
        }
        return arr;
    }
    public getArrFromDicItemByKey(key: string, value: any): any[] {
        let arr: any[] = [];
        for(let i: number = 0; i < this.m_dicItems.values.length; i++) {
            if(this.m_dicItems.values[i][key] + "" == value + "" ) {
                arr.push(this.m_dicItems.values[i]);
            } 
        }
        return arr;
    }
    public initConfigObject(name: string, BaseItemClass: any = null, callBack: Laya.Handler = null): SheetDictionary {
        let url: string = "config/" + name + ".json";
        console.log("initConfigObject： url: " + url);
        let data: any = GameResourceManager.instance.getResByURL(url).data;
        let dic: SheetDictionary = new SheetDictionary(data, BaseItemClass);
        return dic;
    }
    public initConfig(name: string, keyArr: any[], BaseItemClass: any = null, clearJson: boolean = false, callBack: Laya.Handler = null): Dictionary {
        let url: string = "config/" + name + ".json";
        console.log("initConfig url: " + url);
        let data: any[] = GameResourceManager.instance.getResByURL(url).data;
        let dic: Dictionary = new Dictionary();
        if(!data ) {
            console.log("ERROR : SheetDataManager -->initConfig()" + "  url:" + url);
            return dic;
        } 
        let item: BaseItem;
        for(let i: number = 0; i < data.length; i++) {
            let key: string = this.compoundKey(data[i], keyArr);
            if(BaseItemClass ) {
                item = new BaseItemClass();
                if( item.init == null ) {
                    console.log("ERROR :BaseItem> SheetDataManager -->initConfig()" + "  url:" + url);
                } 
                item.init(data[i]);
                dic.set(key, item);
                if(callBack ) {
                    callBack.runWith(item)
                } 
            } else {
                dic.set(key, data[i]);
                if(callBack ) {
                    callBack.runWith(data[i])
                } 
            }
        }
        if(clearJson ) {
            let targetURL: string = GameResourceManager.instance.setResURL(url);
            Laya.Loader.clearRes(targetURL);
            
        } 
        return dic;
    }
    private compoundKey(itemData: any, keyArr: any[]): string {
        let key: any = "";
        let len: number = keyArr.length;
        if(len > 1){
            for(let j: number = 0; j < len; j++) {
                key += itemData[keyArr[j]];
                if(j != len - 1 ) {
                    key += "_"
                } 
            }
        }else{
            key = itemData[keyArr[0]];
        }
        
        return key;
    }
    private getCompoundKey(valueArr: any[]): string {
        let key: any = "";
        let len: number = valueArr.length;
        if(len > 1){
            for(let j: number = 0; j < len; j++) {
                key += valueArr[j];
                if(j != len - 1 ) {
                    key += "_"
                } 
            }
        }else{
            key = valueArr[0];
        }
        
        return key;
    }
    public initConfig2(name: string, key1: string, BaseItemClass: any, clearJson: boolean = true): Dictionary {
        let url: string = "config/" + name + ".json";
        let data: any[] = GameResourceManager.instance.getResByURL(url).data;
        let dic: Dictionary = new Dictionary();
        if(!data ) {
            console.log("ERROR : SheetDataManager -->initConfig2()" + "  url:" + url);
            return dic;
        } 
        let item: BaseItem;
        for(let i: number = 0; i < data.length; i++) {
            let key: string = data[i][key1];
            let dataArr: any[];
            if(dic.get(key) == null ) {
                dataArr = [];
                dic.set(key, dataArr);
            } else {
                dataArr = dic.get(key);
            }
            item = new BaseItemClass();
            if( item.init == null ) {
                console.log("ERROR :<BaseItem> SheetDataManager -->initConfig2()" + "  url:" + url);
            } 
            item.init(data[i]);
            dataArr.push(item);
        }
        if(clearJson && name != "items" ) {
            let targetURL: string = GameResourceManager.instance.setResURL(url);
            Laya.Loader.clearRes(targetURL);
        } 
        return dic;
    }
    public getItemsBy(itemType: number, childType: number): any[] {
        let petSheets: any[] = this.getArrFromDicItemByKey("itm_type", itemType);
        let result: any[] = [];
        for(let itemS of petSheets) {
            if(itemS.child_type.toString() == childType.toString() ) {
                result.push(itemS);
            } 
        }
        return result;
    }
    public getSheetDataByMultiKey(m_dic: Dictionary, multiKeyValue: any[]): any {
        let key: string = this.getCompoundKey(multiKeyValue);
        return m_dic.get(key);
    }
