const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { ModuleManager } from "../../common/ModuleManager";
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { ModuleName } from "../../global/ModuleName";
import { HeroVo } from "../../global/contract/vo/HeroVo";
import { StringTool } from "../../util/StringTool";
import { HeroModel } from "../luckdraw/model/HeroModel";
import { HeroListItemBase } from "./HeroListItem.generated";

@regClass('PfeS1F4rQCqM4vXJpBZVWQ')
export class HeroListItem extends HeroListItemBase {



    public heroVo: HeroVo;
    private isAddEvent:boolean = false;
    public constructor() {
        super();
    }

    public createChildren():void{
        super.createChildren();
        // this.visible = false;
    }

    private addEvent(){
        if(!this.isAddEvent){
            // this.img_chanchu.on(Laya.Event.CLICK, this, this.onDigGet);
            this.on(Laya.Event.CLICK,this, this.toDetail)
            this.isAddEvent = true;
        }

    }


    public set dataSource(value: any) {
        super.dataSource = value;
        this.heroVo = value as HeroVo;
        if(!this.heroVo ) {
            this.img_back.skin = GameResourceManager.instance.getHeroBackIcon(0);
            this.box_show.visible = false;
            return
        }
        this.addEvent();
        this.setCurrentOutPut();
    }

    async setCurrentOutPut() {
        //============  添加状态  =============
        var stateData = [];
        if(GlobalDataManager.instance.playerDto.fightHeroPk == this.heroVo.pk){
            stateData.push({"img_state":{"skin":"gameIcon/hero/heroDetail/img_hero_state_1.png"}});
        }
        this.list_state.dataSource = stateData;

        this.img_back.skin = GameResourceManager.instance.getHeroBackIcon(this.heroVo.heroItem.quality);
        this.img_front.skin = GameResourceManager.instance.getHeroFrontIcon(this.heroVo.heroItem.quality);


        this.img_hero.skin = this.heroVo.simgurl;

        this.tf_name.text = StringTool.shortHeroStr(this.heroVo.name);
        let star = this.heroVo.heroItem.level + '';
        HeroModel.instance.setStar(this, star);
        // let status:any = this.heroVo.status;
        this.box_show.visible = true;
        if(this.destroyed){
            return;
        }

        // let lanId:number = 100+ parseInt(status);

        // this.tf_status.text = GameLanguageMgr.instance.getLanguage(lanId);

    }

    
    toDetail() {
        if(this.heroVo.id){
            ModuleManager.intance.openModule(ModuleName.HeroDetailDialog,this.heroVo) 
        }
    }


    
}
