
/** 玩家移动推送 */
class RoomPlayerMovePush {

    pid: number = 0;
    x: number = 0;
    y: number = 0;
    /** 速度 */
    speed: number = 0;
    /** 方向 */
    dire: number = 0;
    time: number = 0;
    /** 区域 */
    area: number = 0;

    static PROTOCOL_ID: number = 1018;

    protocolId(): number {
        return RoomPlayerMovePush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: RoomPlayerMovePush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.area);
        buffer.writeInt(packet.dire);
        buffer.writeLong(packet.pid);
        buffer.writeInt(packet.speed);
        buffer.writeLong(packet.time);
        buffer.writeInt(packet.x);
        buffer.writeInt(packet.y);
    }

    static read(buffer: any): RoomPlayerMovePush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new RoomPlayerMovePush();
        const result0 = buffer.readInt();
        packet.area = result0;
        const result1 = buffer.readInt();
        packet.dire = result1;
        const result2 = buffer.readLong();
        packet.pid = result2;
        const result3 = buffer.readInt();
        packet.speed = result3;
        const result4 = buffer.readLong();
        packet.time = result4;
        const result5 = buffer.readInt();
        packet.x = result5;
        const result6 = buffer.readInt();
        packet.y = result6;
        return packet;
    }
}

export default RoomPlayerMovePush;
