    public heroVoDic: Dictionary;


    public constructor() {
        this.heroVoDic = new Dictionary();


    }
    public initData(data: any[]): void {

        if(!this.heroVoDic ) {
            this.heroVoDic = new Dictionary()
        } 

        let heroVo: HeroVo;
        for(let i: number = 0; i < data.length; i++) {
            heroVo = new HeroVo();
            var heroDto = data[i] as HeroDto;
            heroVo.id = heroDto.id;
                heroVo.lv = heroDto.lv;
                heroVo.pk = heroDto.pk;
                heroVo.color = heroDto.color;
            this.pushItem(heroVo);
        }



        console.log("@@@@@@@@@@@@@@@@@@-----------------------------------------packVo: ");
    }



    public updateData(data: any[]): void {
        console.dir("更新英雄：" + data);

        this.updataItemVoByArr(data);

    }



