    private pushItem(heroVo: HeroVo): void {
        this.heroVoDic.set(heroVo.pk, heroVo);

        
    }
    private removeItem(heroVo: HeroVo): void {
        this.heroVoDic.remove(heroVo.pk);

    }

    public updataItemVoByArr(dataArr: any[], isLvUp: boolean = false): void {

        let updateIds: any[] = [];
        let dispatchGetItem: boolean = false;
        let heroDto: HeroDto;
        for(let i: number = 0; i < dataArr.length; i++) {
            heroDto = dataArr[i];
            let heroVo: HeroVo = this.heroVoDic.get(heroDto.pk);
            if(heroVo == null ) {
                heroVo = new HeroVo();
                heroVo.id = heroDto.id;
                heroVo.pk = heroDto.pk;
                heroVo.lv = heroDto.lv;
                heroVo.color = heroDto.color;
                heroVo.m_bNew = true;
                // if(isLvUp ) {
                    heroVo.newAddCnt = 1;
                // } else {
                //     heroVo.newAddCnt = heroDto.count;
                // }
                // let suitInfo: SuitShowInfo = SuitSheetDataManager.instance.getSuitById(heroVo.m_heroVo.parent_suite);
                // if(suitInfo ) {
                //     SuitSheetDataManager.instance.checkSuitOk(suitInfo);
                // } 
                if(heroVo.newAddCnt > 0 ) {
                    this.pushItem(heroVo);
                } 
            } else {
                // if(isLvUp ) {
                    heroVo.newAddCnt = 1;
                // } else {
                //     heroVo.newAddCnt = heroDto.count - heroVo.m_iNum;
                // }
            }
            heroVo.isLvUp = isLvUp;
            // heroVo.initDataByArr(heroDto);
            updateIds.push(heroVo.pk);
            if(heroVo.m_iNum <= 0 ) {
                this.removeItem(heroVo)
            } 
            if(heroVo.newAddCnt > 0 && GetItemDataModel.instance.needPop ) {
                
                GetItemDataModel.instance.addHeroItem(heroVo);
                dispatchGetItem = true;
            } 
        }
        if((dispatchGetItem && GetItemDataModel.instance.needPop) && GetItemDataModel.instance.autoPop ) {
            GetItemService.instance.startShow();
        } 
        if(updateIds.length > 0 ) {
            Signal.intance.event(GameEvent.PACKAGE_UPDATE_ITEM, [updateIds])
        } 
    }
    public getItemById(typeId: number): HeroVo {
        let heroVo: HeroVo = this.heroVoDic.get(typeId);
        return heroVo;
    }

