import {BaseItem} from "../BaseItem";
export class ItemItem extends BaseItem {

    
    public id: number;
    public name: string;


    public quality: number;
    public type: number;
    

    public subType: number;

    public icon: number;//英雄品质或者道具ID

        
    //instruction|道具介绍
    public des: string;

    
    public realIcon(): string {
        return "https://cdn.bunnyswap.cc/images/items/"+this.icon;
    }





    //tokenId|合约
    private tokenId: string;

    public resourse: any[];


    public max_quality: number;
    public item_maxnum: number;
    public buy_price: any[];
    public sell_price: any[];
    public sell_exp: number;
    public item_detail: number;


    public use:string;




    private _starArr: any[];


    public get starArr(): any[] {
        if(this._starArr ) {
            return this._starArr
        } 
        this._starArr = [];
        if(this.max_quality == 0 ) {
            return this._starArr
        } 
        let rate: number = this.quality / this.max_quality * 5;
        for(let k: number = 1; k <= 5; k++) {
            if(rate >= k ) {
                this._starArr.push(0)
            } else if(k - rate <= .5 ) {
                this._starArr.push(2)
            } else {
                this._starArr.push(1)
            }
        }
        return this._starArr;
    }
}