import {LayerManager} from "../../common/LayerManager";
import { GameResourceManager } from "../../common/GameResourceManager";
import { TxtNoticeView } from "./TxtNoticeView";
export class NoticeMgr {
    private static _instance: NoticeMgr;
    private _list: any[] = [];
    public static Tip_Interval: number = 1000;
    private _isNoticing: boolean = false;
    private _isListener: boolean = false;
    public static get instance(): NoticeMgr {
        if(!NoticeMgr._instance ) {
            NoticeMgr._instance = new NoticeMgr();
        } 
        return NoticeMgr._instance;
    }
    public constructor() {
        
    }
    public notice(value: any): void {
        if(!this._isNoticing ) {
            this._list.push(value);
            this.noticeOne();
        } else {
            if(!this._isListener ) {
                this._isListener = true;
                this._list.push(value);
                Laya.timer.loop(NoticeMgr.Tip_Interval, this, this.onInterVal);
            } 
        }
    }
    private noticeOne(): void {

        var moduleView = GameResourceManager.instance.m_objModuleViews["TxtNoticeView"];
        let noticeView: TxtNoticeView;
        
        Laya.loader.load(moduleView).then(res=> {
            if(!res){
                console.log("noticeOne : " + moduleView + "  res is null");
                return;
            }

            noticeView = Laya.Pool.getItemByCreateFun("txtNotice", res.create, res);
            

            let notice: any = this._list.shift();

            // if("string"==typeof notice||notice instanceof String || !isNaN(notice) ) {
            
            //     noticeView.notice = notice;
            // }
            // noticeView.m_strName = "TxtNoticeView";

        
            noticeView.notice(notice);
            Laya.stage.addChild(noticeView);
            noticeView.x = LayerManager.instence.stageWidth * 0.5 - noticeView.width * 0.5;
            noticeView.y = LayerManager.instence.stageHeight * 0.5 - noticeView.height * 0.5;
            Laya.Tween.to(noticeView, {"y": noticeView.y - 250, "alpha": 0.5}, 1000, null, Laya.Handler.create(this, this.onFinish, [noticeView]), 1000);
            
        });
        
        
    }

    private onFinish(view: TxtNoticeView): void {
        if(view ) {
            view.visible = false;
            view.removeSelf();
        } 
    }
    private onInterVal(): void {
        if(this._list.length >= 1 ) {
            this.noticeOne()
        } else {
            this._isNoticing = false;
            this._isListener = false;
            Laya.timer.clear(this, this.onInterVal);
        }
    }
}