    private initLoad(): void {
        this.onAssetLoaded();
    }
    private onAssetLoaded(): void {
        this.loadingProcess = 0;
        this.m_nProgress = 0;
        Laya.timer.loop(1800, this, this.onLoading, [this.m_nProgress]);
        // this.completeFun();
    }
    private set loadingProcess(_value: number) {
        if(_value > 1 ) {
            _value = 1
        } 
        this.mcProcess.value = _value;
        this.txtProcess.text = Math.floor(_value * 100) + "%";
        var img_guangX = _value * this.mcProcess.width - 70;
        if(img_guangX<0)
        img_guangX = 0;

        this.img_guang.x = img_guangX;
        console.log("loadingProcess: "+  _value+ ", width: " + this.mcProcess.width);
        // this.img_arrow.rotation = -132 + 132 * 2 * _value;
    }

