    private async loginCallBack(): Promise<void> {
        console.log("loginCallBack++++++++++++++++++++++++++++++++");
        // PlatFormManager.instance.sendCustumEvent(3);
        LoadingManager.instance.hideLoading();
        // PreLoadingView.m_iState = 2;

        var playerDto = GlobalDataManager.instance.playerDto;
        if(playerDto.roomId> 0){
            let data:any = await HttpCmd.requestPost("wxgame/room/info", {roomId: playerDto.roomId})
    
            MatchModel.instance.roomDto = data.roomDto;

                if(data.roomDto){  
                    if(playerDto.roomState == 0){
                        ModuleManager.intance.openModule(ModuleName.EnterRoomView);            
                    }else if(playerDto.roomState == 1){
                        WebSocketService.instance.initSocket();
                    }

                }else{
                    NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(135));//error
                }


        }else{
            Quick.logs("进入主界面", 5);
            //初始化 链配置信息 TODO::33进入主界面
            PlatFormManager.instance.sendCustumEvent(33);
            SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN);
        }


        
        // let storyID: string = GameUserInfo.instance.playerName;
        // console.log("storyID ==========" + storyID);
        
        
        HttpCmd.startHeart();
    }
    private m_nProgress: number = 0;
    private onLoading(progress: number): void {
        this.m_nProgress += 0.01;
        if(this.mcProcess ) {
            if(progress > this.mcProcess.value ) {
                this.loadingProcess = progress * 0.4;
            } 
        } 
    }
    private onLoading2(progress: number): void {
        if(this.mcProcess ) {
            this.loadingProcess = 0 + progress * 1.1
        } 
    }
    public removeEvent(): void {
        // this.ani1.stop();
        // Laya.timer.clear(this, this.txtAnima);
        Signal.intance.off(GameEvent.EVENT_BACK_TO_LOGIN, this, this.backToLogin);
        Signal.intance.off(GameEvent.EVENT_LOGIN_GAME_END, this, this.loginGameEnd);
        Signal.intance.off(GameEvent.EVENT_ENTER_MAIN, this, this.loginCallBack);
        
    }



