    private getChainId(){
        let key = '';
        if (typeof (window as any)['ethereum'] === 'undefined') {
            key = '0x38';
        } 
        if(((window as any)['ethereum'].chainId + '').indexOf('0x') == -1){
            key = '0x' + (window as any)['ethereum'].chainId.toString(16);
        }
        else{
            key = (window as any)['ethereum'].chainId;
        }

        if(key !== '0x38' && key !== '0x61'){
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(18));   
        }
        return key;
    }

    async signAndLogin(mustSign:boolean = false) {
        console.log('signAndLogin mustSign: '+ mustSign);

        var needSign:boolean = true;
        let time = new Date().getTime();
        let randomHex = "";
        let r = "";
        let s = "";
        let v = "";
        if(GameSetting.Debug != 0){
            needSign = false;
        }else{
            //如果不是必须签名，并且缓存有数据
            if(!mustSign && localStorage.getItem('TOX-COOKIE' + this.walletAddress) && GameSetting.Debug == 0){
                let loginToken = localStorage.getItem('TOX-COOKIE'+ EthereumManager.instance.walletAddress);
                let _code = localStorage.getItem('TOX-COOKIE-CODE'+ EthereumManager.instance.walletAddress);
                _code = ETHUtils.decode(_code)
                let codes = _code.split("&");
                time = parseFloat(codes[0]);
                randomHex = codes[1];
                r = codes[2];
                s = codes[3];
                v = codes[4];
            
                
                if(loginToken && time && randomHex && r && s && v){
                    // let res =  await HttpCmd.requestPost('wxgame/player/checkJwt',{loginToken},false,false)
                    // if(res.valid){//是否有效,不用再签名
                        needSign = false;
                    // }
                }
                console.log("needSign: " + needSign);
            }
        }
        
        
        
        if(needSign){
            console.log("111");
            const privateKey = generatePrivateKey();
            var addr = privateKey.substring(0, 32);
            // var acc = privateKeyToAccount(privateKey);
            randomHex = toHex(
                addr, 
                { size: 32 } 
              )
            // randomHex = (window as any)['web3'].utils.randomHex(32);
            console.log("222");
            time = new Date().getTime();
            let loginInfoStr = this.walletAddress+randomHex+time;
        
            const shaObj = new jsSHA("SHA-256", "TEXT", { encoding: "UTF8" });
            shaObj.update(loginInfoStr);
            const hash = shaObj.getHash("HEX");
        
            console.log('进入签名',1);
            const signature = await this.walletClient.signMessage({
                account: this.walletAddress!,
                message: hash
                // message: {raw:hash},
              });
            // let signature = await (window as any)['web3'].eth.personal.sign(hash, this.walletAddress, (err: any,res: any) => {if(err){console.log(err,1)}});
            console.log('签名结束',1);
            let signature1 = signature.substring(2);
        
            r = "0x" + signature1.substring(0, 64);
            s = "0x" + signature1.substring(64, 128);
            v = "0x" + signature1.substring(128, 130);
        }

        let loginRequest = {
            debug:GameSetting.Debug, //debug=1的时候 读取address，类似于之前的token字段 ；其他情况 读取address、time、randomHex、r、s、v的数据
            address:this.walletAddress,

            time:time,
            randomHex:randomHex,
            r:r,
            s:s,
            v:v
        };


        
        //TODO::15服务器登录
        PlatFormManager.instance.sendCustumEvent(15);

        let loginDto:LoginDto = await HttpCmd.requestPost('wxgame/player/login',loginRequest, false, false);
        // .catch((err: any)=> {
            
        //     return;
        // })
        if(loginDto){
            console.log("服务器登录成功");
            //TODO::16服务器登录成功
            PlatFormManager.instance.sendCustumEvent(16);
            localStorage.setItem('TOX-COOKIE' + EthereumManager.instance.walletAddress, loginDto.loginToken);
            var _code = ETHUtils.encode(time+"&"+randomHex+"&"+r+"&"+s+"&"+v);
            localStorage.setItem('TOX-COOKIE-CODE' + EthereumManager.instance.walletAddress, _code);
            // var _code = ETHUtils.decode(_code);
            // console.log('解码 _code: ',_code);

            // return loginDto;

        }else{
            ModuleManager.intance.openModule(ModuleName.NoServerView);
            // return null;
        }

        Signal.intance.event(GameEvent.EVENT_LOGIN_GAME_END, loginDto);
        
        // localStorage.setItem('TOX-COOKIE' + EthereumManager.instance.walletAddress, loginDto.loginToken);
        // //发送邀请人
        // let inviterAddress = ETHUtils.getInviteWalletAddress();
        // if(inviterAddress){
        //     HttpCmd.requestPost('cybertron-sys-api/api/v1/invites/save',{inviterAddress:inviterAddress})
        // }
    }


