    async refreshToken() {
        let loginDto:LoginDto;


        // let loginToken = localStorage.getItem('TOX-COOKIE'+ EthereumManager.instance.walletAddress);
        let _code = localStorage.getItem('TOX-COOKIE-CODE'+ EthereumManager.instance.walletAddress);
        _code = ETHUtils.decode(_code)
        let codes = _code.split("&");
        let time = parseFloat(codes[0]);
        let randomHex = codes[1];
        let r = codes[2];
        let s = codes[3];
        let v = codes[4];
    
        // console.log("loginToken: " + loginToken);
        if(time && randomHex && r && s && v){
            let loginRequest = {
                debug:GameSetting.Debug, //debug=1的时候 读取address，类似于之前的token字段 ；其他情况 读取address、time、randomHex、r、s、v的数据
                address:this.walletAddress,
    
                time:time,
                randomHex:randomHex,
                r:r,
                s:s,
                v:v
            };
    

    
            loginDto = await HttpCmd.requestPost('wxgame/player/getJwt',loginRequest, false, false).catch((err: any)=> {
                // ModuleManager.intance.openModule(ModuleName.NoServerView);
                // return;
            })
            if(loginDto){
                localStorage.setItem('TOX-COOKIE' + EthereumManager.instance.walletAddress, loginDto.loginToken);
            }
        }
        return loginDto;

    }
