    //获取TOX资产余额
    async getBalance(){
        return 0;
        let walletItemVo:WalletItemVo =  WalletModel.instance.walletDic.get(EnumWallet.ASSETS_TOX);
        return walletItemVo.ethValue;
        if (typeof (window as any)['ethereum'] === 'undefined') {
            return 0;
        }     
        let account = await this.dappLogin();
        let balance = await this.tokenContract.methods.balanceOf(account).call()
        balance = (window as any)['web3'].utils.fromWei(balance);

        ETHAssetsProxy.instance.updateCwt(balance);//tox 跟其他资源相同处理，防止后期也改为服务器取
        console.log('roleInfo.money1============', balance);
        return balance;
    }

    async allowanceAddress(address: string){
        let allowance = await this.tokenContract.methods.allowance(this.walletAddress, address).call();
        if (allowance == "0") {
            console.log('ERC20 allowance!');

            await this.tokenContract.methods.approve(address, '100000000000000000000000000000' + '000000000000000000').send({ from: this.walletAddress}, (err: any, result: any) => {
                if (err) {
                    LoadingManager.instance.hideLoading();
                    return false;
                }
            });
            return true;
        }
        else{
            return true;
        }
    }

    async allowanceAddressByTokenAddress(contractAddress: string,tokenAddress: string){
        let tToken = new (window as any)['web3'].eth.Contract(EthereumConfig.TOKEN_ERC20ABI, tokenAddress)
        let allowance = await tToken.methods.allowance(this.walletAddress, contractAddress).call();
        if (allowance == "0") {
            console.log('ERC20 allowance!');

            await tToken.methods.approve(contractAddress, '100000000000000000000000000000' + '000000000000000000').send({ from: this.walletAddress}, (err: any, result: any) => {
                if (err) {
                    LoadingManager.instance.hideLoading();
                    return false;
                }
            });
            return true;
        }
        else{
            return true;
        }
    }

