    //获取ERC20 资产余额
    async getERC20TokenBalance(address: number | String){
        let tokenC = new (window as any)['web3'].eth.Contract(EthereumConfig.TOKEN_ERC20ABI, address)
        let balance = await tokenC.methods.balanceOf(this.walletAddress).call()
        balance = (window as any)['web3'].utils.fromWei(balance);
        return balance;  
    }
    
    //查询英雄状态  0空闲  1上架在出售  2在挖矿中
    async getHeroStatus(tokenId:string){
        return await this.hero_contract.methods.getStatus(parseInt(tokenId)).call();
    }

    async checkHeroIsBuzy(tokenId:string){
        let status = await this.getHeroStatus(tokenId);
        if(parseInt(status)){
            NoticeMgr.instance.notice(GameLanguageMgr.instance.getLanguage(19))
            return true;
        }
        return false;
    }


    






