
/** 攻击返回 */
class AttackResponse {

    errorCode: number = 0;

    static PROTOCOL_ID: number = 1022;

    protocolId(): number {
        return AttackResponse.PROTOCOL_ID;
    }

    static write(buffer: any, packet: AttackResponse | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.errorCode);
    }

    static read(buffer: any): AttackResponse | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new AttackResponse();
        const result0 = buffer.readInt();
        packet.errorCode = result0;
        return packet;
    }
}

export default AttackResponse;
