const { regClass } = Laya;
import { GlobalDataManager } from "../../global/GlobalDataManager";
import { EnumConsumeType } from "../../global/data/enumData/EnumConsumeType";
import { GameEvent } from "../../global/event/GameEvent";
import { Signal } from "../../global/event/Signal";
import { NumberUtil } from "../../global/util/NumberUtil";
import { WalletEvent } from "../wallet/event/WalletEvent";
import { CoinsBoxBase } from "./CoinsBox.generated";

@regClass('7P9MlqipSaG6QoKpQowDbQ')
export class CoinsBox extends CoinsBoxBase {





    onEnable(): void {
        this.updateLocalAssets();
        
        Signal.intance.on(GameEvent.ROLE_INFO_CHANGE, this, this.updateLocalAssets);
        Signal.intance.on(WalletEvent.UPDATE_LOCAL_WALLET_LIST_EVENT, this, this.updateLocalAssets);
        

    }
    


    onDisable(): void {

        
        Signal.intance.off(GameEvent.ROLE_INFO_CHANGE, this, this.updateLocalAssets);
        Signal.intance.off(WalletEvent.UPDATE_LOCAL_WALLET_LIST_EVENT, this, this.updateLocalAssets);
    }







        /**
     * 我的资产：My Assets
        BTC 8位小数: 9.12345678
        ETH 4位小数：999.1234
        TOX 2位小数：999,999.12
        USDT 2位小数：999,999.12
        积分：0位小数：999,999
     */

        private updateLocalAssets(): void {

            this.tf_3001.text = NumberUtil.filterNumFormat(GlobalDataManager.instance.getConsumeValue(EnumConsumeType.TYPE_TOX),2);
            this.tf_3006.text = NumberUtil.filterNumFormat(GlobalDataManager.instance.getConsumeValue(EnumConsumeType.TYPE_BTC),8);
            this.tf_3007.text = NumberUtil.filterNumFormat(GlobalDataManager.instance.getConsumeValue(EnumConsumeType.TYPE_ETH),4);
            this.tf_3004.text = NumberUtil.filterNumFormat(GlobalDataManager.instance.getConsumeValue(EnumConsumeType.TYPE_USDT),2);
            this.tf_3005.text = NumberUtil.filterNumFormat(GlobalDataManager.instance.getConsumeValue(EnumConsumeType.TYPE_GOLD),0);
    
    
    
            // this.tf_10000.text = NumberUtil.filterNumFormat(WalletModel.instance.getValue(EnumWallet.ASSETS_TOX),0);
            // this.tf_10001.text = NumberUtil.filterNumFormat(WalletModel.instance.getValue(EnumWallet.ASSETS_ARCANITE), 0);
            // this.tf_10002.text = NumberUtil.filterNumFormat(WalletModel.instance.getValue(EnumWallet.ASSETS_CRYSTAL), 0);
            // this.tf_10003.text = NumberUtil.filterNumFormat(WalletModel.instance.getValue(EnumWallet.ASSETS_MITHRIL), 0);
            // this.tf_10004.text = NumberUtil.filterNumFormat(WalletModel.instance.getValue(EnumWallet.ASSETS_REDSTONE), 0);
            // this.tf_10005.text = NumberUtil.filterNumFormat(WalletModel.instance.getValue(EnumWallet.ASSETS_TRUEIRON), 0);
        
        }

        

}