import { ETHBlindBoxProxy } from "../../global/contract/service/ETHBlindBoxProxy";
import { HeroVo } from "../../global/contract/vo/HeroVo";
import { Signal } from "../../global/event/Signal";
import { HeroEvent } from "../luckdraw/event/HeroEvent";
import { MyHeroSceneBase } from "./MyHeroScene.generated";
import { Hero1Event } from "./event/Hero1Event";

@regClass('eEFn-xcRTXagUEZFDdSujw')
export class MyHeroScene extends MyHeroSceneBase {

    public m_iLayerType: number;
    public m_iPositionType: number;
    onAwake(): void {
        this.m_iLayerType = LayerManager.M_POP;
        this.m_iPositionType = LayerManager.UP;
        // this.m_ioffsetX = 20;
        // if(GameSetting.m_bIsIphoneX ) {
        //     this.m_ioffsetY = GameSetting.IPHONEX_TOP;
        // } 
    }
    
    onEnable(): void {
        


        this.list_hero.vScrollBarSkin = "";
        // SoundMgr.instance.playMusicByName(SoundMgr.soundName_bg, ".mp3");

        if(this.btn_back ) {
            this.btn_back.on(Laya.Event.CLICK, this, this.setToMainScene)
        }
        Signal.intance.on(Hero1Event.UPDATE_HERO_EVENT, this, this.createHeadList);

        this.initMyHeroView();

        // this.btn_formation.clickHandler = new Laya.Handler(this,this.handleClickBtn,[this.btn_formation])

        // this.dHeight = (Laya.stage.height - GameSetting.DESIGN_HEIGHT)/2;
        
        // this.btn_back.y -= this.dHeight;

        this.initContainer();
    }

    private setToMainScene():void{
        SceneManager.intance.setCurrentScene(SceneType.M_SCENE_MAIN);
    }
    
    private async initMyHeroView():Promise<void>{
        // await ETHBlindBoxProxy.instance.initData();

    }

    private onListEvent(e: Laya.Event, index: number): void {
        switch(e.type) {
            case Laya.Event.CLICK:
            {
                this.list_hero.selectedIndex = index;
                this.handleSelectHero(index)
            }
            break;
        }
    }

    private onRenderList(cell: Laya.Box, index: number): void {
        let data = cell.dataSource as HeroVo;
        // let selected = cell.getChildByName("selected") as Laya.Image;
        // selected.visible = this.list_hero.selectedIndex == index;
        // let emojiImg = cell.getChildByName("img_emoji") as Laya.Image;
        // emojiImg.skin = GameResourceManager.instance.setResURL ("gameIcon/head/headRect/"+data.head+".png");
    }

    private initContainer(): void {
        this.list_hero.visible = false;
        this.list_hero.vScrollBarSkin = "";
        this.list_hero.mouseHandler = new Laya.Handler(this, this.onListEvent);
        this.list_hero.renderHandler = new Laya.Handler(this, this.onRenderList);
        
        this.createHeadList();
    }
    private handleSelectHero(index: number): void {
        let vo: HeroVo = this.list_hero.selectedItem;
        
    }
    private async createHeadList(): Promise<void> {

        await ETHBlindBoxProxy.instance.ownerHero();
        if(!this){
            return;
        }
        let heroVos:any[] = GlobalDataManager.instance.m_heroData.heroVoDic.values;
        if(heroVos && heroVos.length>9){
            this.list_hero.array = heroVos;
            this.list_hero.visible = true;
            this.tf_noData.visible = false;
            let selectIndex = -1;

            this.list_hero.selectedIndex = selectIndex;

        }else{
            let heroVos1:any[] = [];
            for(var i = 0;i<10;i++){
                var heroVo:HeroVo = heroVos[i];
                if(heroVo){
                    heroVos1.push(heroVo);
                }else{
                    heroVos1.push(null);
                }

            }

            this.list_hero.array = heroVos1;
            this.list_hero.visible = true;


            this.tf_noData.visible = false;
        }

    }

    private handleClickBtn(currentTarget: Laya.Button): void {
        switch(currentTarget) {

            // case this.btn_formation:
            // {
            //     console.log('HeroFormationDialog');
            //     ModuleManager.intance.openModule(ModuleName.HeroFormationDialog);
            // }
            // break;

        }
    }

    onDisable(): void {
        Signal.intance.off(Hero1Event.UPDATE_HERO_EVENT, this, this.createHeadList);
    }


}