
/** 服务器断开推送 */
class DisconnectPush {

    

    static PROTOCOL_ID: number = 1010;

    protocolId(): number {
        return DisconnectPush.PROTOCOL_ID;
    }

    static write(buffer: any, packet: DisconnectPush | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        
    }

    static read(buffer: any): DisconnectPush | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new DisconnectPush();
        
        return packet;
    }
}

export default DisconnectPush;
