
/** 攻击 */
class AttackRequest {

    /** 目标pid */
    targetId: number = 0;
    /** 攻击类型,1=普攻，2=技能 */
    attackType: number = 0;
    /** 技能id */
    skillId: number = 0;

    static PROTOCOL_ID: number = 1021;

    protocolId(): number {
        return AttackRequest.PROTOCOL_ID;
    }

    static write(buffer: any, packet: AttackRequest | null) {
        if (buffer.writePacketFlag(packet) || packet == null) {
            return;
        }
        buffer.writeInt(packet.attackType);
        buffer.writeInt(packet.skillId);
        buffer.writeLong(packet.targetId);
    }

    static read(buffer: any): AttackRequest | null {
        if (!buffer.readBoolean()) {
            return null;
        }
        const packet = new AttackRequest();
        const result0 = buffer.readInt();
        packet.attackType = result0;
        const result1 = buffer.readInt();
        packet.skillId = result1;
        const result2 = buffer.readLong();
        packet.targetId = result2;
        return packet;
    }
}

export default AttackRequest;
