const { regClass } = Laya;
import { GameLanguageMgr } from "../../common/GameLanguageMgr";
import { GameResourceManager } from "../../common/GameResourceManager";
import { CountryItem } from "../../global/vo/CountryItem";
import { CountryListItemBase } from "./CountryListItem.generated";

@regClass('LJ8PUf7kRCGUr4eT8Mhy3g')
export class CountryListItem extends CountryListItemBase {
    isAddEvent: boolean;

    private addEvent(){
        // if(!this.isAddEvent){
            
        //     // this.img_chanchu.on(Laya.Event.CLICK, this, this.onDigGet);
        //     this.selectBox.on(Laya.Event.CHANGE,this, this.onChange);
        //     this.isAddEvent = true;
        // }

    }
    public set dataSource(value: any) {
        super.dataSource = value;
        // console.log("value: " + value);
        var countryItem:CountryItem = value as CountryItem;
        if(!countryItem ) {
            return
        }
        this.addEvent();


        this.img_icon.skin = GameResourceManager.instance.getCountryIcon(countryItem.id);
        this.tf_name.text = GameLanguageMgr.instance.getLanguage(countryItem.name);


    }

    onChange(isSelect:boolean){
        if(isSelect){
            this.selectImg.visible = true;
            this.tf_name.color = "#ffffff";
        }else{
            this.tf_name.color = "#357cc0";
            this.selectImg.visible = false;
        }
    }

    onDisable(): void {
        // this.selectBox.off(Laya.Event.CHANGE,this, this.onChange);
    }
}