const { regClass } = Laya;
import { LayerManager } from "../../common/LayerManager";
import { SoundMgr } from "../../common/SoundMgr";
import { ModuleName } from "../../global/ModuleName";
import { HeroVo } from "../../global/contract/vo/HeroVo";
import { GameEvent } from "../../global/event/GameEvent";
import { Signal } from "../../global/event/Signal";
import { HeroListItem } from "../hero/HeroListItem";
import { GetItemDataModel } from "../item/model/GetItemDataModel";
import { DrawHeroItem } from "./DrawHeroItem";
import { HeroGetDialogBase } from "./HeroGetDialog.generated";

@regClass('mrqJhWahTxuTjeSGIs559Q')
export class HeroGetDialog extends HeroGetDialogBase {


    public m_strName: string = "我是OpenSceneRuntime";
    public m_iLayerType:number = LayerManager.M_POP;
    public m_iPositionType:number = LayerManager.CENTER;

    public constructor(_data: any) {
        super();

    }



    onEnable(): void {
        
        this.initContainer();
			SoundMgr.instance.playSoundByName(SoundMgr.soundName32);
    }
    
    private initContainer():void
    {
        
        var data:HeroVo = GetItemDataModel.instance.getOneItem();
        if(data)
        {

            this.showItem(data);

        }

    }
    
    /**
     * 显示道具 
     * @param itemVO
     */		
    private showItem(data:HeroVo):void
    {
        let drawHeroItem:HeroListItem = this.heroListItem as HeroListItem;
        // let drawHeroItem:DrawHeroItem = this.box_card as DrawHeroItem;
                drawHeroItem.dataSource = data;
        

    }

    onDisable(): void {

        Signal.intance.event(GameEvent.EVENT_CHECK_REMAIN,ModuleName.HeroGetDialog);
    }



    
}